.on-board {
	position: relative;
	.on_board_lang {
		font-family: "Poppins", "Arial", sans-serif;
		padding: 0 25px;
		width: 100%;
		background-image: url("/assets/images/vibo/bg1.svg"),
			url("/assets/images/vibo/bg2.svg"),
			url("/assets/images/vibo/bg3.svg");
		background-repeat: no-repeat;
		background-position: 90% 5%, 0% 35%, 100% 100%;
		.title {
			margin-top: 100px;
			margin-bottom: 0;
		}
		.logo {
			margin: 10px 0;
		}
		.languages {
			margin-top: 200px;
		}
		.language {
			display: flex;
			align-items: center;
			padding: 15px;
			border: 1px solid #f0f2f3;
			border-radius: 8px;
			margin-bottom: 10px;
			.lang_img {
				width: 40px;
				height: 40px;
				margin-right: 25px;
				border-radius: 50%;
				display: flex;
				align-items: center;
				justify-content: center;
				box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
				img {
					width: 34px;
					height: 34px;
				}
			}
			p {
				margin-bottom: 0;
			}
		}
	}
	.skip {
		position: absolute;
		top: 3%;
		right: 3%;
		font-size: 10px;
		color: #353945;
		cursor: pointer;
	}
	.splide {
		margin-top: 200px;
		width: 90%;
		margin-left: auto;
		margin-right: auto;
		.splide__pagination {
			top: -20% !important;
			bottom: auto !important;
			.splide__pagination__page {
				background: #b1b5c3 !important;
				&.is-active {
					background: #05c832 !important;
				}
			}
		}
	}
	.next-btn {
		width: 60px;
		height: 60px;
		background: linear-gradient(to right, #87d8a3, #33b05f);
		color: #fff;
		border-radius: 50%;
		font-size: 22px;
		position: relative;
		&::before {
			content: "";
			position: absolute;
			top: -9px;
			left: -9px;
			right: -9px;
			bottom: -9px;
			background-image: url("/assets/images/vibo/loaderbutton.svg");
			background-size: contain;
			animation: rotateAnimate 2s linear infinite forwards;
		}
		@keyframes rotateAnimate {
			0% {
				transform: rotateZ(0);
			}
			100% {
				transform: rotateZ(360deg);
			}
		}
	}
}
