.pre-loader-two {
	position: absolute;
	height: 100vh;
	align-items: center;
	display: flex;
	justify-content: center;
	width: 100vw;
	z-index: 2001;
	background: #fff;
	.bg {
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		z-index: 1;
		overflow: hidden;
		background-color: $primary-color;
		&::before {
			content: "";
			position: absolute;
			transform: translate(-50%, -50%) scale(1);
			top: 40%;
			left: 50%;
			opacity: 1;
			z-index: -1;
			width: 250px;
			height: 250px;
			animation: animateBg 6s ease-in-out infinite;
			background-color: #fff;
			border-radius: 50%;
		}
	}
	.logo {
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
		top: 40%;
		transform: translateY(-50%);
		width: max-content;
		height: max-content;
		animation: animeLogo 2s ease-in-out;
		z-index: 2002;
		border-radius: 100%;
	}
}
@keyframes animeLogo {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@keyframes animateBg {
	0% {
		transform: translate(-50%, -50%) scale(10);
	}
	50% {
		transform: translate(-50%, -50%) scale(1);
	}
	100% {
		transform: translate(-50%, -50%) scale(10);
	}
}
