/* Shopping Cart */
.cart {
	@include mq("lg") {
		.col-lg-3 {
			-ms-flex: 0 0 30%;
			flex: 0 0 30%;
			max-width: 30%;
		}

		.col-lg-9 {
			-ms-flex: 0 70%;
			flex: 0 0 70%;
			max-width: 70%;
		}
	}
	.cart-item-infoblock {
		a {
			display: -webkit-box;
			-webkit-line-clamp: 2;
			-webkit-box-orient: vertical;
			overflow: hidden;
			text-overflow: ellipsis;
		}
	}
	.cart-item {
		position: relative;
		.not-count-message {
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			background-color: rgba(#000, 0.2);
			z-index: 9999;
			opacity: 0.5;
			p {
				color: red;
				font-weight: bold;
				text-align: center;
				margin: 20px auto;
			}
		}
	}
}

.table {
	.total-col {
		color: $primary-color;
		font-weight: 400;
		font-size: 1.6rem;
	}
}

.table.table-cart {
	.price-col {
		width: 120px;
	}

	.total-col {
		width: 80px;
		line-height: 18px;
	}

	.quantity-col {
		width: 135px;
	}
}

.cart-product-quantity {
	width: 100px;

	.form-control {
		padding: 0.85rem 2.6rem;
		height: 40px;
		max-width: 100%;
		border-color: #dadada;
		background-color: #fff;
		margin: 0;
	}
}

.input-spinner {
	position: relative;

	.input-group-prepend,
	.input-group-append {
		position: absolute;
		top: 0;
	}

	.input-group-prepend {
		left: 0;
	}

	.input-group-append {
		right: 0;
	}

	.form-control {
		padding: 0.85rem 2.6rem;
		height: auto;
		border-color: #dadada;
		background-color: #fff;
		margin: 0;

		&[disabled] {
			background-color: #f9f9f9;
		}
	}
}

.btn.btn-spinner {
	min-width: 0;
	color: #333333;
	padding: 1.25rem 0.4rem;
	font-size: 1rem;
	border: none;
	background: none;

	i {
		margin: 0 !important;
	}

	&:hover,
	&:focus {
		color: $primary-color;
	}
}

.cart-bottom {
	display: flex;
	align-items: flex-start;
	margin-bottom: 5rem;

	.btn {
		margin-left: auto;
		min-width: 152px;
	}
}

.cart-discount {
	flex: 0 0 50%;
	max-width: 326px;

	.form-control {
		margin-right: 1.1rem;
	}

	.btn {
		min-width: 40px;
		padding-left: 0;
		padding-right: 0;
	}
}

.summary.summary-cart {
	margin-bottom: 3rem;

	.summary-title {
		margin-bottom: 0;
	}
}

.table.table-summary {
	.summary-shipping {
		color: $dark-text;
		font-weight: 400;
		font-size: 1.6rem;

		td {
			padding-top: 2rem;
			padding-bottom: 1.3rem;
			height: auto;
			border-bottom: none;
		}
	}

	.summary-shipping-row {
		color: $dark-text;

		label {
			color: inherit;
		}

		td {
			height: auto;
			padding-top: 0.5rem;
			padding-bottom: 0.5rem;
			border-bottom: none;
		}
	}

	.custom-control {
		margin: 0;
	}

	.summary-shipping-estimate {
		color: $dark-text;
		font-weight: 400;
		font-size: 1.2rem;

		td {
			height: auto;
			padding-top: 0rem;
			padding-bottom: 2.3rem;
		}

		a {
			margin-left: 7px;
			margin-top: 0.6rem;
			display: inline-block;
			font-weight: 300;
			color: $light-text;
			font-size: 1.2rem;
			border-bottom: 0.1rem solid $light-text;

			&:hover,
			&:focus {
				color: $primary-color;
				border-bottom-color: $primary-color;
			}
		}
	}
}

.summary-cart {
	.table.table-summary thead th:last-child,
	.table.table-summary tbody td:last-child {
		min-width: 0;
	}
}

///////////////////////////////////////
// custom CHECKBOX ////////////////////
///////////////////////////////////////

$gray: #9a9999;

.toggle {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 50px;
	height: 28px;
	cursor: pointer;
	margin: 0;
	-webkit-tap-highlight-color: transparent;
	transform: translate3d(0, 0, 0);

	&:before {
		content: "";
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		height: 100%;
		background: #fff;
		border: 1px solid $gray;
		border-radius: 14px;
		transition: 0.2s ease;
	}
	span {
		position: absolute;
		top: calc(50% - 10px);
		left: 5px;
		width: 20px;
		height: 20px;
		display: block;
		background: $gray;
		border-radius: 10px;
		box-shadow: 0 3px 8px rgba($gray, 0.5);
		transition: all 0.2s ease;
	}
}
#cbx:checked + .toggle {
	&:before {
		background: #fff;
		border: 1px solid $primary-color;
	}
	span {
		background: $primary-color;
		transform: translateX(20px);
		transition: all 0.2s ease;
		box-shadow: 0 3px 8px rgba($primary-color, 0.2);
		&:before {
			transform: scale(1);
			opacity: 0;
			transition: all 0.4s ease;
		}
	}
}

.cart {
	.balance-input {
		display: block !important;
	}
	.input-group.input-spinner {
		display: none;
	}
	.balance-input::-webkit-outer-spin-button,
	.balance-input::-webkit-inner-spin-button {
		-webkit-appearance: none;
		margin: 0;
	}

	/* Firefox */
	.balance-input[type="number"] {
		-moz-appearance: textfield;
	}

	.active-sell {
		border-radius: "5px";
		transform: "scale(1)";
		transition: "0.3s";
	}

	.unactive-sell {
		position: relative;
		transform: "scale(0.97)";
		transition: "0.3s";
	}
	.unactive-sell::after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 999;
		user-select: none;
		background: #aca4a438;
	}
}

@media (max-width: 560px) {
	.cart {
		.cart-item-infoblock {
			a,
			.cart-variation-pr {
				display: -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
				overflow: hidden;
				text-overflow: ellipsis;
			}
			.d-flex {
				flex-direction: column;
			}
		}
	}
}
