.checkout_two {
	// max-width: 700px;

	margin: 0 auto !important;
	user-select: none;
	.page-content {
		padding-bottom: 0 !important;
	}
	.checkout_header {
		display: flex;
		align-items: center;
		position: absolute;
		padding: 15px 15px;
		top: 0;
		left: 0;
		right: 0;
		background: #fff;
		z-index: 999;
		.back-block {
			padding: 0;
			i::before {
				font-weight: bold;
			}
		}
		h3 {
			flex-grow: 1;
			font-weight: bold;
			margin: 0;
			text-align: center;
		}
	}

	.cart-block-main {
		border-radius: 10px;
		box-shadow: 0 0 5px #eee;
		.filter-item {
			.cart_delivery_info {
				background: #f8f8f8 !important;
				.left {
					.left_top,
					.left_bottom {
						h4,
						p,
						span {
							color: #000 !important;
						}
						img {
							filter: brightness(0) invert(0) !important;
						}
					}
				}
				.right {
					i {
						&::before {
							color: #000 !important;
						}
					}
				}
			}
		}
		.warning_sell_time {
			width: 100%;
			padding: 12px;
			border-radius: 10px;
			background-color: #ffecec;
			border: 1px solid #ffd0d0;
			margin-bottom: 10px;
			p {
				font-size: 12px;
				display: flex;
				align-items: center;
				margin-bottom: 0;
				line-height: 1.4;
				opacity: 0.5;
				color: #000;
				img {
					margin-right: 5px;
				}
			}
			.warning_title {
				font-size: 13px;
				font-weight: 700;
				opacity: 1;
				margin-bottom: 5px;
			}
		}
		.cart_delivery_info {
			padding: 18px;
			border-radius: 10px;
			h4 {
				cursor: pointer;
			}
			.left {
				img {
					margin-right: 10px;
				}
				span {
					font-size: 12px;
					font-style: italic;
				}
				.left_top {
					flex-wrap: wrap;
					p {
						font-size: 16px;
						font-weight: bold;
					}
				}
			}
			.right {
				.icon-angle-down {
					&::before {
						transition: 0.2s;
						font-weight: bold;
						color: black;
					}
				}
				.icon-angle-down.open::before {
					transform: rotate(180deg);
				}
			}
		}
		.sell_group {
			&::-webkit-scrollbar {
				width: 8px;
			}

			&::-webkit-scrollbar-track {
				box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
			}

			&::-webkit-scrollbar-thumb {
				background: #e5e5e5;
				border-radius: 10px;
			}
			.info_sell {
				display: flex;
				align-items: center;
				justify-content: space-between;
				padding: 5px 10px;
				color: black;
				background-color: #eee;
				font-weight: bold;
				div {
					span {
						font-weight: normal;
						font-style: italic;
					}
				}
				.sell_location {
					display: flex;
					align-items: center;
					justify-content: flex-end;
					margin-left: auto;
					width: 65%;
					span {
						overflow: hidden;
						white-space: nowrap;
						text-overflow: ellipsis;
						padding: 0 5px;
					}
				}
			}
			.cart-product-card {
				position: relative;
				.loading-count {
					margin: 0 auto;
					height: 101px;
					display: flex;
					align-items: center;
					justify-content: center;
				}
			}
		}
	}

	.sidebar_two {
		border-radius: 10px;
		margin: 0 2px;
		.summary {
			border: none;
			background-color: #fff !important;
			padding: 0 !important;
		}
		.summary > div:first-child {
			background-color: #f8f8f8;
			padding: 10px 0;
			border-radius: 10px;
			.table-wrapper {
				padding: 0;
			}
			.table {
				div {
					display: flex !important;
					align-items: center !important;
					justify-content: space-between !important;
					margin-bottom: 10px;
					padding: 10px;
				}
				p {
					padding: 0 !important;
					margin: 0;
					font-size: 16px !important;
					color: black !important;
					white-space: nowrap;
				}
				.summary-subtotal.discount p:last-child {
					color: #e27d7d !important;
				}
			}
			.summary-shipping-estimate {
				display: flex;
				align-items: center;
				justify-content: space-between;
				margin: 0 10px;
				padding: 10px;
				font-size: 16px;
				background-color: #fff;
				overflow: hidden;
				border-radius: 10px !important;
				margin-bottom: 20px;
				p {
					margin: 0;
					text-align: left;
					width: fit-content;
					display: -webkit-box;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
					overflow: hidden;
					--overfdropdownlow: hidden;
					text-overflow: ellipsis;
				}
			}
			.summary-user_card {
				display: flex;
				align-items: center;
				justify-content: space-between;
				padding: 20px 0 !important;
				border-top: 1px dashed #d4d4d4;
				border-bottom: 1px dashed #d4d4d4;
				.payment_methods {
					display: none !important;
					.payment_wrapper {
						background: $body-bg;
						cursor: pointer;
						transition: 0.3s;
						border: 2px solid transparent;
						transform: scale(0.95);
						p {
							padding: 0 !important;
						}
					}
					.payment_wrapper.selected {
						border: 2px solid $primary-color !important;
						transform: scale(1);
					}
				}

				p {
					margin: 0;
					padding: 0 10px !important;
				}
			}
			.summary-promo_code {
				padding: 10px;
				display: flex;
				align-items: center;
				justify-content: space-between;
				p {
					margin: 0;
				}
				.add {
					color: #797979 !important;
				}
			}
		}
		.debt-trading {
			margin-bottom: 15px;
			padding: 19px 14px !important;
			background: #f1f7fc;
			border: 1px solid #eff4f8;
			border-radius: 5px;
			.debt-body {
				padding: 0;
				.debt-location,
				.percent-round-up,
				.debt-info,
				.will-rewiev,
				.warning {
					display: flex;
					padding: 0;
					justify-content: space-between;
				}
				.will-rewiev {
					margin: 15px 0;
					.switcher {
						background: #ffffff;
						border-radius: 45px;
						height: 37px;
						width: 69px;
						cursor: pointer;
						padding: 2px;
						button.switch {
							background: #c0c0c0;
							width: 33px;
							height: 100%;
							transition: 0.3s;
							border-radius: 50%;
							margin-left: 0;
						}
					}
					.switcher.active {
						background: $third-color;
						button.switch {
							margin-left: auto;
							background: $light-text;
						}
					}
				}
				select,
				input {
					padding: 8px;
					border: 1px solid #babec2;
					border-radius: 5px !important;
					color: $dark-text;
					margin-bottom: 16px;
					option {
						color: $dark-text;
					}
				}
				input {
					margin-right: 12px;
				}
			}
			.warning {
				padding: 12px !important;
				border-radius: 5px;
				background: #fbe3e4;
				border: 1px solid #f9cbcb;
				.text-content {
					padding: 0;
					p {
						font-size: 12px;
						line-height: 14px;
						color: #8f1a1a;
						margin: 0;
					}
				}
				.img {
					display: flex;
					align-items: center;
					justify-content: center;
					min-width: 50px;
					padding: 0;
					margin-right: 20px;
				}
			}
		}
		.summary-add_comment {
			display: flex;
			align-items: center;
			padding: 15px;
			margin-top: 20px;
			background-color: #fff;
			border: 1px solid #eee;
			border-radius: 10px;
			justify-content: center;
			cursor: pointer;
			p {
				margin: 0;
				margin-left: 10px;
			}
		}

		.summary-pay-info {
			margin: 40px 0 80px 0;
			div {
				padding: 0 20px;
				p {
					font-weight: bold;
					color: black;
				}
			}
			.btn {
				width: 100%;
				padding: 15px 20px;
				color: black;
				font-size: 12px;
				font-weight: bold;
			}
		}

		.pay_wrapper {
			border-radius: 10px;
			position: fixed;
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 10px 30px;
			right: 0;
			left: 0;
			bottom: 76px;
			background-color: $primary-color;
			z-index: 99;
			max-width: 1200px !important;
			margin: 0 auto;
			.pay_info {
				width: 70%;
				p {
					color: black;
					margin: 0;
				}
				.sale_price {
					font-weight: bold;
				}
			}
			.pay-btn {
				background-color: #fff !important;
				color: black !important;
				border-radius: 15px;
				padding: 15px 20px;
				width: 30%;
			}
			img {
				width: 100% !important;
			}
		}

		@media (min-width: 560px) {
			.pay_wrapper {
				padding: 10px;
				.pay_info {
					width: 80%;
				}
				.pay-btn {
					width: 20%;
					padding: 10px;
				}
			}
		}
	}
}

.calendar-wrapper,
.time-wrapper {
	max-width: 700px;
	margin: 0 auto;
	position: fixed;
	width: 100%;
	bottom: 35%;
	left: 0;
	right: 0;
	text-align: center;
	padding: 10px;
	z-index: 99999;
	background-color: #fff;
	animation-name: translateBottomToTop;
	animation-duration: 0.2s;
	animation-fill-mode: forwards;
	border-top-left-radius: 10px;
	border-top-right-radius: 10px;
	.react-calendar {
		border-radius: 10px;
		overflow: hidden;
		margin: 0 !important;
		width: max-content;
	}
	.change-date {
		background-color: $primary-color;
		border-radius: 10px;
		margin-top: 10px;
		font-weight: bold;
		width: 100%;
		color: black;
	}
}

.time-wrapper {
	display: flex;
	align-items: center;
	flex-direction: column;
	justify-content: space-between;
	box-shadow: 0 -5px 20px #ccc;
	.change-time {
		margin-top: auto;
		background-color: $primary-color;
		border-radius: 10px;
		margin-top: 10px;
		font-weight: bold;
		color: black;
		width: 100%;
	}
	.react-time-picker {
		display: flex;
		flex-direction: column-reverse;
		width: 100% !important;
		.react-time-picker__wrapper {
			border-radius: 10px;
			border-color: #ebebeb;
			padding: 30px;
		}
		.react-time-picker__clock {
			position: relative !important;
			width: 100% !important;
			display: flex;
			align-items: center;
			justify-content: center;
		}
		.react-time-picker__clear-button,
		.react-time-picker__clock-button {
			display: none;
		}
	}
}

#shipping_service_modal {
	position: fixed;
	bottom: 35%;
	left: 0;
	right: 0;
	z-index: 999;
	border-radius: 10px;
	background-color: #fff;
	animation: translateBottomToTop 0.3s forwards;
	max-width: 700px;
	margin: 0 auto;
	.ymaps-2-1-79-controls__control.ymaps-2-1-79-user-selection-none {
		inset: auto 10px 10px auto !important;
	}
	.shipping_service_modal_content {
		width: 100%;

		.locations-map {
			margin-bottom: 40px;
		}
		.locations-map {
			.map_content {
				border-radius: $border-item;
				overflow: hidden;
			}
		}
		.shipping-service {
			.tab-nav {
				display: flex;
				align-items: center;
				justify-content: space-around;
				max-width: 100%;
				.tab-item {
					padding: 12px;
					color: black;
					border-radius: 10px;
					border: 1px solid transparent;
					transition: 0.3s;
					&:hover {
						border-color: $primary-color;
						cursor: pointer;
					}
					&.active {
						background: $primary-color;
						color: $light-text;
					}
				}
				margin-bottom: 20px;
			}
		}
		.times_heading {
			margin-top: 20px;
			margin-bottom: 20px;
			div {
				border: 1px solid #e1e1e1;
				// color: #000 !important;
				border-radius: 15px;
			}
			div:first-child {
				margin-right: 10px;
			}
			.active {
				background-color: $primary-color !important;
				color: $light-text;
			}
			&:hover {
				cursor: pointer;
			}
		}
		.times_body {
			position: relative;
			margin-bottom: 20px;
			padding: 10px;
			border-radius: 15px;
			background-color: #f2f2f2;
			&::before {
				content: " ";
				position: absolute;
				top: 0;
				right: 20%;
				transform: translateY(-100%);
				border: 12px solid #f2f2f2;
				border-left-color: transparent;
				border-right-color: transparent;
				border-top-color: transparent;
			}
			div {
				width: 48%;
				padding: 10px;
				border-radius: 15px;
				color: black !important;
				background-color: #fff;
				border: 1px solid #e1e1e1;
			}
		}
		.btn {
			font-weight: bold;
			width: 100%;
			background: $primary-color;
			border-radius: 15px;
			color: $light-text;
		}
	}
}

#map-modal-two {
	position: fixed;
	top: 30px;
	left: 5%;
	right: 5%;
	width: 90%;
	background-color: #fff;
	animation-name: translateBottomToTop;
	.address-history {
		display: flex;
		align-items: center;
		flex-wrap: nowrap;
		overflow: auto;
		&::-webkit-scrollbar {
			width: 0 !important;
		}
		li {
			width: max-content;
			margin-right: 5px;
			white-space: nowrap;
			padding: 5px 10px;
			background-color: #f0f0f0;
			border-radius: 10px;
			&:first-child {
				display: none;
			}
		}
	}
	h3.text-center {
		display: none;
	}
	.map_content {
		border-radius: 10px !important;
		overflow: hidden;
		width: 100%;
		height: 70vh;
		.location-address {
			font-size: 14px;
			position: absolute;
			bottom: 0;
		}
	}
	.location-wrapper {
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.btn {
		width: 100%;
		padding: 10px 20px;
		margin-top: 10px;
		border-radius: 10px;
		&:hover,
		&:focus {
			background-color: $secondary-color !important;
		}
	}
}
#comment-modal {
	position: fixed;
	bottom: 35%;
	left: 0;
	right: 0;
	z-index: 999;
	border-radius: 10px;
	padding: 15px;
	background-color: #fff;
	animation-name: translateBottomToTop;
	animation-duration: 0.2s;
	animation-fill-mode: forwards;
	max-width: 700px !important;
	margin: 0 auto;

	h3 {
		margin: 0;
	}
	textarea {
		width: 100%;
		padding: 10px;
		margin: 20px 0;
		outline: none;
		resize: none;
		border-radius: 10px;
		border-color: #797979;
	}
	.btn {
		width: 100%;
		padding: 15px 20px;
		margin-top: 10px;
		font-weight: bold;
		color: black;
		border-radius: 15px;
		&:hover,
		&:focus {
			background-color: $secondary-color !important;
		}
	}
}

#usercard-modal {
	position: fixed;
	left: 0;
	right: 0;
	bottom: 50%;
	z-index: 999;
	border-radius: 10px;
	animation-name: translateBottomToTop;
	animation-duration: 0.2s;
	animation-fill-mode: forwards;
	max-width: 450px !important;
	margin: 0 auto;

	.form-group {
		.add-new-card {
			width: max-content;
			padding: 10px;
			margin-left: auto;
			text-align: center;
			border-radius: 15px;
			font-size: 14px;
			color: $primary-color;
		}
		display: flex;
		justify-content: center;
		flex-direction: column;
		div {
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin-top: 20px;
		}
		label {
			display: flex;
			align-items: center;
			margin: 0;
			input {
				display: none;
			}
			.radio {
				width: 20px;
				height: 20px;
				border-radius: 50%;
				margin-right: 5px;
				padding: 2px;
				border: 1px solid #ccc;
				span {
					display: block;
					width: 100%;
					height: 100%;
					border-radius: 50%;
					background-color: #fff;
				}
			}
			input:checked + .radio {
				span {
					background-color: $primary-color;
				}
			}
		}
	}
	button {
		width: 100%;
		color: black;
		font-weight: bold;
		border-radius: 15px;
	}

	.add_new_card,
	.add_new_card_code {
		max-width: 700px !important;
		height: max-content;
		position: fixed;
		bottom: 35%;
		left: 0;
		right: 0;
		border-radius: 10px;
		padding: 20px;
		background-color: #fff;
		animation-name: translateBottomToTop;
		animation-duration: 0.2s;
		animation-fill-mode: forwards;
		user-select: none;
		.correct,
		.warning {
			margin: 0;
		}
		input {
			min-width: 100px;
			width: 100%;
			display: block !important;
			padding: 10px;
			color: black;
			margin: 0 !important;
			margin-top: 20px !important;
			border-radius: 10px;
			border: 1px solid #eee;
			outline: none;
		}
		.new_card_inputs {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			padding: 10px;
			margin: 0 !important;
			margin-top: 20px !important;
			border-radius: 10px;
			border: 1px solid #eee;
			input {
				border: none;
				padding: 0;
				width: 25px;
				text-align: center;
				margin: 0 !important;
			}
			span {
				font-weight: normal;
				margin: 0 5px;
			}
		}
		.warning + span {
			color: red !important;
		}
		button {
			width: 100%;
			font-weight: bold;
			margin-top: 40px;
			color: black;
		}
		.input-group.input-spinner {
			display: none;
		}
	}
	.add_new_card,
	.add_new_card_code {
		top: 0;
	}
	.add_new_card_code {
		height: 140%;
		display: flex;
		align-items: center;
		flex-direction: column;
		justify-content: space-between;
		input::-webkit-outer-spin-button,
		input::-webkit-inner-spin-button {
			-webkit-appearance: none;
			margin: 0;
		}
	}
}

@media (max-width: 991px) {
	.shipping_service_modal {
		.shipping_service_modal_content {
			.shipping-service {
				form {
					select {
						font-size: 16px;
						line-height: 16px;
						height: 47px;
						border-radius: 10px;
						margin-bottom: 20px;
					}
				}
			}

			.times_heading {
				.flex-grow-1 {
					font-size: 16px;
					line-height: 16px;
					border-radius: 10px;
					margin: 5px;
					text-align: center;
				}
			}

			.times_body {
				border-radius: 10px;
				padding: 8px;
				margin: 12px 4px;

				div {
					padding: 12px 30px;
					font-size: 16px;
					line-height: 16px;
				}
			}
		}
	}

	.dashboard,
	.checkout_two {
		width: 100% !important;
		.cart-block-main {
			.filter-item {
				margin-top: 16px;
			}
		}
		.widget.widget-collapsible {
			.sell_group {
				overflow-y: unset;
				height: auto;
				.cart-supplier-info {
					.cart-amount-product {
						.search-result-price {
							span {
								font-size: 12px;
							}
						}
					}
				}
			}
		}
		.info_user {
			margin-top: 12px;
			padding: 0 !important;
			.dashboard-banner,
			.col-4 {
				min-width: 300px !important;
				.banner-big {
					.lazy-overlay-image {
						height: 86px !important;
						width: 86px;
					}
					.banner-content {
