.block-section {
	display: block;
	width: 100%;
	height: 100%;

	.blog-title,
	.brand-box,
	.slider-banner,
	.simple-carousel,
	.carousel-with-banner {
		margin-bottom: 30px;

		.owl-carousel {
			.owl-nav {
				display: none;
			}
		}
	}

	.right-banner {
		display: block;
		width: 100%;
		height: 100%;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}

@media (max-width: 991px) {
	.block-section.mt-10 {
		margin-top: 20px !important;
		.carousel-with-banner {
			display: none;
		}
	}
}

@media (max-width: 768px) {
	.block-section {
		.simple-carousel .owl-carousel {
			.owl-stage-outer {
				.owl-stage {
					width: max-content !important;
					.owl-item {
						margin-right: 7px !important;
					}
				}
			}
		}
	}
}
