.header-eight {
	box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
	border-radius: 0px 0px 20px 20px;
	margin-bottom: 30px;

	.header-middle.sticky-header {
		border-bottom: none;
		padding: 22px 0;
		.container {
			display: flex !important;
		}
		.header-left {
			.logo {
				width: 190px;
				margin: 0 30px 0 0;
				.div-shimmer {
					.photo-shimmer {
						height: 50px;
					}
				}
			}
			.categroy-button {
				background: $primary-color;
				padding: 9.25px 16.5px 9.25px 16.5px;
				border-radius: $border-item;
				width: max-content;
				img {
					margin-right: 11.25px;
				}
				span {
					font-weight: 500;
					font-size: 16px;
					line-height: 19px;
					color: $light-text;
				}
			}
		}
		.header-center {
			width: 100%;
			margin: unset;
			.search-box-wrapper {
				width: 100%;
				.header-search.header-search-extended {
					form {
						.header-search-wrapper.search-wrapper-wide {
							position: unset;
							left: unset;
							right: unset;
							top: unset;
							margin-top: unset;
							background-color: unset;
							border: unset;
							margin-left: 10px;
							border: 1px solid rgba(137, 148, 153, 0.2);
							border-radius: $border-item;
							background-color: $body-bg;
							align-items: center;
							width: 100%;

							.serch-icon.d-none {
								display: inline-block !important;
								min-width: 24px;
								height: 24px;
								margin: 8px 10px 8px 15px;
								img {
									width: 100%;
								}
							}
							.form-control.my-input {
								height: unset;
								padding: 8px 10px 8px 0 !important;
								&::placeholder {
									font-weight: 500;
									font-size: 16px;
									line-height: 19px;
								}
							}
							.btn.search-button {
								flex: unset;
								max-width: unset;
								min-height: 100%;
								padding: 0 20px !important;
								background: $primary-color;
								line-height: 1;
								border-radius: $border-item;
								padding: unset;
								margin: unset;
								min-width: unset;
								margin-top: unset;
								margin-right: unset;
								margin-bottom: unset;
								h6 {
									margin: 0;
									span {
										font-weight: 500;
										font-size: 16px;
										line-height: 19px;
										color: $light-text;
									}
								}
							}
						}
					}
				}
			}
		}
		.header-center.mobile {
			display: none;
		}
		.header-right {
			width: inherit;
			justify-content: end;
			.dropdown.cart-dropdown {
				padding: 0;
			}
			.change-lang-three {
				.d-flex {
					img {
						width: 20px;
						height: 20px;
						border-radius: 150px;
						object-fit: cover;
					}
					.arrow {
						transform: rotate(90deg);
						filter: brightness(0) invert(0);
						opacity: 0.5;
					}
				}
				.lang-selector {
					width: 250px;
					right: 0;
					max-width: 250px;
					font-size: 14px;
					line-height: 20px;
					border: unset;
					border-radius: unset;
					margin-right: unset;
					.lang-body {
						background: $body-bg;
						padding: 20px;
						border-radius: $border-item;
						border: 1px solid $primary-color;
						margin-top: 20px;
					}
					button {
						width: 100%;
						display: flex;
						min-width: 100%;
						margin-bottom: 10px;
						padding-right: 10px;
						align-items: center;
						justify-content: space-between;

						span {
							display: block;
							font-size: 16px;
							line-height: 22px;
							white-space: nowrap;
							min-width: max-content;
						}
					}
					.langBut.active {
						button {
							padding: 10px;
						}
						span {
							color: $light-text;
						}
					}
					.choose-ragion {
						font-size: 16px;
						line-height: 22px;
						text-overflow: ellipsis;
						overflow: hidden;
						color: $dark-text;
						font-weight: 700;
						margin: 0 0 10px;
						white-space: nowrap;
					}
				}
			}
			.profile-link,
			.choosen-products-icon,
			.dropdown-toggle {
				padding: 8px 15px;
				display: flex;
				flex-direction: row !important;
				height: fit-content !important;
				width: fit-content !important;
				transition: 0.3s;
				h6 {
					margin: 0 0 0 12px;
					font-weight: 500;
					font-size: 16px;
					line-height: 19px;
				}
				&:hover {
					border-radius: $border-item;
					background: $third-color;
				}
			}
			.profile-link {
				margin-left: 20px;
			}
			.dropdown-toggle,
			.choosen-products-icon {
				.cart-count,
				span {
					position: absolute;
					right: -5px;
					top: -10px;
				}
			}
			.choosen-products-icon {
				span {
					width: 20px;
					height: 20px;
				}
			}
			.cart-dropdown {
				.cart-body {
					border-radius: $border-item !important;
					border: 1px solid $primary-color;
				}
				.dropdown-menu-right {
					padding: 0;
					padding-top: 5px;
					width: 450px;
					max-height: unset;
					overflow-y: unset;
					border-radius: none !important;
					visibility: hidden;
					opacity: 0;
					.title-closer {
						padding: 20px 20px 6px 20px;
						display: flex;
						justify-content: space-between;
						align-items: center;
						span {
							font-weight: 700;
							font-size: 18px;
							line-height: 24px;
							color: $dark-text;
						}
						// .closer {
						// 	padding: 0 0 0 10px;
						// }
					}
					.products-gradient {
						height: auto;
						position: relative;
						.gradient-div {
							position: absolute;
							bottom: 0;
							width: 100%;
							height: 60px;
							background: linear-gradient(
								180deg,
								rgba(255, 255, 255, 0) 0%,
								#ffffff 100%
							);
							z-index: 1;
						}
					}
					.dropdown-cart-products {
						margin: 0 auto;
						max-height: 70vh;
						.content-body {
							.product {
								padding: 10px 0;
								margin: 0 20px !important;
								.product-detail-left {
									display: flex;
									.product-image-container {
										min-width: 80px;
										height: 80px;
										border: 0.5px solid #f2f3f5;
										border-radius: $border-item;
										margin-right: 20px;
										.product-image {
											height: 100%;
											width: 100%;
											img {
												width: 100%;
												height: 100%;
												object-fit: contain;
											}
										}
									}
									.title-price {
										width: 220px;
										display: flex;
										flex-direction: column;
										justify-content: space-between;
										.product-title {
											a {
												display: block;
												max-width: 100%;
												-webkit-line-clamp: 2;
												-webkit-box-orient: vertical;
												overflow: hidden;
												text-overflow: ellipsis;
												font-size: 14px;
												line-height: 16px;
												color: $body-text;
											}
										}
										.conut-price {
											display: flex;
											justify-content: space-between;
											.cart-product-qty {
												color: $dark-text;
											}
											.cart-product-info,
											.cart-product-qty {
												font-weight: 700;
												font-size: 16px;
												line-height: 19px;
											}
											.cart-product-info {
												margin: 0;
												color: $primary-color;
											}
										}
									}
								}
								.btn-remove {
									margin: 0;
									margin-left: auto;
									padding: 14px;
									height: max-content;
									width: max-content;
									background: $third-color;
									border-radius: $border-item;
								}
								margin-left: 20px;
							}
						}
					}
					.dropdown-cart-total {
						padding: 20px;
						margin: 0;
						.cart-total {
							font-size: 16px;
							line-height: 19px;
							color: $body-text;
						}
						.cart-total-price {
							font-weight: 700;
							font-size: 18px;
							line-height: 22px;
							color: $primary-color;
						}
					}
					.dropdown-cart-action {
						padding: 0 20px 20px 20px;
						a {
							border-radius: $border-item;
							font-size: 16px;
							line-height: 19px;
						}
					}
				}
				.dropdown-menu.dropdown-menu-right.visible-cart {
					visibility: visible;
					opacity: 1;
					z-index: 1043;
				}
			}
		}
	}
	.header-buttom-menu {
		display: flex;
		align-items: center;
		.for-container {
			display: flex;
			padding-bottom: 20px;
			&::-webkit-scrollbar {
				display: none;
			}
			.header-left {
				width: 100%;
				.location {
					width: 200px;
					padding: 0 !important;
					span {
						font-weight: 500;
						font-size: 16px;
						line-height: 19px;
						color: $dark-text;
						text-align: left;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
					}
				}
			}
			.header-left-menu,
			.header-right-menu {
				margin-left: 60px;
				a {
					padding: 5px 10px;
					margin-right: 10px;
					color: $dark-text;
					font-weight: 500;
					font-size: 16px;
					line-height: 19px;

					&:last-child {
						margin-right: 0;
						padding-right: 0;
					}
				}
			}
			.header-right-menu {
				margin: 0;
			}
		}
	}
}

@media (max-width: 1100px) {
	.header-eight {
		.header-middle.sticky-header {
			.container.search {
				.header-right {
					padding: 0 !important;
					.profile-link,
					.choosen-products-icon,
					.dropdown-toggle {
						h6 {
							display: none;
						}
					}
				}
			}
		}
	}
}
@media (max-width: 991px) {
	.header-eight {
		.header-middle.sticky-header {
			.container.search {
				.header-right {
					padding: 0 !important;
				}
			}
		}
	}

	.header-eight .header-middle.sticky-header {
		.header-left {
			justify-content: unset !important;
			.header-six-ver-menu {
				display: flex !important;
				margin-left: 20px;
				.categroy-button {
					padding: 9.25px;
					img {
						margin: 0 auto;
					}
					span {
						display: none;
					}
				}
			}
		}
		.header-right {
			display: flex !important;
			.input-opener {
				padding: 8px 8px;
				margin-right: 7px;
				img {
					width: 22px;
				}
			}
		}
		.container.search {
			display: flex !important;
			align-items: center;
			justify-content: space-between;
			.dropdown.cart-dropdown,
			.choosen-products-icon {
				h6 {
					display: none;
				}
				.profile-link {
					margin: 0;
				}
			}
			.header-center {
				display: none;
			}
		}
		.header-center.mobile {
			padding: 0 10px;
			display: flex;
			.search-box-wrapper {
				.header-search {
					display: flex;
					form {
						width: 100%;
						.header-search-wrapper {
							display: flex;
							margin: 20px auto 0 auto !important;
							.btn.search-button {
								// display: none;
								background: transparent !important;
								padding: 0 !important;
								&:disabled {
									background: transparent !important;
									img {
										display: none !important;
									}
								}
							}
							&::before {
								display: none;
							}
						}
					}
				}
			}
		}
		.header-center {
			.search-box-wrapper {
				.header-search.header-search-extended {
					display: flex;
					form {
						width: 100%;
						.header-search-wrapper.search-wrapper-wide {
							display: flex;
							opacity: 1;
							transition: 0.3;
							margin: 0 auto;
							&::before {
								display: none;
							}
							input[type="search"]::-webkit-search-cancel-button {
								display: none;
							}
							.btn.search-button {
								background: transparent;
								padding: 0 !important;
								font-size: 1rem;
								margin-right: 10px;
								&:disabled {
									background-color: transparent !important;
									color: $primary-color !important;
								}
								h6 {
									display: none;
								}
							}
						}
						.header-search-wrapper.search-wrapper-wide.header-search-wrapper.long-input-bg {
							display: none;
							opacity: 0;
						}
					}
				}
			}
		}
	}

	.header-buttom-menu {
		.for-container {
			padding-right: 0;
			padding-bottom: 10px;
			overflow: auto;
			.location {
				p {
					padding: 5px 10px;
					margin-right: 5px;
					img {
						width: 18px !important;
					}
				}
				width: max-content;
			}
			.header-left .left-side .header-left-menu,
			.header-right-menu {
				margin-left: 0;
				a,
				a:last-child {
					// padding: 5px 10px !important;
					white-space: nowrap;
					padding-right: 10px !important;
					margin-right: 5px !important;
				}
			}
		}
	}
}

@media (max-width: 560px) {
	.header-eight {
		border-radius: 0px 0px 10px 10px;
		.header-middle.sticky-header {
			.container {
				display: block !important;
			}
			padding: 18px 0 10px 0;
			.header-left {
				justify-content: space-between;
				.logo {
					width: 130px;
					margin: 0 30px 0 0;
				}
				.header-six-ver-menu {
					display: none !important;
				}
			}

			.header-right {
				.dropdown.cart-dropdown,
				.choosen-products-icon,
				.profile-link {
					display: none;
				}
			}
		}
		.header-buttom-menu {
			.for-container {
				.header-left .location p,
				.header-left-menu a,
				.header-right-menu a {
					background: rgba(242, 243, 245, 0.5);
					border-radius: $border-item;
				}
			}
		}
	}
}
.mmenu-active-two {
	.mobile-menu-container-two {
		z-index: 15545411 !important;
		opacity: 1;
		position: fixed;
	}

	.mobile-menu-wrapper {
		transform: translateX(0) !important;
	}
}

.header-middle {
	.header-right {
		.dropdown-menu {
			display: block;
		}
	}
	.my-menu {
		position: relative;
		top: 0;
		left: -100%;
		background: #000;
	}

	.header-six-ver-menu {
		background: $primary-color;
		border-radius: 12px;
		display: flex;
		justify-content: center;

		.menu-burger {
			display: flex;
			align-items: center;
			justify-content: center;

			&:before {
				content: "\F131";
				font-family: "molla";
				border: none;
				font-size: 2.3rem;
				line-height: 1;
				padding: 10px;
				align-items: center;
				font-weight: 500;
				color: #ffffff;
				transition: all 0.4s;
			}
		}

		.category-dropdown {
			.dropdown-menu {
				border-radius: 0 !important;
				position: fixed;
				z-index: 16 !important;
				width: 26%;
				top: 0;
				height: 100vh;
				display: block;

				.mobile-menu-close {
					top: unset !important;
					right: unset !important;
					i {
						font-weight: 900 !important;
						font-size: 40px;
						color: #ffffff;
					}
				}

				.side-nav {
					margin-top: 20px;
					.menu-vertical {
						padding: 0 0 20px 30px !important;
						overflow: auto;
						&::-webkit-scrollbar {
							display: none;
						}
					}
					.menu-vertical > li > a {
						border-bottom: none;
						font-size: 18px;
						line-height: 2.5;
						padding: 0 0 0 10px;
					}

					.menu-vertical > li {
						padding-right: 24px;
					}
				}

				.megamenu-container {
					a.sf-with-ul {
						&::after {
							color: $primary-color;
							font-size: 16px;
						}
					}
					a.sf-with-ul.active {
						background: $primary-color;
						color: $light-text;
						&::after {
							color: $light-text;
						}
					}
					a,
					.megamenu .mega-menu-links {
						font-size: 18px;
						// line-height: 30px;
						padding: 10px 20px;

						border-radius: 15px;
						color: $dark-text;
						transition: 0.2s ease-in-out all;

						&:hover {
							background: $third-color;
							// color: $primary-color;
						}
					}

					.megamenu .menu-title,
					.megamenu .mega-menu-links {
						border: none;
						position: relative;
						padding: 5px 10px !important;
						display: flex;
						align-items: center;
						.menu-icon {
							width: 30px;
							margin: 0 8px 0 0;
						}
						&:hover {
							background: $third-color;
						}
					}
				}

				.banner-menu {
					background: unset !important;
					color: unset !important;
					float: unset !important;

					img {
						filter: unset !important;
					}

					&:hover {
						transform: scale(1.01);
					}
				}

				.megamenu {
					position: absolute;
					top: -20px;
					right: 0 !important;
					// width: fit-content;
					min-height: 100vh !important;
					border-radius: 0 !important;

					a {
						line-height: 30px !important;
					}
					.no-gutters {
						.col-md-12 {
							.menu-col {
								padding: 0 15px;
								margin-top: 0;
								height: 100vh;
								overflow-y: scroll;
								.row {
									// column-count: 2;
									display: flex;
									padding: 20px 0;
									margin-bottom: 20px;
								}
								.col-12 {
									max-width: 49%;
									margin-bottom: 0;
								}
								&::-webkit-scrollbar {
									width: 12px;
								}

								&::-webkit-scrollbar-track {
									box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
								}

								&::-webkit-scrollbar-thumb {
									background: $primary-color;
									border-radius: 10px;
								}
							}
						}
					}
				}
			}

			.banner {
				display: none;
			}
		}
	}
}
