.card-row {
	position: relative;
	width: 100%;
	margin-bottom: 20px;
	border: 1px solid #bcc5d9;
	border-radius: 16px;
	padding: 5px;
	.product-header {
		height: 200px;
		// border-radius: 5px;
		overflow: hidden;
		padding: 0 15px;
		.change-image {
			display: none;
		}
		a {
			display: inline-block;
			height: 100%;
			width: 100%;
			border-radius: 5px;
			overflow: hidden;
			img {
				width: 100%;
				height: 100%;
				object-fit: contain;
			}
		}

		.btn-heart {
			position: absolute;
			padding: 7px;
			border-radius: 50%;
			background: #f2f2f2;
			top: 15px;
			right: 15px;
		}
	}

	.body-product {
		padding: 10px 0;
		.title-product,
		.price-product {
			font-weight: 500;
			font-size: 16px;
			color: $dark-text;
			padding: 0 10px;
			margin: 5px 0;
			text-align: left;
			display: -webkit-box;
			-webkit-line-clamp: 1;
			-webkit-box-orient: vertical;
			overflow: hidden;
			text-overflow: ellipsis;
		}
		.price-product {
			flex-direction: row-reverse;
			justify-content: flex-end;
			align-items: center;
		}
	}
	.product-supplier {
		margin-top: 20px;
		display: flex;
		align-items: center;
		.supplier-img {
			height: 70px;
			width: 70px;
			img {
				width: 100%;
				height: 100%;
				object-fit: contain;
			}
		}
		p {
			margin-left: 20px;
			font-size: 16px;
		}
	}
}

@media (max-width: 1200px) {
	.card-row {
		.product-header {
			height: 200px;
		}
	}
}
@media (max-width: 991px) {
	.card-row {
		.product-header {
			height: 120px;
			width: 120px;
		}

		.body-product {
			padding: 5px 0;
			.title-product,
			.price-product {
				padding: 0 5px;
			}
			.title-product a,
			.price-product {
				font-size: 1.2rem;
				line-height: 1.25;
			}
		}
		.product-supplier {
			.supplier-img {
				width: 60px;
				height: 60px;
			}
			p {
				margin: 0 0 0 16px;
				font-size: 12px;
			}
		}
	}
}
