html {
	scroll-behavior: smooth;
}
.all-categories {
	margin-bottom: 80px;
	& .col-12.menu {
		padding: 20px 0;
		background-color: #f2f2f2;
	}
	&.fixed {
		.menu {
			position: fixed;
			top: 0;
			left: 0;
			right: 0;
			z-index: 1;
			padding: 10px 0;
			.container {
				flex-wrap: nowrap !important;
				overflow: auto;
				&::-webkit-scrollbar {
					height: 3px;
				}
				&::-webkit-scrollbar-thumb {
					background-color: #aaa;
				}
				.col-lg-3.col-md-4.col-6 {
					display: block;
					min-width: max-content !important;
					max-width: max-content !important;
					.menu-item {
						width: max-content !important;
						margin-right: 0;
						padding: 10px;
						margin-bottom: 10px;
					}
				}
			}
		}
		.menus {
			margin-top: 85px;
			.row {
				align-items: flex-start;
			}
		}
	}
	.category-title,
	.category-item {
		font-weight: 600;
		font-size: 20px;
		line-height: 20px;
		margin: 14px 0;
		display: inline-block;
	}
	.category-title {
		margin-top: 50px;
		color: $primary-color;
		font-size: 24px;
	}
	.category-item,
	.menu-item {
		font-weight: 300;
		margin: 0 0 14px 0;
		font-size: 16px;
		line-height: 16px;
		color: black;
		font-weight: bold;
	}

	a.d-block {
		margin-bottom: 5px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	.menu-item {
		width: 100%;
		padding: 16px;
		border: 1px solid #e5e8eb;
		border-radius: 8px;
		background-color: #fff;
		color: #637381;
		align-items: center;
		&:hover {
			background: $primary-color;
			color: #fff;
			img {
				filter: brightness(0) invert(1);
			}
			cursor: pointer;
		}
		img {
			width: 24px;
			margin-right: 8px;
		}
	}
	.menu-item.active {
		background: $primary-color;
		color: #fff;
		img {
			filter: brightness(0) invert(1);
		}
	}
}
