@import "../header/header_bottom_one.scss";

.header-six {
	.header-left {
		flex-basis: calc(25% + 10px) !important;
		padding-left: 10px;
		padding-right: 10px;
	}

	.header-right {
		padding-left: 10px;
		padding-right: 10px;
	}

	.header-center {
		margin: 0;
		flex: 0 0 50%;
		max-width: 50%;
	}

	.category-dropdown.show .dropdown-toggle {
		color: #fff !important;
		background-color: #333333;
	}

	.category-dropdown .dropdown-toggle:hover,
	.category-dropdown .dropdown-toggle:focus {
		color: $primary-color;
		background-color: #333333;
		outline: none !important;
	}

	.category-dropdown .dropdown-toggle {
		display: flex;
		align-items: center;
		align-self: stretch;
		color: #fff;
		font-weight: 400;
		font-size: 1.4rem;
		line-height: 1.5;
		letter-spacing: -0.01em;
		text-transform: uppercase;
		min-width: 270px;
		transition: background 0.4s, color 0.1s;
	}

	.category-dropdown .dropdown-toggle:before {
		height: 0;
	}

	.category-dropdown:not(.is-on):hover .dropdown-toggle {
		color: unset;
		background-color: transparent;
	}

	.header-bottom .for-container .header-left {
		position: relative;

		&::after {
			content: "";
			position: absolute;
			top: -0.5rem;
			right: 2rem;
			bottom: -0.4rem;
			width: 0.1rem;
			background-color: #444;
		}
	}

	.menu.sf-arrows .sf-with-ul::after {
		top: 49%;
		font-weight: 300;
		color: #ebebeb;
	}

	.header-bottom .menu > li + li {
		margin-left: 0.8rem;
	}

	.header-bottom .for-container .header-center {
		flex: 0 0 58.33%;
		max-width: 58.33%;

		margin: 0;
		padding-left: 2rem;
	}

	.header-bottom .for-container .header-right {
		margin-left: -3rem;
		margin-right: auto;

		i {
			padding-right: 0.8rem;
			color: $primary-color;
		}

		a {
			text-transform: none;
			letter-spacing: 0em;
			// color: #fff;
			transition: all 0.3s;
			line-height: 17px;

			&:hover,
			&:focus {
				color: $primary-color;
			}
		}

		position: relative;

		&::before {
			content: "";
			position: absolute;
			top: 0;
			bottom: 0;
			left: -3rem;
			width: 0.1rem;
			background-color: #444;
		}
	}

	.header-bottom .menu > li > .sf-with-ul {
		padding-right: 3rem;
		font-size: 1.4rem;
		font-weight: 500;
		font-family: $font-family;
		letter-spacing: -0.01rem;
	}

	.header-bottom .menu > li > a {
		color: #fff;
		padding: 1.6rem 1rem;
	}

	.header-bottom .menu > li > a:before {
		height: 0;
	}

	.menu ul,
	.menu .megamenu {
		margin-top: 0;
	}

	span.wishlist-count,
	span.cart-count {
		font-size: 1.2rem;
		font-weight: 400;
		color: #333;
	}

	.category-dropdown .dropdown-menu {
		left: 0;
		width: 100%;
		// margin-top: 0.65rem;
		border-radius: 0;
		border: none;
		padding: 0;
		box-shadow: 0 10px 30px rgba(0, 0, 0, 0.05);
	}

	.category-dropdown .dropdown-toggle {
		font-size: 1.4rem;
		font-weight: 700;
		font-family: $font-family;
		letter-spacing: -0.01rem;
		color: #fff;
	}

	.category-dropdown .dropdown-toggle:before {
		content: "\f131";
		font-family: "molla";
		border: none;
		font-size: 2.3rem;
		line-height: 1;
		margin-right: 1.5rem;
		display: flex;
		align-items: center;
		font-weight: 500;
		position: relative;
		color: $primary-color;
		transition: all 0s;
	}

	.category-dropdown .dropdown-toggle:after {
		content: "\f110";
		font-family: "molla";
		border: none;
		font-size: 1rem;
		line-height: 1;
		margin-left: 3.8rem;
		margin-bottom: 0.2rem;
		font-weight: 300;
	}

	.category-dropdown.show .dropdown-toggle:before {
		content: "\f191";
	}

	.category-dropdown.show .dropdown-toggle:after {
		content: "\f110";
	}

	.menu.sf-arrows li:last-child .sf-with-ul::after {
		font-family: "molla";
		content: "\f110";
		position: absolute;
		right: 0;
		top: 50%;
		display: none;
		font-size: 1rem;
		line-height: 1;
		margin-top: -0.5rem;
	}

	.sticky-header.fixed {
		position: fixed;
		left: 0;
		right: 0;
		top: 0;
		z-index: 999;
		animation-name: fixedHeader;
		animation-duration: 0.4s;
		background-color: #fff;
		box-shadow: 0 3px 6px rgba(51, 51, 51, 0.05);
	}

	.logo {
		@include mq("md", "max") {
			margin-top: 1.7rem;
			margin-bottom: 1.7rem;
			// width: 80px;
		}
	}

	.header-top {
		background: $header-top;
		.for-container {
			display: flex;
			justify-content: space-between;
		}
		z-index: 10 !important;
		border-bottom: 0.1rem solid #444444;
		.for-container,
		.container-fluid {
			position: relative;

			&:after {
				left: 15px;
				right: 15px;
			}
		}
	}

	.top-menu {
		a {
			font-size: 1.4rem;
			font-weight: 400;
			font-family: $font-family;
			letter-spacing: 0;
			color: #fff;
		}

		i {
			font-size: 1.8rem;
		}
	}

	.header-middle {
		border-bottom: 0.1rem solid #444444;
		padding: 14px 0px;

		.for-container {
			display: flex;
			padding: 0;
		}
		.only-desktop.container {
			// max-width: 100%;

			.header-left {
				.logo {
					margin-right: 20px;
					margin-left: 0;
					width: 200px;
				}
			}
		}
		.header-right {
			h6 {
				white-space: nowrap;
				text-align: center;
			}
		}
	}

	.cart-dropdown,
	.compare-dropdown {
		display: flex;
		align-self: stretch;
		align-items: center;

		.dropdown-toggle {
			color: #fff;
		}
	}

	.compare-dropdown {
		padding-left: 0;

		.dropdown-toggle {
			font-size: 2.6rem;
		}

		.compare-product-title {
			font-weight: 400;
			font-size: 1.3rem;
			line-height: 1.3;
			color: #666;
			text-align: left;
			margin-bottom: 0.4rem;

			a {
				color: inherit;

				&:hover,
				&:focus {
					color: $primary-color;
				}
			}
		}
	}

	.compare-products {
		list-style: none;
		padding: 0;
		margin: 0;
	}

	.compare-product {
		position: relative;
		padding: 0.5rem 0;
		margin: 0;
		box-shadow: none !important;

		&:hover {
			box-shadow: none;
		}
	}

	.cart-dropdown {
		padding-left: 1.5rem;

		.cart-count {
			color: #000;
		}

		.btn-primary {
			color: white;
			background-color: $primary-color;
			border-color: $primary-color;

			&:hover,
			&:focus {
				color: $primary-color;
				background-color: white;
				border-color: $primary-color;
			}
		}

		.btn-outline-primary-2 {
			color: $primary-color;
			background-color: white;
			border-color: $primary-color;

			&:hover {
				color: white;
				background-color: $primary-color;
			}
		}
	}

	.wishlist-link {
		margin-left: 2.2rem;
		color: #fff;

		.wishlist-count {
			color: #000;
		}
	}

	/* Header Search */
	.search-toggle {
		color: #666;
	}

	.header-search {
		.search-wrapper-wide {
			width: 57.5rem;
			max-width: 100%;
		}
	}

	.header-search-extended {
		margin-right: 0;
		max-width: none;

		i {
			color: #333;
		}

		.form-control,
		select {
			font-size: 1.4rem;
			font-weight: 400;
			font-family: $font-family;
			color: #777;
		}

		.form-control {
			height: 44px;

			padding: 1.2rem 2.4rem 1.2rem 2rem;
		}

		.btn {
			flex: 0 0 8rem;
			max-width: 8rem;
			height: 4.6rem;
		}
	}

	.mobile-menu-toggler {
		color: $body-text;
		padding-top: 0.5rem;
	}
	.header-bottom {
		.for-container {
			padding: 0;
		}
	}
	.mobile-menu-container-two {
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		opacity: 0;
		z-index: -25;
		background: rgba(0, 0, 0, 0.4) !important;

		.mobile-menu-wrapper {
			transform: translateX(-100%);
			transition: 0.4s ease-in-out;
		}
	}

	.mmenu-active-two {
		.mobile-menu-container-two {
			z-index: 15545411 !important;
			opacity: 1;
			position: fixed;
		}

		.mobile-menu-wrapper {
			transform: translateX(0) !important;
		}
	}

	.mobile-menu-close {
		right: 50px;
		top: 20px;
		mix-blend-mode: difference;
		i {
			font-weight: 900 !important;
			font-size: 40px;
			color: #ffffff;
		}
		[class^="icon-"]:before {
			z-index: 1003;
		}
	}

	.mmenu-active-two {
		.mobile-menu-container-two {
			transform: unset;
		}
	}

	@include mq("md", "max") {
		.dropdown.category-dropdown {
			.dropdown-toggle {
				min-width: 230px;

				&:after {
					margin-left: 3.5rem;
				}
			}
		}
	}

	@include mq("sm", "max") {
		.dropdown.category-dropdown {
			.dropdown-toggle {
				min-width: 200px;

				&:after {
					margin-left: 2.3rem;
				}
			}
		}
	}

	background: #ffffff;

	.header-top {
		border: none;
		padding: 10px 0 6px 0;
		margin: 0;

		.location,
		.header-right {
			display: flex;
			align-items: center;
			padding: 0 !important;

			span {
				text-align: left;
				display: -webkit-box;
				-webkit-line-clamp: 1;
				-webkit-box-orient: vertical;
				overflow: hidden;
				--overfdropdownlow: hidden;
				text-overflow: ellipsis;
			}
		}
	}

	.header-right,
	.header-left {
		.location-link,
		.location,
		.change-lang,
		.info {
			z-index: 3 !important;
			margin-right: 31px;
			font-style: normal;
			font-weight: 300;
			font-size: 14px;
			line-height: 24px;
			color: #002930;

			.langBut {
				width: 120px;

				button {
					padding: 10px 12px;
					width: auto;
				}
			}

			.langBut.active {
				button {
					color: #ffffff !important;
				}
			}
		}

		flex-basis: unset;
		max-width: unset;

		.location-link,
		.location {
			z-index: 15;
			img {
				width: 24px;
			}
		}

		.change-lang {
			margin-right: 0;

			img {
				width: 22px;
				height: 20px;
				margin-right: 5px;
			}

			i {
				margin-left: 6px;
			}

			&:hover {
				cursor: pointer;
			}

			.lang-selector {
				margin: 8px 0 0 0 !important;
				background: #ffffff;
				box-shadow: 5px 10px 16px rgb(51 51 51 / 5%),
					-5px 10px 16px rgb(51 51 51 / 5%);
				width: auto;
			}
		}
	}

	.header-middle,
	.header-top {
		z-index: 25;
		border: none;
	}

	.cart-dropdown {
		.dropdown-menu-right {
			padding: 12px 0;
			.dropdown-cart-products {
				margin: 0;
				.product {
					padding: 14px 24px 8px 24px;
					align-items: unset;
					justify-content: space-between;
					.product-detail-left {
						flex-direction: row;
						display: flex !important;
						align-items: center;
						// justify-content: flex-start !important;
						.product-image-container {
							margin: 0 0 6px 0 !important;
							img {
								height: 100%;
								min-width: 100%;
								object-fit: auto;
							}
						}
						.product-title {
							font-size: 16px;
							margin-left: 10px;
						}
					}
					.product-detail-right {
						text-align: right;
						.cart-product-qty {
							display: block;
						}
						span {
							font-size: 16px;
						}
						.btn-remove {
							margin: 4px 0 0 auto;
							position: unset !important;
							font-size: 14px;
							color: #f14f66;
						}
					}
				}
			}
			.dropdown-cart-action,
			.dropdown-cart-total {
				font-weight: 600;
				padding: 0 24px;
				a {
					border-radius: 10px;
					margin: 0;
				}
			}
			.dropdown-cart-total {
				margin-top: 12px;
			}
		}
	}
	.logo {
		// margin-right: 33px;
		flex-shrink: unset;
		min-height: unset;

		img {
			width: 150px;
		}
	}

	.sticky-header {
		padding: 24px 0;
	}

	.header-middle {
		padding: 10px 0;
		top: 0;
		z-index: 2;

		.my-menu {
			position: relative;
			top: 0;
			left: -100%;
			background: #000;
		}

		.header-six-ver-menu {
			background: $primary-color;
			border-radius: 12px;
			display: flex;
			justify-content: center;

			.menu-burger {
				display: flex;
				align-items: center;
				justify-content: center;

				&:before {
					content: "\F131";
					font-family: "molla";
					border: none;
					font-size: 2.3rem;
					line-height: 1;
					padding: 10px;
					align-items: center;
					font-weight: 500;
					color: #ffffff;
					transition: all 0.4s;
				}
			}

			.category-dropdown {
				.dropdown-menu {
					border-radius: 0 !important;

					position: fixed;
					z-index: 16 !important;
					width: 26%;
					top: 0;
					height: 100vh;
					display: block;

					.mobile-menu-close {
						top: unset !important;
						right: unset !important;
						i {
							font-weight: 900 !important;
							font-size: 40px;
							color: #ffffff;
						}
					}

					.side-nav {
						margin-top: 20px;
					}
					.menu-vertical {
						padding: 0 0 20px 30px !important;
						overflow: auto;
						&::-webkit-scrollbar {
							width: 0;
						}
					}
					.menu-vertical > li > a {
						border-bottom: none;
						font-size: 18px;
						line-height: 2.5;
						padding: 0 0 0 10px;
					}

					.menu-vertical > li {
						padding-right: 24px;
					}

					.megamenu-container {
						a,
						.megamenu .mega-menu-links {
							font-size: 18px;
							// line-height: 30px;
							padding: 10px 20px;
							border-radius: 15px;
							transition: 0.2s ease-in-out all;

							&:hover {
								background: $primary-color;

								img {
									filter: brightness(0) invert(1);
								}

								color: #ffffff;
							}
						}

						.megamenu .menu-title,
						.megamenu .mega-menu-links {
							border: none;
							position: relative;
							padding: 5px 10px !important;
							display: flex;
							align-items: center;
							.menu-icon {
								width: 30px;
								margin: 0 8px 0 0;
							}
							&:hover {
								color: #ffffff;
								background: $primary-color;
							}
						}
					}

					.banner-menu {
						background: unset !important;
						color: unset !important;
						float: unset !important;

						img {
							filter: unset !important;
						}

						&:hover {
							transform: scale(1.01);
						}
					}

					.megamenu {
						position: absolute;
						top: -20px;
						right: 0 !important;
						// width: fit-content;
						min-height: 100vh !important;
						border-radius: 0 !important;

						a {
							line-height: 30px !important;
						}
						.no-gutters {
							.col-md-12 {
								.menu-col {
									padding: 0 15px;
									margin-top: 0;
									height: 100vh;
									overflow-y: scroll;
									.row {
										// column-count: 2;
										display: flex;
										padding: 20px 0;
										margin-bottom: 20px;
									}
									.col-12 {
										max-width: 49%;
										margin-bottom: 0;
									}
									&::-webkit-scrollbar {
										width: 12px;
									}

									&::-webkit-scrollbar-track {
										box-shadow: inset 0 0 6px
											rgba(0, 0, 0, 0.3);
									}

									&::-webkit-scrollbar-thumb {
										background: $primary-color;
										border-radius: 10px;
									}
								}
							}
						}
					}
				}

				.banner {
					margin-top: 15px;
					margin-left: 50px;
					max-width: 450px !important;
					float: unset !important;
				}
			}
		}

		.header-left {
			padding: 0;
		}

		.header-right {
			.dropdown.cart-dropdown {
				padding: 0;

				.dropdown-menu-right {
					width: 500px;
				}
			}

			h6 {
				margin: 10px 0 0 0;
				font-size: 14px;
				line-height: 8px;
			}

			a {
				margin-left: 32px;
				align-items: center;
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				width: unset !important;
				height: unset !important;
			}

			a:last-child {
				margin-left: 0;
			}

			.cart-count,
			.choosen-products-icon span {
				position: absolute;
				top: -7px;
				right: -10px;
				color: #ffffff;
				min-width: 15px;
				height: 15px;
				font-size: 10px;
			}
		}

		.header-center {
			margin-left: 41px;
			width: 100%;
			flex: unset;

			.search-box-wrapper {
				width: 100%;
				position: relative;
				.header-search-wrapper {
					width: unset;
				}
			}

			.header-search-extended .form-control {
				height: 48px;
				padding: 12px 20px !important;
			}

			.header-search {
				.header-search-wrapper.search-wrapper-wide.search {
					border-radius: 11px;
					overflow: hidden;
				}

				.search-button {
					width: 103px !important;
					margin: 0;
					height: 48px;
					border-radius: 0 11px 11px 0;
					background: $primary-color;

					h6 {
						line-height: 24px;
						margin: 0;
						font-size: 14px;
						color: #ffffff;
					}
				}
			}
		}
	}

	.container::after {
		display: none !important;
	}

	.header-left::after {
		display: none !important;
	}

	.header-top {
		.header-left,
		.header-right {
			padding: 0;
		}
	}
}
.search-result {
	top: 30px;
	box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.15);
	border-radius: $border-item;

	&::-webkit-scrollbar {
		height: 1px;
		width: 4px;
	}
	&::-webkit-scrollbar-thumb {
		background-color: #ccc;
		border-radius: 5px;
	}
	.menus,
	.products {
		.menu-item,
		.search-result-item {
			display: flex;
			align-items: center;
			padding: 10px;
			margin-bottom: 0;
			border-bottom: 1px solid #f3f3f3;
			.img {
				margin-right: 12px;
				min-width: 50px;
				min-height: 50px;
				max-width: 50px;
				max-height: 50px;
				background: #f7f7f7;
				border: 1px solid #f3f3f3;
				border-radius: 5px;
				overflow: hidden;
				img {
					object-fit: contain;
					background: white;
					width: -webkit-fill-available;
					height: 100%;
					max-height: inherit;
				}
			}
			.menu-result-item {
				a {
					font-weight: 400;
					font-size: 14px;
					line-height: 17px;
					letter-spacing: -0.02em;
					color: $dark-text;
				}
			}
			.menu-breadcrumb {
				.breadcrumb-item {
					font-size: 12px;
					line-height: 15px;
					letter-spacing: -0.02em;
				}
			}
			a {
				.new-price {
					margin-left: 10px;
				}
				.new-price,
				.old-price,
				.price {
					font-size: 12px;
				}
			}
		}
	}
}
