.only-desktop {
	display: flex !important;
}
.only-mobile {
	display: none !important;
}
@media (max-width: 991px) {
	.header-middle.sticky-header.fixed {
		position: relative;
		left: unset;
		right: unset;
		top: unset;
		animation-name: unset;
		box-shadow: unset;
	}

	.only-desktop {
		display: none !important;
	}

	.only-mobile {
		display: block !important;
	}
	.header-six {
		background: $primary-color;
		.searcher-row {
			.search-box-wrapper {
				.header-search {
					form {
						.btn.search-button {
							.only-desktop.active-span {
								display: flex !important;
								color: $light-text;
							}
							.only-mobile.no-active-span {
								display: none !important;
							}
						}
					}
				}
			}
		}

		// z-index: 0;
		.header-top {
			display: none;
		}
		.header-search {
			display: flex;

			form {
				width: 100%;
			}
			.header-search-wrapper.search-wrapper-wide.search {
				display: flex;
				min-width: 100% !important;
				position: unset;
				border: 1px solid #eeeeee;
				&::before {
					display: none;
				}
			}
			button:disabled {
				background-color: transparent !important;
			}
		}
		.only-mobile.home-page-header {
			.location-link {
				span {
					text-align: left;
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
				}
				img {
					margin-right: 4px;
					width: 20px;
					filter: brightness(0) invert(1);
				}
			}
			.logo {
				.photo-shimmer {
					height: 100%;
				}
			}
			.searcher-row {
				width: 100%;
				margin: 0 !important;

				.header-search {
					display: block;
				}

				padding: 5px 0 0 0;

				.header-search-wrapper.search-wrapper-wide.search {
					position: relative;
					margin: 0;
					width: 100%;
					border: none;
					.btn.search-button {
						img {
							display: none !important;
						}
					}
					&:before {
						display: none;
					}
				}

				.header-search-visible .header-search-wrapper {
					display: flex !important;
					right: 0;
					height: 41px;
					transition: 0.4s ease-in-out;
					border: none !important;
					input[type="search"]::-webkit-search-cancel-button {
						display: none;
					}

					border-radius: 12px;
					background: rgba(255, 255, 255, 0.3);

					.sr-only {
						display: none;
					}

					input {
						&::placeholder {
							color: rgba(255, 255, 255, 0.49);
						}

						background: transparent;
						color: #ffffff;
						height: 100%;
						padding: 8px 16px !important;

						&::placeholder {
							color: rgba(255, 255, 255, 0.49);
						}
					}
					.btn.search-button {
						display: flex;
						align-items: center;
						flex: unset;
						min-width: 24px;
						height: 100%;
						margin: 0 5px 0 5px;
						h6 {
							margin: 0;
						}
					}
				}
			}
		}

		.search-result-wrapper {
			z-index: 10000 !important;
			.search-result {
				z-index: 10000 !important;
			}
		}
	}
}
