.header-four {
	.container {
		max-width: 1550px !important;
	}
	.container::after {
		content: none;
	}
	.header-top {
		.container {
			display: flex;
			justify-content: space-between;
			font-size: 16px !important;
			p {
				margin: 5px;
			}
			.locationBut {
				margin: 0;
			}
			.header-right {
				.info {
					margin-right: 24px;
				}
				p {
					margin: 0 12px;
				}
				.change-lang {
					font-size: 14px;
					width: 90px;

					img {
						display: none;
					}
					div {
						justify-content: space-between;
					}
				}
				.lang-selector {
					left: 0;
					right: 0;
					top: 30px;
					margin: 0;
					border-color: #ccc;
					border-radius: 5px;
					overflow: hidden;
					.langBut {
						border-radius: 0 !important;
						padding: 5px 8px;
						margin: 0;
						line-height: 1;
						background-color: #fff;
						&.active {
							background-color: #e5e8eb;
						}
						.text-white {
							color: black !important;
						}
					}
				}
			}
		}
	}

	.header-middle {
		display: flex;
		position: relative;
		border: none;
		padding: 24px 0;
		width: 100%;
		margin: 0 auto;
		.header-left {
			margin-right: auto;
			.logo {
				img {
					width: 156px;
				}
				margin: 0 !important;
			}
		}
		.btn-catalog {
			min-width: max-content !important;
			padding: 12px 15px;
			font-size: 14px;
			display: flex;
			align-items: center;
			white-space: nowrap;
			border-radius: 12px;
			img {
				display: none;
			}
			.icon-bars {
				width: 20px;
				height: 20px;
				display: flex;
				font-size: 18px;
				align-items: center;
				margin: 0;
				margin-right: 5px;
				overflow: hidden;
				&::before {
					display: flex;
					align-items: center;
				}
			}
		}
		.header-center {
			max-width: none !important;
			min-width: none !important;
			flex-grow: 1 !important;
			margin: 0 !important;

			.search-wrapper {
				width: 100%;
				.search-wrapper {
					.search-form {
						display: flex;
						height: 50px;
						padding: 3px;
						border-radius: 12px;
						overflow: hidden;
						border: 1px solid #dfe4d5;
						transition: 0.2s;
						&:hover {
							border-color: $primary-color;
						}
						.btn-everywhere {
							padding: 10px;
							font-size: 16px;
							min-width: 107px;
							border-radius: 8px;
							background-color: #f4f6f8;
							cursor: pointer;
							span {
								white-space: nowrap;
								overflow: hidden;
								text-overflow: ellipsis;
							}
						}
						input.form-control {
							height: 100%;
							padding: 12px;
							border: none;
							outline: none;
							margin-bottom: 0 !important;
							border-radius: 0 !important;
						}
						.btn.search-btn {
							min-width: 103px !important;
							padding: 12px 18px !important;
							border-radius: 12px;
							i {
								display: none;
							}
						}
					}
				}
				.categories_overlay {
					position: fixed;
					top: 0;
					left: 0;
					right: 0;
					bottom: 0;
					background-color: rgba($color: #000000, $alpha: 0.4);
					z-index: 999;
					.categories_content {
						max-width: 45%;
						height: 98vh;
						margin: 10px auto 0;
						padding: 32px;
						border-radius: 24px;
						background-color: #fff;
						.categories_list {
							height: 100%;
							column-count: 2;
							flex-wrap: wrap;
							overflow-y: scroll;
							&::-webkit-scrollbar {
								width: 5px;
							}
							&::-webkit-scrollbar-thumb {
								background-color: #aaa;
							}
							.categories_item {
								width: 100%;
								display: flex;
								margin-right: auto;
								padding: 2px 10px;
								background-color: #fff;
								border-radius: 8px;
								cursor: pointer;
								overflow: hidden;
								align-items: center;
								img {
									width: 25px;
									height: 25px;
									margin-right: 10px;
								}
								p {
									margin: 0;
									color: black;
									font-weight: 500;
									overflow: hidden;
									text-overflow: ellipsis;
									white-space: nowrap;
								}
								&:hover {
									background-color: #f4f4f4;
								}
								span {
									color: #000000;
								}
							}
						}
					}
				}
			}
		}
		.header-right {
			display: flex;
			align-items: center;
			padding: 0;
			margin-left: 25px;
			.header-right_icon {
				display: flex;
				align-items: center;
				flex-direction: column;
				margin: 0 15px;
				color: black !important;
				img {
					width: 25px;
					height: 25px;
					margin-bottom: 5px;
				}
				span {
					white-space: nowrap;
					margin: 0;
					font-size: 12px;
					color: #002930;
				}
				.position-relative {
					span {
						position: absolute;
						top: -7px;
						right: -7px;
						background: $primary-color;
						border-radius: 50%;
						color: #fff;
						width: 20px;
						height: 20px;
						font-size: 10px;
						line-height: 0;
						display: flex;
						align-items: center;
						justify-content: center;
					}
				}
			}
			.change-lang {
				display: none;
			}
		}
	}
}
.catalog-menu {
	position: absolute;
	z-index: 10;
	top: 141px;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #0e0e0e99;
	.catalog-menu-body {
		position: relative;
		max-height: 600px;
		background: #f4f4f4;
		overflow: hidden;
		&.active {
			background: linear-gradient(
				45deg,
				#f4f4f4 50%,
				#fff 50%
			) !important;
		}
		.catalog-header {
			display: none;
		}
		.container {
			position: static;
			display: flex;
			align-items: stretch;
			.col-3 {
				max-height: 600px;
				overflow: auto;
				&::-webkit-scrollbar {
					width: 4px;
				}
				&::-webkit-scrollbar-thumb {
					border-radius: 8px;
					background-color: #858ea5;
				}
				&::-webkit-scrollbar-track {
					background-color: transparent;
				}
				.menu-item {
					.menu-link {
						padding: 12px 15px;
						display: flex;
						align-items: center;
						justify-content: space-between;
						color: #002930;
						font-size: 16px;
					}
					.menu-link-mobile {
						display: none;
					}
					&.active {
						background-color: #fff;
					}
				}
			}
			.col-9 {
				min-height: 100%;
				position: relative;
				padding: 0;
				.child-menu-list {
					height: 600px;
					margin: 0;
					padding: 24px 0 0 24px;
					background-color: #fff;
					overflow: auto !important;
					&::before {
						content: "";
						position: absolute;
						right: 0;
						top: 0;
						bottom: 0;
						background-color: #fff;
						width: 200px;
					}
					&::-webkit-scrollbar {
						width: 4px;
					}
					&::-webkit-scrollbar-thumb {
						border-radius: 8px;
						background-color: #858ea5;
					}
					&::-webkit-scrollbar-track {
						background-color: transparent;
					}
					.menu-item {
						flex-grow: 1;
						padding-top: 0 !important;
						.menu-link {
							font-size: 18px;
							font-weight: bold;
							color: #002930;
						}
						.menu-list {
							li {
								font-size: 14px;
								margin: 12px 0;
								a {
									color: #002930;
								}
							}
							.see-more {
								font-size: 16px;
								color: #0092fd;
								cursor: pointer;
							}
						}
					}
				}
			}
			.see-all-btn {
				display: none;
			}
		}
	}
}
.only-desktope {
	display: block;
}
.only-mobilee {
	display: none;
}
@media (max-width: 1400px) {
	.header-four {
		.container {
			min-width: 97% !important;
		}
		.header-middle {
			.categories_overlay {
				.categories_content {
					max-width: 80% !important;
				}
			}
		}
		.btn-catalog {
			i {
				margin: 0 !important;
			}
			span {
				display: none !important;
			}
		}
		.child-menu-list {
			&::before {
				width: 60px !important;
			}
		}
	}
}
@media (max-width: 700px) {
	.only-desktope {
		display: none;
	}
	.only-mobilee {
		display: flex;
	}
}
@media (max-width: 1000px) {
	.header-four {
		background-color: $primary-color;
		.container {
			min-width: 97% !important;
		}
		.header-top {
			display: none;
		}
		.header-middle {
			padding: 12px 0 !important;
			.col-3 {
				display: none !important;
			}
			.col-9 {
				padding: 0;
				min-width: 100%;
			}
			.header-center {
				display: flex;
				align-items: center;
				flex-direction: row-reverse;
				padding: 0 !important;
				.search-wrapper {
					// margin-right: 8px;
					.btn-everywhere {
						display: none;
					}
					.search-form-wrapper {
						.search-form {
							flex-direction: row-reverse;
							border: none !important;
							background-color: rgba(
								$color: #dfffe8,
								$alpha: 15%
							);
							border-radius: 5px !important;
							height: 40px !important;
							.form-control {
								color: #fff;
								padding: 0 10px !important;
								&::placeholder {
									color: #fff;
								}
								&:focus {
									background-color: transparent !important;
								}
							}
							button.btn.btn-primary.search-btn {
								min-width: 40px !important;
								padding: 0 !important;
								border: none;
								color: #fff;
								background-color: transparent;
								i {
									width: 18px;
									height: 18px;
									font-size: 20px;
									z-index: 2;
									display: block !important;
									transform: rotate(-90deg);
								}
								span {
									display: none;
								}
							}
						}
					}
					.categories_overlay {
						display: none;
					}
				}
			}
			.header-left {
				margin: 0 20px 0 12px;
				img {
					width: 10px;
				}
			}
			.header-right {
				margin-left: 5px !important;
				.header-right_icon {
					display: none !important;
				}
				.header-right_icon.check_out {
					display: flex !important;
					margin: 0 10px !important;
					.cart-count {
						background: #fff;
						color: $primary-color;
					}
					img {
						filter: brightness(0) invert(1);
						margin-bottom: 0;
					}
					.icon-name {
						display: none;
					}
				}
				.change-lang {
					display: block;
					img {
						width: 36px;
						margin-left: 10px;
					}
					p,
					i {
						display: none;
					}
					.lang-selector {
						width: max-content;
						background-color: #fff !important;
						margin-top: 5px !important;
						margin-right: 0 !important;
						left: -10px;
						.langBut {
							padding: 5px !important;
							border-radius: 0;
							button {
								padding: 0;
							}
							span {
								display: none;
							}
						}
					}
				}
			}
		}
	}
	.catalog-menu {
		top: 0 !important;
		.catalog-menu-body {
			position: fixed !important;
			left: 0;
			right: 0;
			min-height: 100vh !important;
			background-color: #fff !important;
			box-shadow: 0 10px 10px #ccc;
			.catalog-header {
				display: flex !important;
				align-items: center;
				padding: 15px;
				h3 {
					flex-grow: 1;
					text-align: center;
				}
				i::before {
					font-size: 20px;
					font-weight: bold !important;
					color: black;
				}
			}
			.container {
				.col-3 {
					min-width: 100% !important;
					min-height: 90vh !important;
					background-color: #fff;
					padding-bottom: 20px;
					margin: 0 !important;
					overflow: auto;
					.menu-item {
						border-bottom: 0.5px solid #e5e8eb;
						padding: 0 !important;
						margin: 0 !important;
						.icon-angle-right::before {
							font-weight: bold;
						}
					}
				}
				.col-9 {
					position: absolute !important;
					min-width: 100% !important;
					top: 57px;
					left: 0 !important;
					right: 0 !important;
					bottom: 0 !important;
					.child-menu-list.row {
						height: 100%;
						padding: 0 !important;
						flex-direction: column;
						&::before {
							display: none;
						}
						.menu-item {
							min-width: 100%;
							flex-grow: 0 !important;
							flex: 0 !important;
							height: max-content !important;
							padding: 12px 15px 0 !important;
							&:hover {
								background-color: rgba(
									$color: $primary-color,
									$alpha: 0.2
								);
								cursor: pointer;
							}
							a {
								display: block;
								font-size: 16px !important;
								padding-bottom: 12px;
								font-weight: normal !important;
								margin-bottom: 0;
								border-bottom: 0.5px solid #e5e8eb;
							}
							.menu-list {
								display: none;
								.see-more {
									display: none;
								}
							}
						}
					}
				}
			}
		}
	}
}

@media (max-width: 575px) {
	.header-four {
		// padding-top: 35px;
		.header-right {
			padding: 0 !important;
			width: max-content;
		}
	}
