.banners {
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-wrap: wrap;
	.banner {
		margin-bottom: 8px;
	}
}

// mini-slider
.mini-slider {
	margin-bottom: 20px;
	.banner-one {
		margin-right: 10px;
		.div-shimmer {
			margin: 0 !important;
		}
		img {
			border-radius: 20px;
			height: 200px;
			max-height: 200px;
		}
	}

	.splide__pagination {
		bottom: -20px;
	}
	.slider-title {
		margin: 10px 0 0 0 !important;
		font-size: 16px;
		font-weight: 600;
	}
}

@media (max-width: 560px) {
	.mini-slider {
		.div-shimmer .photo-shimmer {
			height: 100px !important;
		}
		.slider-title {
			font-size: 14px;
		}
	}
}
