.login-mobile {
	width: 100%;
	padding-top: 20px;
	// padding-top: 50px;
	margin-bottom: 0 !important;
	.container {
		width: 94% !important;
		margin: 0 auto;
		.logo {
			margin-bottom: 20px;
		}
		form {
			display: flex;
			flex-direction: column;
			.form-group {
				margin-bottom: 0;
			}
			.input-wrapper-div {
				position: relative;
				img {
					position: absolute;
					right: 10px;
					bottom: 50%;
					transform: translateY(50%);
				}
			}
			.login-btn {
				margin-top: 20px;
				padding: 12px 0;
			}
			.spinner-border {
				width: 12px;
				height: 12px;
				border-width: 1.5px;
			}
		}
		.footer-btns {
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin-top: 10px !important;
			button {
				min-width: 48% !important;
				background-color: #f4f5f6;
				white-space: nowrap;
				font-size: 12px;
			}
		}
		.form-group {
			label {
				margin-top: 15px;
				margin-bottom: 0;
				span {
					color: red;
				}
			}
			.input-wrapper-div {
				& + span {
					color: red;
					font-size: 10px;
				}
			}
			.form-control {
				margin-bottom: 0;
				border-radius: 8px !important;
			}
			.form-control + span {
				color: red;
				font-size: 10px;
			}
		}
		.auth-form + .footer-btns {
			button.btn {
				border-radius: 8px !important;
				background-color: rgba(55, 114, 255, 0.05);
				color: rgba(55, 114, 255, 1);
				&.forgot-link {
					background-color: rgba(239, 70, 111, 0.05);
					color: rgba(239, 70, 111, 1);
				}
			}
		}

		.registration-form {
			margin-top: 0;
			label {
				margin-top: 20px !important;
			}
			.custom-control-label {
				font-size: 10px;
				margin: 0 !important;
				margin-top: 10px !important;
				padding: 0 !important;
				padding-left: 25px !important;
				z-index: 999 !important;
				&::before,
				&::after {
					top: 50%;
					transform: translateY(-50%);
				}
			}
		}
		.verify-inputs-wrapper {
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: space-between;
			input.form-control {
				width: 50px;
				height: 50px;
				font-size: 28px;
				text-align: center;
				padding: 0;
				margin-right: 15px;
				color: black !important;
				border: 1px solid #eee;
				&::placeholder {
					font-size: 38px;
					color: black;
					letter-spacing: -5px;
					margin-left: 3px;
				}
				&:focus {
					background-color: rgba(
						$color: $primary-color,
						$alpha: 0.05
					);
					border-color: $primary-color;
				}
				&:last-child {
					margin-right: 0;
				}
				&.warning {
					border-color: red;
				}
			}
		}
		.resend-code button:disabled {
			background-color: #fff !important;
			color: rgba(55, 114, 255, 1) !important;
		}
		button:disabled {
			background-color: #f4f5f6 !important;
			color: #b1b5c3 !important;
			border: none !important;
		}
	}
}
