.map_content {
	height: 100%;
	.user-location,
	.map-serach-panel {
		display: none !important;
	}
	max-width: 100%;
	position: relative;

	.ymaps-2-1-78-controls__control_toolbar {
		margin-right: 70px !important;
		position: absolute !important;
		left: 50%;
		transform: translateX(-50%);
	}

	.ymaps-2-1-78-searchbox-button {
		background-color: $secondary-color !important;
	}

	input.ymaps-2-1-78-searchbox-input__input {
		display: block;
		width: 100%;
		height: 48px !important;
		padding: 0 12px !important;
		margin: 0 !important;
		font-size: 14px !important;
		line-height: 28px !important;
		box-sizing: unset !important;
		border: none !important;
	}

	.ymaps-2-1-78-searchbox__normal-layout .ymaps-2-1-78-searchbox-button {
		height: 48px !important;

		.ymaps-2-1-78-searchbox-button-text {
			line-height: 46px !important;
			color: white;
		}
	}

	.ymaps-2-1-78-searchbox__normal-layout:after {
		border-radius: $border-radius-blocks !important;
	}

	.ymaps-2-1-78-float-button.ymaps-2-1-78-_hidden-text {
		padding: 25px;
		border-radius: 50% !important;
		position: relative;
	}

	.ymaps-2-1-78-float-button-icon.ymaps-2-1-78-float-button-icon_icon_geolocation {
		display: block;
		position: absolute;
		top: 50%;
		left: 50%;
		width: 35px;
		height: 35px;
		transform: translate(-50%, -50%);
		background-size: cover;
	}

	.close.closeMap {
		position: absolute;
		width: 40px;
		height: 40px;
		top: 10px;
		right: 10px;
		z-index: 999;
		opacity: 1;
	}

	.closeMap span {
		background: snow;
		display: flex;
		width: 100%;
		height: 100%;
		align-items: center;
		justify-content: center;
		opacity: 1;
		padding: 7px;
		border-radius: 50%;
		// box-shadow: 0 0 10px rgb(0 0 0 / 20%);
		i {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 30px;
			height: 30px;
		}
	}

	.location-wrapper {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -90%);
		z-index: 999;
		img {
			width: 40px;
		}
	}

	.location-address {
		position: absolute;
		bottom: 100px;
		left: 50%;
		width: 80%;
		font-weight: bold;
		transform: translate(-50%, -50%);
		z-index: 999;
		font-size: 25px;
		text-align: center;
		line-height: 1.25;
		text-shadow: -1px -1px 0 rgb(255, 255, 255),
			1px -1px 0 rgb(255, 255, 255), -1px 1px 0 rgb(255, 255, 255),
			1px 1px 0 rgb(255, 255, 255);
	}

	.btn-address {
		position: absolute;
		bottom: 35px;
		right: 50%;
		transform: translateX(50%);
		z-index: 999;
		@include mq("md", "max") {
			bottom: 45px;
			width: 190px;
			height: 40px;
			font-size: 15px;
			line-height: 14px;
		}
	}

	@include mq("md", "max") {
		.ymaps-2-1-78-controls__control.ymaps-2-1-78-user-selection-none {
			inset: auto 22px 50px auto !important;
		}
		.ymaps-2-1-78-controls__control_toolbar {
			top: 35px;
		}
	}

	.map-select {
		position: absolute;
		width: 240px !important;
		bottom: 25%;
		left: 50%;
		padding: 5px 15px;
		transform: translate(-50%);
		border: 1px solid $primary-color;
		z-index: 999;
		background-color: #fff;
	}

	.address-history {
		max-width: 85%;
		position: absolute;
		bottom: 50px;
		left: 50%;
		transform: translate(-50%);
		z-index: 999;
		display: flex;
		justify-content: flex-start;
		overflow-y: auto;
		padding: 20px;

		li {
			min-width: 220px;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			flex-grow: 1;
			padding: 5px 10px;
			margin: 5px;
			border-radius: 10px;
			background-color: #fff;
			box-shadow: 0 2px 5px #00000021;
			cursor: pointer;
		}

		li:first-child {
			display: none;
		}
	}

	@include mq("md", "max") {
		.location-address {
			width: 90%;
			font-size: 18px;
			margin-bottom: 0;
		}
		.address-history {
			display: none !important;
		}
	}
}
#map-modal {
	padding: 0px !important;
	height: 100%;
	width: 100%;
	top: 0;
	margin: 0;
	.modal-content {
		.map-body,
		.map_content,
		.ymaps-2-1-79-map {
			height: 100% !important;
		}
	}
}
@media (max-width: 991px) {
	#map-modal {
		padding: 0px !important;
		height: 100%;
		width: 100%;
		top: 0;
		margin: 0;
		.user-location,
		.map-serach-panel {
			display: block !important;
		}
		.modal-content {
			overflow: hidden;
			height: 100%;
			top: unset;
			bottom: unset;
			border-radius: 0 !important;
			.map_content,
			.ymaps-2-1-79-map,
			.map-serach-panel {
				height: 100% !important;
				.close.closeMap {
					top: 25px;
					span {
						border: none;
					}
				}
			}
			.search-results {
				height: 100%;
				.result {
					border-top: 1px solid #e5e5e5;
					padding: 12px;
					.title,
					.description {
						margin: 0;
					}
					.title {
						margin-bottom: 8px;
						font-size: 14px;
						line-height: 16px;
					}
					.description {
						font-size: 11px;
						line-height: 13px;
						color: #8f8f8f;
					}
				}
				.search-loader {
					width: 100%;
					height: 100%;
					background-image: url("../../assets/images/serach-anime.gif");
					background-size: contain;
					background-repeat: no-repeat;
				}
			}
			.btn.btn-primary.btn-address,
			select.form-control,
			.location-address,
			.ymaps-2-1-79-controls__toolbar {
				display: none !important;
			}
			.add-address-button,
			.btn.search-button,
			input.form-control {
				padding: 10px;
				height: 50px;
				font-size: 16px;
				margin-bottom: 12px;
				background: $primary-color;
				color: #fff;
				font-weight: bold;
				border-radius: 10px !important;
				width: 100%;
			}
		}
		.map_content {
			.user-location {
				position: absolute;
				z-index: 2001;
				background: #fff;
				padding: 5px 20px 20px 20px;
				transition: 0.4s;
				border-top-left-radius: 15px;
				border-top-right-radius: 15px;
				box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
				bottom: 0;
				width: 100%;
				.user-address-info {
					display: flex;
				}
				.btn.opener {
					width: 100%;
				}
				.address-history {
					display: flex !important;
					margin-bottom: 0;
					max-width: 100%;
					bottom: unset;
					left: unset;
					transform: unset;
					position: relative;
					overflow-x: auto;
					padding: 5px 0;
					li {
						background: #f4f4f4;
						border-radius: 12.4275px;
						box-shadow: unset;
						padding: 10px;
						color: $dark-text;
						&:nth-child(2) {
							display: block;
							margin-left: 0;
						}
					}
					&::-webkit-scrollbar {
						display: none;
					}
					.block-menu-item {
						margin-right: 10px;
						a {
							padding: 5px 10px;
							border-radius: 10px;
							font-size: 14px;
							white-space: nowrap;
							background-color: #ebf0f7;
						}
					}
				}
				.btn.search-button,
				input.form-control {
					background: #fff;
					color: $dark-text;
					border: 1px solid #e5e5e5;
					span {
						width: fit-content;
						text-align: left;
						display: -webkit-box;
						-webkit-line-clamp: 1;
						-webkit-box-orient: vertical;
						overflow: hidden;
						--overfdropdownlow: hidden;
						text-overflow: ellipsis;
					}
				}
			}
			.user-location.activePanel {
				transform: translateY(90%);
			}
			.map-serach-panel {
				background: #fff;
				position: absolute;
				z-index: 2002;
				width: 100%;
				transform: translateX(100%);
				transition: 0.4s;
				.serarch-panel {
					padding: 10px;
					margin-top: 25px;
					padding-bottom: 15px;
					.button-title {
						.back-button {
							height: 40px;
							width: 40px;
							border-radius: 50%;
							box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
						}
						margin-bottom: 20px;
						.title {
							margin: 0 auto;
							font-weight: 600;
						}
					}
					input {
						background: transparent;
						color: $dark-text;
					}
				}
			}
			.map-serach-panel.search-active {
				transform: translateX(0);
			}
		}
	}
	// @keyframes locationWrapper {
	// 	0% {
	// 		transform: translateY(0);
	// 	}
	// 	50% {
	// 		transform: translateY(10px);
	// 	}
	// 	100% {
	// 		transform: translateY(0);
	// 	}
	// }
}
.ymaps-2-1-79-controls__control.ymaps-2-1-79-user-selection-none {
	width: 40px;
	height: 40px;

	.ymaps-2-1-79-float-button.ymaps-2-1-79-_hidden-text {
		box-shadow: 0 0 10px rgb(0 0 0 / 20%);
		border-radius: 50%;
		width: 100%;
		height: 100%;
		padding: 5px;
		background-image: url("../../assets/images/geoLocation.svg");
		background-repeat: no-repeat;
		background-position: center center;
		background-size: 20px;

		.ymaps-2-1-79-float-button-icon.ymaps-2-1-79-float-button-icon_icon_geolocation {
			display: none;
		}
	}
	transition: 0.4s;
}
