.card-two.card-six {
	border-radius: 5px;
	padding: 13px 11px;
	width: 100%;
	.product-header {
		height: 216px;
		img {
			object-fit: contain;
		}
		.favourites-button {
			display: flex;
			justify-content: center;
			align-items: center;
			img {
				width: 25px;
				height: 25px;
			}
		}
	}
	.body-product {
		.price-product {
			margin-top: 10px;
			.price-new,
			.price-old {
				font-weight: bold;
				font-size: 16px;
				line-height: 18px;
				color: $dark-text;
			}
			.price-new.discount {
				color: $main-menu-link;
			}
			.price-old {
				font-size: 12px;
				margin-left: 8px;
			}
		}
		.title-product {
			line-height: 1;
			text-align: left;
			display: -webkit-box;
			-webkit-line-clamp: 1;
			-webkit-box-orient: vertical;
			overflow: hidden;
			--overfdropdownlow: hidden;
			text-overflow: ellipsis;
			margin: 0;
			a {
				color: $dark-text;
				font-size: 12px;
			}
		}
		.sale-with-cart,
		.sale {
			display: flex;
			align-items: center;
			justify-content: space-between;
			p {
				margin: 0;
				line-height: 1;
			}
			.sale-price {
				display: flex;
				justify-content: center;
				align-items: center;
				color: $dark-text;
				font-weight: bold;
			}
		}
		.sale {
			flex-direction: column;
			align-items: center;
		}
		.action-product {
			button {
				background: $secondary-color;
				color: $light-text;
				border-radius: 5px;
				padding: 10px 15px;
				font-weight: bold;
				font-size: 12px;
			}
		}
	}
}
