.card-five {
	.product-header {
		position: relative;
		height: 232px;
		border-radius: 5px;
		overflow: hidden;
		.change-image {
			display: none;
		}
		a {
			display: inline-block;
			height: 100%;
			position: relative;
			width: 100%;
			border-radius: 5px;
			overflow: hidden;
			img {
				position: absolute;
				width: 100%;
				height: 100%;
				object-fit: contain;
				transition: 1s;
			}
			.image-one-product {
				opacity: 1;
			}
			.image-two-product {
				opacity: 0;
			}
		}

		.btn-heart,
		.btn-quickview,
		.cart-btn {
			position: absolute;
			display: flex;
			justify-content: center;
			align-items: center;
		}

		.btn-heart,
		.btn-quickview,
		.cart-btn {
			opacity: 0;
			visibility: hidden;
			transform: translateX(15px);
			transition: 0.3s ease-in-out;
		}
		.btn-heart {
			top: 5px;
			left: 5px;
			transform: translateX(-15px);
		}
		.btn-quickview {
			top: 5px;
			right: 5px;
		}
		.cart-btn {
			width: 100%;
			opacity: 0;
			bottom: 0;
			padding: 5px 0;
			transform: translateX(0);
			background-color: rgba($color: $dark-text, $alpha: 1);
			color: #fff;
			border-radius: 5px;
			font-size: 1.3rem;
			letter-spacing: -0.01em;
			&:before {
				content: "\e812";
				color: $primary-color;
				font-family: molla;
				margin-right: 10px;
				font-size: 18px;
				transition: 0.3s ease-in-out;
			}
			&:hover {
				background: $primary-color;
				&:before {
					color: #fff;
				}
			}
		}
	}
	.body-product {
		padding: 10px 0;
		.title-product,
		.price-product {
			padding: 0 10px;
			display: flex;
			margin-top: 10px;
			text-align: left;
			display: -webkit-box;
			-webkit-line-clamp: 1;
			-webkit-box-orient: vertical;
			overflow: hidden;
			text-overflow: ellipsis;
		}
		.title-product,
		.title-product a {
			font-weight: 400;
			font-size: 1.2rem;
			line-height: 1.25;
			letter-spacing: -0.01em;
			margin-bottom: 0.2rem;
		}
		.supplier-name,
		.title-product a {
			color: #919eab;
		}
		.price-product {
			display: flex;
			flex-direction: row-reverse;
			justify-content: flex-end;
			align-items: center;
			font-size: 16px;
			.price-new {
				line-height: 1.25;
				color: $dark-text;
			}
		}
	}
	&:hover {
		.cart-btn,
		.btn-heart,
		.btn-quickview {
			transform: translateX(0);
			visibility: visible;
			opacity: 1;
		}
	}
}
@media (max-width: 991px) {
	.card-five {
		.product-header {
			height: 170px !important;
			.btn-heart,
			.cart-btn,
			.btn-quickview {
				display: none;
				opacity: 1;
				visibility: visible;
				transform: translateX(0);
			}
			.cart-btn {
				font-size: 11px;
				&:before {
					display: none;
				}
			}
		}

		.body-product {
			padding: 5px 0;
			.title-product,
			.price-product {
				padding: 0 5px;
			}
			.title-product a,
			.price-product {
				font-size: 1.2rem;
				line-height: 1.25;
			}
			.price-product {
				font-weight: 700;
			}
		}
	}
}
