.block-banner-collection-wrapper {
	.block-banner-collection {
		display: flex;
		margin-top: 50px;
		.block-menus {
			display: flex;
			flex-direction: column;
			padding: 30px 0 0 30px;
			.title {
				font-size: 20px;
				font-weight: bold;
			}
			.block-menu-list {
				flex-grow: 1;
				&::-webkit-scrollbar {
					display: none;
				}
				.block-menu-item a {
					font-size: 16px;
				}
			}
		}
		.block-banner {
			padding: 0;
			img {
				width: 100%;
				object-fit: content;
				border-radius: 20px;
			}
		}
		.block-collection {
			padding: 0;
			.only-desktop {
				display: flex;
				flex-wrap: wrap;
				& > div {
					& > div {
						margin: 0 !important;
					}
				}
			}
			.only-mobile {
				display: none !important;
			}
		}
	}
	.block-slider {
		.banner-one {
			margin-top: 20px;
			.div-shimmer {
				height: 440px;
			}
			img {
				height: 100%;
				border-radius: 20px;
				object-fit: cover;
			}
		}
	}
	@media (max-width: 1440px) {
		.home-page-four {
			.block-collection {
				.only-desktop {
					& > div {
						& > div {
							margin: 0 !important;
							.product-header {
								height: 200px !important;
							}
						}
					}
				}
				.only-mobile {
					display: none !important;
				}
			}
		}
	}

	@media (max-width: 991px) {
		.block-banner-collection {
			margin-top: 15px;
			flex-direction: column;
			.block-menus {
				padding: 0;
				.title {
					margin-bottom: 10px;
				}
				.block-menu-list {
					margin-bottom: 0;
					width: 100%;
					height: max-content;
					display: flex;
					align-items: center;
					overflow-x: auto;
					padding: 10px 0;

					&::-webkit-scrollbar {
						height: 2px;
					}
					&::-webkit-scrollbar-thumb {
						background-color: #ccc;
					}
					.block-menu-item {
						margin-right: 10px;
						a {
							padding: 5px 10px;
							border-radius: 10px;
							font-size: 14px;
							white-space: nowrap;
							background-color: #ebf0f7;
						}
					}
				}
			}
			.block-collection {
				.only-desktop {
					& > div {
						& > div {
							margin: unset !important;
						}
					}
				}
				.only-mobile {
					display: none !important;
				}
			}
			.block-banner {
				display: none !important;
			}
			.block-collection {
				.only-desktop {
					display: none !important;
				}
				.only-mobile {
					display: block !important;
					.double-product {
						&:last-child {
							padding-right: 0;
						}
						& > div {
							margin: 0;
							.product-header {
								height: 250px;
								a {
									border-radius: 10px !important;
									img {
										height: 100% !important;
									}
								}
							}
						}
						.body-product {
							.title-product {
								max-width: 200px;
								a {
									display: block;
									overflow: hidden;
									text-overflow: ellipsis;
									white-space: nowrap;
								}
							}
						}
					}
				}
			}
		}
		.block-slider {
			.banner-one {
				height: 240px;
				.div-shimmer {
					height: 240px;
				}
				img {
					height: 100% !important;
				}
			}
		}
	}

	@media (max-width: 620px) {
		.block-slider {
			.banner-one {
				height: 120px;
				.div-shimmer {
					height: 120px;
				}
				img {
					height: 100% !important;
				}
			}
		}
	}
}
