.taosty {
	position: fixed;
	width: 100%;
	left: 0;
	right: 0;
	top: 40px;
	transition: 3s;
	display: flex;
	justify-content: center;
	z-index: 1041;
	.content {
		padding: 12px 10px;
		width: 50%;
		background: #fff;
		border: 1px solid #e5e8eb;
		box-shadow: 0px 7px 15px rgba(0, 0, 0, 0.08);
		border-radius: 10px;
		display: flex;
		align-items: center;
		.product-img {
			img {
				width: 60px;
			}
		}
		.product-name {
			margin: 0 10px;
			h3 {
				font-size: 18px;
				line-height: 24px;
			}
		}
		.close-with-pay {
			height: 100%;
			display: flex;
			flex-direction: column;
			align-items: flex-end;
			justify-content: space-between;
			margin-left: auto;
			.pay-btn {
				margin-top: 20px;
				padding: 14px 30px;
				border-radius: 10px;
				background: $primary-color;
				font-size: 18px;
				line-height: 20px;
				color: #fff;
			}
		}
	}
}

@media (max-width: 991px) {
	.taosty {
		top: 0;
		.content {
			padding: 10px 8px;
			width: 100%;
			border-radius: 0;
			.product-img {
				img {
					width: 40px;
				}
			}
			.product-name {
				margin: 0 10px;
				h3 {
					font-size: 14px;
					line-height: 16px;
				}
			}
			.close-with-pay {
				.closer {
					img {
						width: 15px;
					}
				}
				.pay-btn {
					padding: 10px 12px;
					border-radius: 5px;
					white-space: nowrap;
					font-size: 14px;
					line-height: 1;
				}
			}
		}
	}
}
