.pre-loader-one {
	position: absolute;
	height: 100vh;
	width: 100vw;
	z-index: 20;
	animation: animateBg 7s linear infinite;
	background: radial-gradient(
		circle,
		$primary-color 25%,
		$primary-color-dark 100%
	);
	background-size: 100% 300%;

	.logo {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		height: 100%;
		div {
			width: 150px;
		}
	}
}
@keyframes animateBg {
	0% {
		background-position: 0% 100%;
	}
	100% {
		background-position: 0% 0%;
	}
}
