.product-page-five {
	.product-main-image {
		margin-top: 10px;
		width: 100%;
		img {
			height: 400px;
			object-fit: cover;
		}
	}
	.supliers-row {
		.seller-info-name {
			display: flex;
			justify-content: center;
			flex-direction: column;
			p,
			span {
				color: $dark-text;
				margin: 0;
			}
		}
		.seller-info-img {
			.div-shimmer {
				.photo-shimmer.shine-shimmer {
					height: 100%;
				}
			}
		}
	}
	.splide {
		.splide__track {
			.splide__list {
				.big-img-with-view-button {
					img {
						width: 100%;
					}
				}
			}
		}
		.splide__arrows {
			button {
				background-color: $primary-color;
			}
		}
		.splide__pagination {
			li {
				.splide__pagination__page.is-active {
					background: $primary-color;
				}
			}
		}
	}
	.product-image-gallery {
		margin-top: 20px;

		button.product-gallery-item {
			width: 80px;
			max-width: 90px;
			min-width: 100%;
			height: 80px;
			border-radius: 10px;
			overflow: hidden;
			&::before {
				border-radius: 10px;
			}
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}

	.product-details.sticky-content {
		position: relative !important;
		top: 0 !important;
	}

	.skel-pro-single {
		margin: 20px 0 30px 0;

		.col-md-5.pl-5 {
			padding: 0 !important;
		}
	}

	.page-content {
		.product-details-top {
			margin-top: 26px;
			.col-md-4.col-12 {
				padding-left: 0;
			}
			.product-gallery {
				margin-bottom: 0 !important;

				.product-main-image {
					margin: 0;

					.media-sticky.mt-2:first-child {
						margin-top: 0 !important;
					}
				}
			}

			.product-details.sticky-content {
				margin: 0 0 0 40px;
				padding: 0;
				border: none;
				.react-tabs {
					display: none;
				}
			}
			.row.supliers-row {
				.product-cat-suppliers {
					.d-flex.suppliers_ul {
						margin-bottom: 12px;
					}
				}
			}
			.empty-brand {
				.product-brand {
					display: none;
				}
			}
			.product-details.sticky-content {
				width: unset !important;

				.product-details {
					padding: 0;
					border: none;
					.product-title {
						color: #000000 !important;
						font-weight: 400;
						letter-spacing: unset;
						margin: 0 0 18px 0;
					}

					.product-content {
						margin-bottom: 18px;

						h6 {
							font-size: 14px;
							color: #000000;
						}
					}

					.product-price {
						font-weight: bold;
						color: $primary-color;
						margin-bottom: 18px;
					}

					.details-filter-row.details-row-size {
						flex-direction: column;
						align-items: start;
						display: unset;

						label,
						.properties-block,
						.activeproperties-block,
						.not-allowedproperties-block {
							margin-bottom: 12px !important;
							font-size: 16px;
							line-height: 20px;
							color: #000000;
						}

						label {
							display: flex;
							width: 100%;
							justify-content: space-between;

							a {
								text-decoration: underline;
								font-size: 16px;
								line-height: 20px;
								color: #1890ff;
							}
						}

						.product-nav {
							margin-bottom: 0 !important;
							align-items: flex-end;

							.activeproperties-block {
								border: 1px solid #1d75ff !important;
							}

							margin-bottom: 24px;

							.properties-block,
							.activeproperties-block,
							.not-allowedproperties-block {
								border-radius: 10px !important;
								margin: 0 20px 20px 0 !important;
							}

							.not-allowedproperties-block {
								border: 1px solid #b7b7b7 !important;
							}

							.color-properties {
								padding: 0;
								img {
									width: 68px;
									height: 94px;
									border-radius: 7px;
								}
								span {
									padding: 5px;
								}
							}

							.size-properties {
								padding: 16px !important;
							}
						}

						.product-details-quantity {
							margin-right: 85px;
							max-width: 218px;

							.form-control.input-changer-count.d-flex.p-3.justify-content-between {
								border-radius: 10px;
								padding: 16px 26px !important;

								height: unset;

								span,
								input {
									background: transparent;
									font-size: 20px;
									line-height: 24px;
									color: #000000;
								}

								input {
									margin: 0 40px;
								}
							}
						}
					}
				}
			}
		}

		.price-and-articul {
			display: flex;
			flex-direction: column-reverse;
		}

		.product-details-action {
			margin: 24px 0 40px 0;

			span {
				box-shadow: none;
				display: flex !important;
				align-items: center;
				font-size: 16px !important;
				line-height: 20px !important;
			}

			svg {
				margin-right: 10px;
			}

			.btn-product.btn-cart {
				padding: 16px 0;
				min-width: 252px;
				color: $primary-color;
				border: 1px solid $primary-color;
				background-color: $primary-color;
				border-radius: 20px !important;
				&:hover {
					background-color: transparent;
					span {
						color: $primary-color;
					}
				}
				span {
					text-transform: unset;
				}

				&::before {
					display: none;
				}
			}
			.home-plus-chooser {
				display: none !important;
			}

			.one-click {
				color: #323232 !important;

				svg {
					width: 24px;
				}

				&:hover {
					color: $primary-color !important;

					svg {
						path {
							transition: all 0.35s ease;

							fill: $primary-color;
						}
					}
				}
			}
		}
	}
}

@media (max-width: 991px) {
	.product-page-five {
		.skel-pro-single {
			margin: 0 0 20px 0;
		}

		.page-content {
			.col-md-4.col-12 {
				display: none;
			}
			.col-md-5.pl-5 {
				min-width: 100%;
			}
			.col-md-3 {
				min-width: 100%;
			}
			.product-details-top {
				margin-top: 0px;
				.product-details.sticky-content {
					margin: 0;

					.product-details {
						margin: 0;

						.blue-heart-button {
							color: #000000;
							svg {
								width: 22px;
							}
						}

						.product-title {
							margin: 0 0 12px 0;
							font-size: 16px;
						}

						.product-content {
							margin-bottom: 12px;

							h6 {
								margin-bottom: 0;
								font-size: 12px;
							}
						}

						.product-price {
							font-size: 16px;
							margin-bottom: 14px;
						}

						.details-filter-row.details-row-size {
							label,
							.properties-block,
							.activeproperties-block,
							.not-allowedproperties-block {
								margin-bottom: 6px !important;
								font-size: 12px;
								line-height: 16px;
								color: #000000;
							}

							label {
								a {
									font-size: 12px;
									line-height: 16px;
								}
							}

							.product-nav {
								margin-bottom: 10px;

								.properties-block,
								.activeproperties-block,
								.not-allowedproperties-block {
									border-radius: 7px !important;
									margin: 0 10px 10px 0 !important;
									overflow: hidden;
								}

								.color-properties {
									width: 49px;
									height: 74px;

									img {
										border-radius: 0;
									}
								}

								.size-properties {
									padding: 12px !important;
								}
							}

							.product-details-quantity {
								max-width: 110px;

								.form-control.input-changer-count.d-flex.p-3.justify-content-between {
									border-radius: 7px;
									padding: 8px 13px !important;

									span,
									input {
										font-size: 16px;
										line-height: 16px;
									}

									input {
										margin: 0 20px;
									}
								}
							}
						}

						.details-row-size {
							.product-nav {
								.properties-block {
									margin-bottom: 4px;
								}
							}
						}
					}
				}
			}

			.price-and-articul {
				justify-content: space-between;
				flex-direction: row-reverse;
				align-items: center;
			}

			.product-details-action {
				justify-content: space-between;
				flex-direction: row;
				align-items: center;
				margin: 14px 0 15px 0;

				span {
					font-size: 15px !important;
					line-height: 18px !important;
				}

				svg {
					margin-right: 6px;
					width: 22px;
				}

				.btn-product.btn-cart {
					margin-left: -1px !important;
					margin: 0;
					padding: 6px 0;
					height: 50px;
					min-width: 160px;
					border-radius: 15px !important;
				}
			}
		}

		.product-image-gallery {
			margin-top: 10px;
			padding: 0 10px;
			button.product-gallery-item {
				max-width: 70px;
				height: 60px;
				min-width: 100%;
			}
		}

		.product-details.sticky-content {
			position: relative !important;
			top: 0 !important;
		}
	}
}
