.toast-container {
    width: 250px !important ;
    line-height: 18px;

    .Toastify__toast--success {
        background-color: $primary-color;
        border-radius: .5rem;
    }

    .Toastify__toast--error {
        background-color: red;
        border-radius: .5rem;
        color: #fff !important;
    }

    .Toastify__toast-body{
        padding-left: 2rem;
    }

    .Toastify__close-button{
        margin-top: auto;
        margin-bottom: auto;
        font-size: 12px;
    }

    .Toastify__toast{
        min-height: 55px;
        padding-right: 25px;
    }

    .Toastify__progress-bar {
        opacity: 0 !important;
    }
}

@media (-ms-high-contrast: active), (-ms-high-contrast: none) {
    .toast-container .Toastify__toast-body {
        margin-top: 7px;
        line-height: 18px;
    }
}

@include mq('480px', 'max') {
    .Toastify__toast-container {
        left: 10px !important;
        top: 10px !important;
        .Toastify__toast {
            margin-bottom: 10px;
        }
    }
}