.choosen-product_two {
	display: flex;
	// justify-content: space-between;
	flex-direction: row;
	box-shadow: 0 10px 20px -2px #eee;
	border-radius: 16px;
	border: 1px solid #bcc5d9;
	padding: 15px 5px;
	margin: 10px;
	position: relative;

	.product_img_two {
		width: 30%;
		min-height: 100px;

		img {
			width: 100%;
			height: 150px;
			display: block;
			object-fit: contain;
		}

		span {
			position: absolute;
			top: 15px;
			right: 20px;
			width: 30px;
			height: 30px;
			border-radius: 50%;
			overflow: hidden;
			display: flex;
			align-items: center;
			justify-content: center;
			color: #fff;
			img {
				width: 20px;
				height: 20px;
				display: block;
				object-fit: contain;
			}
		}
	}

	.product_body_two {
		padding-top: 20px;
		width: 65%;
		.product-price {
			display: flex;
			align-items: flex-start;
			flex-direction: column;
			margin-bottom: 0;

			.old-price {
				font-size: 12px;
			}
		}

		.product_name {
			p {
				margin: 0;
				font-size: 12px;
				line-height: 1.5;
				text-align: left;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
				overflow: hidden;
				text-overflow: ellipsis;
			}
		}
	}
}
