.no-gutters {
	margin-left: 0;
	margin-right: 0;

	> .col,
	> [class*="col-"] {
		padding-left: 0;
		padding-right: 0;
	}
}

.container {
	// width: 1188px;
	width: $container-width;
	max-width: 100%;
	padding-left: 10px;
	padding-right: 10px;
}

.page-wrapper {
	position: relative;
	display: flex;
	flex-direction: column;
	min-height: 100%;
	transition: transform 0.4s ease;

	// .mmenu-active & {
	// 	transform: translateX(200px);
	// }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
	.page-wrapper {
		display: block;
	}
}

.main {
	flex: 1;
	width: 100%;
}

.page-content {
	padding-bottom: 5rem;
}

@include mq("lg") {
	.col-5col {
		-ms-flex: 0 0 20%;
		flex: 0 0 20%;
		max-width: 20%;
	}
}

.col-xl-5col {
	width: 100%;
	padding-left: 10px;
	padding-right: 10px;
}

.col-xl-4-5col {
	width: 100%;
	padding-left: 10px;
	padding-right: 10px;
}

.col-xl-2-5col {
	width: 100%;
	padding-left: 10px;
	padding-right: 10px;
}

.col-xl-3-5col {
	width: 100%;
	padding-left: 10px;
	padding-right: 10px;
}

@include mq("xl") {
	.container-fluid {
		padding-left: 30px;
		padding-right: 30px;
	}

	.col-xl-5col {
		-ms-flex: 0 0 20%;
		flex: 0 0 20%;
		max-width: 20%;
	}

	.col-xl-4-5col {
		-ms-flex: 0 0 80%;
		flex: 0 0 80%;
		max-width: 80%;
	}

	.col-xl-2-5col {
		-ms-flex: 0 0 calc(40%);
		flex: 0 0 calc(40%);
		max-width: calc(40%);
	}

	.col-xl-3-5col {
		-ms-flex: 0 0 calc(60%);
		flex: 0 0 calc(60%);
		max-width: calc(60%);
	}

	.no-gutters {
		.col-xl-2-5col {
			-ms-flex: 0 0 calc(40% + 8px);
			flex-basis: calc(40% + 8px);
			max-width: calc(40% + 8px);
		}

		.col-xl-3-5col {
			-ms-flex: 0 0 calc(60% - 8px);
			flex-basis: calc(60% - 8px);
			max-width: calc(60% - 8px);
		}
	}
}

@media screen and (min-width: 1280px) {
	.container {
		max-width: $container-width;
	}
}

/* Extend grid - 1600px */
@include mq("xxl") {
	.container-fluid {
		padding-left: 70px;
		padding-right: 70px;
	}

	.col-xxl {
		-ms-flex-preferred-size: 0;
		flex-basis: 0;
		-ms-flex-positive: 1;
		flex-grow: 1;
		max-width: 100%;
	}

	.col-xxl-auto {
		-ms-flex: 0 0 auto;
		flex: 0 0 auto;
		width: auto;
		max-width: 100%;
	}

	.col-xxl-1 {
		-ms-flex: 0 0 8.333333%;
		flex: 0 0 8.333333%;
		max-width: 8.333333%;
	}

	.col-xxl-2 {
		-ms-flex: 0 0 16.666667%;
		flex: 0 0 16.666667%;
		max-width: 16.666667%;
	}

	.offset-xxl-2 {
		margin-left: 16.666667%;
	}

	.col-xxl-3 {
		-ms-flex: 0 0 25%;
		flex: 0 0 25%;
		max-width: 25%;
	}

	.col-xxl-4 {
		-ms-flex: 0 0 33.333333%;
		flex: 0 0 33.333333%;
		max-width: 33.333333%;
	}

	.col-xxl-5 {
		-ms-flex: 0 0 41.666667%;
		flex: 0 0 41.666667%;
		max-width: 41.666667%;
	}

	.col-xxl-6 {
		-ms-flex: 0 0 50%;
		flex: 0 0 50%;
		max-width: 50%;
	}

	.col-xxl-7 {
		-ms-flex: 0 0 58.333333%;
		flex: 0 0 58.333333%;
		max-width: 58.333333%;
	}

	.col-xxl-8 {
		-ms-flex: 0 0 66.666667%;
		flex: 0 0 66.666667%;
		max-width: 66.666667%;
	}

	.col-xxl-9 {
		-ms-flex: 0 0 75%;
		flex: 0 0 75%;
		max-width: 75%;
	}

	.col-xxl-10 {
		-ms-flex: 0 0 83.333333%;
		flex: 0 0 83.333333%;
		max-width: 83.333333%;
	}

	.col-xxl-11 {
		-ms-flex: 0 0 91.666667%;
		flex: 0 0 91.666667%;
		max-width: 91.666667%;
	}

	.col-xxl-12 {
		-ms-flex: 0 0 100%;
		flex: 0 0 100%;
		max-width: 100%;
	}

	.col-xxl-5col {
		-ms-flex: 0 0 20%;
		flex: 0 0 20%;
		max-width: 20%;
	}

	.col-xxl-4-5col {
		-ms-flex: 0 0 80%;
		flex: 0 0 80%;
		max-width: 80%;
	}

	.d-xxl-block {
		display: block !important;
	}

	.d-xxl-flex {
		display: flex !important;
	}
}

@include mq("xlst") {
	.container-fluid {
		max-width: 1920px;
		margin-left: auto;
		margin-right: auto;
	}
}

// define cols
@for $i from 1 through 8 {
	[class*="cols-#{$i}"] {
		&:not(.owl-loaded).owl-carousel {
			display: flex;
			overflow: hidden;

			> * {
				&.brand {
					flex: 0 0 calc(100% / #{$i});
					max-width: calc(100% / #{$i});
				}

				&:not(.brand) {
					flex: 0 0 calc(100% / #{$i} - 20px * (#{$i} - 1) / #{$i});
					max-width: calc(100% / #{$i} - 20px * (#{$i} - 1) / #{$i});

					&:not(:last-child) {
						margin-right: 20px;
					}
				}
			}
		}

		&.owl-loaded.owl-carousel {
			display: block;
		}
	}
}

