.card-seven {
	width: 100%;
	height: 100%;
	transition: 0.3s;
	padding: 10px;
	display: flex;
	flex-direction: column;
	.product-header {
		border: 0.5px solid #f2f3f5;
		border-radius: $border-radius-image;
		overflow: hidden;
		position: relative;
		max-height: 320px;
		flex-grow: 1;

		.image-blur {
			height: 100%;
			width: 100%;
			z-index: 1;
			img {
				object-fit: cover;
				height: 100%;
				width: 100%;
			}
		}
		.active-image {
			z-index: 2;
			position: absolute;
			top: 0;
			bottom: 0;
			width: 100%;
			background: rgba(255, 255, 255, 0.01);
			backdrop-filter: blur(60px);
		}
		.splide,
		.splide__track,
		.splide__slide {
			padding: 0;
			height: 100%;
			li {
				padding: 0;
				width: unset;
			}
		}
		.splide__arrows {
			display: none;
		}
		.change-image {
			display: none;
		}
		a {
			img {
				width: 100%;
				object-fit: cover;
				height: 100%;
			}
			.image_pagination_wrapper {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0px;
				padding: 15px 30px;
				display: flex;

				.other_image {
					opacity: 0;
					position: relative;
					flex-grow: 1;
					margin: 1px;
					&::before {
						position: absolute;
						height: 10px !important;
						width: 10px;
						margin: auto;
						left: 0;
						bottom: -5px;
						right: 0;
						height: 3px;
						border-radius: 50%;
						background-color: #a2ffb78f;
						content: " ";
					}

					&.active {
						&::before {
							background: $primary-color;
							border: 1px solid $primary-color;
						}
					}
				}
			}
		}
		.favourites-button {
			position: absolute;
			right: 21px;
			top: 21px;
			padding: 0;
			z-index: 2;
			transition: 0.3s;
			svg {
				width: 35px;
				height: 35px;
			}
		}
	}
	.body-product {
		.title-brand {
			height: 74px;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
		}
		.title-product {
			margin: 10px 0px;
			a {
				font-size: 14px;
				line-height: 18px;
				color: #758399;
				text-align: left;
				display: flex;
				text-align: left;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
				overflow: hidden;
				text-overflow: ellipsis;
			}
		}
		.name-supplier {
			font-weight: 700;
			font-size: 14px;
			line-height: 17px;
			display: flex;
			align-items: center;
			color: $secondary-color;
		}
		.price-new,
		.price-monthly {
			font-weight: 700;
			font-size: 12px;
			line-height: 15px;
			color: $secondary-color-dark;
		}
		.price-monthly {
			font-size: 16px;
			line-height: 19px;
			color: $primary-color;
		}
	}
	.action-product {
		margin-top: 10px;
		background: $primary-color;
		// display: none;
		opacity: 0;
		transition: 0.3s;
		border-radius: $border-radius-image;
		padding: 8px 0;
		.cart-btn {
			color: $light-text;
			img {
				filter: brightness(0) invert(1);
			}
			width: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			span {
				margin-left: 10px;
				font-size: 16px;
				line-height: 19px;
			}
		}
	}
	&:hover {
		box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
		// box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.15);
		border-radius: 30px;
		.action-product {
			opacity: 1;
		}
	}
}

@media (max-width: 991px) {
	.card-seven {
		padding: 0;
		.product-header {
			// height: 200px !important;
			a {
				.image_pagination_wrapper {
					display: none;
				}
			}
			.favourites-button {
				top: 10px;
				right: 10px;
			}
			.change-image {
				opacity: 0;
				display: flex;
				span {
					position: absolute;
					top: 40%;
					left: -10px;
					i {
						padding: 20px;
					}
				}
				.right {
					left: unset;
					right: -10px;
				}
			}
			.splide__arrows {
				display: block;
				.splide__arrow {
					background-color: #04a0e600 !important;
					width: 20px;
					height: 20px;
					box-shadow: 0px 2px 7px rgb(0 0 0 / 10%);
					svg {
						width: 15px;
						path {
							fill: $primary-color;
						}
					}
				}
				.splide__arrow--prev {
					left: 1rem;
				}
				.splide__arrow--next {
					right: 1rem;
				}
			}
			.splide__pagination {
				display: none;
			}
		}
		.body-product {
			.price-new {
				margin-bottom: 2px;
			}
			.price-monthly {
				font-size: 14px;
				span {
					display: block;
				}
			}

			.title-brand {
				height: 56px;
			}
			.title-product a,
			.name-supplier {
				font-size: 12px;
				line-height: 12px;
			}
			.action-product {
				opacity: 1;
				border-radius: $border-item;
				.cart-btn {
					img {
						width: 16px;
					}
					span {
						margin-left: 6px;
						font-size: 13px;
						line-height: 15px;
					}
				}
			}
		}
		&:hover {
			box-shadow: unset;
			border-radius: unset;
		}
	}
}
