/* Header 6 */

.header-three .header-bottom .header-right {
	display: none;
}

.search-result {
	color: #222 !important;
}

.header-three .header-search .search-wrapper-wide {
	width: 400px;
}

.sticky-header.fixed,
.header-three .header-bottom {
	background-color: #fff !important;
}

.header-three .header-bottom .header-right p a {
	color: green !important;
}

.header-three .header-bottom .header-right i {
	font-size: 2rem;
	letter-spacing: -0.01em;
	margin-right: 0.5rem;
	color: green;
	margin-bottom: 1rem;
}

.sticky-header.fixed,
.header-three .header-bottom .menu > li > a {
	color: #191919;
}

.header-three .header-middle .header-center a {
	width: 130px;
}

.header-three {
	.header-top {
		.container {
			display: flex;
			align-items: center;
			justify-content: space-between;
			&::after {
				display: none;
			}
		}
	}
	.header-middle {
		display: flex;
		min-height: 84px;
		color: #777;
		font-weight: 400;
		font-size: 1.6rem;
		line-height: 1.5;
		letter-spacing: 0;
		border-bottom: none;
		margin-bottom: 0.1rem;
		z-index: 99999;
		.container {
			display: flex;
			align-items: center;
			justify-content: space-between;
			position: relative;
			min-height: 84px;
			a {
				color: #666;

				&:hover,
				&:focus {
					color: $primary-color;
				}
			}

			.header-left {
				.logo > div {
					width: 200px !important;
					margin: 0;
					img {
						width: 100%;
						object-fit: cover;
					}
				}
			}

			.header-center {
				.search-box-wrapper {
					.search-box {
						.header-search-wrapper {
							border: 1px solid $primary-color !important;
							border-radius: 5px;
							overflow: hidden;
							input {
								border-top-left-radius: 5px;
								border-bottom-left-radius: 5px;
							}
							button {
								color: #666;
								background-color: initial;
								padding: 0 1rem;
								font-size: 2.6rem;
								max-width: fit-content !important;
								border-top-right-radius: 5px;
								border-bottom-right-radius: 5px;
							}
						}
						.search-result {
							&::-webkit-scrollbar {
								width: 1px;
							}
						}
					}
				}
			}

			.header-right {
				.dropdown-menu {
					min-width: 400px;
					padding: 10px;

					.dropdown-cart-action {
						.btn.btn-primary {
							color: $light-text !important;
						}
						.btn.btn-primary,
						.btn.border {
							&:hover {
								color: $dark-text !important;
							}
						}
					}
					.dropdown-cart-products {
						padding: 0;
						.product {
							display: flex;
							justify-content: space-between;
							padding: 10px 0;
							.product-detail-left {
								height: 100%;
								.product-image-container {
									margin: 0;
									margin-bottom: 10px;
								}
							}
							.product-detail-right {
								min-height: 100% !important;
								display: flex;
								align-items: center;
								flex-direction: column;
								justify-content: space-between;
								.btn-remove {
									margin-top: 10px;
								}
							}
						}
					}
				}
			}
		}
	}

	.header-bottom {
		margin-bottom: 0;
		.header-left {
			.horizontal-menu {
				li {
					.category-menu-img-bg {
						display: none;
					}
					.megamenu.megamenu-md {
						width: 70vw !important;
						.banner {
							max-width: 100% !important;
						}
					}
					a {
						white-space: nowrap;
						color: #191919;
						text-transform: none !important;
					}
				}
			}
		}
	}
	.header-dropdown {
		padding-top: 0.55rem;
		padding-bottom: 0.55rem;

		+ .header-dropdown {
			margin-left: 2rem;
		}
	}

	.header-right .top-menu + .header-dropdown {
		margin-left: 2rem;
	}

	.header-dropdown > a,
	.header-dropdown > span {
		font-weight: 400;
		text-transform: initial;
		padding-right: 2.8rem;
	}

	.header-menu {
		margin-top: 0;
		font-size: 1.5rem;
	}

	.top-menu {
		text-transform: capitalize;

		i {
			font-size: 1.6rem;
		}

		li + li {
			margin-left: 2rem;
		}
	}

	.menu.sf-arrows .sf-with-ul::after {
		letter-spacing: -0.01em;
	}

	.header-middle a.btn:hover,
	.header-middle a.btn:focus {
		color: #fff;
	}
	.btn-primary:hover,
	.btn-primary:focus,
	.btn-primary.focus,
	.btn-primary:not(:disabled):not(.disabled):active,
	.btn-primary:not(:disabled):not(.disabled).active,
	.show > .btn-primary.dropdown-toggle {
		color: #c15454;
		background-color: transparent;
	}
}
.header-three {
	.cart-dropdown {
		h6 {
			display: none;
		}
	}
}

.header-three .wishlist-link .wishlist-count,
.header-three .cart-dropdown .cart-count {
	top: -5px;
	right: -5px;
	position: absolute;

	min-width: 1.5rem;
	height: 1.5rem;
	margin-bottom: 1.5rem;
	margin-left: -1.2rem;
	z-index: 1;
}

@media (min-width: 991px) {
	.search-mobile {
		display: none;
	}
	.home-page-three {
		.horizontal-menu {
			display: none !important;
		}
	}
}

@media (max-width: 991px) {
	.container {
		width: 93% !important;
	}
	.header-three .header-middle .header-center {
		display: none;
	}
	.header-top {
		.location {
			padding-right: 0 !important;
		}
		.header-right {
			width: 100%;
		}
	}
	.header-middle {
		.header-right {
			.dropdown-menu {
				display: none;
			}
		}
	}
	.header-middle {
		.container::after {
			display: none;
		}
	}

	.header-bottom {
		display: block;
		.container {
			display: flex;
			align-items: center;
			justify-content: space-between;
		}
		.horizontal-menu {
			display: none !important;
		}
		.row.search-mobile {
			width: 100%;
			margin: 0;
			margin-left: 20px;
			.search-box-wrapper,
			.search-box {
				min-width: 100% !important;
				.header-search {
					min-width: 100%;
					display: block;
					min-height: 100%;
				}
			}

			.header-search-wrapper.search-wrapper-wide.search {
				position: relative;
				margin: 0;
				width: 100%;
				border: none;
				&:before {
					display: none;
				}
			}

			.header-search-visible .header-search-wrapper {
				display: flex !important;
				border-radius: 6px !important;
				border: 1px solid $primary-color !important;
				min-height: 100%;
				right: 0;
				.btn-primary {
					background: $primary-color;
					i {
						filter: brightness(0) invert(1);
					}
				}

				.sr-only {
					display: none;
				}

				input {
					height: 48px;
					background: transparent;
					color: $primary-color;
					padding: 8px 16px !important;
				}
				.btn {
					height: 50px;
				}
			}

			.search-result-wrapper {
				z-index: 10000 !important;
				.search-result {
					z-index: 10000 !important;
				}
			}
		}
	}
}

@media (max-width: 991px) {
	.header-top {
		.header-left {
			a {
				p {
					white-space: nowrap;
				}
			}
		}
		.header-right {
			padding: 0 !important;
			justify-content: flex-end;
			.location {
				padding-left: 0;
				.location-link {
					display: block;
					span {
						width: 150px;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
					}
				}
			}
		}
		.header-left {
			.social-icons {
				display: none;
			}
		}
	}
	.header-middle {
		.header-left {
			.logo {
				margin: 0 !important;
			}
		}
	}
}

@media (max-width: 575px) {
	.header-three {
		.header-top {
			.header-right {
				justify-content: space-between;
			}
			.location {
				padding: 10px 0 !important;
			}
		}
		.header-middle.sticky-header {
			.container {
				.header-left {
					.logo > div {
						max-width: 150px !important;
						height: 50px !important;
					}
				}
				.header-center {
					width: max-content;
				}
				.header-right {
					width: max-content;
				}
			}
		}
		.header-bottom {
			.header-right {
				flex-direction: row;
				justify-content: flex-end !important;
			}
		}
	}
}

.header-three {
	.header-top {
		background-color: #fafafa;
		.container::after,
		.container-fluid::after {
			height: 0;
		}
	}
	.header-middle .container,
	.header-middle .container-fluid {
		position: relative;
		&::after {
			content: "";
			position: absolute;
			left: 1rem;
			right: 1rem;
			height: 0.1rem;
			bottom: 0;
			background-color: #ebebeb;
		}
	}
	.header-bottom {
		// background-color: #fff;
		.menu > li {
			> a {
				color: rgb(255, 255, 255);
				letter-spacing: 0.01em;
				padding-top: 1.5rem;
				padding-bottom: 1.5rem;

				&::before {
					bottom: 0;
					height: 0.2rem;
				}
			}

			&:hover > a,
			&:focus > a,
			&.active > a {
				color: $primary-color;
			}
			+ li {
				margin-left: 0.8rem;
			}
		}
		.header-right {
			display: flex;
			align-items: center;
			i {
				margin: 0;
				margin-right: 10px;
			}
			p {
				margin-top: 0 !important;
			}
			padding-right: 1rem;
		}
	}
}

.header-three {
	.header-middle,
	.header-menu {
		font-size: 1.4rem;
	}

	.search-toggle:not(:hover):not(.active),
	.wishlist-link:not(:hover):not(:focus),
	.cart-dropdown:not(.show):not(:hover) .dropdown-toggle,
	.compare-dropdown:not(.show):not(:hover) .dropdown-toggle {
		color: #333;
	}

	.cart-dropdown,
	.wishlist-link {
		&:hover,
		&:focus {
			.cart-txt {
				color: $primary-color;
			}
			.wishlist-txt {
				color: $primary-color;
			}
		}
	}
}

.footer-bottom .container,
.footer-bottom .container-fluid {
	padding-top: 2.9rem;
}

.container-fluid {
	padding-left: 1rem;
	padding-right: 1rem;
}

.footer-2 {
	.footer-bottom .container,
	.footer-bottom .container-fluid {
		padding-bottom: 2.9rem;
		padding-top: 2.9rem;
	}
}

.category-dropdown:not(.is-on):hover .dropdown-toggle {
	color: unset;
	background-color: transparent;
}

@include mq("lg", "max") {
	.top-menu.top-link-menu ul {
		min-width: 1px;
	}
}
