.menu-with-colletion-two {
	padding-top: 50px;
	.row {
		padding: 0 !important;
		.col-lg-4.col-12.row {
			margin-top: 0;
			padding: 0;
			height: unset !important;
		}
	}
	.row {
		width: 100%;
		margin: 0;
		.col-lg-4 {
			padding-left: 0;
			.menu-list {
				padding-left: 0;
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				.title {
					font-weight: 600;
					font-size: 2rem;
					line-height: 1.1;
					letter-spacing: -0.025em;
				}
				.list {
					padding-left: 30px;
					list-style-type: disc;
					color: black;
					a {
						margin-bottom: 8px;
						cursor: pointer;
						&:hover {
							color: $primary-color;
						}
						&.active {
							color: $primary-color;
						}
					}
				}
			}
			.banner {
				height: 400px;
				border-radius: 0;
				position: relative;
				img {
					height: 100%;
					display: block;
					object-fit: cover;
				}
				&::before {
					opacity: 0;
					transform: translate(0) !important;
					transition: 0.3s;
					background-color: rgba(51, 51, 51, 0.2);
				}
				&::after {
					display: none;
				}
				&:hover {
					&::before {
						opacity: 1;
					}
				}
			}
		}
		.col-lg-8.col-12 {
			align-self: center;
			.collection-list-wrapper {
				.collections-list {
					padding: 0;
					.collections-scroll {
						.collection-item {
							.product-five {
								padding: 10px;
								.product-5 {
									&:hover {
										box-shadow: 20px rgb(0 0 0 / 5%);
									}
									.product-body {
										padding: 16px 30px 30px 30px;
									}
								}
							}
						}
					}
				}
			}
		}
	}
}

@media (max-width: 991px) {
	.menu-with-colletion-two {
		padding-left: 0;
		padding-right: 0;
		.row {
			.col-lg-4.col-12.row {
				background: #f7f7f7;
				.menu-list {
					.title {
						padding: 14px 0 0 14px;
					}
					.list {
						li {
							color: #7a8183;
						}
					}
				}
				.banner {
					height: 300px;
					.banner-link {
						bottom: 10px;
						left: 10px;
						background-color: $primary-color;
					}
				}
			}
			.collections-list {
				.collection-item {
					.product-5 {
						margin: 0;
					}
				}
			}
		}
	}
}
