.splide {
	.splide__arrows {
		.splide__arrow {
			svg {
				fill: #fff;
			}
		}
	}
	.intro-slide {
		position: relative;
		.count-down {
			position: absolute;
			left: 50px;
			bottom: 80px;
			display: flex;
			align-items: center;
			height: max-content;
			z-index: 999;
			color: #ffffff;
			div {
				display: flex;
				align-items: center;
				justify-content: center;
				flex-direction: column;
				margin: 0 10px;
				div {
					position: relative;
					padding: 5px 20px;
					border-radius: 10px;
					font-size: 60px;
					perspective: 1000px;
					&::before {
						content: "";
						position: absolute;
						top: 50%;
						left: 0;
						right: 0;
						height: 2px;
						transform: translateY(-50%);
						background-color: #111;
					}
					span {
						position: absolute;
						width: 100%;
						height: 50%;
						top: 0;
						left: 0;
						right: 0;
						border-bottom: none;
						border-top-right-radius: 10px;
						border-top-left-radius: 10px;
						animation-name: slide;
						animation-duration: 1s;
						animation-iteration-count: infinite;
						transform-origin: bottom center;
					}
					@keyframes slide {
						0% {
							background-color: rgba(0, 0, 0, 0.5);
							transform: rotateX(0);
						}
						100% {
							background-color: transparent;
							transform: rotateX(-180deg);
						}
					}
				}
				p {
					margin: 0;
					font-weight: bold;
					font-size: 20px;
					color: black;
				}
			}
		}
		.div-shimmer .photo-shimmer {
			margin: 0;
		}
		img {
			width: 100%;
			height: 350px;
			object-fit: cover;
		}
	}
}

