.card-three {
	width: 100%;
	.product-header {
		position: relative;
		height: 363px;
		border-radius: 5px;
		overflow: hidden;
		.change-image {
			display: none;
		}
		a {
			display: inline-block;
			height: 100%;
			width: 100%;
			border-radius: 5px;
			overflow: hidden;
			img {
				width: 100%;
			}
			&:hover {
				.image_pagination_wrapper {
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					bottom: 0;
					z-index: 2;
					display: flex;

					.other_image {
						position: relative;
						flex-grow: 1;
						margin: 1px;

						&::before {
							position: absolute;
							top: 0;
							left: 0;
							right: 0;
							height: 3px;
							border-radius: 5px;
							background-color: #c3c3c3;
							content: " ";
						}

						&.active {
							&::before {
								background: #7a7a7a;
							}
						}
					}
				}
			}
		}

		.btn-heart,
		.discount,
		.btn-quickview {
			position: absolute;
			display: flex;
			justify-content: center;
			align-items: center;
			z-index: 999;
		}
		.btn-heart .active {
			background-color: red;
			&::before {
				color: #fff;
			}
		}

		.btn-heart,
		.btn-quickview {
			opacity: 0;
			visibility: hidden;
			transform: translateX(-15px);
			transition: 0.3s ease-in-out;
		}
		.btn-heart {
			top: 5px;
			right: 5px;
		}
		.btn-quickview {
			top: 5px;
			left: 5px;
		}
		.discount {
			bottom: 5px;
			right: 15px;
			width: 35px;
			height: 35px;
			z-index: 2;
			border-radius: 50%;
			background-color: $primary-color;
			color: #fff;
			font-size: 12px;
		}
	}

	.body-product {
		padding: 10px 0;
		.cart-btn {
			width: 100%;
			opacity: 0;
			padding: 5px 0;
			visibility: hidden;
			transition: 0.3s ease-in-out;
			background-color: $primary-color;
			color: #fff;
			margin-bottom: 10px;
			border-radius: 5px;
			font-size: 1.3rem;
			letter-spacing: -0.01em;
			&:before {
				content: "\e812";
				color: #fff;
				font-family: molla;
				margin-right: 10px;
				font-size: 18px;
			}
		}
		.title-product,
		.price-product {
			padding: 0 10px;
			display: flex;
			text-align: left;
			display: -webkit-box;
			-webkit-line-clamp: 2;
			-webkit-box-orient: vertical;
			overflow: hidden;
			text-overflow: ellipsis;
		}
		.title-product,
		.title-product a {
			color: #333;
			font-weight: 400;
			font-size: 1.2rem;
			line-height: 1.25;
			letter-spacing: -0.01em;
			margin-bottom: 0.2rem;
		}
		.price-product {
			display: flex;
			flex-direction: row-reverse;
			justify-content: flex-end;
			align-items: center;
			font-size: 16px;
			.price-new {
				line-height: 1.25;
				color: $primary-color;
			}
			.price-old {
				font-size: 12px;
				margin-right: 10px;
				color: #ccc;
			}
		}
	}
	&:hover {
		.cart-btn,
		.btn-heart,
		.btn-quickview {
			transform: translateX(0);
			visibility: visible;
			opacity: 1;
		}
	}
}
@media (max-width: 991px) {
	.card-three {
		.product-header {
			height: 250px;
			.btn-heart,
			.btn-quickview {
				opacity: 1;
				visibility: visible;
				transform: translateX(0);
			}
			.discount {
				right: 5px;
				width: 30px;
				height: 30px;
				font-size: 12px;
			}
			a {
				&:hover {
					.image_pagination_wrapper {
						display: none !important;
					}
				}
			}
			.change-image {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				display: flex;
				span {
					position: absolute;
					top: 50%;
					transform: translateY(-50%);
					z-index: 10001 !important;
					&.left {
						left: 0;
					}
					&.right {
						right: 0;
					}
					i {
						display: block;
						font-size: 20px;
					}
				}
			}
		}

		.body-product {
			padding: 5px 0;
			.cart-btn {
				opacity: 1;
				visibility: visible;
				margin: 10px 0;
				&:before {
					display: none;
				}
			}
			.title-product,
			.price-product {
				padding: 0 5px;
			}
			.title-product a,
			.price-product {
				font-size: 1.2rem;
				line-height: 1.25;
			}
			.price-product {
				font-weight: 700;
				.price-new {
					color: $primary-color;
				}
			}
		}
	}
}
