.profile-two {
	.dashboard {
		max-width: 700px;
		margin: 0 auto;
	}
	.dashboard_header {
		display: none;
		align-items: center;
		justify-content: space-between;
		max-width: 700px;
		margin: 0 auto;
		padding: 12px;
		position: fixed;
		top: 0;
		right: 0;
		left: 0;
		background-color: #fff;
		z-index: 999;
		h3 {
			flex-grow: 1;
			text-align: center;
			font-weight: bold;
			margin: 0;
		}
	}

	.warning_sell_time {
		width: 100%;
		padding: 12px;
		border-radius: 10px;
		background-color: #ffecec;
		border: 1px solid #ffd0d0;
		margin-bottom: 10px;
		p {
			font-size: 12px;
			display: flex;
			align-items: center;
			margin-bottom: 0;
			line-height: 1.4;
			color: #000;
			opacity: 0.5;
			img {
				margin-right: 5px;
			}
		}
		.warning_title {
			font-size: 13px;
			font-weight: 700;
			margin-bottom: 5px;
			opacity: 1;
		}
	}

	.tab-pane {
		min-height: 50vh;
	}
	.info_user {
		overflow-x: auto;
		flex-wrap: nowrap;

		.col-4 {
			min-width: 330px;
		}

		.dashboard-banner {
			padding-right: 0;

			.banner-big {
				display: flex;
				margin: 0;
				padding: 12px;
				border-radius: 13.5px !important;
				background: #eef6ff;

				.lazy-overlay-image {
					width: 106px;
					height: 106px !important;

					span {
						img {
							border-radius: 50%;
							width: 100%;
							height: 100%;
						}
					}
				}

				.banner-content {
					.banner-subtitle {
						font-weight: bold;
						font-size: 20px;
						line-height: 20px;
						color: #000;
						margin-left: 14px;
					}

					.banner-subtitle:nth-child(2) {
						padding-bottom: 11.6px;
						margin-bottom: 11px;
						border-bottom: 1.36px solid rgba(255, 255, 255, 0.5);
					}

					.btn-edit {
						color: #000;
						display: flex;
						align-items: center;

						img {
							margin-right: 8px;
							width: 14px;
							filter: brightness(0) invert(0);
						}
					}
				}
			}
		}

		.banner-content {
			position: unset;
			transform: unset;
		}

		.balance {
			margin-bottom: 0;
			align-items: center;
			display: flex;
			padding-left: 24px;
			min-height: 100% !important;
			border-radius: 13px;
		}
	}

	.nav-dashboard {
		margin-top: 30px;
		width: 100%;
		li {
			box-sizing: border-box;
			border-radius: 13.6px;
			margin-right: 15px;
			background: #fbfbfb;
			padding: 17px 21px;

			.nav-link {
				padding: 0;
				font-weight: bold;
				font-size: 16px;
				line-height: 20px;
				color: #252525;
			}
		}

		li:last-child {
			margin-right: 0;

			a {
				color: #ff7c7c !important;
			}
		}

		li.active {
			border: 2px solid $primary-color !important;
		}

		.profile-info {
			margin-bottom: 80px;
			.dashboard-banner {
				min-height: 30vh;
			}
		}
	}

	.info_user::-webkit-scrollbar {
		width: 0;
	}

	.info_support {
		justify-content: center;
		width: 100%;
		align-items: center;
		margin-top: 35px;
		box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
		border-radius: 20px;
		a {
			display: block;
			padding: 20px;
			font-weight: bold;
			font-size: 16px;
			line-height: 19px;
			align-items: center;
			color: #000000;
			i {
				margin-right: 17px;
			}
		}
	}
	.dashboard-sells {
		.dashboard-sells-groups {
			background: #f8f8f8 !important;
			flex-direction: column;
			.delivery_content {
				padding-bottom: 16px;
			}
			.map {
				#custom-map {
					overflow: hidden;
					border-radius: 20px;
					height: 400px !important;
				}
				#custom-map div {
					height: 100% !important;
				}
			}
			.sell_title {
				color: #000;
				font-weight: 600;
				padding: 10px 0 0 20px;
				font-size: 24px;
			}

			.sell_group_info {
				background: #ffffff;
				border-radius: 10px;

				.sell_group_info_header {
					padding: 10px !important;
					border-bottom: 1px solid #eeeeee;
					display: flex;
					align-items: center;
					span {
						margin-right: 8px;
						font-weight: bold;
						font-size: 20px;
						line-height: 23px;
						color: #000000;
					}
					.orders {
						display: flex;
						align-items: center;
						justify-content: space-between;
						// .order-status {
						// 	padding: 3px 4px;
						// 	font-size: 14px;
						// 	line-height: 16px;
						// 	color: #ffffff;
						// 	background: $primary-color !important;
						// 	border-radius: 5px;
						// }
					}
				}

				.sell_group_info_body {
					padding: 19px 25px;
					color: #7a7a7a;
					border-bottom: 1px solid #eeeeee;

					.delivery_type,
					.delivery_time,
					.delivery_courier,
					.delivery_car,
					.delivery_car_number {
						display: flex;
						margin-bottom: 20px;
						align-items: center;

						.delivery_name,
						.delivery_time_name,
						.courier_title,
						.delivery_car_title {
							line-height: 23px;
							font-size: 20px;
							margin-left: 8px;
						}
						.pending,
						.completed,
						.finished {
							font-size: 11px;
							line-height: 13px;
							color: #ffffff;
							display: inline-block;
							border-radius: 5px;
							text-align: right;
							padding: 3px 6px;
						}
						.pending {
							background: #fc7e7e;
						}
						.finished,
						.completed {
							background: #75d24a;
						}

						.delivery_price,
						.delivery_time_current,
						.delivery_car_name,
						.car_number,
						.courier_name {
							font-weight: bold;
							font-size: 20px;
							line-height: 23px;
							color: #232323;
							flex: 1;
							text-align: right;
						}
						.car_number {
							border: 1px solid #cacaca;
							padding: 3px 5px;
							border-radius: 4px;
							font-size: 14px;
							flex: unset;
						}
						.body-panding {
							flex: 1;
							text-align: right;
							.delivery_price {
								flex: unset;
								display: inline-block;
								margin-left: 6px;
							}
						}
					}
					.delivery_car_number {
						display: block;
					}
				}

				.sell_group_info_footer {
					display: flex;
					align-items: center;
					padding: 24px 18px;

					.check {
						font-size: 20px;
						line-height: 23px;
						color: #232323;
						user-select: none;
						cursor: pointer;
					}

					div {
						display: flex;
						align-items: center;
						flex: 1;
						justify-content: right;
						.pending,
						.completed,
						.finished {
							font-size: 11px;
							line-height: 13px;
							color: #ffffff;
							display: inline-block;
							border-radius: 5px;
							text-align: right;
							padding: 3px 6px;
							margin-right: 8px;
						}
						.price,
						.order-count {
							text-align: right;
							font-weight: bold;
							font-size: 20px;
							line-height: 23px;
							color: #232323;
							white-space: nowrap;
						}
						.order-count {
							margin-left: 8px;
							padding: 5px;
							border-radius: 5px;
							font-size: 12px;
							border: 1px solid $primary-color;
						}
						.pending {
							background: #fc7e7e;
						}
						.finished,
						.completed {
							background: #75d24a;
						}
					}
				}
			}
		}

		img {
			width: 22px;
		}
	}

	.edit_user {
		.radios {
			text-align: center;

			input {
				transform: scale(1.3);
			}
		}
	}

	.user_addresses_page {
		p {
			margin: 12px 0;
			font-size: 14px !important;
		}
	}
}
.no-data {
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	h3 {
		font-weight: 600;
		font-size: 24px;
		line-height: 28px;
		color: #758399;
		margin-bottom: 50px;
	}
	img {
		width: 360px !important;
	}
	.shopping-button {
		border: 2px solid $primary-color;
		border-radius: 13.6px;
		padding: 12px 24px;
		width: 360px;
		font-weight: 600;
		font-size: 22px;
		line-height: 26px;
		color: $primary-color;
		transition: 0.3s ease-in-out;
		&:hover {
			color: #fff;
			background: $primary-color;
		}
	}
}
.shipping-address {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 10px !important;
	margin: 15px 0 !important;
	border: 1px solid #ccc;
	border-radius: 10px;
	p {
		margin: 0;
		cursor: pointer;
	}
	i {
		cursor: pointer;
		color: red;
		font-weight: bold;
	}
}

.new-profile-style {
	.page-content {
		.dashboard,
		.checkout_two {
			min-width: 100%;
			.container.content-container {
				display: flex;
				.old-user-info {
					display: none;
				}
				.info_user {
					overflow-x: unset;
					flex-wrap: wrap;
				}
				.nav-dashboard {
					width: 100%;
					.profile-info {
						.col-12.p-5.col-md-3 {
							position: relative;
							padding: 0 10px !important;
							.profile-dropdown-menu {
								position: sticky !important;
								top: 80px;
								.profile-dropdown-menu-list {
									display: block;
									padding: 0;
									ul {
										padding: 0;
										display: block;
										position: relative;
										border-radius: 15px;
										background: #fff;
										li {
											margin: 0;
											background: transparent;
										}
									}
								}
							}
							.info_user.new-user-info {
								.col-4 {
									padding: 0 10px;
									margin-bottom: 12px;
									min-width: unset;
									min-height: unset;
									.banner {
										align-items: center;
										width: 100%;
										height: 150px;
									}
								}
							}
						}
						.col-12.col-md-9 {
							padding-right: 0;
							.tab-pane {
								height: 100%;
								padding: 0 10px 20px 10px;
								.active {
									height: 100%;
									.row {
										height: 100%;
									}
								}
								.user-cards-list {
									.row {
										margin: 0 auto;
										width: 100%;
									}
									.title-add-card {
										margin-bottom: 20px;

										h3 {
											margin: 0;
										}
										button {
											// padding: 0;
											color: $light-text !important;
											background: $primary-color;
											svg {
												width: 16px;
												path {
													fill: $light-text;
												}
											}
											font-size: 18px;
											color: $primary-color;
										}
									}
									.user-card {
										border-radius: 20px;
										padding: 9px 7px 9px 24px;
										background: #f8f8f8;

										h3 {
											font-weight: bold;
											font-size: 18px;
											line-height: 21px;
											color: #2a3141;
											margin-bottom: 0;
										}
										.card-date {
											margin-left: 32px;
											font-weight: normal;
										}
										.is-active {
											.active,
											.not-active {
												margin-right: 7px;
												min-width: 11px;
												min-height: 11px;
												border-radius: 50%;
											}
											.active {
												background: #42d76c;
											}
											.not-active {
												background: #f0f0f0;
											}
										}
										.delete-button {
											border-radius: 20px;
											width: 78px;
											height: 78px;
											background: #fff;
											margin-left: 72px;
										}
									}
								}
								.purchases {
									.dashboard-banner {
										height: 100%;
										.banner-big {
											height: 100%;
											.dashboard-sells {
												height: 100%;
												.dashboard-sells-groups {
													.sells-group {
														border-radius: 20px;
														.sell_group_info {
															background-color: #f8f8f8 !important;
															.sell_group_info_header {
																border-bottom: none;
																.order-id {
																	color: $primary-color !important;
																}
																.status-wrapper {
																	p {
																		padding: 3px
																			8px;
																		border-radius: 5px;
																	}
																}
																.delivery_time {
																	margin-top: 8px;
																	img {
																		width: 17px;
																	}
																	.delivery_time_current {
																		font-size: 18px;
																		color: #7a7a7a;
																	}
																}
															}
															.sell_group_info_footer {
																.price-count {
																	flex-direction: column;
																	align-items: flex-end;
																	.price {
																		white-space: nowrap;
																	}
																	.order-count {
																		padding: 0;
																		border: none;
																		font-size: 18px;
																		color: #7a7a7a;
																		font-weight: normal;
																	}
																}
																.view-check {
																	margin-left: 31px;
																	button {
																		display: flex;
																		justify-content: center;
																		align-items: center;
																		background: #fff;

																		width: 78px;
																		height: 78px;
																		border-radius: 20px;
																	}
																}
															}
														}
													}
												}
											}
										}
									}
								}
								.dashboard-banner {
									.banner-big {
										.dashboard-sells {
											.dashboard-sells-groups {
												height: 100%;
												margin: 0 !important;
												margin-top: 20px !important;
												border-radius: 15px;
												&:first-child {
													margin-top: 0 !important;
												}
											}
										}
									}
								}
								.info_support,
								.purchases-block {
									margin: 0 !important;
									padding: 0;
								}
								// .info_support {
								// 	// margin-top: 50px !important;
								// }
								.user_addresses_page {
									p,
									.new_address_add {
										margin-top: 0;
										.location-wrapper {
											z-index: 15;
										}
										.add_address_btn {
											button {
												border-radius: 10px;
												&:hover {
													background: #fff;
													color: $primary-color;
													border-color: $primary-color;
												}
											}
										}
									}
								}
							}
						}
					}
				}
			}
		}
		.dashboard {
			margin-top: 20px;
		}
	}
}

.ReactModal__Overlay.ReactModal__Overlay--after-open {
	&::-webkit-scrollbar {
		width: 2px;
	}
	&::-webkit-scrollbar-thumb {
		background-color: #ccc;
	}
}
@media (max-width: 991px) {
	.no-data {
		h3 {
			font-size: 16px;
			line-height: 20px;
			margin-bottom: 20px;
		}
		img {
			width: 160px !important;
		}
		.shopping-button {
			padding: 8px 16px;
			width: 260px;
			font-size: 16px;
			line-height: 20px;
		}
	}
	.map {
		margin-top: 20px;
	}
	.user-cards-list {
		.title-add-card {
			button {
				font-size: 16px !important;
			}
		}
		.user-card {
			border-radius: 10px !important;
			padding: 10px !important;
			.card-number-date {
				display: block !important;
				.card-date {
					margin-top: 8px;
					margin-left: 0 !important;
				}
			}
			h3 {
				font-size: 14px !important;
			}
			.is-active {
				.active,
				.not-active {
					margin-right: 10px !important;
					min-width: 8px !important;
					min-height: 8px !important;
				}
			}
			.delete-button {
				border-radius: 10px !important;
				width: 38px !important;
				height: 38px !important;
				margin-left: 12px !important;
			}
		}
	}
}

