.product-page-six {
	.page-content {
		.product-details-top {
			.product-gallery.product-gallery-gorizontal.only-desktop {
				.product-main-image {
					height: auto;
					width: 100%;
					div,
					img {
						height: 100%;
						object-fit: cover;
					}
				}
				.gallery-scrollable {
					width: 100%;
				}
				button.product-gallery-item {
					max-width: 20% !important;
					height: 100px;
					min-width: 20%;

					overflow: hidden;
					img {
						object-fit: cover;
						height: 100%;
					}
				}

				button.product-gallery-item.active::before {
					border-radius: $border-item;
					border: 2px solid #c5d2ab;
				}
			}
			.skel-pro-single.sticky {
				background: unset;
				.col-12 {
					padding: 0;
				}
				.relation-products {
					margin-top: 10px !important;
					padding: 0 20px;

					.row.align-items-center.w-100 {
						margin: 0 auto;
					}
					.shimmer-products {
						min-width: 100%;
					}
					.text-center {
						text-align: center !important;
						margin-bottom: 10px !important;
					}
				}
			}
		}

		.product-details.sticky-content {
			position: relative !important;
			top: 10px !important;

			.product-details {
				.product-title {
					font-weight: bold;
					font-size: 26px;
					line-height: 32px;
				}
				.price-and-articul {
					h6 {
						font-size: 18px;
						line-height: 32px;
					}
					.product-price {
						font-weight: bold;
						font-size: 26px;
						align-items: flex-end;
						line-height: 32px;
						color: $dark-text;
						margin-bottom: 50px;
						.new-price,
						.old-price {
							color: $dark-text;
						}
						.old-price {
							font-size: 18px;
							line-height: 26px;
						}
					}
				}
				.details-filter-row.details-row-size {
					margin-bottom: 30px;
					flex-direction: column;
					align-items: flex-start;

					.product-nav {
						.properties-block.color-properties {
							width: 100px !important;
							height: 100px !important;
							img {
								object-fit: contain !important;
								width: 100% !important;
								height: 100% !important;
							}
						}
						.properties-block.size-properties {
							min-width: 60px;
							min-height: 60px;
						}
					}
					.product-details-quantity {
						flex: unset;
						max-width: 175px;
						.form-control.input-changer-count {
							height: 50px;
							align-items: center;
							font-size: 20.2222px;
							line-height: 1;
							color: $dark-text;
							input {
								background: transparent;
							}
						}
					}
					label {
						margin-bottom: 8px;
						font-size: 18px;
						line-height: 22px;
						color: $dark-text;
						width: unset;
					}
				}
				.product-details-action {
					display: block;
					.home-plus-flex-div {
						display: flex;
						align-items: center;
						width: 80%;
						.align-items-center.blue-heart-button.home-plus-chooser {
							margin-top: 0 !important;
							span {
								display: none;
								svg {
									path {
										fill: #c5c5c5;
									}
								}
							}
						}
						.btn-product.btn-cart.text-white.mr-4 {
							border: 1px solid $primary-color;
							border-radius: 6px;
							margin-right: 30px !important;
							width: 342px;
							background-color: $primary-color;
							max-width: unset;
							padding: 14px;
							span {
								color: $light-text !important;
							}
							&:hover {
								background-color: $secondary-color !important;
							}

							.d-none {
								margin-right: 8px;
								// display: inline !important;
								/////////////////volidam///////
							}
							&::before {
								display: none;
							}
							span {
								display: flex;
								align-items: center;
								color: $primary-color;
								font-size: 17.0196px;
								line-height: 26px;
								text-transform: unset;
							}
						}
					}
					.details-action-wrapper {
						margin: 20px 0;
						border-radius: 6px;
						margin-right: 30px !important;
						max-width: unset;
						width: 70%;
						justify-content: space-between;
						span {
							color: $light-text;
							&:hover {
								box-shadow: unset;
							}
						}
						button {
							width: 45%;
							color: $light-text;
							padding: 14px;
							border: 1px solid $primary-color;
							border-radius: $border-radius-buttons;
							background: $primary-color;
							transition: 0.3s;
							&:hover {
								background: $secondary-color;
							}
						}
						.compare-btn.active {
							background: $primary-color;
						}
					}

					button:hover {
						background: $primary-color;
					}
				}
				.product-details-footer.d-block {
					border-top: none;
					.product-count {
						.content-bg,
						.btn.collapse-btn {
							background: #f9f9f9;
							border-radius: 5px;
						}
						.btn.collapse-btn,
						.location-with-count {
							width: 100%;
							margin: 0 auto;
							display: flex;
							align-items: center;
							justify-content: space-between;
							padding: 6px 8px;
							img {
								transition: 0.3s;
							}
							p {
								margin: 0;
								color: $dark-text;
								font-size: 14px;
								line-height: 32px;
							}
							img.rotate {
								transform: rotate(90deg);
							}
							.location-name,
							.product-count {
								padding: 0;
								display: flex;
								align-items: center;
								img {
									margin-right: 6px;
								}
							}
							.product-count {
								justify-content: flex-end;
								p {
									margin-right: 20px;
								}
							}
						}
						.btn.collapse-btn {
							p {
								color: $body-text;
								font-weight: 700;
								opacity: 0.5;
							}
							img {
								margin-right: 10px;
								width: 20px;
							}
						}
					}
					.row.supliers-row {
						.empty-brand {
							display: none !important;
						}
						align-items: center;
						justify-content: space-between;
						.product-cat,
						.col-4.mr-auto.mt-3 {
							.product-cat-suppliers {
								width: 100%;
								justify-content: space-between;
							}
							.suppliers_list {
								.supplier {
									.seller-info-name {
										justify-content: center;
										span {
											color: $body-text;
										}
										p {
											margin: 0;
										}
									}
									img {
										object-fit: contain;
									}
								}
							}
							.product-brand {
								.seller-info-img.brand-img {
									background: #f3f3f3;
								}
								.seller-info-name.brand-name {
									display: flex;
									justify-content: center;
									flex-direction: column;
									span,
									p {
										color: #949494;
										font-size: 18px;
										line-height: 20px;
										margin: 0;
									}
								}
							}
						}
						.col-4.mr-auto.mt-3 {
							margin: 0 !important;
							.social-label {
								display: none;
							}
						}
					}
				}
			}
		}
		.col-12.product-info {
			.react-tabs {
				display: block !important;
				.product-details-tab {
					margin-bottom: 0;
					color: $secondary-color-dark;
				}
			}
		}
	}
	.info-desc,
	.accordion.accordion-plus {
		display: none;
	}
}

@media (max-width: 991px) {
	.product-page-six.no-header {
		header {
			.header-middle.sticky-header {
				display: none;
			}
			.top-header {
				padding: 6px 0;
				// background: $body-bg;
				/////////////////GSHOP//////////////
				border-bottom: 1px solid $header-top;
			}
		}
		.bread-crumb {
			display: none;
		}
		.col-lg-6.col-md-12.pl-md-5 {
			padding: 0;
		}
		.category-header {
			display: flex;
			justify-content: space-between;
			align-items: center;
			z-index: 155;
			text-align: left !important;
			width: 100%;
			padding: 10px;

			.d-flex.align-items-center {
				margin: 0 auto;
				width: 88%;
			}
			h2 {
				margin-left: 4px !important;
				display: flex;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
				margin: 0;
				font-size: 18px;
				color: $dark-text;
			}

			.header-left {
				flex-basis: unset;
				max-width: unset;
			}

			.back-block {
				.exit-product-page {
					i {
						color: $dark-text;
						font-size: 20px;
						margin-right: 10px;
					}
				}
			}

			.row.searcher-row.w-100 {
				padding: 0;
				margin: 0 auto;
				input[type="search"]::-webkit-search-cancel-button {
					display: none;
				}
				width: fit-content;
				.long-input {
					max-width: 0;
					padding: 0 !important;
				}
				.header-search {
					display: block;
				}
				.header-search-wrapper.search-wrapper-wide.search.long-input-bg {
					background: transparent;
				}

				.header-search-wrapper.search-wrapper-wide.search {
					// background: rgba(0, 0, 0, 0.5);
					min-width: unset;
					background: rgba(0, 0, 0, 0.1);
					&::before {
						display: none;
					}
					border-radius: 10px;
					margin-top: 0;
					float: right;
					width: 100%;
					display: flex;
					align-items: center;
					position: relative;
					top: unset;
					right: unset;
					left: unset;
					border: none;
					justify-content: right;

					.btn.search-button {
						display: flex;
						align-items: center;
						flex: unset;
						min-width: 24px;
						height: 100%;
						margin: 0 5px 0 5px;
						img {
							filter: brightness(0);
						}
						h6 {
							margin: 0;
						}
					}
					input {
						background: transparent;
						color: $dark-text;
						&::placeholder {
							color: $dark-text;
						}
					}
					button:disabled {
						background-color: transparent !important;
					}
				}
				.col-12.only-mobile.d-none {
					padding: 0;
				}

				padding-top: 0;
			}

			.search-result-wrapper {
				right: 0;
				left: 0;
				position: absolute !important;
				margin-top: 15px !important;
			}
		}
		.page-content {
			.product-gallery.product-gallery-gorizontal {
				.product-main-image {
					height: 500px;
					img {
						border-radius: 0;
					}
				}
			}
			.splide.only-mobile.splide--slide {
				height: 500px;
				.splide__arrows {
					display: none;
				}
				.splide__pagination {
					position: relative;
					top: unset !important;
					margin-top: 20px;
					li {
						button {
							width: 25px;
							border-radius: 6px;
							margin: 0 7px;
						}
						button.is-active {
							background: $primary-color !important;
						}
					}
				}
				.splide__track,
				.big-img-with-view-button,
				.big-img-with-view-button img {
					height: 100%;
					object-fit: contain;
					width: 100%;
				}
			}
			.product-details-top {
				margin-top: 20px;
			}
			.skel-pro-single.sticky {
				width: 100%;
				margin: 0 auto;
				.col-md-5.pl-5 {
					padding: 0 !important;
				}
				.product-details {
					.product-title {
						display: none;
					}
					.price-and-articul {
						.product-content {
							margin: 0;
							h6 {
								margin: 0;
								font-size: 12px;
								line-height: 32px;
							}
						}
						.product-price {
							margin-bottom: 15px;
							font-size: 18px;
							line-height: 32px;
							.old-price {
								font-size: 14px;
								line-height: 32px;
								color: #949494;
							}
						}
					}
					.d-flex.align-items-center {
						.details-filter-row.details-row-size {
							display: none;
						}
					}
					.details-filter-row.details-row-size {
						.product-nav {
							.properties-block.color-properties {
								width: 60px !important;
								height: 60px !important;
							}
							.properties-block.size-properties {
								min-width: 40px;
								min-height: 40px;
							}
						}
					}
					.product-details-action {
						width: 100%;
						.home-plus-flex-div,
						.details-action-wrapper {
							width: 100%;
						}
						.btn-product.btn-cart.text-white.mr-4 {
							margin-right: 20px !important;
							max-width: 80%;

							span {
								font-size: 14px;
								line-height: 21px;
							}
						}
					}
					.product-details-footer.d-block {
						.product-cat {
							.product-brand {
								.seller-info-img {
									width: 60px;
									height: 60px;
								}
								.seller-info-name {
									span,
									p {
										width: 120px;
										font-size: 14px !important;
										line-height: 15px;
										white-space: nowrap;
										overflow: hidden;
										text-overflow: ellipsis;
									}
								}
							}
						}
					}
				}
				.relation-products {
					padding: 0;
				}
			}
		}
	}
}
