.profile-three {
	background: $body-bg;
	z-index: 1000;
	.warning_sell_time {
		width: 100%;
		padding: 12px;
		border-radius: 10px;
		background-color: #ffecec;
		border: 1px solid #ffd0d0;
		margin-bottom: 10px;
		p {
			font-size: 12px;
			display: flex;
			align-items: center;
			margin-bottom: 0;
			line-height: 1.4;
			color: #000;
			opacity: 0.5;
			img {
				margin-right: 5px;
			}
		}
		.warning_title {
			font-size: 13px;
			font-weight: 700;
			margin-bottom: 5px;
			opacity: 1;
		}
	}
	.page-content {
		.dashboard {
			.dashboard_header {
				display: flex;
				align-items: center;
				padding: 15px !important;
				position: relative;
				h3 {
					margin: 0 auto;
					line-height: 18px;
					letter-spacing: 0.2px;
					font-weight: bold;
				}
				.header-left.only-mobile.back-block {
					position: absolute;
				}
			}
			.nav.nav-dashboard {
				.active.show {
					width: 100%;
				}
				.profile-info {
					.menu-list {
						padding: 0 0 0 10px;
						align-items: center;
						margin: 16px 0;
						overflow-x: auto;
						display: flex;
						flex-wrap: nowrap;

						&::-webkit-scrollbar {
							display: none;
						}
						.nav-item {
							padding: 0;
							margin-right: 12px;
							.nav-link {
								transition: 0.2s;
								border-radius: $border-item;
								border: 2px solid $light-text;
								white-space: nowrap;
								color: $secondary-color-dark;
								padding: 8px 12px;
								&::before {
									display: none;
								}
							}
						}
						.nav-item.active {
							.nav-link {
								border: 2px solid $primary-color;
							}
						}
					}
					.tab-pane {
						.user-cards-list {
							.row {
								width: 100%;
								margin: 0 auto;
							}
							.title-add-card {
								margin-bottom: 20px;
								h3 {
									display: none;
								}
								button {
									border-radius: 15px;
									padding: 0;
									svg {
										path {
											fill: $secondary-color-dark;
										}
									}
									font-size: 20px;
									color: $secondary-color-dark;
								}
							}
							.user-card {
								border-radius: 20px;
								padding: 9px 7px 9px 24px;
								background: #f8f8f8;

								h3 {
									font-weight: bold;
									font-size: 18px;
									line-height: 21px;

									color: #2a3141;
									margin-bottom: 0;
								}
								.card-date {
									margin-left: 32px;
									font-weight: normal;
								}
								.is-active {
									.active,
									.not-active {
										margin-right: 7px;
										min-width: 11px;
										min-height: 11px;
										border-radius: 50%;
									}
									.active {
										background: #42d76c;
									}
									.not-active {
										background: #f0f0f0;
									}
								}
								.delete-button {
									border-radius: 20px;
									width: 78px;
									height: 78px;
									background: #fff;
									margin-left: 72px;
								}
							}
						}
						.purchases {
							.row {
								width: 100%;
								margin: 0 auto;
							}
							.dashboard-banner {
								height: 100%;
								.banner-big {
									height: 100%;
									.dashboard-sells {
										height: 100%;
										.dashboard-sells-groups {
											border: none !important;
											.col-12 {
												padding: 0;
											}
											.sells-group {
												background-color: #f8f8f8 !important;
												border-radius: 20px;
												padding: 9px 7px 9px 24px;
												.sell_group_info {
													.sell_group_info_header {
														border-bottom: none;
														.orders {
															flex-direction: column;
															justify-content: center;
															display: flex !important;
														}
														.order-id {
															color: $secondary-color-dark !important;
															font-size: 18px;
															line-height: 21px;
															margin-right: 11px;
														}
														.status-wrapper {
															p {
																padding: 3px 8px;
																border-radius: 5px;
															}
														}

														.delivery_time {
															margin-top: 8px;
															img {
																width: 17px;
															}
															.delivery_time_current {
																font-size: 18px;
																color: #7a7a7a;
															}
														}
													}
													.sell_group_info_footer {
														display: flex;
														align-items: center;
														.price-count {
															flex-direction: column;
															justify-content: flex-end;
															.price {
																font-size: 18px;
																line-height: 21px;
																white-space: nowrap;
																font-weight: bold;
															}
															.order-count {
																padding: 0;
																border: none;
																text-align: right;
																font-size: 18px;
																color: #7a7a7a;
																font-weight: normal;
															}
														}
														.view-check {
															margin-left: 31px;
															button {
																display: flex;
																justify-content: center;
																align-items: center;
																background: #fff;
																width: 78px;
																height: 78px;
																border-radius: 20px;
																img {
																	width: 40px;
																}
															}
														}
													}
												}
											}
										}
									}
								}
							}
						}
						.dashboard-banner {
							.banner.banner-big {
								background: $body-bg;
								color: $secondary-color-dark;
								border-radius: unset;
								.dashboard-sells {
									.dashboard-sells-groups {
										border-radius: $border-item;
										background-color: $body-bg !important;
										border: 1px solid $light-text;
										padding: 0;
										.sell_title {
											display: none;
										}
										.delivery_content {
											width: 100%;
											margin: 0 auto;
											.col-md-6.col-12 {
												padding: 0;
												.sells-group {
													.sell_group_info {
														.sell_group_info_header {
															padding: 12px !important;
															border-bottom: 1px
																solid
																$light-text;
															.order {
																display: flex;
																justify-content: space-between;
																h3 {
																	margin: 0;
																}
															}
															.orders {
																font-size: 14px;
																line-height: 18px;
																letter-spacing: 0.2px;
																color: $secondary-color;
															}
														}

														.sell_group_info_body,
														.sell_group_info_footer {
															padding: 12px 12px 0
																12px !important;

															.order-sum,
															.delivery_type,
															.delivery_time,
															.delivery_courier,
															.delivery_car,
															.order-footer {
																display: flex;
																align-items: flex-start;
																justify-content: space-between;
																margin-bottom: 12px;
																p {
																	font-size: 16px !important;
																	line-height: 20px !important;
																	letter-spacing: 0.2px !important;
																}
																h3 {
																	display: flex;
																	flex-direction: column;
																	align-items: flex-end;
																	font-size: 16px;
																	line-height: 20px;
																	font-weight: bold;
																	margin: 0;
																	span {
																		margin-top: 4px;
																		font-weight: normal !important;
																		padding: 4px
																			8px;
																		font-size: 12px;
																		line-height: 16px;
																		color: $body-bg;
																		border-radius: 6px;
																		background: #37cc5d;
																	}
																	span.pending {
																		background: #ff4d4f;
																	}
																}
															}
														}
														.sell_group_info_footer {
															display: flex;
															align-items: center;
															justify-content: space-between;
															padding: 12px !important;
															.order-footer {
																display: block;
																margin: 0;
															}
															.check {
																padding: 4px 8px;
																border-radius: $border-item;
																transition: 0.3s;
																border: 2px
																	solid
																	$light-text;
																&:hover {
																	border-color: $primary-color;
																}
															}
														}
													}
												}
											}
										}
										.map {
											margin: 0;
											padding: 12px !important;
											img {
												margin: 0 auto;
												width: 60% !important;
											}
											#custom-map {
												overflow: hidden;
												border-radius: $border-radius-image;
												height: 320px !important;
											}
											#custom-map div {
												height: 100% !important;
											}
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}
}
