@media (max-width: 991px) {
	.profile-three {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		.page-content {
			.dashboard {
				.header-container {
					display: block !important;
					padding: 0;
				}
				.dashboard_header {
					padding: 16px 10px;
					border-bottom: 1px solid $light-text;
				}
				.tab-pane {
					.dashboard-banner {
						padding: 0 10px;
					}
					.purchases {
						.dashboard-banner {
							padding: 0;
							.banner-big {
								.dashboard-sells {
									.dashboard-sells-groups {
										.sells-group {
											border-radius: 10px !important;
											padding: 9px 7px !important;
											.sell_group_info {
												.sell_group_info_header {
													padding: 0 !important;
													.order-id {
														font-size: 14px !important;
														line-height: 16px !important;
														margin-right: 8px !important;
													}
													.status-wrapper {
														p {
															background: #37cc5d !important;
															font-size: 10px !important;
														}
													}

													.delivery_time {
														img {
															width: 15px !important;
														}
														.delivery_time_current {
															font-size: 12px !important;
														}
													}
												}
												.sell_group_info_footer {
													padding: 0 !important;
													.price-count {
														.price {
															font-size: 14px !important;
															line-height: 16px !important;
														}
														.order-count {
															font-size: 14px !important;
														}
													}
													.view-check {
														margin-left: 15px !important;
														button {
															width: 48px !important;
															height: 48px !important;
															border-radius: 10px !important;
															img {
																width: 20px !important;
															}
														}
													}
												}
											}
										}
									}
								}
							}
						}
					}
				}
				.delivery_content {
					.sells-group {
						.sell_group_info {
							.sell_group_info_body {
								border-bottom: 1px solid $light-text;
							}
							.sell_group_info_footer {
								border-bottom: 1px solid $light-text;
							}
						}
					}
				}
			}
		}
	}
	.user-cards-list {
		.title-add-card {
			button {
				font-size: 16px !important;
				background: $primary-color;
				padding: 8px !important;
				border-radius: $border-item;
			}
		}
		.user-card {
			border-radius: 10px !important;
			padding: 10px !important;
			.card-number-date {
				display: block !important;
				.card-date {
					margin-top: 8px;
					margin-left: 0 !important;
				}
			}
			h3 {
				font-size: 14px !important;
			}
			.is-active {
				.active,
				.not-active {
					margin-right: 10px !important;
					min-width: 8px !important;
					min-height: 8px !important;
				}
			}
			.delete-button {
				border-radius: 10px !important;
				width: 38px !important;
				height: 38px !important;
				margin-left: 12px !important;
			}
		}
	}
}
