#acsept-modal {
	.modal-content {
		width: fit-content;
		margin: 0 auto;
		.question {
			margin: 40px 80px 20px 80px;
			font-weight: bold;
			font-size: 28px;
			line-height: 33px;
		}
		.acsept-buttons {
			padding: 10px 80px 40px 80px;
			display: inline-flex;
			justify-content: space-between;
			.acsept-no,
			.acsept-yes {
				padding: 10px 20px;
				font-weight: bold;
				font-size: 18px;
				line-height: 21px;
				border-radius: 10px;
				transition: all 0.3s;
				&:hover {
					color: #fff;
				}
			}
			.acsept-yes {
				border: 1px solid #e16363;
				&:hover {
					background: #e16363;
				}
			}
			.acsept-no {
				border: 1px solid #42d76c;
				&:hover {
					background: #42d76c;
				}
			}
		}
	}
}
@media (max-width: 768px) {
	#acsept-modal {
		max-width: unset;
		.modal-content {
			text-align: center;
			.question {
				font-size: 18px;
				margin: 20px 10px;
			}
			.acsept-buttons {
				padding: 10px 40px;
				.acsept-no,
				.acsept-yes {
					padding: 10px 20px;
					font-size: 14px;
				}
				.acsept-yes {
					margin-left: 20px;
				}
			}
		}
	}
}
