.suppliers {
	.suppliers_header {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin: 40px 0;
		h3 {
			font-size: 32px;
			font-weight: bold;
		}
		a {
			color: #858ea5;
			font-size: 24px;
		}
	}
	.splide__arrows {
		button.splide__arrow {
			position: absolute;
			top: 50%;
			left: -10px;
			width: 40px;
			height: 40px;
			background-color: #fff !important;
			box-shadow: 0 0 15px #ccc;
			border-radius: 50%;
			transform: translateY(-50%);
			opacity: 1;
			display: flex;
			align-items: center;
			justify-content: center;
			svg {
				width: 15px;
				height: 15px;
				path {
					fill: black;
				}
			}
		}
		.splide__arrow.splide__arrow--next {
			left: auto;
			right: -10px;
		}
		button:disabled {
			background: #fff !important;
		}
	}
}

.supplier-wrapper {
	height: 100%;
	width: 100%;
	padding: 24px;
	background: #f4f6f8;
	border-radius: 24px;
	.no-logo,
	.no-product {
		picture {
			display: none;
		}
		background: #f4f6f8;
	}
	.no-product {
		height: 210px;
	}
	.small-images {
		.no-product {
			height: 50%;
			width: 100%;
			border-radius: 10px;
		}
	}
	.logo {
		width: 90px;
		height: 90px;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 50%;
		overflow: hidden;
		border: 1px solid #bcc5d9;
	}

	.about-supplier {
		width: 100%;
		padding-left: 16px;
		overflow: hidden;
		a,
		.sub-title,
		.rating {
			width: 100%;
			display: block;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
		}
		.title {
			width: 100%;
			font-size: 18px;
			line-height: 18px;
		}
		.sub-title {
			font-size: 14px;
			line-height: 30px;
			display: block;
			margin-bottom: 4px;
		}
		.rating {
			font-size: 10px;
			background: #ffffff;
			border-radius: 50px;
			color: #002930;
			display: inline;
			padding: 4px 10px;
		}
	}
	.images {
		display: flex;
		padding-top: 14px;
		img {
			width: 100%;
			height: 100% !important;
			display: block;
			object-fit: cover;
		}
		.big-img {
			width: 65%;
			height: 210px;
			border-radius: 10px !important;
			overflow: hidden;
		}
		.small-images {
			width: 35%;
			height: 210px;
			margin-left: 10px;
			display: flex;
			align-items: center;
			flex-direction: column;
			.small-img,
			.no-product {
				height: 46%;
				width: 100%;
			}
			img {
				width: 100%;
				flex-grow: 1;
				border-radius: 8px;
			}
		}
	}
}

@media (max-width: 1300px) {
	.suppliers {
		.supplier-wrapper {
			padding: 10px;
			.title {
				font-size: 14px;
			}
			.sub-title {
				font-size: 12px;
				margin: 0;
			}
		}
	}
}

@media (max-width: 1000px) {
	.suppliers {
		h3 {
			font-size: 20px;
		}
		a {
			font-size: 14px;
		}
	}
}

@media (max-width: 600px) {
	.suppliers {
		.suppliers_header {
			margin-top: 10px !important;
			margin-bottom: 10px !important;
			h3 {
				font-size: 20px !important;
			}
			a {
				span {
					display: none !important;
				}
				i {
					display: block !important;
					&::before {
						font-size: 20px;
						font-weight: bold;
					}
				}
			}
		}
		.supplier-wrapper {
			.logo {
				margin: auto 0;
				width: 70px;
				height: 70px;
			}

			.no-product,
			.big-img,
			.small-images {
				height: 200px;
			}
		}
	}
}
