.footer.footer-three {
	background-color: hsl(224, 15%, 14%) !important;
	.container {
		&::before {
			display: none !important;
		}
	}
	.footer-middle {
		padding: 0;
		padding-top: 50px;
		.footer-info {
			display: flex;
			& > div {
				width: 20%;
			}
			.widget {
				margin: 0;
				.footer-collapse {
					display: none;
				}
			}
		}
		.footer-logo {
			width: 152px;
			margin: 0;
			margin-bottom: 20px;
			img {
				width: 100%;
				display: block;
				object-fit: cover;
			}
		}
		h4 {
			font-weight: bold;
			font-size: 18px;
			color: $light-text;
		}
		a {
			color: $light-text;
			font-size: 16px;
		}
		.widget {
			padding: 0 10px 10px;
			.widget-title {
				margin-bottom: 20px;
			}
		}
		.payment-methods {
			display: flex;
			align-items: center;
			flex-wrap: wrap;
			.payment-method {
				width: 70px;
				margin-bottom: 8px;
				margin-left: 6px;
				overflow: hidden;
				border-radius: 8px;
				img {
					width: 100% !important;
					height: 32px;
					display: block !important;
					object-fit: cover !important;
				}
			}
		}

		.contacts {
			.contact_list {
				.contact_item {
					color: $light-text !important;
					margin: 0;
					a {
						display: block;
					}
				}
			}
			.footer-social-links {
				margin-top: 20px;
				a {
					margin-right: 15px;
					padding: 10px;
					background-color: rgba(0, 0, 0, 0.4);
					border-radius: 16px;
				}
			}
		}
	}
	.footer-bottom {
		background: rgba(255, 255, 255, 0.12);
		.container {
			padding: 15px 10px !important;
		}
		.footer-bottom-wrap {
			width: 100%;
			justify-content: space-between;
			.bottom-links {
				.bottom-item {
					padding-right: 24px;
					font-size: 16px;
					color: $light-text;
					&:hover {
						a {
							color: $light-text;
						}
					}
				}
			}
			.copy-right {
				font-size: 16px;
				color: $light-text;
				&:hover {
					a {
						color: $light-text !important;
					}
				}
			}
		}
	}
}

@media (max-width: 1000px) {
	.footer-three {
		margin-bottom: 80px;
		.footer-middle {
			padding: 0;
			.container {
				min-width: 100% !important;
				margin: 0;
				padding: 0;
				.footer-info {
					flex-direction: column;

					.widget {
						width: 100%;
						margin-bottom: 10px;
						border-bottom: 1px solid #00000012;

						.widget-title {
							margin-bottom: 0;
						}
						.footer-collapse {
							display: block;
						}
						.widget-list {
							height: 0;
							transition: 0.3s;
							overflow: hidden;
							.contact_item {
								a {
									display: block;
								}
							}
						}
						.widget-list.active {
							padding: 20px 0;
							height: auto !important;
						}
						.payment-methods {
							width: 100%;
							flex-wrap: nowrap;
							padding: 10px;
							overflow-x: auto;
							.payment-method {
								min-width: 70px !important;
							}
						}
						&:last-child .widget-list {
							border-bottom: none;
						}
						.footer-logo {
							padding: 20px;
							margin-bottom: 0 !important;
							// border-bottom: 1px solid #00000012;
							img {
								width: 30%;
							}
						}
						.social-links-block {
							margin-top: 20px !important;
							margin-bottom: 0 !important;
						}
					}
					.widget-about {
						padding: 0;
					}
				}
			}
		}
		.footer-bottom {
			.footer-bottom-wrap,
			.bottom-links {
				display: flex;
				flex-direction: column;
				span {
					font-size: 14px !important;
				}
			}
		}
	}
}
