#map-modal.map-modal-three {
	width: 80% !important;
	margin: 0 auto;
	.modal-body {
		display: flex;
		height: 80vh;
		.map_content {
			width: 60%;
			.map-modal-details {
				.map-select {
					display: none;
				}
			}
		}
		.regions {
			width: 40%;
			padding: 50px 30px;
			.region_list {
				width: 100%;
				margin: 0;
				padding: 0;
				height: 100%;
				overflow: auto;
				&::-webkit-scrollbar {
					width: 0;
				}
				.region_item {
					margin: 10px 0;
					padding: 10px;
					border-radius: 8px;
					cursor: pointer;
					&:hover {
						background-color: rgba(
							$color: $primary-color,
							$alpha: 0.2
						);
						color: $primary-color;
					}
				}
			}
		}
	}
}

@media (max-width: 800px) {
	#map-modal {
		// width: 98% !important;
		.map_content {
			width: 100% !important;
		}
		.map-modal-details {
			.map-select {
				display: block !important;
			}
		}
		.regions {
			display: none;
		}
	}
}
