.blog-page,
.blog-main {
	margin-bottom: 50px;
	.breadcrumb-nav {
		margin: 22px 0 36px 0;
		padding-left: 10px;
		border-bottom: none;
		.breadcrumb {
			.breadcrumb-item {
				&:last-child {
					a {
						color: $primary-color;
					}
				}
			}
		}
	}
	.left-side {
		.search {
			position: relative !important;
			.search-result {
				.search-result-list {
					a {
						display: block;
						padding: 10px;
					}
				}
			}
		}
		.title {
			h3 {
				font-size: 20px;
				line-height: 23px;
				font-weight: bold;
				margin-bottom: 12px;
				color: $dark-text;
			}
			p {
				font-size: 12px;
				line-height: 14px;
				color: $body-text;
			}
		}
		.menu-list {
			margin: 40px 0;
			.menu-item {
				border-bottom: 1px solid $third-color;
				&:last-child {
					border-bottom: none;
				}
				a {
					font-size: 16px;
					line-height: 19px;
					color: $dark-text;
					display: flex;
					justify-content: space-between;
					align-items: center;
					padding: 10px 0;
				}
			}
		}
		.lates-content {
			.blog-content {
				display: flex;
				align-items: center;
				.blog-img {
					width: 55px;
					height: 55px;
					margin-right: 6px;
					border-radius: 5px;
					img {
						object-fit: contain;
					}
				}
				.title {
					font-size: 12px;
					line-height: 14px;
					color: $primary-color;
					font-weight: bold;
				}
			}
		}
	}
	.right-side {
		.blog-content {
			display: flex;
			margin-bottom: 30px;
			.blog-img {
				width: 230px;
				height: 150px;
				border-radius: $border-radius-image;
				img {
					object-fit: contain;
				}
			}
			.title-content {
				margin-left: 10px;
				a {
					font-size: 24px;
					line-height: 28px;
					color: $primary-color;
					font-weight: bold;
				}
				p {
					font-weight: bold;
					margin: 5px 0;
					font-size: 14px;
					line-height: 16px;
					color: $secondary-color;
				}
				.content {
					font-size: 14px;
					line-height: 19px;
					color: $body-text;
				}
			}
		}
	}
}
.blog-main {
	.right-side {
		.title {
			h2 {
				font-size: 32px;
				line-height: 38px;
				color: $dark-text;
				font-weight: bold;
				margin-bottom: 16px;
			}
		}
		p {
			font-size: 16px;
			line-height: 19px;
			color: $dark-text;
			margin-bottom: 35px;
			span {
				margin-right: 5px;
				color: $body-text;
			}
		}
		.blog-img {
			height: 520px;
			border-radius: $border-radius-image;
			img {
				object-fit: contain;
			}
		}
		.content {
			margin-top: 20px;
			font-size: 20px;
			line-height: 23px;
			color: $body-text;
		}
	}
}

@media (max-width: 991px) {
	.blog-page,
	.blog-main {
		margin-bottom: 150px;
		.header {
			.breadcrumb-nav {
				margin-top: 0;
			}
		}
		.side-row {
			flex-direction: column-reverse;
			.blog-content {
				.blog-img {
					width: 30%;
					height: auto;
				}
				.title-content {
					a {
						font-size: 18px;
					}
					p {
						font-size: 16px;
					}
				}
			}
		}
	}
}
