.checkout-four {
	.page-content {
		.cart {
			.change-address,
			.cart-block-main {
				margin-bottom: 30px;
				background: $third-color;
				border-radius: $border-radius-image;
				padding: 20px;
				border: none;
			}
			.change-address {
				.summary-shipping-estimate {
					h4 {
						margin-bottom: 20px;
						font-weight: 700;
						font-size: 20px;
						line-height: 24px;
						color: $dark-text;
					}
					button {
						display: flex;
						width: 100%;
						padding: 22px 20px !important;
						align-items: center;
						justify-content: space-between;
						background: $light-text;
						margin: 0 !important;
						text-decoration: none !important;
						border-radius: $border-item;
						p {
							margin: 0;
							font-size: 16px;
							line-height: 19px;
						}
					}
				}
			}

			.cart-block-main {
				.filter-item {
					.widget.widget-collapsible {
						margin-bottom: 0;
					}
					.cart_delivery_info {
						padding: 20px;
						margin-bottom: 20px;
						border-radius: $border-item;
						background: $light-text;
						.left {
							.left_top {
								p,
								span,
								.time {
									font-size: 16px;
									line-height: 1;
									color: $body-text;
									margin: 0;
								}
								span {
									color: $dark-text;
								}
								p {
									margin-left: 10px;
								}
								.time {
									color: $body-text;
									margin-left: 10px;
									font-size: 14px;
								}
							}
						}
						.right {
							cursor: pointer;
							i {
								color: $secondary-color;
							}
						}
					}
				}
				.warning_sell_time {
					width: 100%;
					padding: 12px;
					border-radius: 10px;
					background-color: #ffecec;
					border: 1px solid #ffd0d0;
					margin-bottom: 10px;
					p {
						font-size: 12px;
						display: flex;
						align-items: center;
						margin-bottom: 0;
						line-height: 1.4;
						opacity: 0.5;
						color: #000;
						img {
							margin-right: 5px;
						}
					}
					.warning_title {
						font-size: 13px;
						font-weight: 700;
						opacity: 1;
						margin-bottom: 5px;
					}
				}
				.widget-body {
					.sell_group {
						.active-sell {
							padding: 20px;
							margin-bottom: 20px;
							border-radius: $border-item;
							background: $light-text;
							&:last-child {
								margin-bottom: 0;
							}
							.info_sell {
								display: flex;
								align-items: center;
								justify-content: space-between;
								margin-bottom: 20px;

								p {
									font-weight: 700;
									font-size: 20px;
									line-height: 24px;
									color: $dark-text;
								}
								.sell_location {
									i {
										display: none;
									}
									font-size: 16px;
									line-height: 1;

									color: $secondary-color;
									// border-bottom: 1px solid $secondary-color;
								}
							}
							.cart-supplier-info {
								padding: 0 !important;
								.row.cart-product-card {
									width: 100%;
									margin: 0 auto;
									align-items: center;
									margin-bottom: 20px;
									.col-2 {
										padding-left: 0;
										.product-media-wrapper {
											border-radius: $border-item;
											border: 0.5px solid #f2f3f5;
											overflow: hidden;
										}
									}
									.col-9.cart-product-info {
										.cart-item-infoblock {
											margin-top: 0;
											a {
												font-size: 12px;
												line-height: 14px;
												color: $body-text !important;
												margin-bottom: 10px;
											}
											.d-md-block {
												display: none !important;
											}
										}
										.row.align-items-center {
											flex-direction: row-reverse;
											.cart-product-action {
												width: max-content !important;
												padding: 0;
												.input-changer-count {
													padding: 0 !important;
													margin: 0;
													background: none;
													border: none;
													width: max-content;
													height: max-content;

													align-items: center;
													input {
														background: none;
														font-weight: 700;
														font-size: 16px;
														width: 60px !important;
														line-height: 19px;
														color: $dark-text;
													}
													.loader {
														width: 60px;
														.spinner-border {
															border-color: $primary-color;
															border-right-color: transparent;
														}
													}
													span {
														display: flex;
														align-items: center;
														justify-content: center;
														min-width: 30px;
														height: 30px;
														color: $primary-color !important;
														border-radius: 40px;
														background: rgba(
															11,
															190,
															83,
															0.2
														);
													}
												}
											}

											.cart-amount-product {
												text-align: right;
												font-size: 16px;
												line-height: 19px;
												color: $primary-color;
											}
										}
									}
									.col-1 {
										button {
											width: 50px;
											height: 50px;
											display: flex;
											align-items: center;
											justify-content: center;
											background: $third-color;
											border-radius: $border-item;
										}
									}
								}
							}
						}
					}
				}
			}
			.sidebar_three {
				.summary.summary-cart {
					padding: 20px;
					border: none;
					border-radius: $border-radius-image;
					background: $third-color;
					.title {
						font-weight: 700;
						font-size: 20px;
						line-height: 24px;
						color: $dark-text;
						margin-bottom: 20px;
					}
					.pay_info {
						background: $light-text;
						padding: 20px;
						border-radius: $border-item;
						.summary-subtotal,
						.summary-shipping,
						.total {
							display: flex;
							justify-content: space-between;
							align-items: center;
							margin-bottom: 10px;

							p {
								white-space: nowrap;
								width: max-content;
								margin: 0 10px 0 0;
								color: $body-text;
								font-size: 14px;
								line-height: 16px;
							}
							.dashed {
								display: inline !important;
								width: 100%;
								border-bottom: 1px dashed #899499;
							}

							.priceSell {
								font-size: 16px;
								line-height: 19px;
								color: $dark-text;
								margin-right: 0;
								margin-left: 10px;
							}
						}
						.total {
							margin-bottom: 0;
							.priceSell {
								color: $primary-color;
								font-weight: 700;
							}
						}
						.hr {
							width: 100%;
							border-top: 0.05rem solid rgba(137, 148, 153, 0.2);
							margin: 20px auto;
						}
						margin-bottom: 20px;
					}
				}
				.summary-add_comment {
					textarea {
						width: 100%;
						border-radius: $border-item;
						background: $light-text;
						border: none;
						padding: 20px;
						color: $dark-text;
						&::placeholder {
							color: $body-text;
						}
						&:focus,
						&:active {
							border: none;
							box-shadow: none;
							outline: none;
						}
					}
					margin-bottom: 20px;
				}

				.pay_wrapper {
					.summary-user_card {
						.payments {
							button {
								margin-bottom: 20px;
								border: 1px solid $primary-color;
								color: $primary-color;
								background: $light-text;
								&:hover {
									color: $light-text;
									background: $primary-color;
								}
							}
						}
					}
					button {
						width: 100%;
						font-size: 16px;
						// line-height: 1;
						padding: 10.5px 0;
						border-radius: $border-item;
						background: $primary-color;
						color: $light-text;
						&:hover {
							color: $primary-color;
							background: $light-text;
						}
					}
				}
			}
		}
	}
}
@media (max-width: 991px) {
	.checkout-four {
		.page-content {
			.cart {
				.change-address {
					padding: 16px;
					margin-bottom: 20px;
					.summary-shipping-estimate {
						h4 {
							font-size: 18px;
							margin-bottom: 10px;
						}
						button {
							padding: 10px !important;
							img {
								width: 20px;
							}
						}
					}
				}
				.cart-block-main {
					padding: 16px;
					.filter-item {
						.widget.widget-collapsible {
							.cart_delivery_info {
								padding: 10px;
								margin-bottom: 10px;
								.left {
									.left_top {
										img {
											width: 16px;
										}
										p,
										span,
										.time {
											font-size: 14px;
										}
										p {
											margin-left: 6px;
										}
										.time {
											margin-left: 6px;
											font-size: 12px;
										}
									}
								}
							}
						}
					}
					.widget-body {
						.sell_group {
							.active-sell {
								padding: 10px;
								margin-bottom: 10px;
								.info_sell {
									margin-bottom: 10px;
									p {
										font-size: 16px;
										line-height: 19px;
									}
									.sell_location {
										font-size: 14px;
									}
								}
								.cart-supplier-info {
									.row.cart-product-card {
										margin-bottom: 10px;
										.col-9.cart-product-info {
											padding: 0;
											.row.align-items-center {
												padding: 0;
												.cart-product-action {
													.input-changer-count {
														input {
															font-size: 12px;
														}
														span {
															img {
																width: 10px;
															}
															min-width: 20px;
															height: 20px;
														}
													}
												}

												.cart-amount-product {
													font-size: 14px;
													line-height: 16px;
												}
											}
										}
										.col-1 {
											padding-right: 0;
											button {
												height: 30px;
												width: 30px;
												border-radius: 5px;
											}
										}
									}
								}
							}
						}
					}
				}
				.sidebar_three {
					.summary.summary-cart {
						padding: 16px;
						.title {
							font-size: 18px;
							line-height: 20px;
							margin-bottom: 10px;
						}
						.pay_info {
							padding: 10px;
							.total {
								p {
									font-size: 12px;
									line-height: 14px;
								}

								.priceSell {
									font-size: 14px;
									line-height: 16px;
								}
							}
							.hr {
								margin: 10px auto;
							}
							margin-bottom: 16px;
						}
					}
					.summary-add_comment {
						textarea {
							padding: 10px;
						}
						margin-bottom: 10px;
					}
					.pay_wrapper {
						.summary-user_card {
							.payments {
								button {
									margin-bottom: 10px;
								}
							}
						}
						button {
							font-size: 14px;
							padding: 8px 0;
						}
					}
				}
			}
		}
	}
}
