.compare-card-one {
	padding: 10px;
	border-radius: $border-item;
	.product-header {
		position: relative;
		border-radius: $border-radius-image;
		height: 250px;
		background: #ffffff;
		overflow: hidden;
		margin-bottom: 11px;
		.discount {
			background: #fd6b6b;
			width: 55px;
			height: 55px;
			display: flex;
			align-items: center;
			justify-content: center;
			position: absolute;
			right: 15px;
			border-radius: 50%;
			top: 15px;
			span {
				font-weight: 700;
				font-size: 18.0329px;
				line-height: 22px;
				color: #ffffff;
			}
		}

		img {
			object-fit: contain;
		}
		.header-buttons {
			width: 100%;
			height: 100%;
			background: linear-gradient(
				0deg,
				rgba(0, 0, 0, 0.4),
				rgba(0, 0, 0, 0.4)
			);
			opacity: 0;
			border-radius: 5px;
			transition: 0.3s;
			position: absolute;
			button {
				transform: translateX(-100%);
				width: 36px;
				height: 36px;
				background: #ffffff;
				display: flex;
				align-items: center;
				justify-content: center;
				padding: 0;
				border-radius: 50%;
				margin-top: 14px;
				margin-left: 14px;
				transition: 0.3s;
				img {
					transition: 0.3s;
					width: 18px;
					filter: brightness(0) invert(0.7);
				}
			}
			.have-compare:hover {
				img {
					filter: unset;
				}
			}
			.have-choosen,
			.have-cart {
				img {
					filter: unset;
				}
			}
			.cart-btn {
				.spinner-border {
					border-color: $primary-color;
					border-right-color: transparent;
				}
			}
		}
		&:hover {
			.header-buttons {
				opacity: 1;
				button {
					transform: translateX(0);
				}
			}
		}
	}
	.body-product {
		.title-product {
			margin-bottom: 20px;
			a {
				font-size: 18px;
				line-height: 22px;
				color: #1d2530;
				text-align: left;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
				overflow: hidden;
				--overfdropdownlow: hidden;
				text-overflow: ellipsis;
			}
			&:hover {
				a {
					color: $primary-color !important;
				}
			}
		}
		.price-product {
			.price-new,
			.price-old {
				font-weight: 700;
				font-size: 18.0329px;
				line-height: 22px;
				color: #1d2530;
			}
			.price-old {
				margin-left: 10px;
				color: #cacaca;
				font-size: 14px;
				line-height: 17px;
			}
		}
	}
	margin-bottom: 20px;
}

@media (max-width: 991px) {
	.compare-card-one {
		.product-header {
			height: 150px;
		}
		.body-product {
			.title-product {
				margin-bottom: 6px;

				a {
					font-size: 12px;
				}
			}
			.price-product {
				margin-bottom: 10px;
				.price-new {
					font-size: 14px;
				}
				.price-old {
					font-size: 12px;
				}
			}
		}
	}
}
