.product-page-two {
	.skel-pro-single {
		margin: 20px 0 30px 0;

		.col-md-6.pl-5 {
			padding: 0 !important;
		}
	}
	.w-100.p-5 {
		padding: 0 !important;
	}
	.btn.btn-load-more {
		border-radius: 10px;
	}

	.page-content {
		.toolbox-right {
			.select-custom {
				#sortby {
					margin-bottom: 0 !important;
				}
			}
		}
		.toolbox-layout {
			.btn-layout:first-child {
				display: none;
			}
		}
		.card.card-box {
			.card-body {
				.product-desc-content {
					a {
						color: $primary-color;
					}
					ul {
						list-style: decimal;
						padding-left: 16px;
						li {
							padding: 0;
							&::before {
								display: none;
							}
						}
					}
				}
			}
		}
		.product-details-top {
			margin-top: 26px;

			.product-gallery {
				margin-bottom: 0 !important;

				.product-main-image {
					margin: 0;

					.media-sticky.mt-2:first-child {
						margin-top: 0 !important;
					}
				}
			}

			.product-details.sticky-content {
				margin: 0 0 0 40px;
				padding: 0;
				border: none;
				.react-tabs {
					display: none;
				}
			}

			.product-details.sticky-content {
				width: unset !important;

				.product-details {
					padding: 0;
					border: none;
					.product-title {
						color: #000000 !important;
						font-weight: 400;
						letter-spacing: unset;
						margin: 0 0 18px 0;
					}

					.product-content {
						margin-bottom: 18px;

						h6 {
							font-size: 14px;
							color: #000000;
						}
					}

					.product-price {
						font-weight: bold;
						color: #2661ff;
						margin-bottom: 18px;
					}

					.details-filter-row.details-row-size {
						flex-direction: column;
						align-items: start;
						display: unset;

						label,
						.properties-block,
						.activeproperties-block,
						.not-allowedproperties-block {
							margin-bottom: 12px !important;
							font-size: 16px;
							line-height: 20px;
							color: #000000;
						}

						label {
							display: flex;
							width: 100%;
							justify-content: space-between;

							a {
								text-decoration: underline;
								font-size: 16px;
								line-height: 20px;
								color: #1890ff;
							}
						}

						.product-nav {
							margin-bottom: 0 !important;
							align-items: flex-end;

							.activeproperties-block {
								border: 1px solid #1d75ff !important;
							}

							margin-bottom: 24px;

							.properties-block,
							.activeproperties-block,
							.not-allowedproperties-block {
								border-radius: 10px !important;
								margin: 0 20px 20px 0 !important;
							}

							.not-allowedproperties-block {
								border: 1px solid #b7b7b7 !important;
							}

							.color-properties {
								padding: 0;
								img {
									width: 68px;
									height: 94px;
									border-radius: 7px;
								}
								span {
									padding: 5px;
								}
							}

							.size-properties {
								padding: 16px !important;
							}
						}

						.product-details-quantity {
							margin-right: 85px;
							max-width: 218px;

							.form-control.input-changer-count.d-flex.p-3.justify-content-between {
								border-radius: 10px;
								padding: 16px 26px !important;

								height: unset;

								span,
								input {
									background: transparent;
									font-size: 20px;
									line-height: 24px;
									color: #000000;
								}

								input {
									margin: 0 40px;
								}
							}
						}
					}
				}
			}
		}

		.price-and-articul {
			display: flex;

			flex-direction: column-reverse;
		}

		.product-details-action.choose-variations {
			position: absolute;
			top: 70px;
			right: 0;

			span {
				img {
					margin-right: 10px;
				}

				align-items: center;
				display: flex;
				background: #fff172;
				border: 1px solid #ded052;
				box-sizing: border-box;
				border-radius: 10px;
				padding: 12px;
				color: #000000;
			}
		}

		.product-details-action {
			margin: 24px 0 40px 0;

			span {
				box-shadow: none;
				display: flex !important;
				align-items: center;
				font-size: 16px !important;
				line-height: 20px !important;
			}

			svg {
				margin-right: 10px;
			}

			.btn-product.btn-cart {
				padding: 16px 0;
				min-width: 252px;
				color: #4383ff;
				background: $primary-color;
				border: 1px solid #4383ff;
				border-radius: 20px !important;

				span {
					text-transform: unset;
				}

				&::before {
					display: none;
				}
			}
			.home-plus-chooser {
				display: none !important;
			}

			.one-click {
				color: #323232 !important;

				svg {
					width: 24px;
				}

				&:hover {
					color: #4383ff !important;

					svg {
						path {
							transition: all 0.35s ease;

							fill: #4383ff;
						}
					}
				}
			}
		}
		.show-all-button {
			margin: 0 auto;
			color: #1890ff;
			text-decoration: underline;
			&:hover {
				text-decoration: none;
			}
		}
		.product-details-footer {
			.product-cat-suppliers {
				align-items: flex-start;

				.supplier-div.disabled {
					&:hover {
						cursor: not-allowed;
					}
					position: relative;
					&::before {
						content: "";
						position: absolute;
						display: block;
						top: 0;
						z-index: 2;
						bottom: 0;
						left: 0;
						right: 0;
					}
				}
				.supplier-div.active {
					// width: fit-content;
					border-radius: 20px;
					border: 1px solid #1890ff;
				}
				.supplier {
					&:hover {
						box-shadow: none !important;
					}
					.seller-info-name,
					.suplier-product-price {
						display: flex;
						justify-content: center;
						flex-direction: column;
						padding: 10px;
						p {
							margin: 0;
							color: #000;
						}
					}

					.seller-info-img {
						width: 80px !important;
						height: 80px !important;
						background: #ededed;
						border: 1px solid #e5e5e5;
						padding: 0;
						border-radius: 20px;
						overflow: hidden;

						img {
							width: 100%;
						}
					}
					.seller-info-img.no-data-img {
						background: #000;
						z-index: 1;
					}
				}
				.product-brand {
					.brands {
						border-radius: 20px;
						border: 1px solid #e3eaf1;
					}
				}
			}
			.supplier,
			.product-brand {
				.seller-info-img {
					img {
						object-fit: scale-down;
					}
				}
			}

			.more-supplier {
				// padding: 0 10px 0 0;
				.product-cat-suppliers {
					.product-brand {
						margin-left: 40px;
					}
					.suppliers_list {
						overflow: hidden;
						max-height: 205px;
						transition: 0.5s !important;
					}
					.suppliers_list.active {
						max-height: 500px !important;
					}
					.supplier {
						transition: 0.3s !important;
						padding: 10px;

						.suplier-product-price {
							margin-left: 20px;
							p {
								font-weight: 600;
								font-size: 1.7rem;
								color: #1890ff;
							}
							span {
								line-height: 1.2;
								letter-spacing: -0.01em;
								color: #999999;
								font-size: 12px;
								margin: 0 7px 5px 0;
							}
						}
					}
					.brands {
						margin-left: 0 !important;
					}
				}
				.product-cat-suppliers.show-all-supliers {
					.suppliers_list {
						height: auto;
					}
				}
			}
		}

		.mt-5.relation-products {
			.owl-simple .owl-nav .owl-prev {
				left: 0;
			}

			.owl-simple .owl-nav .owl-next {
				right: 0;
			}
			.owl-carousel {
				.owl-stage-outer {
					.card-four,
					.card-three,
					.card-two,
					.card-one {
						width: 100%;
					}
				}
			}
			margin-top: 5px !important;

			.title {
				margin-bottom: 12px !important;
			}
		}
	}
}
@media (max-width: 991px) {
	.product-page-two {
		.skel-pro-single {
			margin: 0 0 20px 0;
		}

		.page-content {
			.product-details-top {
				margin-top: 0px;
				.col-lg-7.pl-5 {
					padding: 0 !important;
				}
				.product-details.sticky-content {
					margin: 0;

					.product-details {
						margin: 0;

						.blue-heart-button {
							font-size: 14px;
							color: #000;

							svg {
								width: 22px;
							}

							&:hover {
								color: #000;
							}
						}

						.product-title {
							margin: 0 0 12px 0;
							font-size: 16px;
						}

						.product-content {
							margin-bottom: 12px;

							h6 {
								margin-bottom: 0;
								font-size: 12px;
							}
						}

						.product-price {
							font-size: 16px;
							margin-bottom: 14px;
						}

						.details-filter-row.details-row-size {
							label,
							.properties-block,
							.activeproperties-block,
							.not-allowedproperties-block {
								margin-bottom: 6px !important;
								font-size: 12px;
								line-height: 16px;
								color: #000000;
							}

							label {
								a {
									font-size: 12px;
									line-height: 16px;
								}
							}

							.product-nav {
								margin-bottom: 10px;

								.properties-block,
								.activeproperties-block,
								.not-allowedproperties-block {
									border-radius: 7px !important;
									margin: 0 10px 10px 0 !important;
									overflow: hidden;
								}

								.color-properties {
									width: 49px;
									height: 74px;

									img {
										border-radius: 0;
									}
								}

								.size-properties {
									padding: 12px !important;
								}
							}

							.product-details-quantity {
								max-width: 110px;

								.form-control.input-changer-count.d-flex.p-3.justify-content-between {
									border-radius: 7px;
									padding: 8px 13px !important;

									span,
									input {
										font-size: 16px;
										line-height: 16px;
									}

									input {
										margin: 0 20px;
									}
								}
							}
						}

						.details-row-size {
							.product-nav {
								.properties-block {
									margin-bottom: 4px;
								}
							}
						}
					}
				}
			}

			// .price-and-articul {
			// 	justify-content: space-between;
			// 	flex-direction: row-reverse;
			// 	align-items: center;
			// }

			.product-details-action.choose-variations {
				top: 40px;
				right: 0;
				width: fit-content;

				span {
					img {
						width: 16px;
						margin-right: 5px;
					}

					border-radius: 7px;
					padding: 6px;
				}
			}

			.product-details-action {
				justify-content: space-between;
				flex-direction: row;
				align-items: center;
				margin: 14px 0 15px 0;

				span {
					font-size: 15px !important;
					line-height: 18px !important;
				}

				svg {
					margin-right: 6px;
					width: 22px;
				}

				.btn-product.btn-cart {
					margin-left: -1px !important;
					margin: 0;
					padding: 6px 0;
					height: 50px;
					min-width: 160px;
					border-radius: 15px !important;
				}

				.one-click {
					svg {
						width: 16px;
					}
				}
			}

			.product-details-footer {
				.product-cat-suppliers {
					width: 100%;
					.suppliers_list.active {
						height: 150px !important;
					}
					display: block;
					.supplier {
						min-width: 100%;
						.seller-info-name,
						.suplier-product-price {
							p {
								font-size: 14px !important;
							}
						}

						.seller-info-img {
							width: 50px !important;
							height: 50px !important;
							border-radius: 15px;
							overflow: hidden;
						}
					}

					.show-all-button {
						font-size: 12px !important;
					}
					.product-brand {
						margin: 0 !important;
					}
				}
			}
		}
		.product-gallery-container {
			padding: 0 !important;
			.product-gallery {
				position: relative;
				margin-bottom: 0;

				.big-img-with-view-button {
					margin: 0;
					img {
						width: 100%;
					}
				}

				.splide.splide--slide.splide--ltr.splide--draggable.is-active.is-initialized {
					button:disabled,
					button[disabled] {
						background-color: #ffffff00 !important;
					}

					.splide__arrow {
						margin: 0 !important;
						display: flex !important;
						animation-name: nextAnimation;
						animation-duration: 2s;
						animation-fill-mode: forwards;
						animation-iteration-count: infinite;
						right: 10px;
						background: none;
					}
					.splide__pagination {
						bottom: 20px;
					}

					.splide__arrow--prev {
						animation-name: prevAnimation;
						left: 10px;
					}
					.big-img-with-view-button {
						height: 590px;
						img {
							height: 100%;
							object-fit: cover;
						}
					}
				}
			}
		}

		.back-button-div {
			top: 12px;
			position: fixed;
			left: 0px;
			z-index: 2;

			.exit-product-page {
				// box-shadow: 4px 4px 8px 0 rgb(34 60 80 / 20%);
				display: flex !important;
				width: 50px;
				height: 50px;
				border-radius: 0px 10px 10px 0px;
				min-width: fit-content;
				background: #00000026;

				i {
					font-size: 25px;
					color: #fff;
				}
			}
		}
	}
}
