.card-two.card-six {
	border-radius: 5px;
	padding: 13px 11px;
	.product-header {
		.discount {
			position: absolute;
			display: none;
			justify-content: center;
			align-items: center;
			background-color: $third-color;
			color: $light-text;
			font-weight: bold;
			border-radius: 50%;
			width: 55px;
			height: 55px;
			top: 0px;
			left: 10px;
			font-size: 18px;
			line-height: 22px;
		}
		height: 196px;
		border-radius: 0;
		.header-buttons {
			position: absolute;
			opacity: 0;
			transform: translateX(-100%);
			transition: 0.3s;
			left: 0;
			z-index: 2;
		}

		.favourites-button,
		.compare-button {
			width: max-content;
			display: flex;
			justify-content: center;
			align-items: center;
			img {
				width: 25px;
				height: 25px;
				filter: brightness(0.5) invert(0.4);
			}
		}
		.favourites-button.have-choosen,
		.compare-button.have-compare {
			img {
				filter: unset;
			}
		}
		.compare-button {
			margin-bottom: 10px;
		}
	}
	&:hover {
		.product-header {
			.header-buttons {
				opacity: 1;
				transform: translateX(0);
			}
		}
	}
	.body-product {
		.price-title {
			height: 65px;
			margin-bottom: 2px;
		}
		.price-product {
			margin-top: 10px;
			line-height: 1;
			.price-new,
			.price-old {
				font-weight: bold;
				font-size: 16px;
				line-height: 18px;
				color: $dark-text;
			}
			.price-new.discount {
				color: $main-menu-link;
			}
			.price-old {
				font-size: 12px;
				margin-left: 8px;
			}
		}
		.title-product {
			line-height: 1;
			margin: 0;
			a {
				color: $dark-text;
				font-size: 12px;
			}
		}
		.sale-with-cart,
		.sale {
			display: flex;
			align-items: center;
			justify-content: space-between;
			p,
			.monthly {
				line-height: 1;
				font-weight: normal;
				color: $body-text;
			}
			.sale-price {
				display: flex;
				justify-content: center;
				align-items: center;
				flex-direction: column;
				color: $dark-text;
				font-weight: bold;
			}
			.monthly {
				margin-top: -5px;
			}
		}
		.sale {
			flex-direction: column;
			align-items: center;
		}
		.brand {
			span {
				margin: 0;
			}
		}
		.action-product {
			.product-details-quantity {
				// width: 100%;
				flex: unset;
				max-width: unset;
				background: #52c41a;
				border-radius: 5px;
				.input-changer-count {
					height: 100%;
					border: none;
					align-items: center;
					font-size: 1.4rem;
					input {
						background: transparent;
						font-weight: 700;
						color: $light-text;
					}
					span {
						color: $light-text;
						width: 10%;
						font-size: 20px;
						line-height: 1;
					}
				}
			}
			.cart-btn {
				background: $secondary-color;
				color: $light-text;
				border-radius: 5px;
				padding: 10px 15px;
				font-weight: bold;
				font-size: 12px;
				img {
					display: none !important;
				}
			}
		}
	}
}
@media (max-width: 991px) {
	.card-two.card-six {
		padding: 9px 7px !important;
		.product-header {
			.discount {
				width: 27px;
				height: 27px;
				font-size: 8.82224px;
				line-height: 11px;
			}
			.header-buttons {
				transform: unset;
				animation: unset;
				opacity: 1;
			}
		}
		.body-product {
			.brand {
				.name-supplier {
					font-size: 10px;
				}
			}
			.sale-with-cart {
				display: flex;
				flex-direction: column;
				.action-product {
					width: 100%;
					margin-bottom: 5px;
					.cart-btn {
						width: 100%;
					}
				}
				.sale {
					width: 100%;
					flex-direction: row;
					justify-content: space-around;
					p,
					.monthly {
						font-size: 10px;
					}
					.monthly {
						margin-top: unset;
					}
				}
			}
			.action-product {
				.input-changer-count {
					padding: 8px !important;
				}
			}
		}
	}
}
