html {
	scroll-behavior: smooth;
}
.trademarks-page {
	.splide__track {
		.splide__list {
			.splide__slide {
				padding: 0 !important;
			}
		}
	}
	.splide {
		.splide__arrows {
			display: block !important;
			.splide__arrow.splide__arrow--prev,
			.splide__arrow.splide__arrow--next {
				background-color: $body-bg !important;
				box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
				border-radius: 40px;
				width: 40px;
				height: 40px;
				opacity: 1;
				svg {
					fill: #899499;
					width: 14px;
					height: 14px;
				}
			}
			.splide__arrow.splide__arrow--next {
				right: 0;
			}

			.splide__arrow.splide__arrow.splide__arrow--prev {
				left: 0;
			}
		}
	}
	margin-bottom: 80px;
	& .col-12.menu {
		padding: 20px 0;
		background-color: #f2f2f2;
	}
	&.fixed {
		.menu {
			position: fixed;
			top: 0;
			left: 0;
			right: 0;
			z-index: 1;
			padding: 10px 0;
			.container {
				flex-wrap: nowrap !important;
				overflow: auto;
				&::-webkit-scrollbar {
					height: 3px;
				}
				&::-webkit-scrollbar-thumb {
					background-color: #aaa;
				}
				.col-lg-3.col-md-4.col-6 {
					display: block;
					min-width: max-content !important;
					max-width: max-content !important;
					.menu-item {
						width: max-content !important;
						margin-right: 0;
						padding: 10px;
						margin-bottom: 10px;
					}
				}
			}
		}
		.menus {
			margin-top: 45px;
			.row {
				align-items: flex-start;
			}
		}
	}
	.category-title {
		padding: 0;
		margin-top: 20px;
		color: $body-text;
	}
	.nav-link {
		font-size: 16px;
		line-height: 24px;
		padding: 0;
		color: $dark-text;
		&:hover {
			color: $body-text;
		}
	}
	a.d-block {
		margin-bottom: 5px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	.menu-item {
		width: 100%;
		padding: 16px;
		border: 1px solid #e5e8eb;
		border-radius: 8px;
		background-color: #fff;
		color: #637381;
		align-items: center;
		&:hover {
			background: $primary-color;
			color: #fff;
			img {
				filter: brightness(0) invert(1);
			}
			cursor: pointer;
		}
		img {
			width: 24px;
			margin-right: 8px;
		}
	}
	.menu-item.active {
		background: $primary-color;
		color: #fff;
		img {
			filter: brightness(0) invert(1);
		}
	}
}
.container {
	.row {
		width: 100%;
		margin: 0 auto;
	}
}
.trademark-page {
	.page-content {
		.skel-shop-products {
			.product-toolbox {
				padding: 0 5px;
				.toolbox {
					width: 100%;
				}
			}
			.products {
				.col-12 {
					padding: 0 10px;
				}
			}
			.category-header {
				display: flex;
				padding: 10px;
				justify-content: flex-start;
				img {
					margin-right: 10px;
				}
			}
			.product-toolbox {
				display: flex !important;
				.toolbox-layout {
					margin-top: 0;
				}
				select {
					color: $dark-text;
				}
			}
		}
		.skel-shop-sidebar {
			.sidebar.sidebar-shop {
				.filter-price {
					div {
						button {
							display: none;
						}
					}
				}
			}
		}
	}
}
.trademarks-header {
	margin-top: 40px;
	z-index: 1041;
	background-color: $body-bg;
	width: 100vw;

	.header-content {
		h3 {
			font-size: 24px;
		}
		a.anchor {
			font-size: 18px;
		}
	}
	a {
		color: $body-text;
		width: 24px;
		height: 24px;
		margin: 0 4px 0 0;
		transition: 0.3s;
		&:hover {
			transform: scale(1.5);
		}
	}
	a.active {
		transform: scale(1.5);
		color: $dark-text;
	}
}
.category-title.active {
	color: $dark-text;
}
.trademarks-header.fixed {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	margin-top: 0;
	animation-name: fixedHeader;
	animation-duration: 0.4s;
	background-color: #fff;
	box-shadow: 0 2px 8px rgb(0, 0, 0, 0.08);
	h3 {
		margin-top: 20px;
	}
}

@media (max-width: 991px) {
	.category-title-filter {
		flex-direction: column;
		justify-content: space-between;
		.product-toolbox {
			margin-top: 10px;
			.sidebar-toggler {
				display: flex !important;
				align-items: center;
				text-transform: capitalize;
				margin: 0;
				img {
					width: 16px;
					margin-right: 6px;
				}
			}
			.toolbox {
				display: flex;
				flex-direction: inherit;
				margin-left: auto;
				width: max-content !important;
				margin-bottom: 0;
				.toolbox-right {
					margin-top: 0;
					label,
					.sort-list {
						display: none !important;
					}
					select {
						border: none;
					}
				}
				.toolbox-layout {
					// margin-top: 10px;
					display: flex !important;
					.btn-layout {
						img {
							width: 16px;
						}
					}
				}
				// select {
				// 	margin-top: 10px;
				// }
			}
		}
	}
	.trademark-page {
		.category-header {
			display: none !important;
		}
		.page-content {
			.skel-shop-products {
				padding: 0;
			}
		}
		.products {
			margin-top: 20px;
			.row {
				.col-12 {
					border-bottom: none;
				}
			}
		}
	}
}
@media (max-width: 991px) {
	.trademarks-page.fixed {
		margin-top: 0;
	}
	.trademarks-page {
		margin-top: 20px;
		.row.trademarks-header {
			.col-12 {
				padding: 0;
				a {
					min-width: fit-content;
					display: inline-flex;
				}
			}
			width: 100%;
			margin: 0 auto;
		}
		.menus {
			.nav-link {
				font-size: 14px;
			}
		}
		.trademarks-header {
			.header-content {
				h3 {
					font-size: 16px;
				}
			}
		}
	}
}
