.header-seven {
	.search-box-wrapper {
		.header-search {
			form {
				.btn.search-button {
					img {
						filter: brightness(0) invert(1);
					}
					.only-desktop {
						display: none !important;
					}
					.only-mobile {
						display: flex !important;
					}
					.only-desktop.active-span {
						display: flex !important;
						color: $light-text;
					}
					.only-mobile.no-active-span {
						display: none !important;
					}
				}
			}
		}
	}
	.top-header {
		padding: 6px 0;
		.container {
			display: flex;
			justify-content: space-between;
			align-items: center;
			.header-top-left {
				width: 50%;
				.location {
					padding: 0 !important;
					span {
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
						margin-right: 10px;
					}
				}
			}
			.header-top-right {
				display: flex;
				align-items: center;

				// .change-mode {
				// 	display: block !important;
				// 	margin-right: 20px;
				// }
				////////////////////gshop//////////
				a {
					margin-right: 20px;
					color: $dark-text;
				}
				.change-lang {
					p.m-0 {
						display: none;
					}
					img {
						width: 22px;
						margin-right: 10px;
					}
					.lang-selector {
						z-index: 1000 !important;
						right: 0;
						margin: 0;
						width: max-content;
						background: $body-bg;
						.langBut {
							padding: 5px 8px;
							width: 100%;
							&.active {
								span {
									color: #fff;
								}
								background-color: $primary-color;
							}
							.text-white {
								color: $dark-text;
							}
						}
					}
				}
			}
		}
		background: $header-top;
	}
	.sticky-header.fixed {
		position: fixed !important;
		left: 0;
		right: 0;
		top: 0;
		z-index: 1040;
		animation-name: fixedHeader;
		animation-duration: 0.4s;
		background-color: #fff;
		box-shadow: 0 3px 6px rgba(51, 51, 51, 0.05);
		padding: 22px 0 !important;
		///////////////others
		// padding: 0 !important;
		////////////munique,tstore
	}

	.header-middle.sticky-header {
		background: $body-bg;
		padding: 30px 0 22px 0;
		border-bottom: none;
		position: relative;
		margin-bottom: 10px;
		.right-header {
			display: flex;
			align-items: center;
			justify-content: flex-end;
			a {
				margin-left: 30px;
				&:last-child {
					margin-left: 0;
				}
				// img {
				// 	filter: brightness(0) invert(0);
				// }
				////////////gshop//////
			}
			.dropdown.cart-dropdown,
			.choosen-products-icon,
			.compare-products-icon {
				.dropdown-toggle,
				.position-relative {
					.cart-count,
					span {
						display: flex;
						align-items: center;
						justify-content: center;
						min-width: 2rem;
						height: 2rem;
						border-radius: 50%;
						font-weight: 400;
						font-size: 1rem;
						line-height: 1;
						margin-left: 0.2rem;
						margin-top: 0.1rem;
						color: #fff;
						background: $primary-color;
						z-index: 1;
						position: absolute;
						top: -10px;
						right: -10px;
					}
				}
				padding: 0;
				.dropdown-menu.dropdown-menu-right {
					width: 500px;
					right: 0;
					&::before {
						display: none;
					}

					.dropdown-cart-products {
						.product {
							.product-detail-left {
								.product-image-container {
									margin: 0;
									width: 100%;
									height: 60px;
									.product-image {
										height: 100%;
										width: 100%;
										img {
											object-fit: contain;
											filter: unset;
										}
									}
								}
							}
							.product-detail-right {
								.cart-product-info {
									white-space: nowrap;
								}
							}
						}
					}
				}
				.dropdown-cart-total {
					.cart-total,
					.cart-total-price {
						font-size: 18px;
					}
				}
			}

			h6 {
				display: none;
			}
		}
		.left-header {
			display: flex;
			justify-content: space-between;
			align-items: center;

			.logo {
				.shine-shimmer {
					height: 100%;
				}
				div {
					// width: 200px !important;
					//////////////////Volidam,Allmax/////////////
					width: 150px !important;
					//////////////////telefonchi,posuz/////////////
					// width: 300px !important;
					//////////////munique///////
					// width: 60px !important;
					//////////////////////GBOX/////////////////
					// width: 120px !important;
					////////////////////////agera,GSHOP/////////////////
					// width: 280px !important;
					////////////////////////tstore/////////////////
				}
			}
		}
		.cart-dropdown {
			.dropdown-menu-right {
				padding: 12px 0;
				.dropdown-cart-products {
					margin: 0;
					.product {
						padding: 14px 24px 8px 24px;
						align-items: unset;
						justify-content: space-between;
						.product-detail-left {
							flex-direction: row;
							display: flex !important;
							align-items: center;
							// justify-content: flex-start !important;
							.product-image-container {
								margin: 0 0 6px 0 !important;
								img {
									height: 100%;
									min-width: 100%;
									object-fit: auto;
								}
							}
							.product-title {
								font-size: 16px;
								margin-left: 10px;
							}
						}
						.product-detail-right {
							text-align: right;
							.cart-product-qty {
								display: block;
							}
							span {
								font-size: 16px;
							}
							.btn-remove {
								margin: 4px 0 0 auto;
								position: unset !important;
								font-size: 14px;
								color: #f14f66;
							}
						}
					}
				}
				.dropdown-cart-action,
				.dropdown-cart-total {
					font-weight: 600;
					padding: 0 24px;
					a {
						border-radius: 10px;
						margin: 0;
					}
				}
				.dropdown-cart-total {
					margin-top: 12px;
				}
			}
		}
		.center-header,
		.left-header,
		.header-menu-list {
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin: 0;
			overflow: auto;
			padding-right: 0;
			&::-webkit-scrollbar {
				height: 2px;
			}
			&::-webkit-scrollbar-thumb {
				background-color: #ccc;
			}
			.first-menu-child {
				font-weight: bold;
				font-size: 17px;
				line-height: 21px;
				margin-left: 15px;
				a {
					// color: $dark-text;
					/////////others/////////////////
					color: $light-text;
					/////////////////////////////gbox,agera,posuz,munique,telefonchi,tstore/////////
					text-align: left;
					display: block;
					font-weight: bold;
					width: 100%;
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
				}
				&:first-child {
					margin-left: 0;
				}
			}
			.d-flex.search-box-wrapper {
				transition: 0.4s;
				border: 1px solid #d5d5d5;
				/////////others
				// border: 1px solid #fff;
				////////volidam
				border-radius: $border-item;
				overflow: hidden;
				.search-button {
					opacity: 1;
					background-color: $primary-color !important;
				}
				.header-search.header-search-extended {
					max-width: unset;
					display: flex !important;
					background: transparent;
					form {
						width: 100%;
						background: transparent;
						input[type="search"]::-webkit-search-cancel-button {
							-webkit-appearance: none;
							appearance: none;
							height: 20px;
							width: 20px;
							opacity: 0.3;
							transition: 0.3s;
							cursor: pointer;
							background-image: url("data:image/svg+xml;base64,");
							background-size: 15px 15px;
							background-position: center center;
							background-repeat: no-repeat;
							&:hover {
								opacity: 0.8;
							}
						}
					}
					.header-search-wrapper.search-wrapper-wide {
						margin: 0;
						border: none;
						align-items: center;
						width: 100% !important;
						display: flex !important;
						position: relative;
						// border: 1px solid $light-text;
						right: unset;
						top: unset;

						// background: transparent;
						////////////////gshop///

						input {
							background: transparent;
							font-size: 17px;
							line-height: 21px;
							padding: 5px 5px 5px 15px !important;
							color: $dark-text;
						}
						.search-button {
							width: 103px !important;
							margin: 0;
							border-radius: $border-item;
							background: $primary-color;

							h6 {
								line-height: 24px;
								margin: 0;
								font-size: 14px;
								color: #ffffff;
							}
						}
						.spinner-border {
							border-color: $light-text;
							border-right-color: transparent;
						}
					}
				}
				.search-result {
					box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.15);
					border-radius: $border-item;
					top: 30px;
					&::-webkit-scrollbar {
						height: 1px;
						width: 4px;
					}
					&::-webkit-scrollbar-thumb {
						background-color: #ccc;
						border-radius: 5px;
					}
					.menus,
					.products {
						.menu-item,
						.search-result-item {
							display: flex;
							align-items: center;
							padding: 20px;
							border-bottom: 1px solid #f3f3f3;
							.img,
							a.d-flex.align-items-center {
								margin-right: 12px;
								min-width: 50px;
								min-height: 50px;
								max-width: 50px;
								max-height: 50px;
								background: #f7f7f7;
								border: 1px solid #f3f3f3;
								border-radius: 5px;
								img {
									object-fit: contain;
									background: white;
								}
							}
							.menu-result-item {
								a {
									font-weight: 400;
									font-size: 14px;
									line-height: 17px;
									letter-spacing: -0.02em;
									color: $dark-text;
								}
							}
							.menu-breadcrumb {
								.breadcrumb-item {
									font-size: 12px;
									line-height: 15px;
									letter-spacing: -0.02em;
								}
							}
							a {
								.new-price {
									margin-left: 10px;
								}
								.new-price,
								.old-price,
								.price {
									font-size: 12px;
								}
							}
						}
					}
				}
			}
			.search-box-wrapper.long-input {
				position: absolute;
				width: 100%;
				left: 0;
				right: 0;
			}
			.search,
			.search-box-wrapper {
				transition: width 1s ease-in-out;
				width: 100%;
			}
			.search {
				width: 50%;
			}
			.search.long-input {
				width: 100%;
			}
			button:disabled {
				background-color: transparent !important;
			}
		}
		// .header-menu-list {
		// 	margin-left: 10px;
		// 	display: none;
		// }
		//////////////tstore

		.center-header,
		.left-header {
			overflow: unset;
		}
	}
}
@media (max-width: 991px) {
	.header-seven {
		.center-header {
			.header-menu-list {
				margin-left: 0 !important;
				display: flex !important;
			}
		}
		.header-bottom-two {
			display: none;
		}
		.top-header {
			.header-top-right {
				.info {
					display: none;
				}
			}
		}
		.header-middle.sticky-header {
			.container {
				.right-header.col-lg-2 {
					display: none;
				}
				.left-header {
					margin-bottom: 20px;
					//////////////////gshop

					.logo {
						margin-left: 0;
						& > div {
							// width: 60px !important;
							///////////////others///////////////
							width: 100px !important;
							/////////////////////////tstore,telefonchi//////////
							// width: 140px !important;
							/////////////////////////volidam,allmax//////////
							// width: 180px !important;
							/////////////////////////munique//////////
						}
						margin-right: 20px;
					}
				}

				.left-header.long-input,
				.left-header.long-input input:focus {
					.logo {
						width: 0;
						overflow: hidden;
					}
					.d-flex.search-box-wrapper {
						width: 100%;
						opacity: 1;
					}
				}

				.center-header,
				.left-header {
					.d-flex.search-box-wrapper {
						// border: 1px solid rgba(0, 0, 0, 0.35);
						overflow: hidden;
						border-radius: 5px;

						.header-search-wrapper.search-wrapper-wide {
							input {
								font-size: 14px !important;
							}
							input[type="search"]::-webkit-search-cancel-button {
								height: 15px !important;
								width: 15px !important;
								background-size: 10px 10px !important;
							}
							.search-button {
								border-radius: 5px !important;
								max-width: max-content;
								padding: 0 5px;

								img {
									width: 20px;
								}
								h6 {
									line-height: 16px;
									font-size: 12px;
									span {
										font-size: 12px;
										width: fit-content;
									}
								}
							}
						}
					}
					padding-left: 0;
					.header-menu-list {
						overflow-y: auto;
						width: 100%;
						justify-content: flex-end;
						// padding-bottom: 15px;
						.first-menu-child {
							a {
								display: block;
								white-space: nowrap;
								font-size: 14px;
								line-height: 17px;
							}
						}
					}
				}
			}
		}
		.search-box-wrapper {
			.search-result {
				.menus,
				.products {
					.menu-item,
					.search-result-item {
						padding: 10px !important;
					}
				}
			}
		}
	}
}
@media (max-width: 991px) {
	.center-header {
		margin-top: 10px !important;
		.header-menu-list {
			justify-content: flex-start !important;
			margin-top: 15px;
		}
	}
}
@media (max-width: 575px) {
	.header-right {
		flex-direction: unset;
	}
}
