.checkout-three {
	.sidebar {
		.summary.summary-cart {
			padding: 0 20px;
			.payment_methods {
				.payment_wrapper {
					background: $body-bg;
					cursor: pointer;
					transition: 0.3s;
					border: 2px solid transparent;
					transform: scale(0.95);
					p {
						padding: 0 !important;
					}
				}
				.payment_wrapper.selected {
					border: 2px solid $primary-color !important;
					transform: scale(1);
				}
			}
		}
	}
}
