/* Login */

.form-box {
	max-width: 575px;
	margin: 0 auto;
	background-color: #fff;
	padding: 2.2rem 2rem 4.4rem;
	box-shadow: 0 3px 16px rgba(51, 51, 51, 0.1);

	.btn {
		min-width: 115px;
	}
}

.form-tab {
	.nav.nav-pills {
		color: $dark-text;
		border-bottom: 0.1rem solid #ebebeb;

		.nav-item {
			margin-bottom: -0.1rem;

			+ .nav-item {
				margin-left: 0;
			}

			&.show .nav-link {
				color: inherit;
			}
		}

		.nav-link {
			text-transform: capitalize;
			font-weight: 400;
			font-size: 2rem;
			letter-spacing: -0.025em;
			color: inherit;
			border-bottom-width: 0.2rem;
			padding: 0.9rem 1rem;

			&.active {
				color: inherit;
			}
		}
	}

	.nav-fill .nav-item {
		flex: 1 1 0;
	}

	.tab-content .tab-pane {
		padding: 2rem 0 0;
	}

	.form-group {
		margin-bottom: 1.3rem;
	}

	.form-footer {
		padding-top: 0.6rem;
		padding-bottom: 3rem;
		border-bottom: 0.1rem solid #ebebeb;
		margin-bottom: 2.3rem;

		.btn {
			margin-left: auto;
			margin-top: 1rem;
			order: 2;
			width: 100%;
		}

		a {
			color: inherit;

			&:hover,
			&:focus {
				color: $primary-color;
			}
		}

		a:not(.forgot-link) {
			text-decoration: underline;
		}

		.custom-control {
			margin: 0;
		}

		.forgot-link {
			order: -1;
			width: 100%;
			margin-bottom: 0.6rem;
		}
	}
}

.form-choice {
	color: $dark-text;
	font-weight: 400;
	font-size: 1.6rem;
	line-height: 1.5;
	letter-spacing: -0.025em;

	p {
		margin-bottom: 2.7rem;
	}
}

.btn.btn-login {
	color: $dark-text;
	font-weight: 300;
	font-size: 1.4rem;
	line-height: 1.5;
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	padding: 0.85rem 1rem;
	border: 0.1rem solid #ebebeb;
	min-width: 0;

	i {
		text-align: left;
		display: inline-block;
		margin-right: 0 !important;
		margin-left: 0 !important;
		min-width: 2rem;
	}

	&.btn-g {
		i {
			color: #cc3333;
			min-width: 2.6rem;
		}
	}

	&.btn-f {
		i {
			color: #3366cc;
		}
	}

	&:hover,
	&:focus {
		background-color: #f5f6f9;
	}
}

.form-choice .col-sm-6:not(:last-child) {
	margin-bottom: 0.6rem;
}

.login-two {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #fff;
	overflow: auto;
	z-index: 999;
	.image-side {
		padding: 50px;
		display: flex;
		flex-direction: column;
		.login-image {
			flex-grow: 1;
			margin-top: 150px;
			width: 100%;
			img {
				display: block;
			}
		}
		.description {
			a,
			p {
				display: block;
				color: #555;
			}
		}
	}
	.form-side {
		padding: 30px 50px 50px;
		background-color: #f4f6f8;
		.change-lang,
		.lang-selector {
			width: 120px;
			margin-left: auto;
			margin-bottom: 30px;
			padding: 8px 20px;
			border-radius: 6px;
			font-size: 14px;
			background-color: #fff;
			border: none;
			p {
				color: black;
			}
			img,
			i {
				display: none;
			}
			div {
				justify-content: space-between;
			}
			.lang-selector {
				margin: 15px 0 0;
				box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.08);
				right: 0;
				.langBut {
					button {
						color: black !important;
						padding: 0 !important;
					}
					&.active {
						background-color: #fff;
						button {
							color: $primary-color !important;
						}
					}
				}
			}
		}
		.form {
			width: 65%;
			margin: 0 auto;
			.heading {
				margin-bottom: 40px;
				font-weight: bold;
				text-align: center;
				white-space: nowrap;
			}
			.steps {
				width: 100%;
				display: flex;
				align-items: center;
				justify-content: space-between;
				margin-bottom: 25px;
				position: relative;
				.step {
					display: flex;
					flex-direction: column;
					align-items: center;
					justify-content: center;
					p {
						margin: 0;
						color: #000;
						opacity: 50%;
					}
					span {
						z-index: 2;
						width: 30px;
						height: 30px;
						border-radius: 50%;
						background-color: #e4e7fb;
						display: flex;
						justify-content: center;
						align-items: center;
					}
					&.active {
						p {
							opacity: 1;
						}
						span {
							color: #fff;
							background-color: $primary-color;
						}
					}
				}
				.between-step {
					width: 100%;
					height: 1px;
					margin: 0 -42px;
					margin-bottom: 24px;
					background-color: #ccc;
					&.active {
						background-color: $primary-color !important;
						box-shadow: 0 0 1px $primary-color;
					}
				}
			}
			.form-box {
				padding: 0;
				margin: 0 !important;
				min-width: 100%;
				background-color: transparent;
				.nav {
					border-radius: 8px;
					padding: 5px;
					box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.08);
					.nav-item.show {
						background-color: $primary-color;
						border-radius: 8px;
						color: #fff;
					}
					.nav-item span {
						padding: 5px;
						font-size: 18px;
						font-weight: bold;
						letter-spacing: 1px;

						border: none;
					}
				}
				.tab-content {
					label {
						margin-bottom: 0;
						span {
							color: red;
						}
					}
					.form-control,
					.btn {
						border-radius: 8px !important;
					}
					.form-control {
						border: 1px solid #c7cedf;
						&.warning {
							border-color: red;
						}
					}
					.refresh-button {
						margin-left: 10px;
						width: 40px;
						height: 40px;
						border-radius: 50%;
						background-color: #fff !important;
						color: $primary-color !important;
						position: relative;
						overflow: hidden;
						border: 1px solid #ccc !important;
						span.time {
							position: absolute;
							top: 50%;
							left: 50%;
							color: black !important;
							transform: translate(-50%, -50%);
							z-index: 99999 !important;
						}
						&.active {
							&::before,
							&::after {
								content: "";
								position: absolute;
								width: 50%;
								height: 100%;
								top: 0%;
							}

							&::before {
								left: 50%;
								transform-origin: left center;
								animation: clock1 30s linear;
								animation-fill-mode: forwards;
								background-color: #fff;
								z-index: 999;
							}
							&::after {
								left: 0%;
								transform-origin: right center;
								animation: clock2 30s linear;
								animation-fill-mode: forwards;
								animation-delay: 30s;
								background-color: $primary-color;
							}
						}
						@keyframes clock1 {
							0% {
								transform: rotate(0deg);
								background-color: $primary-color;
							}
							99.99% {
								transform: rotate(180deg);
								background-color: $primary-color;
							}
							100% {
								transform: rotate(0deg);
								background-color: #fff !important;
								z-index: 1234 !important;
							}
						}

						@keyframes clock2 {
							0% {
								transform: rotate(0deg);
							}
							99.99% {
								transform: rotate(180deg);
								background-color: $primary-color;
							}
							100% {
								transform: rotate(0deg);
								background-color: #fff !important;
							}
						}
					}
					.verify-form {
						margin: 0;
						.verify-inputs-wrapper {
							width: min-content;
							margin-top: 25px;
							display: flex;
							align-items: center;
							justify-content: space-between;
							input.form-control {
								width: 50px;
								height: 50px;
								font-size: 28px;
								text-align: center;
								padding: 0;
								margin-right: 15px;
								color: black !important;
								border: 1px solid #aaa;
								&::placeholder {
									font-size: 38px;
									color: black;
									letter-spacing: -5px;
									margin-left: 3px;
								}
								&.active {
									border-color: $primary-color;
								}
							}
						}
					}
					.completed {
						padding: 20px;
						margin-bottom: 25px;
						background-color: #fff;
						border-radius: 8px;
					}
				}
			}
		}
	}
	.auth {
		.heading {
			font-weight: 600;
			text-align: center;
			margin: 68px auto 80px auto;
		}
		form {
			margin: 0 auto;
			width: 440px;
			label {
				font-style: normal;
				font-weight: 300;
				font-size: 16px;
				line-height: 24px;
				margin-bottom: 8px;
				color: #000000;
			}
			input {
				background: #ffffff;
				border: 1px solid #c7cedf;
				box-sizing: border-box;
				border-radius: 8px;
				color: #000;
			}
			.form-footer {
				display: block;
				.btn.btn-outline-primary-2 {
					display: block;
					width: 100%;
					background: $primary-color;
					color: #fff;
					&:hover {
						background: #fff;
						color: $primary-color;
					}
					padding: 10px 0;
					margin-bottom: 20px;
				}
				.custom-control {
					input,
					label {
						&:hover {
							cursor: pointer;
						}
					}
				}
				.forgot-regis-btn {
					display: flex;
					justify-content: space-between;
					align-items: center;
					font-size: 16px;
					line-height: 24px;
					.forgot-link {
						color: #000;
						&:hover {
							color: $primary-color;
						}
					}
					button {
						display: block !important;
					}
				}
				.reCaptcha {
					width: max-content !important;
					height: max-content !important;
					margin-bottom: 24px;
					border-radius: 8px;
				}
			}
			.btn.btn-outline-primary-2 {
				margin-top: 20px;
			}
		}
	}
}

@include mq("sm") {
	.form-choice .col-sm-6:not(:last-child) {
		margin-bottom: 0;
	}

	.form-tab {
		.form-footer {
			.btn {
				width: auto;
				margin-top: 0;
			}
		}
	}
}

@include mq("md") {
	.form-box {
		padding: 3.7rem 6rem 6.4rem;
	}

	// .form-tab .nav.nav-pills .nav-link {
	// 	font-size: 2.4rem;
	// }

	.form-tab {
		.form-footer {
			.btn {
				margin-left: 0;
				margin-right: 1.6rem;
				order: -1;
			}

			.forgot-link {
				order: 2;
				width: auto;
				margin-bottom: 0;
				margin-left: auto;
			}
		}
	}
}

@media (max-width: 1200px) {
	.login-two {
		.form-side {
			.form {
				width: 85%;
				.heading {
					font-size: 30px;
				}
			}
		}
	}
}

@media (max-width: 991px) {
	.login-two {
		margin-bottom: 70px;
		.image-side {
			display: none;
		}
		.form-side {
			background-color: #fff;
			padding: 30px !important;
			.change-lang {
				display: none;
			}
			.form {
				width: 98%;
				.heading {
					font-size: 18px;
				}
			}
			.steps {
				.step {
					span,
					p {
						font-size: 12px !important;
					}
				}
			}

			.verify-form {
				.verify-inputs-wrapper {
					width: 100% !important;
					justify-content: flex-start !important;
					input.form-control {
						margin-right: 10px !important;
					}
				}
			}
			.completed {
				background-color: #f4f6f8;
				border: 1px solid #aaa;
			}
		}
		.continue {
			margin-top: auto;
		}

		.auth {
			.heading {
				font-size: 18px !important;
				margin: 0 0 30px;
			}
			form {
				width: unset;
			}
		}
	}
}
