.quick-view {
	top: 0 !important;
	bottom: 20% !important;
	height: auto !important;
	transform: unset !important;
	button.mfp-close {
		top: 10px !important;
		right: 40px !important;
		font-size: 40px;
		color: #338bff;
	}
	.quickView-content {
		padding: unset;
		margin: 60px auto 0;
		padding: 20px;
		height: auto;
		border-radius: 15px;
		.image {
			margin-bottom: 10px;
			position: relative;
			.change {
				display: block !important;
				span {
					position: absolute;
					top: 50%;
					transform: translateY(-50%);
					font-size: 30px;
					&.left {
						left: 0;
					}
					&.right {
						right: 0;
					}
				}
			}
		}
		.side-by-side {
			display: none;
		}
		.scrolling-row {
			overflow-x: scroll;
			height: 85vh;
			.product-gallery {
				margin: 0;
				.row.m-0 {
					padding: 0;
				}
				.product-main-image {
					margin: 0;
					.media-sticky.mt-2:first-child {
						margin-top: 0 !important;
					}
				}
			}
		}
		.row:first-child::-webkit-scrollbar {
			display: none;
		}
		.product-details-action {
			align-items: center;
			.btn-product.btn-cart {
				padding: 15px 12px;
				border-radius: 10px !important;
			}
		}
		.quickview-desc {
			width: unset !important;
			.entry-summary {
				display: none;
			}
			.product-details.sticky-content {
				top: 0 !important;
				.product-details {
					height: 100%;
					margin: 0;
					&::-webkit-scrollbar {
						display: none;
					}
					.product-title {
						color: #000000 !important;
						font-weight: 400;
						letter-spacing: unset;
						margin: 0 0 18px 0;
					}
					.product-content {
						margin-bottom: 18px;
						h6 {
							font-size: 14px;
							color: #000000;
						}
					}

					.product-price {
						font-weight: bold;
						color: #2661ff;
						margin-bottom: 18px;
					}
					.details-filter-row.details-row-size {
						flex-direction: column;
						align-items: start;
						display: unset;
						label,
						.properties-block,
						.activeproperties-block,
						.not-allowedproperties-block {
							margin-bottom: 12px !important;
							font-size: 16px;
							line-height: 20px;
							color: #000000;
						}
						.tooltip-property {
							display: none;
							min-width: 30px;
							width: auto !important;
							position: absolute;
							bottom: -25px;
							left: 80%;
							padding: 0 5px;
							border-radius: 5px;
							color: #fff;
							background-color: #333;
							z-index: 999;
							span {
								&::before {
									position: absolute;
									content: "";
									left: 0;
									top: -5px;
									border: 10px solid #333;
									border-right-color: transparent;
									border-top-color: transparent;
									border-bottom-color: transparent;
								}
							}
						}
						label {
							display: flex;
							width: 100%;
							justify-content: space-between;
							a {
								text-decoration: underline;
								font-size: 16px;
								line-height: 20px;
								color: #1890ff;
							}
						}
						.product-nav {
							width: 100%;
							flex-wrap: wrap;
							overflow: visible !important;
							margin-bottom: 10px;
							.activeproperties-block {
								border: 1px solid #1d75ff !important;
							}
							.properties-block,
							.activeproperties-block,
							.not-allowedproperties-block {
								border-radius: 10px !important;
								margin: 0 20px 20px 0 !important;
							}
							.not-allowedproperties-block {
								border: 1px solid #b7b7b7 !important;
							}
							.color-properties {
								width: 68px;
								height: 94px;
								padding: 0;
								img {
									border-radius: 7px;
									height: 100%;
								}
							}
							.size-properties {
								padding: 16px !important;
							}
						}
						.product-details-quantity {
							margin-right: 85px;
							max-width: 218px;
							.form-control.input-changer-count.d-flex.p-3.justify-content-between {
								border-radius: 10px;
								padding: 16px 26px !important;

								height: unset;
								span,
								input {
									background: transparent;
									font-size: 20px;
									line-height: 24px;
									color: #000000;
								}
								input {
									margin: 0 40px;
								}
							}
						}
					}

					.details-row-size {
						.product-nav {
							flex-wrap: wrap;
							.properties-block {
								margin-bottom: 4px;
							}
						}
					}
					.blue-heart-button {
						font-size: 16px;
						color: #000;
					}
				}
			}
			.accordion,
			.react-tabs {
				display: none;
			}
		}
		.product-details-action.choose-variations {
			position: absolute;
			top: 115px;
			right: 0;
			span {
				img {
					margin-right: 10px;
				}
				align-items: center;
				display: flex;
				background: #fff172;
				border: 1px solid #ded052;
				box-sizing: border-box;
				border-radius: 10px;
				padding: 12px;
				color: #000000;
			}
		}

		.product-details-action {
			margin: 24px 0 40px 0;
			span {
				box-shadow: none;
				display: flex !important;
				align-items: center;
				font-size: 16px !important;
				line-height: 20px !important;
			}
			svg {
				margin-right: 10px;
			}
			.btn-product.btn-cart {
				padding: 16px 0;
				min-width: 252px !important;
				color: #4383ff;
				border: 1px solid #4383ff;
				border-radius: 20px !important;

				span {
					text-transform: unset;
				}
				&::before {
					display: none;
				}
			}
			.home-plus-chooser {
				display: none !important;
			}
			.one-click {
				color: #323232 !important;
				svg {
					width: 24px;
				}
				&:hover {
					color: #4383ff !important;
					svg {
						path {
							transition: all 0.35s ease;

							fill: #4383ff;
						}
					}
				}
			}
		}
		.product-details-footer {
			.more-supplier {
				padding: 0;
			}
			.supliers-row {
				.supplier.brands {
					margin-left: 50px;
				}
				.product-cat {
					.product-cat-suppliers {
						align-items: flex-start;
						.suppliers_ul {
							.suppliers_list {
								a {
									display: none;
									&:first-child {
										display: block;
									}
									.suplier-product-price {
										display: none;
									}
								}
								.supplier {
									border: 1px solid transparent;
									border-radius: 10px;
									padding: 5px 15px;
									&:hover {
										border: 1px solid #1d75ff;
									}
								}
							}
						}
						.btn.show-all-button {
							display: none;
						}
					}
				}
			}
		}
	}
}

@media (max-width: 786px) {
	.quick-view {
		button.mfp-close {
			top: 0 !important;
			right: 20px !important;
			padding: 0 !important;
			width: max-content !important;
			height: max-content !important;
		}
		.scrolling-row {
			height: 80vh !important;
		}
		.quickView-content {
			padding: 10px;
			.quickview-desc {
				.product-details.sticky-content {
					.product-title {
						margin-top: 5px !important;
					}
					.product-details {
						.details-filter-row.details-row-size {
							.product-nav {
								div.properties-block {
									margin: 5px !important;
								}
							}
							.product-details-quantity {
								div.form-control.input-changer-count.d-flex.p-3.justify-content-between {
									padding: 10px !important;
								}
							}
						}
						.product-details-action {
							flex-direction: column !important;
							div {
								width: 100%;
								.btn-product.btn-cart {
									padding: 10px !important;
									min-width: 100% !important;
									margin-left: 0 !important;
								}
							}
						}
					}
				}
			}
		}
	}
}
