.category-page-5 {
	.skeleton-body.skel-shop-products {
		padding: 0;
	}
	.category-header {
		.back-block {
			display: none;
		}
		.searcher-row {
			.search-box-wrapper {
				.header-search {
					form {
						.btn.btn-search {
							.only-desktop.active-span {
								display: flex !important;
							}
							.only-mobile.no-active-span {
								display: none !important;
							}
						}
					}
				}
			}
		}
	}
	.toolbox {
		.toolbox-center {
			.toolbox-info {
				display: none;
			}
		}
		.toolbox-layout {
			display: flex;
			align-items: center;

			.btn-layout,
			.filter.d-none {
				padding: 10px;
				border-radius: 4px;
				margin: 0 2px !important;
				background-color: #eee;
				display: flex;
				align-items: center;
				justify-content: center;
				width: 40px;
				height: 40px;

				&.active {
					background-color: $primary-color;
					img {
						filter: brightness(0) invert(1);
					}
				}
			}
		}
		.toolbox-right {
			margin-left: 0 !important;
			margin-right: auto !important;
			label {
				font-size: 18px;
				color: black;
			}
			.select-custom {
				display: none;
				select.form-control {
					margin-bottom: 0;
				}
			}
			ul {
				li {
					padding: 8px 15px;
					margin: 0 5px;
					border-radius: 4px;
					background-color: #eee;
					cursor: pointer;
					span {
						margin-left: 5px;
						&::before {
							color: $primary-color;
							font-weight: bold;
						}
					}
					&.active {
						background-color: $primary-color;
						color: #fff;
					}
				}
			}
		}
	}
	.breadcrumb-nav {
		border-bottom: none !important;
	}
	.category-title {
		font-size: 32px !important;
	}
	.filter-side {
		border: 1px solid #e5e5eb;
		border-radius: 8px;
		padding: 0 !important;
		.breadcrumb-nav-two {
			padding: 10px;
			margin-bottom: 20px;
			border-bottom: 1px solid #e5e5eb !important;
			.title {
				letter-spacing: 0.5px;
				font-size: 16px;
				font-weight: bolder;
				color: black;
			}
			.breadcrumb {
				box-sizing: border-box !important;
				flex-direction: column;
				.breadcrumb-item {
					width: 100%;
					box-sizing: border-box !important;
					margin: 5px 0;
					display: flex;
					align-items: center;
					&::before {
						display: none;
					}
					a {
						display: block !important;
						width: 100%;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
					}
					&.home {
						.circle {
							&::before,
							&::after {
								display: none !important;
							}
						}
					}
					span.circle {
						width: 8px;
						height: 8px;
						border-radius: 50%;
						margin-right: 5px;
						border: 0.5px solid #e5e5eb;
						display: flex;
						align-items: center;
						position: relative;
						&::before {
							position: absolute;
							width: 12px !important;
							height: none !important;
							border: 0.01px solid #e5e5eb;
							content: "";
							left: -13px;
						}
						&::after {
							position: absolute;
							width: none !important;
							height: 28px !important;
							border: 0.01px solid #e5e5eb;
							content: "";
							left: -13px;
							bottom: 3px;
						}
					}
					&:last-child {
						.circle {
							border-color: $primary-color;
							background-color: rgba(
								$color: $primary-color,
								$alpha: 0.1
							);
						}
					}
				}
			}
		}
		.sidebar.sidebar-shop {
			.filter-price {
				padding: 10px !important;
				h4 {
					font-size: 16px;
					line-height: 24px;
					color: #000000;
				}
				button.btn {
					min-width: max-content !important;
					background-color: #f4f6f8;
					padding-left: 10px !important;
					padding-right: 10px !important;
					border: 1px solid #eee !important;
				}
			}
			.widget.widget-collapsible {
				padding: 0 10px;
				.widget-title {
					color: #000000;
					font-size: 16px;
				}
				.widget-side {
					.px-5.py-1 {
						padding-left: 0 !important;
						padding-right: 0 !important;
					}
					p a {
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
						font-weight: normal;
						&:hover {
							color: $primary-color;
						}
					}
				}
				.widget-body {
					.filter-colors {
						padding: 0;
					}
					.custom-checkbox
						.custom-control-input:checked
						~ .custom-control-label::before {
						background-color: $primary-color;
						border: none;
					}
				}
			}
		}
	}
	.col-lg-9.skeleton-body.skel-shop-products.loaded {
		.products.mb-3 {
			.infinite-scroll-component__outerdiv {
				.infinite-scroll-component.row {
					.col-6.col-md-4.col-lg-4.col-xl-3.d-flex {
						margin-bottom: 16px;
						.product-wrapper.w-100 {
							border: 1px solid #e5e8eb;
							overflow: hidden;
							border-radius: 8px;
							.product.product-5 {
								margin-bottom: 0;
								.product-body {
									.product-title {
										a {
											overflow: hidden;
											display: inline-block;
											text-overflow: ellipsis;
											white-space: nowrap;
											font-size: 16px;
											line-height: 24px;
										}
									}
									.product-price {
										font-size: 18px;
										line-height: 24px;
										color: #000;
									}
									.min-order {
										font-size: 14px;
										line-height: 24px;
										color: #919eab;
									}
								}
							}
						}
					}
				}
			}
		}
	}
}

@media (max-width: 768px) {
	.category-page-5 {
		.breadcrumb-nav {
			display: none !important;
		}
		h2.category-title {
			margin-top: 30px;
			text-align: left !important;
			font-weight: bold;
			font-size: 18px !important;
		}
		.row.align-items-center.w-100 {
			width: auto !important;
			.photo-shimmer {
				height: 227px !important;
			}
		}
		.filter-side {
			border: none !important;
			.sidebar-filter {
				// padding-top: 50px;
				max-width: 90% !important;
				.sidebar-filter-wrapper {
					.filter-price {
						padding: 0 !important;
						.btn.ml-4 {
							min-width: max-content !important;
							padding-left: 10px !important;
							padding-right: 10px !important;
						}
					}
					.widget {
						padding: 0;
						.widget-body {
							.px-5.py-1 {
								padding-left: 0 !important;
							}
						}
					}
				}
			}
			.breadcrumb-nav {
				display: block !important;
				.breadcrumb {
					.breadcrumb-item {
						a {
							font-size: 16px;
							color: black;
						}
					}
				}
			}
		}
		.col-lg-9 {
			.products.mb-3 {
				margin-top: 20px;
			}
			.product-toolbox {
				display: flex;
				align-items: center;

				margin-bottom: 10px;
				.toolbox {
					width: 100%;
					margin-top: 0;
					top: 0;
					margin-bottom: 0;
					position: relative;
					display: flex;
					align-items: center;
					flex-direction: unset;
					justify-content: space-between;
					.toolbox-layout {
						flex-direction: row-reverse;
					}
					.btn-layout,
					.btn-layout + .filter {
						width: 30px !important;
						height: 30px !important;
						padding: 5px !important;
					}
					.toolbox-right {
						margin: 0;
						.toolbox-sort {
							label {
								display: none !important;
							}
							select {
								border-radius: 4px;
							}
						}
					}
					.toolbox-center {
						display: flex !important;
					}
					.toolbox-layout {
						margin: 0;

						.filter.d-none {
							display: flex !important;
							line-height: 1;
							align-items: center;
							justify-content: center;
							height: 30px;
							width: 30px;
							span {
								display: none;
							}
							padding: 0 10px;
							border-radius: 4px;
							margin: 0 2px !important;
							background-color: #eee;
							i {
								svg {
									width: 12px;
								}
							}
						}
					}
				}
			}
		}

		.sidebar-fixed-toggler {
			display: none;
		}
		.page-pagination {
			margin-bottom: 16px;
			ul {
				flex-wrap: wrap;
				justify-content: center;
				li {
					margin-bottom: 10px;
				}
			}
		}
	}
}
