.no-header-product-page {
	// .mobile_btm_navbar_box {
	// 	display: none;
	// }
}
.product-page-seven {
	margin-top: 24px;
	.breadcrumb {
		display: none;
	}
	// .breadcrumb-nav {
	// 	border: none;
	// }
	.only-desktop {
		.product-gallery.product-gallery-vertical {
			width: 100%;
			.row.m-0 {
				flex-direction: column;
				.product-main-image {
					max-width: 100%;
					div,
					img {
						object-fit: contain;
						max-height: 500px;
					}
				}
				.product-image-gallery {
					display: flex;
					flex-wrap: nowrap;
					max-width: 100%;
					overflow-x: scroll;
					&::-webkit-scrollbar {
						display: none;
					}
					.product-gallery-item {
						max-width: 100px;
						height: 100px;
						margin-right: 10px;
						min-width: 100px;

						img {
							height: 100%;
							width: 100%;
							object-fit: contain;
						}
					}
				}
			}
		}
	}
	.product-info {
		margin: 20px 0;
		.react-tabs {
			display: block;
			.product-details-tab {
				margin-bottom: 0;
			}
			.nav.nav-pills {
				.nav-item.show {
					background: #026ba2;
					border-radius: 5px 5px 0px 0px;
					span {
						color: #ffffff;
					}
				}
				.nav-item {
					&:first-child {
						margin-left: 10px;
					}
					span {
						font-weight: bold;
						font-size: 18px;
						line-height: 21px;
						padding: 11px 20px;
					}
				}
			}
			.tab-content {
				border: 1px solid #d0d0d0;
				border-radius: 5px;
				font-size: 18px;
				line-height: 21px;
				color: #0e0e0e;
				td {
					border: none;
				}
			}
		}
	}
	.product-details {
		display: flex;
		justify-content: space-between;
		.product-details-left {
			width: 55%;

			.product-title {
				font-weight: bold;
				font-size: 26px;
				line-height: 30px;
			}
			.price-and-articul {
				.product-content {
					h6 {
						font-size: 18px;
						line-height: 32px;
						color: #2f2f2f;
					}
					.product-price {
						display: none;
					}
				}
			}
			.details-filter-row {
				flex-direction: column;
				align-items: flex-start;
				width: 100%;
				label {
					font-size: 16px;
					line-height: 32px;
					color: #202020;
					margin-bottom: 8px;
				}
				.product-nav {
					.properties-block {
						margin-right: 16px !important;
						margin-bottom: 16px !important;
					}
					.properties-block.color-properties,
					.properties-block.size-properties {
						border-radius: 5px !important;
						width: 100px !important;
						height: 100px !important;
						img {
							object-fit: contain !important;
							height: 100% !important;
							width: 100% !important;
						}
					}
					.properties-block.size-properties {
						width: 50px !important;
						height: 50px !important;
					}
				}
			}
			.favourites-button {
				img {
					margin-right: 16px;
					width: 30px;
					filter: brightness(0) invert(0.5);
				}
				font-weight: bold;
				font-size: 16px;
				line-height: 26px;
				color: #676767;
				margin: 20px 0;
			}
			.favourites-button.choosen {
				img {
					filter: unset;
				}
			}
			.more-supplier {
				margin-top: 30px;
				.product-cat-suppliers {
					width: 100%;
					display: flex;
					justify-content: space-between;
					.suppliers_ul,
					.product-brand {
						width: 45%;
					}
					.seller-info-img {
						border-radius: 5px;
						min-width: 80px;
						overflow: hidden;
						img {
							object-fit: scale-down;
							height: 100%;
							width: 100%;
						}
					}
					.seller-info-name {
						justify-content: center;
						display: flex;
						flex-direction: column;

						span,
						p {
							text-align: left;
							display: -webkit-box;
							-webkit-line-clamp: 1;
							-webkit-box-orient: vertical;
							overflow: hidden;
							--overfdropdownlow: hidden;
							text-overflow: ellipsis;
							transition: 0.4s;
							margin: 0 !important;
							font-size: 16px;
							line-height: 32px;
							color: #949494;
						}
						p {
							font-weight: bold;
							color: $dark-text;
						}
					}
					.supplier:hover {
						box-shadow: unset;
						.seller-info-name p {
							color: $primary-color;
						}
					}
					.product-brand {
						display: flex;
					}
				}
			}
			.min-order {
				border: 1px solid #e3e3e3;
				border-radius: 5px;
				padding: 16px;
				display: flex;
				align-items: center;
				h4,
				h5 {
					margin: 0;
					color: #676767;
					font-size: 14px;
					line-height: 16px;
				}
				h5 {
					margin-right: 18px;
				}
				h4 {
					font-size: 20px;
					line-height: 26px;
					font-weight: bold;
				}
			}
		}
		.product-details-right {
			width: 40%;
			.mobile-top {
				display: none;
			}
			.price-with-btn-cart,
			.buy-in-installments {
				margin-bottom: 12px;
				padding: 20px;
				border: 1px solid #ccdae0;
				border-radius: 5px;
			}
			.price-with-btn-cart {
				span {
					font-size: 16px;
					line-height: 18px;
					color: #5f5f5f;
					margin-bottom: 6px;
				}
				.product-price {
					flex-direction: column;
					align-items: flex-start;
					margin-bottom: 5;
					.old-price {
						font-size: 18px;
						line-height: 21px;
						color: #494949;
					}
					.new-price {
						font-size: 28px;
						line-height: 32px;
						color: #ff4d4d;
					}
					.new-price,
					.old-price {
						font-weight: bold;
					}
				}
				.input-changer-count,
				.cart-btn {
					margin-bottom: 20px;
				}
				.cart-btn {
					position: relative;
					img {
						position: absolute;
						top: 0;
						bottom: 0;
						height: 100%;
						left: 0;
						opacity: 0.3;
					}
				}
			}
			.product-details-quantity {
				flex: unset;
				max-width: unset;
				.input-changer-count {
					padding: 16px !important;
				}
			}
			.cart-btn,
			.installment-btn,
			.input-changer-count {
				display: flex;
				align-items: center;
				justify-content: center;
				width: 100%;
				height: auto;
				border-radius: 5px;
				box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.16);
				padding: 16px 0;
				font-size: 16px;
				line-height: 26px;
				font-weight: bold;
				img {
					margin-right: 4px;
				}
			}
			.cart-btn {
				color: $light-text;
				background: $secondary-color;
				img {
					filter: brightness(0) invert(1);
				}
			}
			.installment-btn {
				background: $third-color;
				span {
					display: none;
				}
				p {
					margin: 0;
				}
			}
			.buy-in-installments {
				background: #ebf2f5;
				position: relative;
				.disabled-div {
					position: absolute;
					top: 0;
					left: 0;
					border-radius: 5px;
					height: 100%;
					width: 100%;
					z-index: 1;
					border: 1px solid rgba($color: #fff, $alpha: 0.8);
					background: rgba($color: #fff, $alpha: 0.8);
					display: flex;
					align-items: center;
					justify-content: center;
					&:hover {
						cursor: not-allowed;
					}
					span {
						font-weight: 700;
						font-size: 24px;
						line-height: 28px;
						color: #153242;
						text-transform: uppercase;
					}
				}
				.modal-btn {
					padding: 0;
					margin-bottom: 15px;
					span:last-child {
						margin-left: 8px;
					}
					span {
						border-bottom: 1px dotted transparent;
						padding-bottom: 2px;
						transition: 0.4s;
						cursor: pointer;
						&:hover {
							color: $primary-color-dark;
							border-bottom: 1px dotted $primary-color-dark;
						}
					}
				}
				span,
				h3 {
					font-weight: bold;
				}
				h3 {
					font-size: 24px;
					line-height: 28px;
					margin-bottom: 18px;
				}
			}
		}
	}
}
#buy-installment-modal {
	max-width: 900px;
	.modal-content {
		border-radius: 5px !important;
		.modal-header {
			padding: 24px 26px;
			border-bottom: 1px solid #e7e7e7;
			h4 {
				margin: 0;
			}
		}
		.modal-body {
			.accordion.accordion-plus {
				display: block;
				.card.card-box {
					border: none;
					.card-header {
						border-bottom: 1px solid #e7e7e7;
						padding: 22px 26px 28px 26px;
						.card-title {
							display: flex;
							align-items: center;

							img {
								margin-right: 12px;
							}
							position: relative;
							.toggle-button {
								position: unset;
								background: transparent;
								padding: 0;
								&::before {
									right: 0;
								}
							}
						}
					}
					.card-body {
						padding: 26px 26px 40px 26px;
						border-bottom: 1px solid #e7e7e7;
						.product-desc-content {
							.react-tabs {
								display: flex;
								flex-direction: row-reverse;
								justify-content: space-between;
								.content-left {
									width: 56%;
									padding: 20px 0;
									background: #ebf2f5;
									border: 1px solid #ccdae0;
									border-radius: 5px;
									display: flex;
									align-items: center;
									justify-content: center;
									span,
									h4 {
										font-weight: bold;
										font-size: 14px;
										line-height: 16px;
										color: #8a8d8e;
										text-align: center;
										display: block;
									}
									h4 {
										margin-top: 12px;
										font-size: 24px;
										line-height: 28px;
										color: #202020;
									}
								}
								.content-right {
									width: 40%;
									.days {
										.react-tabs__tab-list {
											display: flex;
											align-items: center;
											list-style: none;
											.react-tabs__tab {
												font-size: 14px;
												line-height: 18px;
												font-weight: bold;
												padding: 10px;
												border-radius: 5px;
												color: #616161;
												background: #f5f5f5;
												margin-left: 2px;
												cursor: pointer;
												&::before {
													display: none;
												}
												&:first-child {
													margin-left: 0;
												}
											}
											.react-tabs__tab.active.show {
												border: 1px solid #000000;
												background: $third-color;
												color: #0e0e0e;
												z-index: 1;
												transform: scale(1.1);
											}
										}
									}
									.installment-btn {
										display: flex;
										align-items: center;
										justify-content: center;
										background: $third-color;
										width: 100%;
										height: auto;
										border-radius: 5px;
										box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.16);
										padding: 16px 0;
										font-size: 16px;
										line-height: 26px;
										font-weight: bold;
										img {
											margin-right: 4px;
										}
									}
								}
							}
						}
					}
				}
			}
		}
		.modal-header h4,
		.card-header .card-title,
		.card-header .card-title span,
		.card-header .card-title span::before {
			font-weight: bold;
			font-size: 18px;
			line-height: 21px;
			color: #272727;
		}
		.card-header .card-title span,
		.card-header .card-title span::before {
			font-size: 16px;
		}
	}
}

.relation-products {
	.title {
		margin-bottom: 24px !important;
		text-align: left !important;
		font-weight: bold;
		font-size: 22px;
		line-height: 25px;
		color: #000000;
	}
	margin-bottom: 70px;
}
@media (max-width: 991px) {
	.no-header-product-page {
		header {
			display: none;
		}
	}
	// .breadcrumb {
	// 	display: none;
	// }
	.product-page-seven {
		margin-top: 0;

		.title-with-backButton {
			padding: 12px 16px;
			display: flex !important;
			align-items: center;
			background: $primary-color;
			position: fixed;
			top: 0;
			left: 0;
			right: 0;
			z-index: 99;
			h3 {
				font-weight: bold;
				font-size: 16px;
				line-height: 32px;
				color: $light-text;
				margin: 0;
				white-space: nowrap;
				display: -webkit-box;
				-webkit-line-clamp: 1;
				-webkit-box-orient: vertical;
				overflow: hidden;
				--overfdropdownlow: hidden;
				text-overflow: ellipsis;
			}
			.back-block {
				.exit-product-page {
					img {
						filter: brightness(1) invert(1);
						width: 20px;
						margin-right: 10px;
					}
				}
			}
		}
		.product-gallery.product-gallery-vertical {
			.product-main-image {
				height: 375px;
				img,
				.big-img-with-view-button {
					height: 100%;
					border-radius: 0;
					object-fit: contain;
				}

				.splide.splide--slide,
				.splide__track {
					height: 375px;
					.splide__arrows {
						display: none;
					}
				}
			}
		}

		.perduct-galery {
			padding: 0;
			margin-top: 60px;
			.product-gallery-vertical {
				margin-bottom: 0;
				.product-navbar {
					display: none !important;
				}
			}
		}
		.product-details {
			flex-direction: column;
			.product-details-left,
			.product-details-right {
				width: 100%;
			}
			.product-details-left {
				.product-title {
					display: none;
				}
				.price-and-articul {
					.product-content {
						margin-bottom: 0;
						h6 {
							font-size: 12px;
							line-height: 32px;
							color: #949494;
							margin: 0;
						}
						.product-price {
							display: flex;
							margin-bottom: 0;
							.new-price,
							.old-price {
								font-weight: bold;
								font-size: 18px;
								line-height: 32px;
								color: #ff4c4c;
							}
							.old-price {
								font-size: 14px;
								line-height: 32px;
								color: #8a8a8a;
							}
						}
					}
				}
				.details-filter-row {
					label {
						color: #949494;
						font-size: 14px;
					}
					.product-nav {
						.properties-block.color-properties {
							width: 65px !important;
							height: 65px !important;
						}
						.properties-block.size-properties {
							width: 45px !important;
							height: 45px !important;
						}
					}
				}
				.favourites-button {
					img {
						width: 23px;
						margin-right: 8px;
					}
				}
				.min-order {
					justify-content: space-between;
				}
			}
			.product-details-right {
				.cart-buy-installment {
					position: fixed;
					z-index: 5;
					background: $secondary-color;
					right: 0;
					left: 0;
					bottom: 75px;
					display: flex;
					justify-content: space-around;
					align-items: center;
					padding: 20px;
					box-shadow: -10px 0 20px rgba(0, 0, 0, 0.5);
					.price-with-btn-cart,
					.buy-in-installments {
						margin-bottom: 0;
						width: 45%;
						border: none;
						padding: 0;
						height: auto;
						box-shadow: none;
						background: transparent;
						span,
						.product-price,
						.modal-btn,
						h3 {
							display: none;
						}

						.cart-btn,
						.installment-btn,
						.input-changer-count {
							border-radius: 5px;
							padding: 15px;
							box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
							font-size: 14px;
							line-height: 21px;
							margin-bottom: 0;
							white-space: nowrap;

							img {
								height: 20px;
								width: 20px;
							}
						}
						.installment-btn {
							span {
								display: block;
							}
							p {
								display: none;
							}
						}

						.cart-btn {
							border: 1px solid #ffffff;
							background: transparent;
							box-shadow: none;
							img {
								position: relative;
								opacity: 1;
								width: 19px;
								height: 16px;
								margin-top: -5px;
							}
						}
						.input-changer-count {
							background: $light-text;
							span {
								display: flex;
								align-items: center;
								justify-content: center;
								margin: 0;
							}
							input {
								background: transparent;
							}
							span,
							input {
								line-height: 1;
								color: $dark-text;
							}
						}
					}
					.price-with-btn-cart {
						width: 100%;
					}
					.buy-in-installments {
						display: none;
					}
				}
			}

			.details-footer {
				.product-cat {
					.supliers-row {
						.product-cat {
							margin: 22px 0;
							.supplier {
								.seller-info-name {
									span,
									p {
										font-size: 14px;
										line-height: 22px;
									}
									p {
										color: $primary-color;
									}
								}
							}
							.seller-info-img {
								min-width: 65px;
								height: 65px;
								img {
									width: 100%;
									height: 100%;
								}
							}
						}
					}
				}
			}
		}
		.product-info {
			.react-tabs {
				.product-details-tab {
					.nav-pills {
						.nav-item {
							span {
								font-size: 14px;
							}
						}
					}
				}

				.tab-content {
					.tab-pane {
						padding: 14px 17px;
						.product-desc-content {
							font-size: 12px;
							line-height: 14px;
						}
					}
				}
			}
		}
	}
	.relation-products {
		.title {
			font-size: 18px;
			line-height: 32px;
			margin-bottom: 12px !important;
		}
		margin: 25px 0 115px 0 !important;
	}
	#buy-installment-modal {
		max-width: 100%;
		.modal-content {
			.modal-header {
				padding: 16px;
				h4 {
					font-size: 16px;
				}
				.close-modal {
					width: 30px;
				}
			}
			.modal-body {
				.accordion.accordion-plus {
					.card.card-box {
						.card-header {
							padding: 16px;
							.card-title {
								span {
									width: 80%;
									font-size: 14px;
									white-space: nowrap;
									-webkit-line-clamp: 1;
									overflow: hidden;
									text-overflow: ellipsis;
								}
							}
						}
						.card-body {
							padding: 16px 16px 26px 16px;
							.product-desc-content {
								.react-tabs {
									flex-direction: column;
									.content-left,
									.content-right {
										width: 100%;
										.days {
											.react-tabs__tab-list {
												justify-content: center;
												.react-tabs__tab {
													font-size: 12px;
													padding: 8px;
												}
											}
										}
										.installment-btn {
											margin-bottom: 12px;
											font-size: 12px;
											padding: 10px;
											img {
												width: 10px;
											}
										}
										.react-tabs__tab-panel {
											span {
												font-size: 12px;
											}
											h4 {
												margin: 8px 0;
												font-size: 14px;
											}
										}
									}
									.content-left {
										padding: 8px 0;
									}
								}
							}
						}
					}
				}
			}
		}
	}
}
