.header-bottom-one {
	.display-flex {
		display: flex;
		align-items: center;
	}

	.menu-search {
		height: 70px;
		background: $primary-color;
		width: 100%;
		margin: 0 auto;
		border-radius: 12px;
		.serach-and-category {
			height: 100%;
			padding: 5px;
			.search-button {
				min-width: 110px;
				height: 100%;
				justify-content: center;
				border-radius: 8px 0 0 8px;
				background: #fff;
				span {
					margin-left: 4px;
					font-size: 16px;
					line-height: 24px;
					color: #293644;
					font-weight: bold;
				}
			}
			.desktop-category-btn {
				justify-content: center;
				width: 100%;
				span {
					font-weight: bold;
					font-size: 16px;
					line-height: 24px;
					margin-left: 8px;
					color: #fff;
				}
			}
			//#28d5fe
			//#9a0755
			border-right: 1px solid #28d5fe;
		}
		.search-form {
			.search-body {
				.search-box-wrapper {
					.search-box {
						max-width: unset;
						padding-bottom: 2.5rem;
						form {
							width: 100%;
							.header-search-wrapper.search-wrapper-wide {
								border: none;
								background: transparent;
							}
							border: none;
							input[type="search"]::-webkit-search-cancel-button {
								display: none;
							}
							input {
								height: 5.25rem;
								font-size: 3rem;
								line-height: 2.25rem;
								padding: 0 !important;
								background: transparent;
								caret-color: $primary-color;

								color: #000000;
								&::placeholder {
									color: $primary-color-dark;
									opacity: 0.5;
									font-weight: bold;
								}
							}
						}
					}
					.btn.search-button {
						display: none;
					}
				}
			}
		}
		.horizontal-menu {
			.first-menu-list {
				margin: 0;
				justify-content: space-around;
				.first-menu-child {
					a {
						font-weight: bold;
						font-size: 16px;
						line-height: 17px;
						color: #fff;
					}
				}
				.first-menu-child.with-soon {
					position: relative;
					.disabled-link.d-none {
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;
						z-index: 2;
						display: block !important;
						&:hover {
							cursor: not-allowed;
						}
					}
					a {
						&:hover {
							user-select: none;
							cursor: not-allowed;
						}
					}
					.soon {
						display: inline-block !important;
						margin-left: 5px;
						padding: 0 4px;
						font-weight: 700;
						font-size: 9px;
						line-height: 17px;
						color: #000000;
						background: #faff00;
						border-radius: 3px;
					}
				}
			}
		}
		.vertical-menu,
		.search-form {
			position: absolute;
			left: 0;
			right: 0;
			height: 100%;
			background: rgba(0, 0, 0, 0.2);
			z-index: 10000;
			animation-name: animationMenu;
			animation-duration: 0.5s;
			animation-fill-mode: forwards;
			.vertical-menu-content,
			.search-content {
				background: rgb(255, 255, 255);
				background: linear-gradient(
					0deg,
					rgba(255, 255, 255, 1) 0%,
					rgba(245, 245, 245, 1) 100%
				);
				border-radius: 0 0 8px 8px;
				box-shadow: 0px 19px 20px rgb(0 0 0 / 18%);
			}
			.search-content {
				background: $body-bg;
				.header-search.header-search-extended {
					display: flex;

					.search-close-button {
						display: flex !important;
						.search-icon,
						.close-button {
							display: flex;
							align-items: center;
							justify-content: center;
							width: 60px;

							img {
								transition: 0.3s;
								opacity: 0.3;
								width: 20px;
							}
						}
						.search-icon.loading-icon {
							img {
								opacity: 1;
							}
						}
						.close-button {
							&:hover {
								img {
									opacity: 1;
								}
							}
						}
						.search-icon {
							border-right: 1px solid $primary-color;
						}
					}
				}
				.search-result-wrapper {
					position: relative;
					top: 0;
					margin-bottom: 20px;
					.search-result {
						top: 0 !important;
						position: relative;
						border-radius: $border-item;
						max-height: 600px;
						background: transparent;
						background: $light-text;
						box-shadow: unset;
						&::-webkit-scrollbar {
							width: 6px;
						}
						&::-webkit-scrollbar-thumb {
							background-color: $primary-color;
							border-radius: $border-item;
						}
						.menus,
						.products {
							.menu-item,
							.search-result-item {
								display: flex;
								align-items: center;
								padding: 10px;
								margin-bottom: 0;
								border-bottom: 1px solid #f3f3f3;
								.img {
									margin-right: 12px;
									min-width: 50px;
									min-height: 50px;
									max-width: 50px;
									max-height: 50px;
									background: #f7f7f7;
									border: 1px solid #f3f3f3;
									border-radius: 5px;
									overflow: hidden;
									img {
										object-fit: contain;
										background: white;
										width: -webkit-fill-available;
										height: 100%;
										max-height: inherit;
									}
								}
								.menu-result-item {
									a {
										font-weight: 400;
										font-size: 14px;
										line-height: 17px;
										letter-spacing: -0.02em;
										color: $dark-text;
									}
								}
								.menu-breadcrumb {
									.breadcrumb-item {
										font-size: 12px;
										line-height: 15px;
										letter-spacing: -0.02em;
									}
								}
								a {
									.new-price {
										margin-left: 10px;
									}
									.new-price,
									.old-price,
									.price {
										font-size: 12px;
									}
								}
							}
						}
					}
				}

				// .search-result {
				// 	margin-bottom: 40px;
				// 	position: unset;
				// 	box-shadow: none;
				// 	background: transparent;
				// 	div.d-flex.justify-content-between {
				// 		p,
				// 		button {
				// 			font-size: 16px;
