				// 	.history-list {
				// 		.d-none.reload-icon {
				// 			display: inline-block !important;
				// 			width: 20px;
				// 			margin-left: 20px;
				// 		}

				// 		.i-icon.icon-refresh {
				// 			display: none;
				// 		}
				// 		li {
				// 			p {
				// 				font-size: 18px;
				// 			}
				// 		}
				// 	}
				// 	.p-3.text-center {
				// 		display: none;
				// 	}
				// 	.search-result-item {
				// 		padding: 20px 0;
				// 		a.d-flex.align-items-center {
				// 			img {
				// 				object-fit: cover;
				// 				width: 100px;
				// 				height: 100px;
				// 			}
				// 			p,
				// 			span {
				// 				font-size: 20px;
				// 				font-weight: bold;
				// 			}
				// 		}
				// 	}
				// }
			}
		}
		.vertical-menu-body {
			margin: 0 auto;
			width: 100%;
			.col-md-3 {
				border-right: 1px solid #e7e7e7;
			}
			.col-md-3,
			.col-md-9 {
				padding: 10px 0;
				height: 70vh;
				overflow-y: auto;
				&::-webkit-scrollbar {
					width: 2px;
				}

				&::-webkit-scrollbar-thumb {
					background: $primary-color;
					border-radius: 5px;
				}
				.vertical-menu-childrens-list {
					margin: 0 auto;
				}
			}
			.col-md-3 {
				.vertical-menu-list {
					margin: 0;
					.vertical-menu-child {
						padding: 15px;
					}
				}
			}
			.vertical-menu-list,
			.submenu {
				.vertical-menu-child,
				.submenu-title {
					display: block;
					font-weight: bold;
					font-size: 16px;
					line-height: 2;
					color: $primary-color;
					border-radius: 8px;
					&:hover {
						background: $primary-color;
						color: $light-text;
					}
				}
				.submenu-title,
				.submenu-child {
					padding: 5px 10px;
					color: #272727;
				}
				.submenu-child {
					font-size: 16px;
				}
			}
			.submenu {
				margin-top: 10px;
			}
		}
	}
}

