.profile {
	.header-eight {
		box-shadow: unset;
		border-radius: unset;
		margin-bottom: unset;
	}
}

.profile-one {
	.warning_sell_time {
		width: 100%;
		padding: 12px;
		border-radius: 10px;
		background-color: #ffecec;
		border: 1px solid #ffd0d0;
		margin-bottom: 10px;
		p {
			font-size: 12px;
			display: flex;
			align-items: center;
			margin-bottom: 0;
			line-height: 1.4;
			color: #000;
			opacity: 0.5;
			img {
				margin-right: 5px;
			}
		}
		.warning_title {
			font-size: 13px;
			font-weight: 700;
			margin-bottom: 5px;
			opacity: 1;
		}
	}
	.page-content {
		.profile-nav {
			background: $light-text;
			box-shadow: rgba(0, 0, 0, 0.05) 0px 20px 25px -5px,
				rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
			border-radius: 0px 0px 20px 20px;
			margin-bottom: 30px;
			z-index: 5;
			ul {
				display: flex;
				margin: 0 auto;
				align-items: center;
				.nav-item {
					padding: 18px 0;
					border-bottom: 2px solid transparent;
					margin-right: 30px;
					transition: 0.3s;
					span,
					a {
						transition: 0.3s;
						font-size: 16px;
						padding: 0;
						line-height: 19px;
						color: $body-text;
					}
				}
				.exit {
					margin-right: 0;
					margin-left: auto;
					padding: 0;
					a {
						padding: 10px 20px;
						background: $main-menu-link;
						border-radius: $border-item;
						color: $light-text !important;
					}
				}
				.nav-item.active {
					span {
						color: $dark-text;
					}
					border-bottom: 2px solid $primary-color;
				}
			}
		}
		.tab-pane {
			.dashboard-banner {
				.banner.banner-big {
					.dashboard-sells {
						.dashboard-sells-groups {
							padding: 0;
							background: transparent !important;
							.sell_title {
								padding: 0;
								font-weight: 700;
								font-size: 20px;
								line-height: 24px;
								color: $dark-text;
								margin-bottom: 20px;
							}
							.delivery_content {
								width: 100%;
								margin: 0 auto;
								.col-md-6.col-12 {
									padding: 0;
									.sells-group {
										margin-right: 30px;
										height: 100%;
										border-radius: 5px;
										border: 1px solid #ececf1;
										.sell_group_info {
											background: transparent;
											.sell_group_info_header {
												padding: 19px 17px !important;
												display: block;
												.order {
													display: flex;
													justify-content: space-between;
													align-items: center;
													h3 {
														margin: 0;
														font-size: 16px;
													}
												}
												.orders,
												.orders span {
													justify-content: flex-start;
													font-size: 14px;
													font-weight: normal;
													line-height: 18px;
													letter-spacing: 0.2px;
													color: #8e8e93;
												}
											}

											.sell_group_info_body {
												padding: 19px !important;
												border-bottom: none;
												.order-sum,
												.delivery_type,
												.delivery_time,
												.delivery_courier,
												.delivery_car {
													display: flex;
													align-items: flex-start;
													justify-content: space-between;
													margin-bottom: 12px;
													p {
														color: $dark-text;
														font-size: 16px !important;
														line-height: 20px !important;
														letter-spacing: 0.2px !important;
														margin: 0;
													}
													h3 {
														display: flex;
														flex-direction: column;
														align-items: flex-end;
														font-size: 16px;
														line-height: 20px;
														font-weight: bold;
														margin: 0;
														span {
															margin-top: 4px;
															font-weight: normal !important;
															padding: 4px 8px;
															font-size: 12px;
															line-height: 16px;
															color: $body-bg;
															border-radius: 6px;
															background: #37cc5d;
														}
														span.pending {
															background: #ff4d4f;
														}
													}
												}
											}
											.sell_group_info_footer {
												display: none;
											}
										}
									}
								}
							}
							.map {
								margin: 0;
								padding: 30px 20px !important;
								border: 1px solid #ececf1;
								img {
									margin: 0 auto;
									width: 60% !important;
								}
								#custom-map {
									overflow: hidden;
									border-radius: 5px;
									height: 320px !important;
								}
								#custom-map div {
									height: 100% !important;
								}
							}
						}
					}
				}
			}
			.purchases {
				.row.w-100 {
					padding: 0 !important;
				}
				.dashboard-banner {
					padding: 0;
					.banner.banner-big {
						margin-bottom: 0;
						.dashboard-sells {
							.dashboard-sells-groups {
								margin-bottom: 10px;
								.col-12 {
									padding: 0;
								}
								.sells-group {
									.sell_group_info {
										border-radius: $border-item;
										padding: 10px 10px 10px 20px;
										background: $third-color;
										.sell_group_info_header {
											.orders {
												.sell-group-header {
													.order-id {
														font-size: 16px;
														line-height: 19px;
														color: $dark-text;
													}
													.status-wrapper {
														p {
															padding: 5px 10px;
															border-radius: 5px;
															margin: 0 0 0 10px;
															font-size: 14px;
															line-height: 16px;
														}
													}
												}
												.delivery_time {
													img {
														width: 13px;
														filter: brightness(1)
															invert(0.4);
													}
													.delivery_time_current {
														margin: 0 0 0 6px;
														padding: 0 !important;
														color: $body-text;
														font-size: 12px;
														line-height: 1;
													}
												}
											}
											.sell_group_info_footer {
												display: flex;
												.price-count {
													p {
														margin: 0;
														font-size: 16px;
														line-height: 19px;
														color: $dark-text;
													}
													.order-count {
														text-align: right;
														color: $body-text;
														font-size: 12px;
														line-height: 14px;
													}
												}
												.view-check {
													.check-button {
														border-radius: 5px;
														display: flex;
														align-items: center;
														justify-content: center;
														width: 50px;
														height: 50px;
														background: $light-text;
														margin-left: 10px;
														img {
															width: 21px;
														}
													}
												}
											}
											.price-count,
											.orders {
												display: flex !important;
												flex-direction: column;
												justify-content: space-between;
											}
										}
									}
								}
							}
						}
					}
				}
			}

			.user-cards-list,
			.user_addresses_page,
			.purchases {
				.row {
					margin: 0 auto;
					width: 100%;
				}
				.title-add-card,
				.title-add-address,
				.title-order {
					margin-bottom: 20px;
					button {
						padding: 0;
						justify-content: flex-end;
						svg {
							width: 18px;
							path {
								fill: $secondary-color;
							}
						}
					}
				}
				.user-card {
					border-radius: $border-item;
					padding: 10px 10px 10px 20px;
					background: $third-color;
					margin-bottom: 10px;

					h3 {
						// font-weight: bold;
						font-size: 16px;
						line-height: 19px;
						color: $dark-text;
						margin-bottom: 0;
					}
					.card-number-date {
						display: block !important;
					}
					.card-date {
						margin-top: 10px;
						font-weight: normal;
						font-size: 12px;
						line-height: 14px;
						color: $body-text;
						img {
							width: 13px;
						}
					}
					.active-delete {
						align-items: unset !important;
					}
					.is-active {
						height: max-content;
						align-items: center;
						.active,
						.not-active {
							margin-right: 5px;
							min-width: 5px;
							min-height: 5px;
							border-radius: 50%;
						}
						h3 {
							font-size: 14px;
							line-height: 16px;
						}
						.active {
							background: $primary-color;
						}
						.not-active {
							background: $third-color;
						}
					}
					.delete-button {
						border-radius: 5px !important;
						width: 50px !important;
						height: 50px !important;
						background: $light-text;
						margin-left: 18px;
						img {
							width: 21px;
						}
					}
				}

				.title-add-card,
				.title-add-address,
				.dashboard-banner,
				.title-order {
					h3 {
						font-weight: bold;
						font-size: 20px;
						line-height: 24px;
						margin: 0;
						color: $dark-text;
					}
					button {
						font-size: 16px;
						line-height: 19px;
						padding: 0 !important;
						span {
							color: $secondary-color;
							border-bottom: 1px solid $secondary-color;
						}
					}
				}
			}
			.title-add-card,
			.edit_user,
			.user_addresses_page {
				h3 {
					font-weight: 700;
					font-size: 20px;
					line-height: 24px;
					margin: 0;
					color: $dark-text;
				}
			}

			.edit_user {
				.radios {
					display: none !important;
				}

				h3 {
					margin-bottom: 20px;
				}
				h3.d-none {
					display: block !important;
				}

				.edit-user-title {
					margin-top: 0;
				}
				label {
					display: none;
				}
				.form-control {
					background: $third-color;
					border-radius: $border-item !important;
					border: none;
					color: $dark-text;
					font-size: 14px;
					line-height: 16px;
					padding: 12px 10px;
					&::placeholder {
						color: $body-text;
					}
				}
				.btn.btn-outline-primary-2 {
					border-radius: $border-item;
					background: $primary-color;
					color: $light-text;
					margin-top: 30px;
					text-transform: inherit;
					width: 300px;
					margin-left: auto;
					margin-right: auto;
					font-size: 16px;
					line-height: 19px;
					padding: 10.5px 0;
					i {
						display: none;
					}
				}
			}
			.edit_user,
			.purchases,
			.user-cards-list,
			.user_addresses_page {
				width: 600px;
				margin: 0 auto;
			}

			.user_addresses_page {
				.title-add-address.d-none {
					display: flex !important;
					margin-bottom: 20px;
				}
				.default-add {
					display: none;
				}
				.shipping-address {
					background: $third-color;
					color: $dark-text;
					margin-top: 0 !important;
					margin-bottom: 10px !important;
					border: none;
					padding: 12.5px 10px;
					border-radius: $border-item;
				}
			}
		}
	}
	.no-data {
		h3 {
			font-size: 20px;
			margin-bottom: 20px;
		}
		img {
			width: 250px !important;
		}
	}
}

.modal-dialog.map-modal {
	.modal-content {
		.modal-body {
			.close.closeMap {
				z-index: 1000;
				background: $light-text;
				box-shadow: 0 0 10px rgb(0 0 0 / 20%);
				border-radius: 50%;
				width: 35px;
				height: 35px;
				padding: 5px;
			}
			.new_address_add {
				height: 100%;
				.location-wrapper,
				.loading {
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					z-index: 100000;
				}

				.add_address_btn,
				.current_address {
					position: absolute;
					bottom: 40px;
					margin-bottom: 40px;
					z-index: 100000;
					left: 0;
					right: 0;
				}
				.current_address {
					bottom: 180px;
					font-size: 20px;
					color: $dark-text;
					width: max-content;
					margin: 0 auto;
				}
				.loading {
					.lds-ellipsis {
						background: $light-text;
						border-radius: 20px;
						div {
							background: $primary-color !important;
						}
					}
				}
			}
		}
	}
}

@media (max-width: 961px) {
	.profile-one {
		margin-bottom: 100px;
		.page-content {
			.profile-nav {
				ul {
					width: 100%;
					overflow: auto;
					&::-webkit-scrollbar {
						display: none;
					}
					.nav-item {
						.nav-link {
							white-space: nowrap;
						}
					}
					.exit {
						margin-right: 0;
						margin-left: 0;
						a {
							padding: 6px 10px;
						}
					}
				}
			}
			.tab-pane {
				padding: 0;
				.dashboard-banner {
					.banner.banner-big {
						.dashboard-sells {
							.dashboard-sells-groups {
								.delivery_content {
									.sells-group {
										width: 100%;
									}
								}
							}
						}
					}
				}
				.user-cards-list {
					.title-add-card {
						button {
							background: transparent;
							padding: 0;
						}
					}
				}
				.user_addresses_page {
					.ymaps-2-1-79-map {
						width: 100% !important;
					}
				}

				.edit_user,
				.purchases,
				.user-cards-list,
				.user_addresses_page {
					width: 100%;
					margin: 0 auto;
				}
			}
		}
		.no-data {
			h3 {
				margin-bottom: 10px;
			}
			img {
				width: 200px !important;
			}
		}
	}
}
