.card-row-two {
	display: flex;
	width: 100%;
	box-shadow: 0px 0px 13px rgba(0, 0, 0, 0.05);
	border-radius: 5px;
	padding: 15px 7px;
	.product-header {
		width: 20%;
		height: 150px;
		position: relative;
		img {
			object-fit: contain;
		}
		.discount {
			width: 50px;
			height: 50px;
			background: $main-menu-link;
			position: absolute;
			top: -8px;
			right: 0;
			border-radius: 50%;
			display: flex;
			align-items: center;
			justify-content: center;
			span {
				font-weight: 700;
				font-size: 14px;
				line-height: 16px;
				color: $light-text;
			}
		}
	}
	.body-product {
		width: 80%;
		margin-left: 30px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		.title-product {
			color: $dark-text;
			margin-bottom: 0;
			font-size: 18px;
		}
		.price-product {
			.price-new {
				color: $dark-text;
				font-weight: bold;
				font-size: 20px;
				margin-right: 10px;
			}
			.price-old {
				font-weight: bold;
			}
		}
		.add-cart-favourites {
			display: flex;
			align-items: center;
			justify-content: space-between;
			.action-product {
				button {
					align-items: center;
					justify-content: center;
					display: flex;
					padding: 10px 20px;
					border: 1px solid $primary-color;
					border-radius: 5px;
					color: $primary-color;
					width: 150px;
					img {
						margin-right: 10px;
					}
				}
				.product-details-quantity {
					flex: unset;
					width: 150px;
					max-width: unset;
					background: #52c41a;
					border-radius: 5px;
					.input-changer-count {
						height: 100%;
						border: none;
						align-items: center;
						font-size: 1.4rem;
						input {
							background: transparent;
							font-weight: 700;
							color: $light-text;
						}
						span {
							color: $light-text;
							width: 10%;
							font-size: 20px;
							line-height: 1;
						}
					}
				}
			}
			.btn-heart {
				img {
					width: 30px;
				}
			}
		}
	}
}

@media (max-width: 991px) {
	.card-row-two {
		.product-header {
			min-width: 92px;
			height: 92px;
			.discount {
				width: 28px;
				height: 28px;
				span {
					font-size: 9px;
				}
			}
		}
		.body-product {
			width: 100%;
			margin-left: 15px;

			.title-product {
				font-size: 11px;
			}
			.price-product {
				.price-new {
					font-size: 12px;
					margin-right: 5px;
				}
				.price-old {
					font-size: 9px;
				}
			}
			.add-cart-favourites {
				.action-product {
					button {
						padding: 6px 16px;
						width: auto;
						font-size: 10px;

						img {
							width: 20px;
							margin-right: 6px;
						}
					}
					.product-details-quantity {
						width: 100px;
						.input-changer-count {
							padding: 8px 10px !important;
							font-size: 10px;
							span {
								font-size: 16px;
							}
						}
					}
				}
				.btn-heart {
					img {
						width: 20px;
					}
				}
			}
		}
	}
}
