.category-six {
	margin-bottom: 80px;

	.category-description {
		.opener {
			font-weight: 800;
			font-size: 16px;
			margin-top: 20px;
			line-height: 20px;
			padding: 0;
			display: flex;
			align-items: center;
			text-decoration-line: underline;
			color: $secondary-color;
		}
		.before-footer-text {
			margin: 13px 0 0 0;
			max-height: 300px;
			overflow: hidden;
			transition: 1s ease max-height;
		}
		.before-footer-text.active {
			max-height: max-content;
		}
	}

	.toolbox {
		.toolbox-center {
			.toolbox-info.filter-cancelers {
				// overflow: auto;
				// &::-webkit-scrollbar {
				// 	display: none;
				// }
				flex-wrap: wrap;
				.canceler-button {
					display: flex;
					margin-bottom: 10px;
					align-items: center;
					border-radius: $border-item;
					padding: 3px 10px 3px 10px;
					span {
						white-space: nowrap;
					}
				}
			}
		}
	}
	.breadcrumb-nav {
		margin-bottom: 50px !important;
		border: none;
		.container.mx-auto {
			padding: 0 10px;
			.breadcrumb {
				&::before {
					display: none;
				}
				.breadcrumb-item {
					display: flex;
					align-items: center;
					a {
						color: $primary-color;
						display: flex;
						align-items: center;
						font-weight: 500;
						font-size: 16px;
						line-height: 19px;
					}
				}
				.breadcrumb-item:last-child {
					a {
						color: $body-text;
					}
				}
			}
		}
	}
	.page-content {
		.skel-shop-products {
			.banners {
				margin-bottom: 30px;
				img {
					width: 100%;
				}
			}
			.product-toolbox,
			.menu-name h4 {
				display: none;
			}
			.menu-name h4 {
				font-weight: 700;
				font-size: 20px;
				line-height: 24px;
				color: $dark-text;
			}
			.menu-name {
				margin-bottom: 30px;
				.filterr {
					display: flex;
					justify-content: space-between;
					align-items: center;
					.toolbox-center {
						display: none;
					}
					.toolbox,
					.toolbox-right {
						width: 100%;
						margin: 0;
						.toolbox-sort {
							label {
								margin: 0;
							}
							span {
								font-size: 16px;
								line-height: 19px;
								color: $body-text;
							}

							.select-custom {
								z-index: 4;
								position: relative;
								&::after {
									display: none;
								}

								* {
									user-select: none;
								}

								*:focus {
									outline: none;
								}

								#app-cover {
									width: max-content;
									height: max-content;
									z-index: 1;
								}
								.options {
									display: none;
									opacity: 0;
									transition: 0.3s;
									border-radius: 20px;
									box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
									background: $light-text;
									padding: 10px 0;
								}
								.select-box.active {
									.options {
										display: block !important;
										opacity: 1;
									}
									#selected-value {
										span {
											img {
												margin-left: 16px;
												transform: rotate(-90deg);
											}
										}
									}
								}
								.select-box {
									#selected-value {
										span {
											img {
												margin-left: 16px;
												transition: 0.3s;
												transform: rotate(90deg);
											}
										}
									}
								}

								#select-button {
									position: relative;
									height: 100%;
									padding: 0 0 0 10px;
									background-color: #fff;
									border-radius: 4px;
									cursor: pointer;
								}

								#options-view-button {
									position: absolute;
									top: 0;
									right: 0;
									bottom: 0;
									left: 0;
									width: 100%;
									height: 100%;
									margin: 0;
									opacity: 0;
									cursor: pointer;
									z-index: 3;
								}

								#selected-value {
									span {
										font-weight: 700;
										line-height: 22px;
										font-size: 18px;
										color: $dark-text;
										display: flex;
										align-items: center;
										justify-content: space-between;
									}
								}

								.option,
								.label {
									color: $dark-text;
									font-size: 16px;
								}

								#options {
									position: absolute;
									top: 42px;
									right: 0;
									width: max-content;
								}

								.option {
									position: relative;
									line-height: 1;
									transition: 0.3s ease all;
									z-index: 2;
								}

								.label {
									padding: 0;
									display: flex;
									padding: 10px 20px;
									justify-content: space-between;
									align-items: center;
									border-radius: $border-item;
									transition: 0.3s;
									img {
										margin-left: 40px;
									}
									&:hover {
										background: $third-color;
									}
								}

								#options-view-button:checked ~ #options .label {
									padding: 11px 20px;
								}

								.s-c {
									white-space: nowrap;
									position: absolute;
									left: 0;
									width: 100%;
									height: 50%;
								}

								.s-c.top {
									top: 0;
									height: 100%;
								}

								input[type="radio"] {
									position: absolute;
									right: 0;
									left: 0;
									width: 100%;
									height: 50%;
									margin: 0;
									opacity: 0;
									cursor: pointer;
								}

								.label {
									transition: 0.3s ease all;
								}
							}
						}
					}
					button {
						display: none;
						align-items: center;
						border-radius: $border-item;
						background: $third-color;
						padding: 9px 15px;
						img {
							width: 20px;
						}
						span {
							font-size: 16px;
							line-height: 1;
						}
					}
				}
				.widget.widget-collapsible {
					.widget-side {
						.widget-body.widget-body-filter-top {
							display: none !important;
							flex-wrap: nowrap;
							overflow: auto;

							&::-webkit-scrollbar {
								display: none;
							}
							.collapsed {
								white-space: nowrap;
								padding: 5.5px 10px;
								margin-right: 5px;
								font-size: 16px;
								line-height: 19px;
								border-radius: $border-item;
								background: $third-color;
								font-weight: normal;
							}
						}
					}
				}
			}
		}

		.footer-desc {
			border-top: 1px solid rgba(137, 148, 153, 0.2);
			padding-top: 20px;
		}
		.skel-shop-sidebar {
			.sidebar.sidebar-shop,
			.sidebar-filter {
				.filter-properties {
					.widget.widget-collapsible {
						margin-bottom: 35px !important;
					}
				}
				// .widget.widget-collapsible {
				// 	.widget-title {
				// 		a {
				// 			&::after {
				// 				// transform: rotate(-180deg);
				// 			}
				// 		}
				// 	}
				// }
				.widget.widget-collapsible {
					.widget-title {
						font-weight: 700;
						font-size: 18px;
						line-height: 22px;
						color: $dark-text;
						margin: 0 0 15px 0 !important;
						a {
							&::after {
								color: $primary-color;
								// transform: rotate(-90deg);
								right: 0;
							}
						}
						a.collapsed {
							&::after {
								transform: unset;
							}
						}
					}
					border: none;
					.widget-side {
						.widget-body {
							.collapsed {
								font-size: 16px;
								line-height: 19px;
								font-weight: normal;
							}
						}
					}
				}
				.widget.widget-collapsible.first {
					.widget-side {
						.widget-body {
							.widget.widget-collapsible.first-child {
								.widget-title {
									border: none;
								}
							}
							.widget.widget-collapsible {
								.widget-title {
									border-top: 0.5px solid
										rgba(137, 148, 153, 0.2);
									// &:first-child {
									// 	border: none;
									// }
									margin: 0 !important;
									a {
										padding: 10px 0;
										font-weight: normal;
										font-size: 16px;
										line-height: 19px;
										&::after {
											color: $primary-color;
											// transform: rotate(-90deg);
											right: 0;
										}
									}
									a.collapsed {
										&::after {
											transform: unset;
										}
									}
								}

								.widget-body {
									p {
										margin: 0;
										a {
											font-weight: normal;
										}
									}
								}
							}
						}
					}
				}
				.filter-price {
					margin-top: 40px;
					.clear-btn {
						margin-bottom: 14px;
						display: flex;
						align-items: center;
						h4 {
							margin: 0;
							font-weight: 700;
							font-size: 18px;
							line-height: 22px;
							margin-right: 10px;
						}
						button {
							padding: 0;
							font-size: 14px;
							line-height: 16px;
							color: $secondary-color;
						}
					}
					input {
						padding: 12px;
						text-align: center;
						border-radius: $border-item !important;
						background: $third-color;
						border: none;
						color: $dark-text;
						font-size: 14px;
						line-height: 16px;
						margin-right: 5px;
						&::placeholder {
							text-align: center;
							color: $body-text;
						}
					}
				}
				.filter-properties {
					.filter-items {
						margin-right: 0;
						.filter-item {
							label {
								font-size: 14px;
								color: $body-text;
								position: relative;
								line-height: 20px;
								margin-top: 0;

								&::before {
									width: 19px;
									height: 19px;
									top: 0;
									bottom: 0;
									border-radius: 5px;
									border: 1px solid $body-text;
								}
								&::after {
									top: 0;
									left: 2px;
									font-size: 7px;
								}
							}
							label.custom-control-label.checked {
								color: $dark-text;
								span {
									&::after {
										content: "";
										position: absolute;
										left: 2px;
										top: 2px;
										width: 1.5rem;
										height: 1.5rem;
										margin: 0;
										border-radius: 4px;
										border: 1px solid $body-bg;
									}
								}
								&::before {
									border: 2px solid $primary-color;
								}
							}
						}
					}
				}

				.menu-name {
					h4 {
						font-weight: bold;
						font-size: 20px;
						line-height: 24px;
						color: $dark-text;
					}
				}
			}
		}
	}
	.col-6.col-md-4.col-lg-4.col-xl-3 {
		padding: 0;
	}
}
@media (max-width: 991px) {
	.category-six {
		.breadcrumb-nav {
			margin-bottom: 20px !important;
			.container.mx-auto {
				padding: 0 10px;
				.breadcrumb {
					.breadcrumb-item {
						a {
							font-size: 14px;
							line-height: 16px;
						}
					}
				}
			}
		}

		.page-content {
			.skel-shop-products {
				padding: 0;
				.menu-name h4 {
					display: block;
				}
				.menu-name {
					.widget.widget-collapsible {
						.widget-side {
							.widget-body.widget-body-filter-top {
								display: flex !important;
							}
						}
					}
					.filterr {
						display: flex;
						margin-top: 20px;
						justify-content: space-between;
						align-items: center;

						.toolbox,
						.toolbox-right {
							width: max-content;
							.toolbox-sort {
								span {
									font-size: 14px;
									line-height: 16px;
									text-transform: capitalize;
								}
								.sort {
									display: none;
								}

								.select-custom {
									#selected-value {
										span {
											font-size: 14px;
											line-height: 17px;
										}
									}
									.options {
										.option {
											span {
												font-size: 14px;
												line-height: 17px;
											}
										}
									}
								}
							}
						}
						button {
							display: flex;
							padding: 9px 15px;
							img {
								width: 20px;
							}
							span {
								font-size: 16px;
							}
						}
					}
				}
				.banners {
					margin-bottom: 0;
					.col-12.col-md-6 {
						margin-bottom: 20px;
					}
				}
				.products {
					margin-top: 20px;
				}

				.menu-name {
					margin-bottom: 0;
					.widget.widget-collapsible.first {
						.widget-side {
							.widget-body {
								.widget.widget-collapsible {
									.widget-title {
										margin: 0 !important;
										a {
											white-space: nowrap;
											padding: 5.5px 10px;
											margin-right: 5px;
											font-size: 16px;
											line-height: 19px;
											border-radius: $border-item;
											background: $third-color;
											font-weight: normal;
											&::after {
												display: none;
											}
										}
									}
									.widget-body {
										p {
											margin: 0;
											a {
												font-weight: normal;
												padding: 5.5px 10px;
											}
										}
									}
								}
							}
						}
					}
				}
			}
		}
		.sidebar-filter {
			max-width: 100% !important;
			.widget.widget-collapsible.first {
				display: none;
			}
			.filter-price {
				margin: 0 auto !important;
			}
			.widget-side {
				.widget-body {
					.widget.widget-collapsible {
						padding: 0;
					}
				}
			}
			.menu-name {
				padding: 20px 10px 0 10px;
				display: flex;
				justify-content: space-between;
				align-items: center;
				h4 {
					margin: 0;
				}
			}
		}
		.col-6.col-md-4.col-lg-4.col-xl-3 {
			padding: 0 10px;
			margin-bottom: 20px;
		}
	}
}
