.card-one {
	width: 100%;
	margin-bottom: 20px;

	.product-header {
		position: relative;
		height: 270px;
		border-radius: 5px;
		overflow: hidden;
		.change-image {
			display: none;
		}
		a {
			display: inline-block;
			height: 100%;
			width: 100%;
			border-radius: 5px;
			overflow: hidden;
			img {
				width: 100%;
				height: 100%;
				object-fit: content;
			}
		}

		.btn-heart,
		.btn-quickview,
		.cart-btn {
			position: absolute;
			display: flex;
			justify-content: center;
			align-items: center;
		}

		.btn-heart,
		.btn-quickview,
		.cart-btn {
			opacity: 0;
			visibility: hidden;
			transform: translateX(15px);
			transition: 0.3s ease-in-out;
		}
		.btn-heart {
			padding: 7px;
			border-radius: 50%;
			img {
				width: 25px;
			}
			top: 8px;
			right: 8px;
			transform: translateX(-15px);
		}
		.btn-quickview {
			top: 5px;
			left: 22px;
		}
		.cart-btn {
			width: 100%;
			opacity: 0;
			bottom: 0;
			padding: 5px 0;
			transform: translateY(15px);
			background-color: rgba($color: $dark-text, $alpha: 1);
			color: #fff;
			border-radius: 5px;
			font-size: 1.3rem;
			letter-spacing: -0.01em;
			&:before {
				content: "\e812";
				color: $primary-color;
				font-family: molla;
				margin-right: 10px;
				font-size: 18px;
				transition: 0.3s ease-in-out;
			}
			&:hover {
				background: $primary-color;
				&:before {
					color: #fff;
				}
			}
		}
	}

	.body-product {
		padding: 10px 0;
		.title-product,
		.price-product {
			font-weight: 500;
			font-size: 16px;
			color: $dark-text;
			padding: 0 10px;
			margin: 5px 0;
			text-align: left;
			a {
				display: block;
				width: 100%;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
			}
		}
		.price-product {
			flex-direction: row-reverse;
			justify-content: flex-end;
			align-items: center;
		}
	}
	.product-supplier {
		border-top: 1px solid #f2f2f2;
		padding: 10px;
		.supplier-img {
			height: 150px;
			width: 150px;
			margin: 0 auto;
			border-radius: 10px;
			overflow: hidden;
			img {
				width: 100%;
				height: 100%;
				object-fit: contain;
			}
		}
		p {
			text-align: center;
			margin-top: 20px;
			font-size: 16px;
		}
	}
	&:hover {
		.cart-btn,
		.btn-heart,
		.btn-quickview {
			transform: translateX(0);
			visibility: visible;
			opacity: 1;
		}
	}
}

@media (max-width: 1440px) {
	.card-one {
		.product-header {
			height: 200px;
		}
	}
}
@media (max-width: 991px) {
	.card-one {
		border: 1px solid #e5e8eb;
		border-radius: 8px;
		border-bottom: 1px solid #e5e8eb !important;
		.product-header {
			height: 170px !important;
			.btn-heart,
			.cart-btn,
			.btn-quickview {
				opacity: 1;
				visibility: visible;
				transform: translateX(0);
			}
			.cart-btn {
				font-size: 11px;
				&:before {
					display: none;
				}
			}
		}

		.body-product {
			padding: 5px 0;
			.title-product,
			.price-product {
				padding: 0 5px;
			}
			.title-product a,
			.price-product {
				font-size: 1.2rem;
				line-height: 1.25;
			}
		}
		.product-supplier {
			.supplier-img {
				width: 130px;
				height: 130px;
			}
			p {
				font-size: 12px;
			}
		}
	}
}
