.services-two {
	.big-service-wrapper,
	.small-service-wrappers {
		a {
			position: relative;
			display: block;
			.info {
				position: absolute;
				padding: 0 12px;
				display: flex;
				justify-content: space-between;
				align-items: center;
				width: 100%;
				bottom: 8px;
				.heading {
					margin: 0;
					font-weight: bold;
					font-size: 18px;
					line-height: 21px;
					color: $light-text;
				}
				img {
					width: 35px;
				}
			}
		}
	}
	.small-service-wrappers {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
}
