.order-details-page {
	animation-name: none !important;
}
#order-details-two {
	min-height: auto;
	top: 0 !important;
	left: 0;
	right: 0;
	max-width: 700px !important;
	margin: 0 auto !important;
	animation-name: translateBottomToTop;
	animation-duration: 0.4s;
	animation-fill-mode: forwards;
	background-color: #fff;
	padding: 0 !important;
	z-index: 2;
	.orders_header {
		display: flex;
		align-items: center;
		padding: 10px;
		border-bottom: 1px solid $light-text;
		.exit-product-page {
			margin: 0;
		}
		h3 {
			font-size: 16px;
			line-height: 18px;
			margin: 0 auto;
			font-weight: bold;
		}
	}
	.order-content {
		.delivery-info,
		.pay-info,
		.order {
			h3 {
				padding: 20px 10px 6px 10px;
				font-size: 20px;
				line-height: 24px;
				letter-spacing: 0.2px;
				font-weight: bold;
				color: $secondary-color-dark;
				margin: 0;
			}
			.location,
			.delivery-type,
			.comment,
			.paying {
				padding: 10px;
				margin: 0;
				border-bottom: 1px solid $light-text;
				display: flex;
				color: $secondary-color-dark;
				align-items: center;
				width: 100%;
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;
				.icon-angle-right {
					margin: 0;
				}
				img {
					margin-right: 8px;
				}
				button {
					display: flex;
					width: 100%;
					justify-content: space-between;
					align-items: center;
					padding: 0;
				}
				span {
					color: $secondary-color;
				}
			}
		}
		.order {
			h3 {
				margin: 12px 0;
			}
			.orders {
				padding: 10px;
				.widget-body {
					border-radius: $border-item;
					border: 1px solid $light-text;
					.sell_group {
						.info_sell,
						.address {
							background: #f6f6fb;
							padding: 8px 12px;
							display: flex;
							align-items: center;
							justify-content: space-between;
							p {
								font-size: 14px;
								line-height: 18px;
								letter-spacing: 0.2px;
								font-weight: bold;
								color: $secondary-color-dark;
								margin: 0;
							}
							.address {
								background: unset;
								padding: unset;
								font-style: italic;
								font-weight: normal;
							}
						}
						.sell_items {
							.cart-item {
								padding: 12px !important;
								border-bottom: 1px solid $light-text;
								.cart-product-card {
									.product-media-wrapper {
										position: relative;
										border-radius: $border-item;
										border: 1px solid $light-text;
										margin-right: 20px;
										.product-media::before {
											background: transparent;
										}
										img {
											object-fit: contain;
										}
										.count_order {
											position: absolute;
											right: -8px;
											top: -8px;
											border-radius: 50%;
											background: #ff6969;
											color: $body-bg;
											width: 16px;
											height: 16px;
											z-index: 3;
											display: flex;
											align-items: center;
											justify-content: center;
											font-size: 10px;
											line-height: 12px;
										}
									}
									.cart-product-info {
										padding: 0;
										margin: 0;
										.cart-item-infoblock {
											margin: 0;
											a {
												font-size: 14px;
												line-height: 18px;
												letter-spacing: 0.2px;
												color: $secondary-color-dark;
											}
											.cart-variation-pr {
												margin-bottom: 12px;
											}
											.cart-variation-pr span,
											.cart-amount-product,
											.new-price,
											.old-price {
												font-size: 12px;
												line-height: 16px;
												color: $secondary-color;
											}
											.cart-amount-product,
											.new-price {
												color: $secondary-color-dark;
											}
											.old-price {
												margin-bottom: 2px;
												text-decoration: line-through;
											}
										}
									}
								}
							}
						}
					}
				}
			}
		}
		.info_sell_price {
			padding: 12px 10px;
			div,
			b {
				width: 100%;
				display: flex;
				align-items: center;
				justify-content: space-between;
				p {
					color: $secondary-color-dark;
					font-size: 16px;
					line-height: 20px;
					letter-spacing: 0.2px;
					margin-bottom: 8px;
				}
			}
		}
	}
}
