.card-two {
	border-radius: 30px;
	background: #fff;
	padding: 18px 16px;
	width: 100%;
	.product-header {
		height: 300px;
		border-radius: 10px;
		overflow: hidden;
		position: relative;
		.splide,
		.splide__track,
		.splide__slide {
			padding: 0;
			height: 100%;
			li {
				padding: 0;
				width: unset;
			}
		}
		.splide__arrows {
			display: none;
		}
		.change-image {
			display: none;
		}
		a {
			img {
				width: 100%;
				object-fit: cover;
				height: 100%;
			}
			.image_pagination_wrapper {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				padding: 10px;
				display: flex;

				.other_image {
					position: relative;
					flex-grow: 1;
					margin: 1px;
					&::before {
						position: absolute;
						margin: 0 5px;
						left: 0;
						bottom: 5px;
						right: 0;
						height: 3px;
						border-radius: 5px;
						background-color: #c3c3c3;
						content: " ";
					}

					&.active {
						&::before {
							background: $light-text;
							border: 1.92775px solid $primary-color;
						}
					}
				}
			}
		}
		.favourites-button,
		.compare-button {
			position: absolute;
			right: 0;
			z-index: 2;
			transition: 0.3s;
			svg {
				width: 35px;
				height: 35px;
			}
		}
		.favourites-button.choosen {
			transform: translateX(180deg);
			svg {
				path {
					fill: #e41a1a;
				}
			}
		}
		.favourites-button {
			svg {
				path {
					fill: $primary-color;
				}
			}
		}
	}
	.body-product {
		.title-product {
			margin: 14px 0px;
			a {
				font-size: 14px;
				line-height: 18px;
				color: #758399;
				text-align: left;
			}
		}
		.brand {
			justify-content: flex-start;
			min-height: unset;
			margin-bottom: 8px;
			span {
				margin-left: 6px;
				font-weight: bold;
				font-size: 14px;
				line-height: 18px;
			}
		}

		.price-with-cart {
			display: flex;
			justify-content: space-between;
			align-items: center;
			.price-product {
				line-height: 1;
				.price-new {
					font-weight: bold;
					font-size: 16px;
					line-height: 1;
					color: #002930;
				}
			}
			.action-product {
				display: flex;
				justify-content: center;
				align-items: center;

				button.btn-cart {
					&:before {
						display: none;
					}
				}
				.product-details-quantity {
					background: #f7fdd4;
					border-radius: 34.4695px;
					.form-control.input-changer-count {
						height: 100%;
						border: none;
						align-items: center;
						padding: 0;
						input {
							background: transparent;
							font-weight: bold;
							font-size: 20.6817px;
							line-height: 26px;
						}
						span {
							&:first-child {
								margin-left: 10px;
							}
							&:last-child {
								margin-right: 10px;
							}
							font-size: 20px;
						}
					}
				}
			}
			.price-product.price-with-discount {
				display: flex;
				flex-direction: column;
				padding: 8px 11px;
				background: #ffee51;
				border-radius: 20px;
				.price-new {
					color: #e41a1a;
				}
				.price-old {
					font-size: 14px;
					line-height: 1;
				}
			}
		}
	}
}

@media (max-width: 991px) {
	.card-two {
		.product-header {
			a {
				.image_pagination_wrapper {
					display: none;
				}
			}
			.change-image {
				display: flex;
				span {
					position: absolute;
					top: 40%;
					left: -10px;
					i {
						padding: 20px;
					}
				}
				.right {
					left: unset;
					right: -10px;
				}
			}
			.splide__arrows {
				display: block;
				.splide__arrow {
					background-color: #04a0e600 !important;
					width: 20px;
					height: 20px;
					box-shadow: 0px 2px 7px rgb(0 0 0 / 10%);
					svg {
						width: 15px;
						path {
							fill: $primary-color;
						}
					}
				}
				.splide__arrow--prev {
					left: 1rem;
				}
				.splide__arrow--next {
					right: 1rem;
				}
			}
			.splide__pagination {
				display: none;
			}
		}
		.body-product {
			.brand {
				span {
					font-size: 8.53558px;
					line-height: 10px;
				}
				img {
					width: 12px !important;
				}
				.name-supplier {
					margin-left: 2px;
				}
			}
		}
		.product-details-quantity {
			border-radius: 10px !important;
			max-width: max-content;
			.form-control.input-changer-count {
				input {
					max-width: 20px !important;
					font-size: 12px !important;
					line-height: 10px !important;
				}
				span {
					&:first-child {
						margin-left: 2px !important;
					}
					&:last-child {
						margin-right: 2px !important;
					}
					font-size: 12px !important;
				}
			}
		}
	}
}
