.order-details-page {
	animation-name: none !important;
}
#order-details {
	min-height: auto;
	top: 0 !important;
	left: 0;
	right: 0;
	max-width: 700px !important;
	margin: 0 auto !important;
	animation-name: translateBottomToTop;
	animation-duration: 0.4s;
	animation-fill-mode: forwards;
	background-color: #fff;
	padding: 10px 15px !important;
	z-index: 2;
	.modal-content {
		box-shadow: none;
	}
	&::-webkit-scrollbar {
		width: 1px !important;
	}
	.info_order {
		height: 100% !important;
		overflow: auto;
		border-radius: 5px;
		box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.07);
	}
	.orders_header {
		display: flex;
		align-items: center;
		margin-bottom: 20px;
		.exit-product-page {
			color: $primary-color !important;
			font-size: 20px;
			margin: 0;
			i::before {
				font-weight: bold;
			}
		}
		h3 {
			flex-grow: 1;
			text-align: center;
			font-weight: bold;
			margin: 0;
		}
	}
	.delivery_type {
		width: 100%;
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		padding: 20px;
		img {
			width: 22px;
			height: 17px;
			object-fit: contain;
			margin-right: 5px;
		}
		p {
			color: black;
			margin: 0;
			font-weight: bold;
		}
		.delivery_name {
			font-size: 20px;
			line-height: 20px;
		}
		.delivery_price {
			font-style: italic;
			font-size: 16px;
			line-height: 1;
			opacity: 0.5;
		}
	}
	.sell_group {
		height: 100% !important;
		overflow: auto;
		.info_sell {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 13px 20px;
			color: black;
			font-weight: bold;
			background-color: #efefef;

			p {
				line-height: 21px;
				font-size: 17px;
			}
			div {
				span {
					font-weight: normal;
					font-style: italic;
				}
			}
			.sell_location {
				display: flex;
				align-items: center;
				margin-left: auto;
				width: 65%;
				span {
					overflow: hidden;
					white-space: nowrap;
					text-overflow: ellipsis;
				}
			}
		}
	}
	.sell_items {
		height: 100% !important;
		overflow: auto !important;
		padding: 10px 0 !important;
		.cart-item {
			padding: 10px !important;
			border-bottom: 1px solid #eee;
			&:last-child {
				border: none;
			}
			.cart-product-card {
				.product-media-wrapper {
					width: 100%;
					position: relative;
					.product-media {
						width: 100%;
						height: 140px;
						img {
							width: 100%;
							height: 100% !important;
							display: block;
							border: 1px solid #eee;
							border-radius: 5px;
							object-fit: contain;
						}
					}
					span {
						position: absolute;
						top: 0;
						right: 0;
						transform: translate(20%, -20%);
						background-color: #ff6969;
						z-index: 999;
						border-radius: 50%;
						width: 20px;
						height: 20px;
						display: flex;
						align-items: center;
						justify-content: center;
						color: #fff;
					}
				}
				.cart-item-infoblock {
					margin: 0;
					& > div {
						width: 100%;
					}
					a {
						text-align: left;
						display: block;
						overflow: hidden;
						text-overflow: ellipsis;
						font-size: 20px;
						line-height: 26px;
						margin-bottom: 11px;
					}
					.cart-variation-pr {
						display: flex;
						align-items: center;
						flex-wrap: wrap;
						font-size: 16px;
						line-height: 19px;
						color: #7a7a7a;
						margin-bottom: 32px;
					}
					.cart-amount-product {
						font-weight: bold;
						font-size: 20px;
						line-height: 20px;
						color: #000000;
					}
					.short-line-shimmer {
						width: 90%;
						height: 20px;
					}
				}
			}
		}
	}
	.summary {
		padding: 0;
		margin-top: 20px;
		border: none;
		.table-wrapper {
			padding: 20px;
			background-color: #f8f8f8;
			border-radius: 10px;
			.table {
				div {
					display: flex;
					align-items: center;
					justify-content: space-between;
					p {
						font-weight: 400;
						color: black;
						font-size: 20px;
						line-height: 20px;
					}
				}
				.summary-subtotal.discount p:last-child {
					color: #e27d7d !important;
				}
			}
			.location {
				padding: 16px;
				border-radius: 20px;
				background-color: #fff;
				p {
					font-size: 18px;
					line-height: 20px;
					margin: 0;
					color: black;
				}
			}
			.payment {
				display: flex;
				align-items: center;
				justify-content: space-between;
				padding: 15px 0 0;
				margin-top: 25px;
				border-top: 1px dashed #d4d4d4;
				p {
					margin: 0;
					color: black;
					display: flex;
					align-items: center;
					font-size: 20px;
					line-height: 20px;
					img {
						margin-right: 5px;
						width: 100px;
						height: 50px;
						display: block;
						object-fit: contain;
					}
				}
			}
		}
	}

	.onlyy-desktop {
		display: flex;
	}
	.onlyy-mobile {
		display: none;
	}
}

@media (max-width: 991px) {
	///////////////////////order-detail-modal-mobile//////////////////////////

	#order-details {
		.orders_header {
			margin-bottom: 10px;
			.exit-product-page {
				font-size: 16px;
			}
		}
		.delivery_type {
			padding: 15px 10px;
			img {
				width: 16px;
				height: 12px;
			}
			.delivery_name {
				font-size: 16px;
				line-height: 18px;
			}
			.delivery_price {
				font-size: 14px;
				line-height: 15px;
			}
		}
		.sell_group {
			.info_sell {
				padding: 10px 15px;
				p {
					line-height: 16px;
					font-size: 14.8286px;
				}
			}
		}
		.sell_items {
			.cart-item {
				.cart-product-card {
					.product-media-wrapper {
						.product-media {
							height: 80px;
						}
						span {
							width: 15px;
							height: 15px;
							font-size: 12px;
						}
					}
					.cart-item-infoblock {
						a,
						.cart-amount-product {
							font-size: 14px;
							line-height: 20px;
							margin-bottom: 8px;
						}
						.cart-variation-pr {
							font-size: 12px;
							line-height: 15px;
							margin-bottom: 14px;
						}
						.cart-amount-product {
							margin-bottom: 0;
						}
					}
				}
			}
		}
		.summary {
			.table-wrapper {
				padding: 10px;
				.table {
					div {
						p {
							font-size: 14px;
							line-height: 14px;
						}
					}
				}
				.location {
					padding: 15px;
					p {
						font-size: 14px;
						line-height: 14px;
					}
				}
				.payment {
					margin-top: 15px;
					p {
						font-size: 14px;
						line-height: 18px;
						img {
							width: 50px;
							height: 25px;
						}
					}
				}
			}
		}
	}
	.new-profile-style {
		.page-content {
			.dashboard {
				.content-container {
					display: block !important;
					.info_user.old-user-info {
						// margin-bottom: 20px;
						display: flex;
						overflow-x: auto;
						flex-wrap: nowrap;
					}
					.nav.nav-dashboard.flex-column.mb-3.mb-md-0 {
						.profile-info {
							.col-12.p-5.col-md-3 {
								margin-bottom: 80px;
								min-width: 100%;
								.row.info_user.new-user-info {
									display: none !important;
								}
								.profile-dropdown-menu {
									.profile-dropdown-menu-list {
										margin-top: 10px;
										ul {
											display: flex;
											padding: 10px !important;
											overflow-x: auto !important;
											flex-direction: nowrap !important;
											position: absolute;
											li {
												margin-right: 12px;
											}
											li:last-child {
												margin-right: 0;
											}
										}
									}
								}
							}
							.col-12.col-md-9 {
								padding: 0 10px;
								min-width: 100%;
								margin-top: 10px;
								.tab-pane {
									padding: 0;
									.info_support {
										text-align: center;
										padding: 10px;
									}
									.dashboard-sells-groups {
										.sell_title {
											font-size: 20px;
										}
										.sells-group {
											.sell_group_info_header {
												padding: 10px !important;
											}
										}
									}
									.purchases-block.mt-2 {
										.link {
											display: none;
										}
										.purchased-item {
											.col-6 {
												padding-right: 0;
												align-items: center;
											}
										}
									}
									.user_addresses_page {
										p,
										.new_address_add {
											margin-top: 0 !important;
											.add_address_btn {
												button {
													border-radius: 10px;
												}
											}
										}
										.new_address_add {
											margin-top: 20px !important;
										}
									}
								}
							}
						}
					}
				}
			}
		}
		.only-desktop {
			display: none !important;
		}
		.only-mobile {
			display: block !important;
		}
	}
	.firmForm {
		label {
			font-size: 12px;
		}
		.btn.btn-primary {
			width: 100%;
			margin-bottom: 20px;
		}
	}
}

@keyframes translateBottomToTop {
	0% {
		transform: translateY(100%);
	}
	100% {
		transform: translateY(0);
	}
}
