@import "../components/header/header_bottom_one.scss";

// @font-face {
// 	font-family: "Circe Rounded";
// 	src: url("../../assets/fonts/CirceRounded-Regular.ttf") format("truetype");
// }
//////babycenter

@font-face {
	font-family: "Nunito";
	src: url("../../assets/fonts/NunitoRegular.ttf") format("truetype");
}
////kidpoint

body {
	background-color: $body-bg !important;
	&::-webkit-scrollbar {
		width: 12px;
	}

	&::-webkit-scrollbar-thumb {
		background: $primary-color;
		border-radius: 10px;
	}
	// font-family: "Circe Rounded", sans-serif !important;
	font-family: "Nunito", sans-serif !important;
}

.header-six {
	background: transparent !important;
	.header-top {
		background: $header-top;
		.d-flex {
			align-items: center;
			img {
				margin-right: 5px;
				width: 22px;
				display: inline-flex;
			}
		}
		.lang-selector {
			width: max-content;
		}
	}
	.header-middle.sticky-header {
		padding: 20px 0;
		.only-desktop.container {
			.header-left {
				.logo {
					width: 155px;
					& > div {
						width: unset !important;
						height: unset !important;
					}
				}
				.header-six-ver-menu {
					display: none;
				}
			}
			.header-center {
				display: none;
			}
			.header-right {
				.profile-link,
				.choosen-products-icon,
				.dropdown-toggle.d-flex.flex-column {
					flex-direction: row !important;
					h6 {
						margin: 0 0 0 8px;
					}
					.cart-count,
					span {
						border: 1px solid $light-text;
						top: -5px;
						right: -5px;
					}
				}
			}
		}
	}
	.header-middle.sticky-header.fixed {
		padding: 20px;
	}

	.dropdown-menu.dropdown-menu-right p,
	.dropdown-cart-products p {
		margin: 20px;
	}
	.cart-dropdown {
		.dropdown-cart-action .btn {
			font-size: 16px;
		}
	}
}

.cart.cart-one {
	.cart-block-main {
		border: 1rem solid #f5fcfd;
		background-color: white;
		a.text-dark {
			line-height: 1.2;
		}
		.cart-price-perone {
			line-height: 1;
			color: #262626;
		}
	}
	.summary {
		border: 1rem solid #f5fcfd !important;
		background-color: white;
	}
	.summary-shipping-estimate {
		font-size: 14px !important;
	}
	.btn {
		border-radius: $border-radius-buttons;
		font-size: 16px !important;
	}
	.btn-order {
		background-color: $primary-color;
		color: white;
		&:hover {
			color: $primary-color;
			background-color: #d3f1ff;
		}
	}
}
.usercard-modal {
	background: $light-text;
	padding: 20px;
	.cash-div {
		button {
			color: $light-text !important;
		}
	}
}
.checkout_two,
.my-modal-content {
	padding-top: 24px !important;
	min-height: 60vh;

	.checkout_header {
		display: none !important;
	}

	.cart-two,
	.modal-content {
		.cart-block-main,
		.order-content {
			border: none;
			border-radius: 10px;

			.filter-item,
			.info-order-item {
				.widget-collapsible {
					margin-bottom: 25px;
				}

				.cart_delivery_info {
					padding: 20px 22px;
					margin: 0 0 20px 0;
					border-radius: 10px 10px 0 0;
					background: linear-gradient(
						277.06deg,
						#00b2ff -36.68%,
						#5200ff 229.66%
					);

					.right {
						i::before {
							color: #ffffff;
						}
					}

					.left_top,
					.delivery_type_left {
						h4 {
							color: #ffffff;

							img {
								filter: brightness(0) invert(1);
								width: 22px;
							}

							p {
								font-weight: bold;
								margin: 0 8px !important;
							}
						}

						span {
							font-style: italic;
							font-weight: bold;
							font-size: 16px;
							line-height: 19px;
							color: #ffffff;
							opacity: 0.5;
						}
					}

					.left_bottom {
						margin-top: 10px;

						img {
							filter: brightness(0) invert(1);
							margin-right: 13px;
							width: 16px;
						}

						p,
						span {
							font-size: 16px;
							line-height: 21px;
							color: #ffffff;
							opacity: 0.5;
						}
					}
				}

				.sell_group {
					.info_sell {
						background: #efefef;
						padding: 13px 22px;
						display: flex;
						justify-content: space-between;

						p {
							font-weight: bold;
							font-size: 17.8286px;
							line-height: 21px;
							color: #000000;
						}

						.sell_location {
							i,
							span {
								color: #000000;
								font-style: italic;
								font-size: 16px;
								line-height: 21px;
							}
						}
					}
				}

				.cart-supplier-info,
				.sell_items {
					padding: 0 !important;
					border-bottom: 1px solid #eaeaea;

					.cart-product-card {
						// align-items: center !important;
						.count_order {
							top: -10px;
							right: 0;
							color: #ffffff;
							border-radius: 50%;
							z-index: 1515;
							display: flex;
							align-items: center;
							justify-content: center;
							position: absolute;
							background: #ff6969;
							width: 27px;
							height: 27px;
						}

						padding: 20px 16px;
						align-items: start;

						.product-media-wrapper {
							.product-media {
								&::before {
									background-image: unset;
									background-color: unset;
								}

								border-radius: 11px;
								border: 1.0885px solid #e6e6e6;

								img {
									object-fit: contain !important;
									height: 100% !important;
								}
							}
						}

						.cart-product-actions {
							justify-content: end;

							.input-changer-count {
								flex-direction: column !important;
								border: none;
								width: auto;
								height: auto;

								span,
								input {
									min-height: 35px;
									width: 35px !important;
									display: flex;
									align-items: center;
									justify-content: center;
									color: $primary-color;
									font-size: 18px;
									line-height: 21px;
									border-radius: 9px;
									border: 1px solid $primary-color;
									font-weight: 600;
								}

								input {
									color: #000000;
									background: unset;
									margin: 6px 0;
									font-size: 14px;
									font-weight: 600;
								}
							}

							.btn-remove {
								display: none;
							}
						}
					}

					.cart-product-info {
						.cart-item-infoblock {
							margin-top: 0;

							a {
								font-size: 16px;
								line-height: 26px;
							}

							.cart-variation-pr {
								margin: 11px 0 12px 0;

								span {
									font-size: 14px;
									line-height: 19px;
									color: #7a7a7a;
								}
							}

							.cart-amount-product {
								font-weight: bold;
								font-size: 18px;
								line-height: 26px;
							}
						}
					}
				}
			}
		}

		.orders_header {
			.exit-product-page i,
			h3 {
				font-weight: bold;
			}
		}

		.orders_header,
		.order-content {
			display: flex;
			padding: 20px 20px;

			h3 {
				margin: 0 auto;
			}
		}

		.info-order-content {
			box-shadow: 0 0 7px rgba(0, 0, 0, 0.07);
			border-radius: 10px;
			border: 1px solid #eeeeee;
			margin-bottom: 12px;
		}

		.order-content {
			border: 0;
			padding-top: 0;
		}

		.sidebar_two,
		.info_order {
			.summary {
				margin-bottom: 0;
				background: unset;
				border: none;
				border-radius: 11px;

				.table-wrapper {
					.table-summary {
						padding: 0 27px 0 20px;
					}
				}

				.summary-subtotal,
				.summary-shipping,
				.summary-shipping-estimate,
				.summary-user_card {
					display: flex;
					align-items: center;
					justify-content: space-between;
					font-size: 16px;
					line-height: 26px;
					color: #000000;
				}

				.summary-shipping-estimate,
				.location {
					background: #ffffff;
					border-radius: 20px;
					padding: 16px 24px;

					p {
						margin: 0;
					}

					img {
						width: 16px;
						filter: brightness(0) invert(0);
						opacity: 0.5;
					}
				}

				.summary-user_card {
					margin-top: 24px;
					padding-top: 24px;
					border-top: 3px dashed #d5d5d5;
				}

				.summary-add_comment {
					margin-top: 20px;
					display: flex;
					align-items: center;
					justify-content: center;
					padding: 16px;
					box-shadow: 0 0 7px rgba(0, 0, 0, 0.07);
					border-radius: 10px;

					img {
						margin-right: 16px;
						filter: brightness(0) invert(0);
					}

					p {
						font-size: 14px;
						line-height: 21px;
						margin: 0;
						color: #000000;
					}
				}

				.summary-pay-info {
					padding: 16px;
					.btn.btn-primary {
						color: #fff;

						&:hover {
							background: #fff;
							color: $primary-color;
							border-color: $primary-color;
						}
					}

					opacity: 1 !important;

					.d-flex {
						margin: 30px 10px;
						padding: 0;
						p {
							font-size: 16px;
							line-height: 26px;
							color: #000000;
							margin: 0;
						}
					}
				}
				button {
					border-radius: 10px;
					width: 100%;
					display: block;
					padding: 10px;
					font-size: 14px;
					line-height: 26px;
				}

				.pay_wrapper {
					display: none;
				}
			}
		}

		.info_order {
			.delivery_type {
				padding: 20px;

				.delivery_type_left {
					display: flex;
					align-items: center;

					.delivery_name,
					.delivery_price {
						margin: 0 0 0 8px;
						font-weight: bold;
						font-size: 22px;
						line-height: 26px;
						color: #000000;
					}

					.delivery_price {
						flex: 1;
						text-align: right;
					}
				}
			}

			.summary {
				background: #f8f8f8;

				.location {
					padding: 10px;
				}
			}
		}
	}
}

.checkout_header,
.choosen-product-list_header {
	display: none !important;
}
.home-page-seven {
	.splide__arrows {
		.splide__arrow {
			background-color: $primary-color !important;
			box-shadow: 0px 2px 7px rgba(0, 0, 0, 0.25);
			opacity: 1;
		}
	}
	.mini-slider {
		margin: 20px auto 20px auto;
		.splide.mini-slider {
			.splide__slide {
				.banner-one {
					height: 170px;
					img {
						object-fit: contain;
					}
				}
				.slider-title {
					display: none;
				}
			}
		}
	}
	.big-slider-container {
		margin: 16px auto 42px auto;
		.splide__arrows .splide__arrow {
			background-color: #04a0e600 !important;
			box-shadow: 0px 2px 7px rgb(0 0 0 / 0%);
		}
		.big-slider {
			border-radius: 20px;
			height: auto;
			overflow: hidden;
			.splide__track,
			.intro-slide {
				height: 100%;
			}
		}
	}
	.banners {
		width: 100%;
		.banner-one {
			width: 49%;
			border-radius: 20px;
			overflow: hidden;
		}
	}
	.brand-block {
		margin: 35px 0;
		ul {
			li {
				.banner-one:first-child {
					margin-bottom: 12px;
				}
				.banner-one {
					border-radius: 20px;
					overflow: hidden;
				}
			}
		}
	}

	.news-recom-container {
		margin-top: 50px;
		.news-recom {
			padding: 20px 0 36px 18px;
			border-radius: 20px;
			align-items: center;
			background: $secondary-color;
			.title {
				h3 {
					font-weight: bold;
					// font-size: 26px;
					line-height: 29px;
					color: $light-text;
				}
			}
			.news {
				.mini-card {
					.header-card {
						border-radius: 20px;
						overflow: hidden;
					}
					.body-card {
						h6 {
							line-height: 15px;
							font-size: 12px;
							text-align: center;
							margin-top: 6px;
							color: $light-text;
						}
					}
				}
			}
		}
	}
	.before-footer-text {
		margin: 13px 0 0 0;
		max-height: 150px;
		overflow: hidden;
		transition: 1s ease max-height;
	}
	.before-footer-text.active {
		max-height: max-content;
	}
	.collections-three {
		margin-top: 50px;
		.title {
			font-weight: bold;
			font-size: 26px;
			line-height: 29px;
			color: #000000;
		}
		.only-desktop {
			.product-item {
				width: 25%;
				padding: 0 10px;
				&:first-child {
					padding-left: 0;
				}
				margin-bottom: 20px;
			}
		}
	}
	.menu-banner-collection,
	.container.news-recom-container {
		.block-banner-collection-wrapper,
		.news-recom.row {
			.block-menus.col-lg-2.col-12 {
				min-width: 300px;
			}
			.col-md-3.col-0.block-banner.only-desktop {
				max-width: 260px;
			}
			.block-menu-list {
				a {
					color: $main-menu-link;
				}
			}
			.block-collection,
			.recoms {
				.only-desktop,
				.product-item {
					.card-two {
						border-radius: 15px;
						margin-bottom: 15px !important;
						padding: 11px;
						.product-header {
							height: 196px;
							.favourites-button {
								svg {
									width: 30px;
									height: 30px;
								}
							}
						}
						.title-product {
							margin: 9px 0px;
						}
						.title-product a,
						.brand span {
							margin: 0;
							font-size: 12px;
							line-height: 15px;
						}
						.price-with-cart {
							.price-product {
								.price-new {
									font-size: 14px;
									line-height: 18px;
								}
							}
							.price-product.price-with-discount {
								.price-old {
									font-size: 12px;
								}
							}
						}
						.action-product {
							.btn-cart {
								img {
									width: 30px;
								}
							}
						}

						.product-details-quantity {
							border-radius: 10px;
							max-width: max-content;
							.form-control.input-changer-count {
								input {
									max-width: 40px;
									font-size: 16px;
									line-height: 10px;
								}
								span {
									&:first-child {
										margin-left: 10px;
									}
									&:last-child {
										margin-right: 10px;
									}
									font-size: 16px;
								}
							}
						}
					}
				}
			}
			.recoms {
				.splide {
					.splide__arrows {
						opacity: 0;
						transition: 0.2s ease-in-out;
						.splide__arrow {
							width: 72px;
							height: 72px;
							opacity: 0.5;
							transition: 0.2s;
							box-shadow: 0px 2px 7px rgba(0, 0, 0, 0.25);
							svg {
								display: none;
							}
							background: {
								image: url("../../assets/images/icons/babycenter/arrow.png");
								position: center center;
								repeat: no-repeat;
								size: 40px;
							}
							&:hover {
								opacity: 1;
							}
						}

						.splide__arrow.splide__arrow--prev {
							transform: rotate(180deg) translateY(50%);
						}
						button:disabled,
						button[disabled] {
							display: none;
						}
					}
					&:hover {
						.splide__arrows {
							opacity: 1;
						}
					}
				}
				.card-two {
					.splide__arrows {
						display: block !important;
						opacity: 1 !important;
						button:disabled,
						button[disabled] {
							display: block;
						}
						.splide__arrow {
							transform: unset !important;
							width: 25px !important;
							height: 25px !important;
							background: unset !important;
							svg {
								display: block !important;
								path {
									fill: $primary-color;
								}
							}
						}
					}
				}
			}
		}
	}
}
.choosen-product-list {
	.choosen-product-list_body {
		.card-two {
			padding: 12px 10px;
			border-radius: 10px;
			.product-header {
				height: 196px;
			}
			.body-product {
				.title-product {
					margin: 10px 0 6px 0;
				}
			}
		}
	}
}
.search-result-page,
.choosen-product-list {
	.card-two {
		margin-bottom: 20px;
	}
}
.btn-load-more {
	&:hover {
		span,
		i {
			color: $light-text !important;
		}
	}
}
.search-result-page,
.product-page-five,
.main.category-page,
.supplier-page {
	.page-content {
		.product-details {
			.product-details-footer {
				.product-count {
					display: none;
				}
			}
		}
		.skel-shop-products,
		.relation-products {
			.toolbox {
				.toolbox-layout {
					display: none !important;
				}
			}
			.card-two {
				.product-header {
					height: 250px !important;
				}
				.body-product {
					.title-product {
						margin: 8px 0;
					}
					.price-with-cart {
						.price-with-discount {
							.price-new {
								font-size: 14px;
								line-height: 1;
							}
						}
					}
					.action-product {
						.product-details-quantity {
							max-width: 100px;
							border-radius: 20px;
							span {
								font-size: 16px !important;
								margin: 0;
							}
							input {
								font-size: 16px !important;
							}
						}
					}
				}
			}
		}
	}
}
.main.supplier-page {
	.page-content {
		.skel-shop-sidebar {
			.filter-price {
				.btn.ml-4.p-0 {
					min-width: unset;
				}
			}
		}
	}
}

.footer {
	.footer-middle {
		padding-bottom: 40px !important;
		background: $primary-color-dark;
		.footer-info {
			.widget-list {
				display: flex;
				flex-direction: column;
			}
			.widget.widget-about {
				.footer-logo {
					width: 68px;
				}
				.social-links-block.d-none,
				.widget-email.d-none,
				.widget-call.d-none {
					display: flex !important;
				}
				.widget-call.d-none {
					border: none;
					padding: 0;
					margin: 43px 0 16px 0;
				}
				.widget-email.d-none {
					a {
						font-size: 18px;
						line-height: 24px;
					}
				}
				.social-links-block.d-none {
					a {
						margin: 24px 24px 0 0;
						img {
							width: 25px;
						}
					}
				}
				.payment-methods {
					display: none;
				}
			}
		}
		.widget.contacts {
			.footer-social-links.d-flex {
				display: none !important;
			}
		}
	}
	.footer-bottom {
		.container {
			&::before {
				display: none;
			}
		}
		background: #226887 !important;
		.bottom-links,
		.copy-right {
			display: none;
		}
		.d-none {
			display: block !important;
			text-align: center;
			width: 100%;
			color: $light-text;
		}
		.footer-payments {
			display: none !important;
		}
	}
}

.card-two {
	.body-product {
		.title-product a,
		.title-product {
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
		}
		.action-product {
			.btn-cart {
				img {
					width: 30px;
				}
			}
		}
	}
}

.supplier-page {
	.category-toolbox {
		.filter-toolbox-block {
			padding: 0;
			position: relative !important;
			.filter-b {
				display: none !important;
			}
			.sort-b {
				min-width: 100% !important;
				padding: 0;
			}
