									margin: 0 auto !important;
								}
							}
							.mobile-m-name {
								text-align: center;
								position: relative !important;
								margin-top: 10px;
								overflow: hidden;
								white-space: nowrap;
								text-overflow: ellipsis;
								line-clamp: 2;
							}
						}
					}
				}
			}
		}
	}

	.footer {
		display: none;
	}
	.mobile-menu-container {
		.widget-body {
			display: none;
		}
		.mobile-menu-grid-item2.active {
			background: $primary-color;
			.mobile-m-img2,
			.mobile-m-name2 {
				img {
					filter: brightness(0) invert(1);
				}
				color: #fff;
			}
		}
		.menu-side::-webkit-scrollbar {
			display: none;
		}
		.categories-side {
			& > div {
				display: flex;
				width: 100%;
				flex-wrap: wrap;
				justify-content: space-between;
			}
			.widget.widget-collapsible {
				width: 46%;
				margin-bottom: 12px;
				height: 100px;
				display: flex;
				flex-direction: column;
				& > .d-none {
					display: flex !important;
					a {
						margin: 0 auto;
					}
					img {
						margin: 0 auto;
					}
				}
				.menu-icon,
				.d-none {
					img {
						margin: 0 auto;
						height: 70px !important;
						width: 70px !important;
					}
				}
				.widget-title {
					padding: 0;
					a {
						padding: 0 !important;
						&::after {
							display: none;
						}
					}
				}
				.widget-title,
				.widget-title a {
					font-size: 14px;
					font-weight: normal;
					display: flex;
					text-align: center;
					display: -webkit-box;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
					overflow: hidden;
					text-overflow: ellipsis;
				}
			}
		}
	}
}
