.page-pagination {
	margin-top: 16px;
	margin-bottom: 0;
	ul {
		display: flex;
		margin: 0;
		flex-wrap: wrap;
		li {
			background: #fff;
			border: 1px solid #ccc;
			border-radius: 8px;
			width: 40px;
			height: 40px;
			margin-right: 8px;
			padding: 8px;
			margin-bottom: 10px;
			&:last-child {
				margin-right: 0;
			}
			&:hover {
				cursor: pointer;
			}
			a {
				display: flex;
				align-items: center;
				justify-content: center;
				font-weight: 600;
				font-size: 16px;
				line-height: 24px;
				color: $dark-text;
				width: 100%;
				height: 100%;
			}
		}
		li.active {
			background: rgba($primary-color, 0.08);
			border: 1px solid $primary-color;
			a {
				// color: $primary-color;
			}
		}
	}
}
