@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
/* header-zero */
.header-zero {
	background-color: #fff;
	.sticky-header.fixed {
		position: fixed;
		margin-top: 0px !important;
		height: 58px !important;
		left: 0;
		right: 0;
		top: 0;
		z-index: 1040;
		animation-name: fixedHeader;
		animation-duration: 0.4s;
		background-color: #333333 !important;
		box-shadow: 0 3px 6px rgba(51, 51, 51, 0.05);
	}

	.header-top .container:after {
		background-color: #ffffff1f;
	}
	.header-left.mobile {
		display: none;
	}
	.header-bottom {
		background-color: $secondary-color-dark;
	}
	.header-left {
		margin-right: auto;
		padding-left: 10px;
		padding-right: 10px;
	}

	.header-right {
		padding-left: 10px;
		padding-right: 10px;
	}

	.header-center {
		margin: 0;
		flex: 0 0 50%;
		max-width: 50%;
	}

	.category-dropdown .dropdown-toggle {
		display: flex;
		align-items: center;
		align-self: stretch;
		color: #fff;
		font-weight: 400;
		font-size: 1.4rem;
		line-height: 1.5;
		letter-spacing: -0.01em;
		text-transform: uppercase;
		min-width: 270px;
		transition: background 0.4s, color 0.1s;
		&:hover {
			background-color: $secondary-color !important;
		}
	}

	.category-dropdown .dropdown-toggle:before {
		height: 0;
	}

	.category-dropdown:not(.is-on):hover .dropdown-toggle {
		background-color: transparent;
	}

	.header-bottom .container .header-left {
		position: relative;
		&::after {
			content: "";
			position: absolute;
			top: 0;
			right: 2rem;
			bottom: 0;
			width: 0.1rem;
			background-color: #444;
		}
	}

	.menu.sf-arrows .sf-with-ul::after {
		top: 49%;
		font-weight: 300;
		color: #ebebeb;
	}

	.menu.sf-arrows li {
		.sf {
			color: white;
		}
	}
	.menu > li > a {
		color: #fff;
		font-size: 1.4rem;
		letter-spacing: -0.01em;
		padding: 3.95rem 3rem;
		text-transform: uppercase;
	}

	.menu li:hover > a,
	.menu li.show > a,
	.menu li.active > a {
		color: $primary-color;
	}

	.header-bottom .menu > li + li {
		margin-left: 0.8rem;
	}

	.header-bottom .container .header-center {
		flex: 0 0 58.33%;
		max-width: 58.33%;

		margin: 0;
		padding-left: 2rem;
	}

	.header-bottom .container .header-right {
		margin-left: -3rem;
		margin-right: auto;

		i {
			padding-right: 0.8rem;
			color: $primary-color;
		}
		a {
			text-transform: none;
			letter-spacing: 0em;
			color: #fff;
			transition: all 0.3s;

			&:hover,
			&:focus {
				color: $primary-color;
			}
		}

		position: relative;

		&::before {
			content: "";
			position: absolute;
			top: 0;
			bottom: 0;
			left: -3rem;
			width: 0.1rem;
			background-color: #444;
		}
	}

	.header-bottom .menu > li > .sf-with-ul {
		padding-right: 3rem;
		font-size: 1.4rem;
		font-weight: 600;
		letter-spacing: -0.01rem;
		color: $primary-color;
	}

	.header-bottom .menu > li > a {
		padding: 1.6rem 1rem;
	}

	.header-bottom .menu > li > a:before {
		height: 0;
	}

	.menu ul,
	.menu .megamenu {
		margin-top: 0;
	}

	span.wishlist-count,
	span.cart-count {
		font-size: 1.2rem;
		font-weight: 400;
		color: #333;
	}

	.category-dropdown {
		.dropdown-menu {
			left: 0;
			width: 100%;
			border-radius: 0;
			border: none;
			padding: 0;
			box-shadow: 0 10px 30px rgba(0, 0, 0, 0.05);
		}

		.dropdown-toggle {
			padding: 1.6rem 1.9rem 1.6rem 2rem;
		}
	}

	.category-dropdown .dropdown-toggle {
		font-size: 1.4rem;
		font-weight: 700;
		letter-spacing: -0.01rem;
		color: #fff;
	}

	.category-dropdown .dropdown-toggle:before {
		content: "\f131";
		font-family: "molla";
		border: none;
		font-size: 2.3rem;
		line-height: 1;
		margin-right: 1.5rem;
		display: flex;
		align-items: center;
		font-weight: 500;
		position: relative;
		color: $primary-color;
		transition: all 0s;
	}

	.category-dropdown .dropdown-toggle:after {
		content: "\f110";
		font-family: "molla";
		border: none;
		font-size: 1rem;
		line-height: 1;
		margin-left: 3.8rem;
		margin-bottom: 0.2rem;
		font-weight: 300;
	}

	.category-dropdown.show .dropdown-toggle:before {
		content: "\f191";
	}

	.category-dropdown.show .dropdown-toggle:after {
		content: "\f110";
	}

	.menu.sf-arrows li:last-child .sf-with-ul::after {
		font-family: "molla";
		content: "\f110";
		position: absolute;
		right: 0;
		top: 50%;
		display: none;
		font-size: 1rem;
		line-height: 1;
		margin-top: -0.5rem;
	}

	.sticky-header.fixed {
		position: fixed;
		left: 0;
		right: 0;
		top: 0;
		z-index: 1040;
		animation-name: fixedHeader;
		animation-duration: 0.4s;
		background-color: #333333;
		box-shadow: 0 3px 6px rgba(51, 51, 51, 0.05);
	}

	.logo {
		margin-top: 3rem;
		margin-bottom: 3rem;
	}

	.header-top {
		.container,
		.container-fluid {
			position: relative;

			&:after {
				left: 15px;
				right: 15px;
			}
		}
	}

	.top-menu {
		a {
			font-size: 1.4rem;
			font-weight: 400;
			letter-spacing: 0;
			color: #fff;
		}

		i {
			font-size: 1.8rem;
		}
	}

	.cart-dropdown,
	.compare-dropdown {
		display: flex;
		align-self: stretch;
		align-items: center;
	}

	.compare-dropdown {
		padding-left: 0;

		.dropdown-toggle {
			font-size: 2.6rem;
		}

		.compare-product-title {
			font-weight: 400;
			font-size: 1.3rem;
			line-height: 1.3;
			color: #666;
			text-align: left;
			margin-bottom: 0.4rem;

			a {
				color: inherit;

				&:hover,
				&:focus {
					color: $primary-color;
				}
			}
		}
	}

	.compare-products {
		list-style: none;
		padding: 0;
		margin: 0;
	}

	.compare-product {
		position: relative;
		padding: 0.5rem 0;
		margin: 0;
		box-shadow: none !important;

		&:hover {
			box-shadow: none;
		}
	}
	.cart-dropdown {
		padding-left: 1.5rem;

		.cart-count {
			color: #000;
		}

		.btn-primary {
			color: white;
			background-color: $primary-color;
			border-color: $primary-color;

			&:hover,
			&:focus {
				color: $primary-color;
				background-color: white;
				border-color: $primary-color;
			}
		}

		.btn-outline-primary-2 {
			color: $primary-color;
			background-color: white;
			border-color: $primary-color;

			&:hover {
				color: white;
				background-color: $primary-color;
			}
		}
	}

	.wishlist-link {
		margin-left: 2.2rem;
		color: #fff;

		.wishlist-count {
			color: #000;
		}
	}

	/* Header Search */
	.search-toggle {
		color: #666;
	}

	.header-search {
		.search-wrapper-wide {
			width: 57.5rem;
			max-width: 100%;
		}
	}

	.header-search-extended {
		margin-right: 0;
		max-width: none;

		i {
			color: #333;
		}

		.form-control,
		select {
			font-size: 1.4rem;
			font-weight: 400;
			color: #777;
		}

		.form-control {
			height: 44px;

			padding: 1.2rem 2.4rem 1.2rem 2rem;
		}
		.btn {
			flex: 0 0 8rem;
			max-width: 8rem;
			height: 4.6rem;
		}
	}

	.mobile-menu-toggler {
		color: $body-text;
		padding-top: 0.5rem;
	}

	.header-bottom {
		.container {
			padding: 0;
		}
	}

	.container {
		max-width: 95%;
		display: flex;
		padding: 5px 0;
		align-items: center;
		justify-content: space-between;
	}
	.header-top {
		.container {
			border-bottom: 1px solid #eee;
			.header-left {
				display: flex;
				align-items: center;
				padding: 0;
				i {
					color: #777;
					margin-right: 5px;
				}
			}
			.header-right {
				font-size: 14px;
				.locationBut {
					display: flex;
					align-items: center;
					margin-right: 20px;
				}
				.change-lang {
					margin-right: 15px;
					.lang-selector {
						overflow: hidden;
						.langBut {
							background-color: #fff;
							&.active {
								background-color: $primary-color !important;
							}
						}
					}
					.icon-angle-down {
						margin-left: 5px;
					}
					img {
						display: none;
					}
				}
			}
		}
	}
	.header-middle {
		display: block !important;
		.container {
			padding: 20px 0;
			.header-left {
				padding: 0;
				.mobile-logo {
					display: none;
				}
				.logo {
					margin: 0;
				}
			}
			.header-center {
				.search-box-wrapper {
					border: 1px solid $primary-color;
					width: 100%;
					.header-search-wrapper {
						width: 100%;
						.icon-search {
							color: #fff;
						}
					}
				}
			}
			.header-right {
				display: flex;
				align-items: center;
				font-family: "Poppins";
				.dropdown {
					.dropdown-toggle .p-2 {
						height: 100%;
						position: relative;
						display: flex;
						align-items: center;
						flex-direction: column;
						padding: 0 !important;
						p {
							font-family: "Poppins", "Arial", sans-serif;
							font-size: 12px;
							margin: 0;
							color: #aaa;
						}
						img {
							width: 25px;
							height: 25px;
							margin-bottom: 5px;
						}
						span {
							position: absolute;
							top: -5px;
							right: 0;
						}
					}
				}
			}
		}
	}
	.header-bottom {
		.container {
			height: 57px;
			align-items: stretch;
			.header-left {
				min-height: 100% !important;
				padding-top: 0;
				padding-bottom: 0;
				padding-left: 0;
				&::after {
					display: none;
				}
				.dropdown {
					width: 100%;
					.dropdown-toggle {
						min-width: 100% !important;
						padding: 0 10px;
						background-color: $primary-color;
						font-size: 14px !important;
						&::before {
							color: rgb(255, 255, 255);
						}
						&::after {
							display: none;
						}
					}
					.dropdown-menu {
						height: 520px !important;
						border: 1px solid #eee;
					}
				}
			}
			.header-center {
				margin-right: auto;
				padding: 0;
				.main-nav {
					li {
						.sf-with-ul {
							color: #fff;
							white-space: nowrap;
							padding: 18px;
							font-size: 12px !important;
							font-weight: normal;
							letter-spacing: 0.5px;
							&::after {
								right: 0 !important;
							}
							&:hover {
								color: $primary-color;
							}
						}
					}
				}
			}
		}
	}

	.wrap-container.sticky-header.fixed {
		height: 56px !important;
		.header-bottom {
			.header-left {
				.dropdown {
					.dropdown-menu {
						display: none !important;
					}
					&:hover {
						.dropdown-menu {
							display: block !important;
						}
					}
				}
			}
		}
	}

	@media screen and (max-width: 1200px) {
		.header-bottom .container .header-center {
			padding: 0;
			display: flex;
			justify-content: center;
		}

		.header-bottom .container .header-left::after {
			visibility: hidden;
		}

		.header-bottom .container .header-right::before {
			visibility: hidden;
		}

		.header-bottom .menu > li + li {
			margin: 0;
		}
	}

	@media (max-width: 992px) {
		.header-top {
			.container {
				padding: 5px 0 !important;
			}
		}
		.header-middle {
			.container {
				padding: 10px 0 20px !important;
			}
		}
		.header-bottom {
			padding: 0 !important;
			display: block !important;
			.header-left,
			.header-center {
				display: none !important;
			}
			.mobile {
				display: block !important;
				i {
					color: #fff;
				}
			}
		}
		.header-middle .header-center {
			display: none;
		}
	}

	@include mq("md", "max") {
		.dropdown.category-dropdown {
			.dropdown-toggle {
				min-width: 230px;

				&:after {
					margin-left: 3.5rem;
				}
			}
		}
	}

	@include mq("sm", "max") {
		.dropdown.category-dropdown {
			.dropdown-toggle {
				min-width: 200px;

				&:after {
					margin-left: 2.3rem;
				}
			}
		}
	}
}

@media (max-width: 1200px) {
	.header-zero {
		.header-middle {
			.container {
				.header-center {
					min-width: 50% !important;
				}
			}
		}
	}
}

@media (max-width: 576px) {
	.header-zero {
		background-color: $primary-color;
		.header-top {
			.container {
				border-bottom: none;
				&::after {
					display: none;
				}
			}
			.header-left {
				display: none !important;
			}
			.header-right {
				display: flex;
				flex-direction: row;
				color: #fff;
				i::before {
					color: #fff !important;
				}
				.change-lang {
					margin-right: 0 !important;
				}
				.auth {
					display: none;
				}
			}
		}
		.header-middle {
			.container {
				display: flex;
				flex-direction: row-reverse;
				.header-left {
					margin: 0;
					.desktop-logo {
						display: none;
					}
					.mobile-logo {
						display: block;
						width: 50px;
						height: 50px;
					}
				}
				.header-center {
					max-width: unset !important;
					min-width: unset !important;

					display: block;
					flex-grow: 1 !important;
					margin-right: 20px;
					.search-box-wrapper {
						border: none;
						.search-box {
							display: block !important;
							form {
								.header-search-wrapper {
									position: static;
									left: auto;
									right: auto;
									top: auto;
									margin-top: 0;
									display: flex;
									z-index: 999;
									border: none;
									background-color: transparent !important;
									&::before {
										display: none;
									}
									input {
										background-color: rgba(
											255,
											255,
											255,
											0.35
										) !important;
										color: #fff;
										border-radius: 0 !important;
										&::placeholder {
											color: #fff;
										}
									}
									button {
										display: none;
									}
								}
							}
						}
					}
				}
				.header-right {
					display: none !important;
				}
			}
		}
		.sticky-wrapper {
			display: none !important;
		}
		.header-middle.d-none {
			display: none !important;
		}
		.header-bottom {
			display: none !important;
		}
	}
}
