.supplier-page-three {
	.location-wrapper {
		display: none !important;
	}
	.page-content {
		.slider-supplier {
			.splide {
				width: 100%;
				.img-block {
					height: 450px;
					img {
						height: 100%;
						width: 100%;
						object-fit: cover;
					}
				}
			}
		}
		.info-supplier {
			width: 95%;
			margin-top: 32px;
			margin-bottom: 52px;
			.logo-content {
				.logo {
					width: 160px;
					height: 160px;
					border-radius: 50%;
					overflow: hidden;
					img {
						object-fit: cover;
					}
					margin-right: 24px;
				}
			}
			.contanet-info {
				width: 100%;
				.supplier-name {
					font-size: 32px;
					line-height: 40px;
					color: #101010;
					margin: 0;
				}
			}

			.title-description,
			.icon-descript {
				p {
					color: #000;
					font-weight: normal;
					font-size: 18px;
					line-height: 24px;
				}
				a {
					margin-bottom: 15px;
				}
			}
			.icon-descript {
				.img-text {
					line-height: 24px;
					img {
						width: 24px;
						height: 24px;
						margin-right: 12px;
					}
					.show-number {
						padding: 8px 12px;
						font-size: 14px;
						line-height: 16px;
						color: #fff;
						background: #fd6e00;
						border-radius: 4px;
					}
				}
			}
		}
		.tab-content-header {
			background: #f2f2f2;
			.container {
				display: flex;
				align-items: center;
				margin-top: 20px;
				margin-bottom: 20px;
				div,
				a {
					padding: 12px 0;
					border-bottom: 2px solid transparent;
					margin-right: 32px;
					font-weight: 300;
					font-size: 24px;
					line-height: 32px;
					cursor: pointer;
				}
			}
		}
		.skeleton-body.skel-shop-sidebar {
			border: 1px solid #eee;
			border-radius: 16px;
			padding: 0;
			.not-top {
				display: none !important;
			}
			.filter-price,
			.filter-minCount,
			.widget.widget-clean {
				button.btn,
				.sidebar-filter-clear {
					min-width: max-content !important;
					background-color: #f4f6f8;
					padding-left: 10px !important;
					padding-right: 10px !important;
					border: 1px solid #eee !important;
				}
			}

			.filter-price,
			.filter-minCount {
				border-bottom: 1px solid #eee;
				padding: 16px;
			}
			.widget {
				margin: 0;
				padding: 16px 16px 0 16px;
				.widget-title {
					margin-bottom: 12px;
				}
				.filter-items {
					&::-webkit-scrollbar {
						display: none;
					}
				}
			}
			.widget.widget-clean {
				display: block;
				text-align: center;
				margin-bottom: 16px;
				label {
					display: none;
				}
				.sidebar-filter-clear {
					display: block;
					padding: 11px 0;
					font-size: 16px;
					line-height: 24px;
					color: #000000;
					border-radius: 8px;
				}
			}
		}

		.skeleton-body.skel-shop-products {
			.card-one {
				border: 1px solid #eee;
				border-radius: 10px;
			}
			.toolbox {
				.toolbox-center {
					.toolbox-info {
						display: none;
					}
				}
				.toolbox-layout {
					display: flex;
					align-items: center;
					flex-direction: row-reverse;

					.btn-layout,
					.filter.d-none {
						padding: 10px !important;
						border-radius: 4px;
						margin: 0 2px !important;
						background-color: #eee;
						display: flex;
						align-items: center;
						justify-content: center;
						width: 40px;
						height: 40px;
						span {
							display: none;
						}

						&.active {
							background-color: $primary-color;
						}
					}
				}
				.toolbox-right {
					margin-left: 0 !important;
					margin-right: auto !important;
					label {
						font-size: 18px;
						color: black;
					}
					.select-custom {
						display: none;
						select.form-control {
							margin-bottom: 0;
						}
					}
					ul {
						li {
							padding: 8px 15px;
							margin: 0 5px;
							border-radius: 4px;
							background-color: #eee;
							cursor: pointer;
							span {
								margin-left: 5px;
								&::before {
									color: $primary-color;
									font-weight: bold;
								}
							}
							&.active {
								background-color: $primary-color;
								color: #fff;
							}
						}
					}
				}
			}
		}
	}
}
@media (max-width: 991px) {
	.container {
		width: 100% !important;
	}
	.supplier-page-three {
		.page-content {
			.slider-supplier {
				.splide {
					.img-block {
						height: 375px;
					}
				}
			}
			.info-supplier {
				margin-top: 8px;
				margin-bottom: 32px;
				.logo-content {
					.only-desktop {
						display: none !important;
					}
					.logo {
						width: 64px;
						height: 64px;
						margin-right: 16px;
					}
				}
				.contanet-info {
					.supplier-name {
						font-size: 24px;
						line-height: 32px;
						margin: 0;
					}
					.col-12.only-mobile {
						display: flex !important;
						align-items: center;
					}
				}

				.title-description,
				.icon-descript {
					p {
						font-size: 16px;
						line-height: 24px;
					}
				}
			}
			.tab-content-header {
				.container div {
					font-size: 18px;
					line-height: 20px;
				}
			}
		}
		.react-tabs__tab-panel.react-tabs__tab-panel--selected {
			.skeleton-body.skel-shop-sidebar {
				border: none;
				.filter-close-but {
					.btn-filter-close {
						span,
						i {
							color: #fff;
						}
					}
				}
			}

			.skeleton-body.skel-shop-products {
				.product-toolbox {
					display: flex;
					align-items: center;
					flex-direction: row-reverse;
					margin-bottom: 10px;
					.toolbox {
						width: 100%;
						margin-top: 0;
						top: 0;
						margin-bottom: 0;
						position: relative;
						display: flex;
						align-items: center;
						flex-direction: unset;
						justify-content: space-between;
						.toolbox-right {
							margin: 0;
							.toolbox-sort {
								label {
									display: none !important;
								}
								select {
									border-radius: 4px;
								}
							}
						}
						.toolbox-layout {
							margin: 0;
							.filter.d-none {
								display: flex !important;
								line-height: 1;
								align-items: center;
								justify-content: center;

								span {
									display: none;
								}
								padding: 0 10px;
								border-radius: 4px;
								margin: 0 2px !important;
								background-color: #eee;
								i {
									svg {
										width: 16px;
									}
								}
							}

							.btn-layout,
							.filter.d-none {
								width: 37px;
								height: 37px;
							}
						}
					}
				}
			}
		}
		.sidebar-fixed-toggler {
			display: none !important;
		}
	}
}
