.product-page-eight {
	margin-bottom: 50px;

	.breadcrumb {
		.breadcrumb-nav {
			margin-bottom: 50px !important;
			border: none;
			.container.mx-auto {
				padding: 0 10px;
				.breadcrumb {
					&::before {
						display: none;
					}
					.breadcrumb-item {
						display: flex;
						align-items: center;
						a {
							color: $primary-color;
							display: flex;
							align-items: center;
							font-weight: 500;
							font-size: 16px;
							line-height: 19px;
						}
					}
					.breadcrumb-item:last-child {
						a {
							color: $body-text;
						}
					}
				}
			}
		}
	}
	.product-gallery.product-gallery-vertical {
		.product-main-image {
			padding: 0;
			div {
				height: 100%;
			}
		}
		.product-image-gallery {
			height: 600px;
			overflow-x: hidden;
			margin: 0;
			overflow-y: auto;
			display: block;
			&::-webkit-scrollbar {
				display: none;
			}
			.product-gallery-item {
				height: 150px;
				min-width: 100%;
				img {
					height: 100%;
					object-fit: contain;
				}
			}
		}
		.gallery-scrollable {
			width: 100%;
			.gradient-div.d-none {
				display: block !important;
				position: absolute;
				width: 100%;
				bottom: 0;
				background: linear-gradient(
					180deg,
					rgba(255, 255, 255, 0) 0%,
					#ffffff 100%
				);
				height: 30px;
				z-index: 5;
			}
		}
	}
	.product-name-infos {
		.product-title {
			font-weight: 700;
			font-size: 32px;
			line-height: 39px;
			margin-bottom: 15px;
		}
		.articul {
			h6 {
				span {
					color: $body-text;
				}
				line-height: 19px;
			}
		}
	}
	.product-details {
		.product-price {
			display: block;
			.new-price,
			.old-price {
				font-weight: 700;
				font-size: 32px;
				line-height: 39px;
				color: $secondary-color-dark;
				align-items: center;
				display: flex;
				span {
					margin-left: 10px;
					padding: 5.5px 15px;
					font-size: 20px;
					line-height: 24px;
					border-radius: $border-radius-image;
					background: $main-menu-link;
					color: $light-text;
				}
			}
			.old-price {
				display: block;
				font-size: 15px;
				line-height: 18px;
				color: $body-text;
				margin-top: 5px;
			}
		}
		.installment {
			padding: 20px;
			display: flex;
			justify-content: space-between;
			align-items: center;
			background: $third-color;
			border-radius: $border-radius-image;
			.installment-price {
				h5 {
					display: flex;
					align-items: center;
					padding-top: 13px;
					margin: 0;
					p {
						margin: 0 10px 0 0;
						font-weight: 700;
						font-size: 24px;
						line-height: 29px;
					}
					span {
						background: $primary-color;
						padding: 5.5px 15px;
						border-radius: $border-radius-image;
						font-weight: 700;
						font-size: 16px;
						line-height: 19px;
						color: $light-text;
					}
				}
			}
			.installment-btn {
				padding: 10px 40px;
				background-color: $body-bg !important;
				border-radius: $border-item;
				span,
				p {
					font-size: 24px;
					line-height: 29px;
					color: $body-text;
					font-weight: bold;
					margin: 0;
					text-transform: uppercase;
				}
				span {
					text-transform: capitalize;
					font-size: 14px;
					line-height: 16px;
					margin-bottom: 5px;
				}
				&:disabled {
					border-color: unset !important;
				}
			}
		}
		.properties {
			display: flex;
			flex-direction: column-reverse;
			margin-top: 20px;
			.details-filter-row.details-row-size {
				position: relative;
				.gradient-div.d-none {
					display: inline-block !important;
					position: absolute;
					right: 0;
					width: 30px;
					height: 100%;
					background: linear-gradient(
						90deg,
						rgba(255, 255, 255, 0) 0%,
						#ffffff 100%
					);
					z-index: 5;
				}
				label {
					font-size: 16px;
					line-height: 19px;
					color: $body-text;
					margin-bottom: 10px;
				}
				.product-nav {
					padding: 0;
					overflow: auto;
					width: 100%;
					flex-wrap: nowrap;

					&::-webkit-scrollbar {
						display: none;
					}
					.tooltip-property {
						display: none !important;
					}
					.active.properties-block {
						border-radius: $border-item !important;
						border: 2px solid $primary-color;
						color: $primary-color;
						// overflow: hidden;
					}
					.properties-block {
						border: 1px solid #f2f3f5;
						color: $body-text;
					}
					// .properties-block.color-properties {
					// 	max-width: 100px;
					// 	min-height: 150px;
					// 	min-width: 100px;
					// 	max-height: 150px;
					// }
					.properties-block.size-properties,
					.properties-block.color-properties {
						min-width: 60px;
						min-height: 60px;
						font-size: 20px;
						max-width: 60px;
						max-height: 60px;
						line-height: 24px;
						overflow: hidden;
					}
					.properties-block.color-properties {
						img {
							width: 100% !important;
							height: 100% !important;
							border-radius: unset;
						}
					}
				}
			}
		}
		.min-order {
			.text {
				margin-bottom: 10px;
				font-size: 16px;
				line-height: 19px;
				color: $body-text;
				span {
					color: $dark-text;
					span {
						color: $main-menu-link;
					}
				}
			}
		}
		.favorite-with-btn-cart {
			display: flex;
			align-items: center;
			margin-bottom: 20px;
			.cart-btn {
				display: flex;
				align-items: center;
				background: $primary-color;
				width: 43%;
				color: $light-text;
				justify-content: center;
				padding: 14px 0;
				border-radius: $border-item;
				img {
					margin-right: 12px;
					filter: brightness(0) invert(1);
				}
			}
			.favourites-button {
				margin-left: 14px;
				img {
					filter: brightness(1) invert(0.4);
				}
			}
		}
	}
	.details-footer {
		.product-cat {
			.product-cat-suppliers {
				display: flex;
				.seller-info-name {
					display: none;
				}
				.seller-info-name.d-none {
					display: flex !important;
					align-items: center;
					margin: 20px 0 10px 0;
					height: unset;
					p,
					span {
						margin: 0 !important;
						font-size: 16px;
						line-height: 19px;
						color: $dark-text;
						font-weight: normal;
					}
					span {
						color: $body-text;
					}
				}
				.supplier {
					.seller-info-img {
						width: 100px;
						height: 100px;
						border: 1px solid #f2f3f5;
						border-radius: $border-item;
						img {
							object-fit: contain;
						}
					}
				}
				.product-brand {
					margin-left: 150px;
					.supplier.brands {
						flex-direction: column;
					}
				}
			}
		}
	}
	.description-and-characterisics {
		width: 100%;
		padding: 20px 10px;
		background: $third-color;
		margin: 0 auto;
		display: flex;
		border-radius: $border-radius-image;
		.characteristics,
		.description {
			h3 {
				font-size: 24px;
				line-height: 29px;
				font-weight: bold;
				margin-bottom: 20px;
			}

			.product-desc-content {
				.content-row {
					display: flex;
					justify-content: space-between;
					align-items: center;
					p {
						white-space: nowrap;
						margin: 0;
						width: max-content;

						margin-right: 10px;
					}
					.info-content {
						margin-left: 10px;
						margin-right: 0;
					}
					.dashed {
						width: 100%;
						border-bottom: 1px dashed #899499;
					}
				}
			}
		}
		.description .product-desc-content,
		.product-desc-content p {
			font-size: 20px;
			line-height: 24px;
			color: $body-text;
		}
	}
	.relation-products {
		margin-bottom: 40px !important;
		.col-lg-2.col-md-4.col-6 {
			padding: 0;
		}
		.title {
			font-size: 24px;
			line-height: 29px;
			margin-bottom: 20px !important;
			color: $dark-text;
		}
		.splide {
			.splide__arrows {
				display: none;
			}
			.splide__track {
				padding-bottom: 30px;
			}
		}
	}
}

@media (max-width: 961px) {
	.product-page-eight {
		.breadcrumb {
			.breadcrumb-nav {
				margin-bottom: 20px !important;
				.container.mx-auto {
					padding: 0 10px;
					.breadcrumb {
						.breadcrumb-item {
							a {
								font-size: 14px;
								line-height: 16px;
							}
						}
					}
				}
			}
		}
		.product-name-infos {
			.product-title {
				font-size: 24px;
				line-height: 29px;
				margin-bottom: 10px;
			}
			.articul {
				h6 {
					margin-bottom: 10px;
					font-size: 14px;
					line-height: 16px;
				}
			}
		}
		.product-gallery.product-gallery-vertical {
			.product-main-image {
				height: 400px;
				img {
					height: 100%;
					object-fit: contain;
				}
			}
			.gallery-scrollable {
				position: relative;
				.gradient-div.d-none {
					width: 30px;
					bottom: unset;
					right: 0;
					height: 100%;
					background: linear-gradient(
						90deg,
						rgba(255, 255, 255, 0) 0%,
						#ffffff 100%
					);
				}
			}
			.product-image-gallery {
				height: auto;
				overflow-x: auto;
				overflow-y: hidden;
				display: flex;
				flex-flow: nowrap;
				.product-gallery-item {
					min-width: 100px;
				}
			}
		}
		.product-details {
			.product-price {
				.new-price,
				.old-price {
					font-size: 24px;
					line-height: 29px;
				}
				.old-price {
					font-size: 14px;
					line-height: 17px;
				}
			}
			.installment {
				flex-direction: column;
				.installment-price {
					width: 100%;
					h5 {
						justify-content: space-between;
						padding-top: 13px;
						p {
							font-size: 20px;
							line-height: 24px;
						}
						span {
							font-size: 14px;
							line-height: 17px;
						}
					}
				}
				.installment-btn {
					padding: 8.5px 0;
					margin-top: 20px;
					width: 100%;

					span,
					p {
						font-size: 20px;
						line-height: 24px;
					}
					span {
						font-size: 12px;
						line-height: 14px;
					}
				}
			}
			.properties {
				.details-filter-row.details-row-size {
					padding-bottom: 20px;
					border-bottom: 0.5px solid rgba(137, 148, 153, 0.2);
					label {
						font-size: 14px;
						line-height: 16px;
					}
					.product-nav {
						overflow: auto;
						width: 100%;
						flex-wrap: nowrap;
						&::-webkit-scrollbar {
							display: none;
						}
					}
				}
			}
			.min-order {
				.text {
					font-size: 14px;
					line-height: 16px;
				}
			}
			.favorite-with-btn-cart {
				border-bottom: 0.5px solid rgba(137, 148, 153, 0.2);
				padding-bottom: 20px;
				.cart-btn {
					width: 85%;
				}
			}
		}
	}
	.aborting-and-delivery {
		flex-direction: column;
		height: 60px;
		align-items: flex-start !important;
		justify-content: space-between;

		.aborting {
			margin-top: 30px !important;
			margin-left: 0 !important;
		}
	}
	.details-footer {
		border-top: 0.5px solid rgba(137, 148, 153, 0.2);
		margin-top: 20px;
		.product-cat {
			padding: 0;
			.product-cat-suppliers {
				flex-direction: column;
				.product-brand {
					margin-left: 0 !important;
					.supplier.brands {
						align-items: flex-start !important;
					}
				}
			}
			.seller-info-name.d-none {
				span,
				p {
					font-size: 14px;
					line-height: 16px;
				}
			}
		}
	}
	.row.w-100 {
		margin: 0 auto;
		padding: 0 10px;
	}
	.description-and-characterisics {
		margin: 0 auto;
		flex-wrap: wrap;
		// .characteristics,
		// .description {
		// 	.product-desc-content {
		// 	}
		// }
		.col-md-6 {
			position: relative;
			.gradient-div {
				position: absolute;
				width: 100%;
				height: 60px;
				background: linear-gradient(
					180deg,
					#f9f9fa 18.23%,
					rgba(249, 249, 250, 0) 100%
				);
				transform: matrix(1, 0, 0, -1, 0, 0);
				z-index: 2;
				bottom: 0;
			}
		}
		.characteristics {
			margin-top: 30px;
		}
		.description .product-desc-content,
		.product-desc-content p {
			font-size: 14px !important;
			line-height: 16px !important;
			margin: 0;
		}
		span.show-all {
			font-size: 14px;
			line-height: 16px;
			color: $primary-color;
			text-align: center;
			text-decoration: dotted;
			display: block;
			z-index: 3;
			position: absolute;
			left: 0;
			right: 0;
			bottom: -10px;
		}
	}
	.relation-products {
		.col-lg-2.col-md-4.col-6 {
			padding: 10px !important;
		}
	}
}
