.footer-one {
	padding-bottom: 100px !important;
	.footer-payments {
		display: flex;
		align-items: center;
		justify-content: space-between;
		img {
			width: 50px;
			height: 50px;
			margin: 0 10px;
			object-fit: cover;
		}
	}
}
@media (max-width: 767px) {
	.footer-one {
		.widget-call {
			border: none !important;
			display: block;
			a {
				font-size: 14px;
			}
		}
	}
}
