#cashback-modal {
	height: 100%;
	margin: 0 auto;
	.modal-content {
		position: absolute;
		padding: 30px 15px;
		height: 100%;
		justify-content: space-around;
		overflow: unset;
		top: 0;
		bottom: 0;
		border-radius: 0 !important;
		background: $primary-color;
		.closer {
			position: absolute;
			top: 24px;
			right: 24px;
		}
		.show-code {
			// margin-top: 40px;
			h2,
			h3 {
				font-weight: 700;
				font-size: 36px;
				line-height: 44px;
				margin-bottom: 0;
			}
			h3 {
				margin-bottom: 0;
				font-size: 18px;
				line-height: 22px;
			}
		}
		.qr_code {
			display: flex;
			align-items: center;
			background: $light-text;
			border-radius: 20px;
			padding: 10px;
			// margin-bottom: 25px;
			.qr {
				padding: 10px;
				width: 65%;
			}

			.br {
				width: 35%;
				transform: rotate(90deg);
			}
		}
		p {
			font-size: 15px;
			line-height: 18px;
			width: 300px;
			margin: 0 auto;
		}
		h3,
		h2,
		p {
			text-align: center;
			color: $light-text;
		}
		.usercard {
			// margin-top: 23px;

			position: relative;
			padding: 30px 20px;
			background: rgba(255, 255, 255, 0.1);
			border: 2px solid rgba(255, 255, 255, 0.1);
			border-radius: 10px;
			p,
			h3 {
				z-index: 2;
				text-align: left;
			}
			h3 {
				font-weight: bold;
				font-size: 28px;
				line-height: 34px;
			}
			p {
				width: 100%;
				margin-bottom: 12px;
				color: rgba(255, 255, 255, 0.5);
			}
			img {
				z-index: 1;
				right: 40px;
				top: 40px;
				position: absolute;
			}
		}
	}
}
