.product-page-homeplus {
	.page-wrapper {
		.main {
			.page-content {
				.product-details-top {
					.product-gallery.product-gallery-gorizontal {
						.owl-dots {
							bottom: 10px !important;
						}
					}
					.skel-pro-single.sticky {
						.col-md-5 {
							.product-gallery.product-gallery-separated {
								width: 100%;
								.product-main-image {
									.media-sticky.mt-2:first-child {
										margin-top: 0 !important;
									}
								}
							}
						}
						.col-lg-7.pl-5 {
							.product-details.sticky-content {
								.product-details {
									.details-filter-row.details-row-size {
										.product-nav {
											.active.properties-block.color-properties {
												margin: 0;
											}
										}
									}
									.product-details-action {
										align-items: flex-start;
									}
									.product-details-footer.d-block {
										.product-count {
											display: none;
										}
										.product-cat {
											.row.supliers-row {
												.col-md-12.more-supplier {
													.product-cat-suppliers {
														.suppliers_ul {
															display: none !important;
															.suppliers_list {
																overflow: hidden;
																max-height: 205px;
																transition: 0.5s !important;
															}
															.suppliers_list.active {
																max-height: 500px !important;
															}
															.suppliers_list {
																a.supplier {
																	.seller-info-name {
																		flex-direction: column;
																		justify-content: center;

																		display: flex;
																		span {
																			color: $dark-text;
																		}
																		p {
																			margin-bottom: 0;
																		}
																	}
																	.suplier-product-price {
																		display: none;
																	}
																}
															}
														}
														.product-brand {
															// margin-left: 30px;
															.supplier.brands {
																.seller-info-name.brand-name {
																	justify-content: center;
																	display: flex;
																	flex-direction: column;
																	span {
																		color: $dark-text;
																	}
																	p {
																		margin-bottom: 0;
																	}
																}
															}
														}
													}
												}
											}
										}
									}
								}
								.accordion.accordion-plus.product-details-accordion {
									display: none;
								}
								.react-tabs {
									.product-details-tab {
										.nav.nav-pills::-webkit-scrollbar {
											display: none;
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}
}

@media (max-width: 991px) {
	.product-page-homeplus {
		.page-wrapper {
			.header-one {
				.header-top {
					display: none;
				}
			}
			.product-name-back-button {
				display: flex;
				align-items: center;
				width: 97% !important;
				padding: 0 10px !important;
				margin: 0 auto;
				.back-button,
				.product-name,
				.breadcrumb-div {
					display: block !important;
				}
				.product-name {
					padding: 14px 0;
					margin: 0;
					font-weight: bold;
					font-size: 18px;
					line-height: 32px;
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
				}
				.back-button {
					margin-right: 10px;
				}
				.breadcrumb-div {
					margin: 0 0 0 auto;
					.breadcrumb-nav {
						border-bottom: none;
						margin: 0 !important;
						.breadcrumb {
							.breadcrumb-item {
								display: none;
							}
							.product-pager {
								.product-pager-link {
									button {
										display: none;
									}
									i {
										color: #000000;
										font-weight: 600;
										font-size: 20px;
									}
								}
								.product-pager-link:last-child {
									margin-left: 0px;
								}
							}
						}
					}
				}
			}
		}
		.main {
			.page-content {
				.product-details-top {
					.breadcrumb-nav {
						display: none;
					}
					.skel-pro-single.sticky {
						.col-md-6.only-mobile {
							.product-gallery.product-gallery-gorizontal {
								.product-main-image {
									.text-primary.bg-white {
										display: none;
									}
								}
								.product-image-gallery {
									position: absolute;
									bottom: 0;
									left: 0;
									right: 0;
									display: flex;
									justify-content: center;
									.product-gallery-item {
										max-width: 50px;
										height: 50px;
										padding: 0;
										&::before,
										&::after {
											width: 100%;
											height: 100%;
											left: 0;
										}
										&::before {
											border: 3px solid $primary-color;
										}
										margin-right: 16px;
									}
									.product-gallery-item:last-child {
										margin-right: 0;
									}
								}
							}
						}
						.col-lg-7.pl-5 {
							padding: 0 10px !important;
							.product-details.sticky-content {
								.product-details {
									margin-bottom: 0;
									.product-title {
										display: none;
									}
									.price-and-articul {
										.product-content {
											margin-bottom: 0;
										}
										.product-content h6 {
											font-size: 12px;
											line-height: 32px;
											color: #949494;
											margin-bottom: 0;
										}
										.product-price {
											line-height: 32px;
											font-size: 18px;
											color: #202020;
											font-weight: 600;
										}
									}
									.details-filter-row.details-row-size {
										label {
											font-size: 14px;
											line-height: 32px;
											color: #949494;
											margin-bottom: 7px;
										}
										.product-nav {
											.active.properties-block.color-properties {
												margin: 0;
											}
										}
									}
									.product-details-action {
										.home-plus-flex-div {
											width: 100%;
											display: flex;
											justify-content: space-between;
											.align-items-center.d-flex.blue-heart-button {
												margin-left: 9px;
												background: #8b8b8b;
												svg {
													path {
														fill: #fff;
													}
												}
												span {
													display: none;
												}
											}
											.btn-product.btn-cart {
												min-width: unset;
												max-width: unset;
											}
											.btn-product.btn-cart,
											.align-items-center.d-flex.blue-heart-button {
												border-radius: 0;
												padding: 15px 10px 10px 15px;
											}
										}
										.details-action-wrapper {
											display: none;
										}
										margin-bottom: 20px;
									}
									.product-details-footer.d-block {
										border-top: 0;
										.product-cat {
											.row.supliers-row {
												.col-4.mr-auto.mt-3 {
													margin-top: 10px !important;
													display: flex;
													align-items: center;
													.d-flex.mt-1 {
														margin-top: 0 !important;
													}
												}
											}
										}
									}
								}
							}
						}
					}
					.mt-5.relation-products {
						margin-top: 20px !important;
						.title.text-center.mb-4 {
							text-align: left !important;
							font-weight: bold;
							font-size: 18px;
							line-height: 32px;
							color: #000000;
							margin-bottom: 8px !important;
						}
						margin-bottom: 40px;
					}
				}
			}
		}
	}
}
