.product-page-four {
	.product-page {
		.page-content {
			.product-details-top {
				.product-gallery.product-gallery-gorizontal {
					.product-main-image {
						padding: 2px;
						border: 1px solid #e5e8eb;
						border-radius: 8px;
					}
					.product-image-gallery {
						.product-gallery-item,
						.product-gallery-item.active {
							&:before {
								border-radius: 8px;
							}
						}
					}
				}
				.order-side {
					margin-bottom: 16px;
					p {
						font-size: 12px;
						line-height: 16px;
						color: #919eab;
						margin: 0 0 8px 0;
					}
					h3 {
						font-size: 16px;
						line-height: 24px;
						b {
							margin: 0 8px;
						}
						span {
							font-size: 10px;
							line-height: 12px;
							color: #00b533;
							padding: 6px 9px;
							background: rgba(0, 181, 51, 0.16);
							border-radius: 4px;
						}
						margin: 0;
						display: flex;
						justify-content: space-between;
						align-items: center;
					}

					h4 {
						margin: 0;
						font-weight: 300;
						font-size: 12px;
						line-height: 16px;
					}
					.count-price,
					.change-div {
						border-bottom: 1px solid #e5e8eb;
						padding-bottom: 16px;
						h4 {
							margin-right: 8px;
						}
					}
					.deliver-time {
						margin: 8px 0 16px 0;
					}
				}

				.info-comp,
				.order-side {
					padding: 24px;
					border: 1px solid #e5e8eb;
					border-radius: 10px;
					button.btn,
					.buy-one-click,
					.supplier-btn {
						padding: 12px 0;
						width: 100%;
						border: 1px solid #e5e8eb;
						border-radius: 8px;
						margin-bottom: 16px;
						color: #637381;
						display: flex;
						align-items: center;
						min-width: unset;
						span {
							font-size: 14px;
							line-height: 16px;
						}
						transition: 0.3s all;

						&:hover {
							span {
								color: #ffffff;
								box-shadow: none;
							}
							background: #00b533;
							img {
								filter: brightness(0) invert(1) !important;
							}
						}
						img {
							margin-right: 8px;
						}
						&:last-child {
							margin-bottom: 0;
						}
					}
					.supplier-btn {
						border-color: #fd6e00;
						margin-top: 16px;
						span {
							color: #fd6e00;
						}
						&:hover {
							background: #fd6e00;
							color: #fff;
						}
					}
				}
				.info-comp {
					background-image: url("../images/vibo/image.svg");
					background-repeat: no-repeat;
					background-size: cover;
					padding-top: 0;
					.vsupplier {
						margin: 0 auto 16px auto;
					}
					h6 {
						font-size: 14px;
						line-height: 24px;
						text-decoration-line: underline;
					}
					.flag-time,
					.flag {
						display: flex;
						p {
							margin: 0;
							padding: 4px 10px;
							line-height: 16px;
							display: flex;
							align-items: center;
						}
					}
					.flag {
						padding: 5px !important;
						margin-right: 5px;
						align-items: center;
					}
					.year {
						margin-left: 8px;
					}
					.flag {
						padding-left: 8px;
						.PhoneInput {
							width: 20px;
							// margin-right: 5px;
							input,
							select {
								display: none;
							}
						}
					}
					.flag,
					.year {
						font-size: 12px;

						background: #e5e8eb;
						border-radius: 4px;
					}
					span {
						font-size: 12px;
						line-height: 16px;
						color: #919eab;
					}
					.time-speed,
					.live-place {
						display: flex;
						align-items: center;
						justify-content: space-between;
						p {
							color: #002930;

							font-weight: 600;
						}
						span {
							font-weight: 300;
						}
					}
					.img-content {
						p {
							display: flex;
							align-items: center;
							font-size: 14px;
							line-height: 24px;
							margin-bottom: 8px;
							img {
								margin-right: 8px;
							}
							&:last-child {
								margin-bottom: 0;
							}
						}
					}
				}
			}
			.product-details {
				border: 1px solid #e5e8eb;
				border-radius: 8px;
				.product-title {
					padding: 16px 24px 0;
					font-size: 18px;
					line-height: 24px;
					color: #002930;
				}
				.hotties-products {
					display: none;
					align-items: center;
					.hotties-title {
						padding: 4px 12px;
						font-size: 12px;
						line-height: 16px;
						background: #f4f6f8;
						border-radius: 4px;
					}
					p {
						margin: 0 0 0 24px;
						font-size: 12px;
					}
				}

				& > div:first-child {
					border-bottom: 1px solid #e4ecf3 !important;
				}
				.row.info-table {
					padding: 16px 24px 0;
					margin: 20px 0;
					.col-md-4 {
						padding: 24px 0 8px 0;
						border: 1px solid #e5e8eb;
						.count-price {
							width: fit-content;
							margin: 0 auto;
							p,
							h3 {
								font-weight: 600;
								margin-bottom: 8px;
								font-size: 18px;
								line-height: 24px;
								color: #fd4c00;
							}

							p,
							del {
								font-weight: 300;
								color: #000;
								font-weight: 300;
								font-size: 14px;
								line-height: 16px;
							}
						}
						.time-day {
							border-top: 1px solid #e5e8eb;
							text-align: center;
							p {
								margin: 8px 0 0 0;
								font-size: 12px;
								line-height: 16px;
								color: #002930;
							}
						}
					}
				}
				.row.defence {
					margin: 0;
					padding: 16px 24px 0;
					border-bottom: 1px solid #e4ecf3 !important;
					.col-12 {
						padding: 0;
					}
					.text-defence {
						font-size: 16px;
						line-height: 24px;
						color: #919eab;
					}
					.content {
						margin-left: 10%;
						p {
							display: flex;
							align-items: flex-start;
							margin-bottom: 8px;
							img {
								margin-right: 6px;
								width: 12px;
							}
							img,
							span {
								line-height: 1.3;
							}
							span {
								color: #919eab;
								b {
									color: black;
									font-weight: normal;
								}
							}
						}
					}
					.text-defence.d-flex.justify-content-between {
						img {
							display: none;
						}
					}
				}
				.row.advertising {
					margin: 0;
					padding: 16px 24px 0;
					border-bottom: 1px solid #e4ecf3 !important;
					.col-12 {
						padding: 0;
						p {
							width: 100%;
							background-color: #e4ecf3;
							border-radius: 4px;
							margin: 0;
							padding: 8px 16px;
						}
					}
				}
				.row.discount {
					padding: 16px 24px 0;
					margin: 16px 0;
					p {
						margin: 0;
						display: flex;
						align-items: center;
						font-weight: 500;
						font-size: 16px;
						line-height: 24px;
						color: #000;
						span {
							color: #00b533;
						}
						img {
							margin: 0 11px 0 15px;
							width: 18px;
						}
					}
				}
				.details-filter-row.details-row-size {
					padding: 16px 24px;
					margin: 0;
					border-bottom: 1px solid #e5e8eb !important;
					flex-flow: nowrap;
					label {
						font-weight: 300;
						color: #919eab;
						font-size: 16px;
						line-height: 24px;
						width: 170px;
						display: inline-block;
					}
					.product-nav {
						padding: 0;
						&::-webkit-scrollbar {
							display: none;
						}
						.color-properties,
						.size-properties {
							min-width: 40px !important;
							height: 40px !important;
							img {
								width: 100% !important;
								height: 100% !important;
							}
						}
					}
				}
				.row.info-content {
					margin: 0;
					padding: 16px 24px 0;
					border-bottom: 1px solid #e5e8eb !important;
					.col-12 {
						display: flex;
						padding: 16px 0;
						.title,
						span {
							font-weight: 300;
							font-size: 16px;
							line-height: 24px;
							color: #919eab;
							width: 170px;
							margin-bottom: 0;
						}
						span {
							width: unset;
						}
						.img-text {
							display: flex;
							img {
								margin-right: 8px;
							}
							b {
								margin-left: 4px;
							}
						}
						p {
							color: #002930;
							margin-bottom: 8px;
						}
						.power {
							border: 0.5px solid #00b533;
							border-radius: 4px;
							color: #002930;
							padding: 4px 8px;
							margin: 0 32px 0 0;
						}
						.details-filter-row.details-row-size {
							padding: 0;
							border: none;
							border-radius: 4px;
							.form-control {
								padding: 0 2px !important;
								display: flex;
								align-items: center;
								height: auto;
								border-radius: 5px;

								span {
									line-height: 1.2;
									background: #00b533;
									border-radius: 3px;
									width: 22px;

									color: #ffffff;
								}
								input {
									background: transparent;
								}
							}
						}

						&:last-child {
							border-bottom: none;
						}
					}
					.defence {
						.text-defence {
							font-size: 16px;
							line-height: 24px;
							color: #919eab;
						}
						.content {
							p {
								display: flex;
								align-items: center;
								margin: 0 18px 8px 0;
								img {
									margin-right: 6px;
									width: 12px;
								}
								span {
									color: #919eab;
								}
							}
						}
					}
				}
			}
			.info_result {
				width: 100%;
				margin: 0 !important;
				background-color: #f4f6f8;
				.col-12 {
					color: #000;
					padding: 16px 24px;
					border-bottom: 1px dashed #e4ecf3;
					&:last-child {
						border: none !important;
					}
					b {
						margin-left: 10%;
					}
				}
			}
			.descone-tabs {
				a,
				span {
					font-weight: bold;
					color: black;
					border-bottom: 1px solid $primary-color;
					font-size: 14px;
				}
				a {
					border-bottom: none;
				}
			}
			.descone {
				padding: 20px;
				margin-top: 50px;
				border: 1px solid #ccc;
				border-radius: 8px;
				.product-details-tab .tab-content {
					border: none;
					& > div {
						padding-left: 0;
						padding-right: 0;
					}
				}
			}
		}
		.like-supplier-products {
			margin: 25px 0;
			padding: 20px;
			border-radius: 20px;
			border: 1px solid #e5e8eb;
			.owl-item > div {
				border-right: 1px solid #e5e8eb;
			}
			.owl-item:last-child > div {
				border-right: none !important;
			}
			.title {
				margin-bottom: 25px !important;
			}
		}
	}
}
@media (max-width: 1000px) {
	.product-page-four {
		background-color: #eee;
		header {
			display: none;
		}
		.container {
			padding: 0;
		}
	}
	.product-page-four {
		.main.product-page {
			.breadcrumb-nav.mb-2 {
				display: none;
			}
			.page-content {
				padding-bottom: 20px;
				.product-details-top {
					.row {
						width: 100%;
						margin: 0 auto;
						& > .col-12,
						& > .col-md-3 {
							padding: 0;
						}
					}
					.row.descone {
						display: none;
					}
					.row.d-none {
						display: flex !important;
						div.about-product {
							p {
								font-size: 16px;
								line-height: 24px;
								color: #919eab;
							}
							span {
								font-size: 12px;
								line-height: 16px;
								color: #002930;
								margin-bottom: 8px;
								padding: 8px;
								background: #f4f6f8;
								border-radius: 4px;
							}
						}
						.btn-product.buy-one-click {
							width: 100%;
							padding: 12px;
							background: #00b533;
							border-radius: 8px;
							margin-top: 16px;
							&::before {
								display: none;
							}
							span {
								color: #fff;
								font-size: 14px;
								line-height: 16px;
								box-shadow: none;
							}
						}
					}
					.product-gallery.product-gallery-gorizontal {
						margin-bottom: 0;
						border-bottom: 1px solid #ededed;
						.product-main-image {
							padding: 0;
							border: none;
							margin-bottom: 0;
							button {
								z-index: 3 !important;
								padding: 0;
							}
						}
						.product-image-gallery {
							display: none;
						}
					}
					.search-cart-fav {
						width: 100%;
						display: flex !important;
						position: absolute;
						justify-content: space-between;
						padding: 0 10px;
						top: 20px;
						z-index: 5;
						.choosen-products-icon,
						.dropdown.cart-dropdown {
							background: rgba(14, 14, 14, 0.8);
							backdrop-filter: blur(6px);
							margin-left: 16px;
							padding: 5px;
							align-self: center;
							border-radius: 50%;
							h6 {
								display: none;
							}
							.position-relative {
								img {
									filter: brightness(0) invert(1);
								}
								span {
									position: absolute;
									top: -10px;
									right: -10px;
									display: flex;
									align-items: center;
									justify-content: center;
									min-width: 2rem;
									height: 2rem;
									border-radius: 50%;
									font-weight: 400;
									font-size: 1rem;
									line-height: 1;
									margin-left: 0.2rem;
									margin-top: 0.1rem;
									color: #fff;
									background-color: #00aa13;
									z-index: 1;
								}
							}
							.dropdown-menu {
								display: none;
							}
						}
						.search-box-wrapper {
							width: 70%;
							.header-search {
								display: block;
								.header-search-wrapper {
									display: inline-block;
									min-width: unset;
									position: unset;
									background: rgba(14, 14, 14, 0.8);
									backdrop-filter: blur(6px);
									margin: 0;
									border: 0;
									input {
										background: transparent;
										color: #ffffff;
										&::placeholder {
											color: #ffffff;
										}
									}
									.btn.search-button {
										display: none;
									}
									.d-none.search-icon {
										top: 13px;
										left: 10px;
										// width: 16px;
										display: block !important;
										position: absolute;
									}
									&::before,
									span,
									label {
										display: none;
									}
								}
							}
						}
					}

					.product-details {
						border: none;
						padding: 0;
						margin-bottom: 0;
						.product-title {
							padding: 10px 0 0 0;
						}
						& > div:first-child {
							padding: 10px !important;
							background-color: #fff;
							// border-radius: 10px;
							margin-bottom: 10px;
							// margin-top: -10px;
							// box-shadow: 0 -5px 10px #aaa;
						}
						.row.info-table {
							padding: 0 !important;
						}
						.hotties-products {
							.hotties-title {
								display: none;
							}
							p {
								&:nth-child(2) {
									margin: 0;
								}
							}
						}

						.row.defence {
							padding: 10px;
							margin: 0;
							margin-bottom: 10px;
							background-color: #fff;
							border-radius: 10px !important;
							.col-12.d-flex {
								display: block !important;
								.text-defence {
									margin-bottom: 12px;
								}
								.content {
									margin: 0;
									p {
										font-size: 12px;
									}
								}
							}
						}
						.row.advertising {
							display: none;
						}
						.row.discount {
							display: none;
						}
						.details-filter-row.details-row-size {
							display: none;
						}
						.row.info-content {
							margin: 0;
							margin-bottom: 10px;
							padding: 0;
							.col-12 {
								&:first-child {
									display: none;
								}
								display: block;
								.title,
								.text-defence {
									width: unset;
									margin-bottom: 12px !important;
								}
								div {
									p,
									span {
										font-size: 12px;
									}
								}
							}
							.col-12.d-none {
								display: block !important;
								padding: 10px 20px;
								border-radius: 10px;
								background-color: #fff;
								.color-size-add-cart {
									.colors {
										overflow-x: scroll;
										display: flex;
										align-items: center;
										margin-bottom: 10px;
										div {
											border: 0.5px solid #e5e8ec;
											border-radius: 4px;
											min-width: 48px;
											min-height: 48px;
											margin-right: 8px;
											overflow: hidden;
										}
									}
								}
								.cart-button-div.d-none {
									display: block !important;
									.btn-product.btn-cart {
										width: 100%;
										padding: 12px;
										background: #00b533;
										border-radius: 8px;
										margin-top: 16px;
										&::before {
											display: none;
										}
										span {
											color: #fff;
											font-size: 14px;
											line-height: 16px;
											box-shadow: none;
										}
									}
								}
							}
							.col-12.defence {
								display: none;
							}
							.simple-power,
							.full-power {
								display: none;
							}
						}
						.info_result {
							display: none;
						}
					}

					.order-side {
						display: none;
					}
				}
				.row.d-none {
					display: block !important;
					background-color: #fff;
					border-radius: 15px;
					padding: 10px;
					margin: 0;
					margin-top: 10px !important;
				}
				.descone,
				.descone-tabs {
					display: none !important;
				}

				.react-tabs {
					margin-top: 24px;
					.product-details-tab {
						margin-bottom: 24px;
						.nav.nav-pills::-webkit-scrollbar {
							display: none;
						}
					}
				}
				.like-supplier-products {
					background-color: #fff;
					margin-top: 12px !important;
					margin-bottom: 0;
					border-radius: 15px;
					.title {
						margin-bottom: 12px !important;
					}
					.div-shimmer {
						.photo-shimmer {
							height: 250px !important;
						}
					}
				}
			}
		}
	}
	.defence-modal,
	.variaty-modal,
	.characteristic-modal {
		.modal-content {
			position: absolute;
			bottom: 75px;
			height: fit-content;
			animation-name: bottomToTop !important;
			animation-duration: 1s;
			border-radius: 8px 8px 0 0 !important;

			@keyframes bottomToTop {
				0% {
					transform: translateY(100%) !important;
				}
				100% {
					transform: translateY(0) !important;
				}
			}
			.modal-body {
				padding: 0 !important;
				.modal-top {
					border-top-left-radius: 8px;
					border-top-right-radius: 8px;
					position: relative;
					padding: 16px;
					background: #ffe2cb;
					.closer-button {
						position: absolute;
						top: 22px;
						right: 25px;
					}
					p,
					span {
						font-weight: 600;
						font-size: 18px;
						line-height: 18px;
						letter-spacing: -0.165px;
						color: #000000;
						margin: 22px 0px 8px 0;
					}
					span {
						margin: 0;
						font-weight: normal;
						font-size: 12px;
					}
				}
				.modal-section {
					padding: 16px;
					.tooltip-property span {
						color: #fff;
					}
					p {
						font-size: 16px;
						line-height: 24px;
						color: #002930;
						margin-bottom: 8px;
					}
					span {
						font-size: 12px;
						line-height: 16px;
						letter-spacing: -0.165px;
						color: rgba(0, 0, 0, 0.7);
					}
					.steply-div {
						margin: 16px 0;

						.step {
							display: flex;
							align-items: flex-start;
							margin-bottom: 24px;
							.green-div {
								margin-right: 8px;
								height: 16px;
								width: 16px;
								border-radius: 50%;
								background: $primary-color;
							}
						}
					}
				}
			}
		}
	}
	.variaty-modal,
	.characteristic-modal {
		.modal-content {
			height: fit-content;
			.modal-body {
				.modal-top {
					background: #fff;
					text-align: center;
					p {
						margin: 5px 0 16px 0;
					}
					.variations-discounts {
						display: flex;
						.product-img {
							border: 1px solid #e5e8eb;
							border-radius: 8px;
							overflow: hidden;
							max-width: 88px;
						}
						.discounts {
							margin-left: 5px;
							flex-wrap: nowrap;
							overflow-x: scroll;
							&::-webkit-scrollbar {
								display: none;
							}
							p,
							h3,
							span {
								font-weight: 300;
								font-size: 12px;
								line-height: 16px;
								color: #000000;
								margin: 0 0 4px 0;
							}
							h3 {
								font-weight: 600;
								font-size: 16px;
								line-height: 24px;
								color: #fd4c00;
							}
							.discount-one,
							.discount-two,
							.discount-three {
								display: flex;
								align-items: center;
								justify-content: center;
							}
						}
					}
				}
				.modal-section {
					padding: 16px;
					.product-quantity {
						.details-filter-row {
							width: 100%;
							.product-details-quantity {
								p {
									font-size: 16px;
									line-height: 24px;
									color: #002930;
									margin-bottom: 25px;
								}
								max-width: unset;
								flex: unset;
								.form-control.input-changer-count {
									width: 140px;
									height: 30px;
									border: 0.5px solid #e5e8eb;
									padding: 0 !important;
									border-radius: 4px;
									input {
										color: #919eab;
									}
									span {
										display: flex;
										align-items: center;
										justify-content: center;
										padding: 11px;
										background: #00b533;
										border-radius: 3px;
										color: #fff;
										font-size: 20px;
									}
								}
							}
						}
					}
					.properties-block {
						margin-bottom: 12px !important;
					}
					.sizes {
						display: flex;
						border-top: 1px solid #e5e8eb;
						border-bottom: 1px solid #e5e8eb;
						.title {
							border-right: 1px solid #e5e8eb;
							padding: 12px 21px 12px 41px;
							p {
								font-size: 12px;
								line-height: 16px;
								color: #000000;
								margin: 0;
							}
						}
						.sizes-list {
							display: flex;
							flex-wrap: nowrap;
							overflow-x: scroll;
							&::-webkit-scrollbar {
								display: none;
							}
							.size {
								padding: 0 20px;
								span {
									width: 15px;
									display: flex;
									align-items: center;
									justify-content: center;
									height: 100%;
									color: #000000;
									border-bottom: 2px solid transparent;
								}
							}
							.size.active {
								span {
									border-bottom: 2px solid #00b533;
								}
							}
						}
					}
				}
				.modal-footer {
					padding: 16px;
					box-shadow: 0px 0px 2px rgba(145, 158, 171, 0.16),
						0px -16px 32px -4px rgba(145, 158, 171, 0.16);
					border-radius: 8px 8px 0px 0px;
					p {
						font-weight: 600;
						color: #000000;
						font-size: 16px;
						line-height: 24px;
					}
					span {
						color: #000000;
						font-weight: 300;
						font-size: 14px;
						line-height: 24px;
						span {
							color: #637381;
						}
					}
					.price-count,
					.countable-price {
						display: flex;
						justify-content: space-between;
						margin-bottom: 16px;
						p {
							display: flex;
							align-items: center;
							margin: 0;
							img {
								margin-left: 14px;
							}
						}
					}
					button.btn {
						padding: 12px 0;
						width: 100%;
						border: 1px solid #e5e8eb;
						border-radius: 8px;
						margin-bottom: 16px;
						display: flex;
						align-items: center;
						background: #00b533;
						span {
							font-size: 14px;
							color: #fff;
							line-height: 16px;
						}
					}
				}
			}
		}
	}
	.characteristic-modal {
		.modal-content {
			height: 70%;
			overflow-y: scroll;
			.modal-body {
				.modal-top {
					p {
						margin-bottom: 0;
					}
				}
				.modal-section {
					padding-top: 0;
					span {
						display: block;
						color: #002930;
						padding: 8px;
						border-radius: 4px;
						background: #f4f6f8;
						margin-bottom: 8px;
					}
				}
			}
		}
	}
}

.ReactModal__Body--open {
	.Toastify {
		z-index: 10001;
	}
}
