.card-four {
	.product-header {
		position: relative;
		height: 376px;
		a {
			display: inline-block;
			height: 100%;
			width: 100%;
			border-radius: 16px;
			overflow: hidden;
			img {
				object-fit: contain;
			}
		}
		.btn-cart,
		.choosen-button,
		.discount,
		.quickView-button {
			position: absolute;
			display: flex;
			justify-content: center;
			align-items: center;
			height: 50px;
			min-width: 50px;
		}
		.btn-cart,
		.choosen-button,
		.quickView-button {
			opacity: 0;
			visibility: hidden;
			transform: translateX(-15px);
			transition: 0.3s ease-in-out;
		}
		.btn-cart {
			bottom: 5px;
			right: 5px;
			background-color: rgb(0 0 0 / 50%) !important;
			border-radius: 15px;
			.verticalDiv,
			.horizontalDiv {
				height: 2.5px;
				position: absolute;
				width: 25px;
				background: #fff;
				border-radius: 30px;
			}

			.verticalDiv {
				transform: rotate(90deg);
			}

			&:hover {
				background-color: #000 !important;
			}
			&::before {
				display: none;
			}
			.spinner-border {
				border-width: 2px;
				color: $primary-color;
			}
		}
		.discount {
			font-size: 15px;
			font-weight: 600;
			text-transform: capitalize;
			line-height: 44px;
			border-radius: 50%;
			padding: 0 5px;
			background-color: #ee0e3f;
			color: #fff;
			top: 5px;
			left: 10px;
		}
		.choosen-button {
			top: 5px;
			right: 5px;
		}
		.quickView-button {
			padding: 12px 20px;
			border-radius: 50px;
			background: rgba(0, 0, 0, 0.5);
			color: #ffffff;
			transform: translateX(-50%);
			left: 50%;
			top: 40%;
			white-space: nowrap;
			box-shadow: 0 0 5px #293644;
			&:hover {
				background: rgba(0, 0, 0, 1);
				color: #ffffff;
			}
		}
		&:hover {
			.btn-cart,
			.choosen-button,
			.quickView-button {
				transform: translateX(0);
				visibility: visible;
				opacity: 1;
			}
			.quickView-button {
				transform: translateX(-50%);
			}
		}
	}
	.body-product {
		padding: 11px 0 0 7px;
		height: 70px;
		.price-product,
		.title-product a {
			text-align: left;
			display: -webkit-box;
			-webkit-line-clamp: 1;
			-webkit-box-orient: vertical;
			overflow: hidden;
			--overfdropdownlow: hidden;
			text-overflow: ellipsis;
		}
		.price-product.old {
			.price-new {
				color: #ee0e3f;
			}
		}
		.price-product {
			margin: 0 0 0 0;
			font-style: normal;
			font-weight: 600;
			font-size: 16px;
			line-height: 18px;
			color: #002930;

			.price-old {
				font-size: 14px;
				margin-right: 10px;
				color: #002930;
			}

			.price-new.price-new-two {
				color: $primary-color !important;
				margin: 0;
			}
		}

		.title-product a {
			max-width: 150px;
			font-style: normal;
			font-weight: 300;
			font-size: 14px;
			line-height: 24px;
			color: #919eab;
		}
	}
}
@media (max-width: 991px) {
	.card-four {
		margin-right: 0 !important;
		.product-header {
			height: 208px !important;

			.choosen-button,
			.btn-cart {
				width: 32px;
				height: 32px;
				min-width: 32px;
				line-height: unset;
				opacity: 1;
				visibility: visible;
				transform: translateX(0);
			}
			.discount {
				width: 34px;
				height: 34px;
				min-width: 34px;
				font-size: 11px;
			}
			.btn-cart {
				width: 35px;
				height: 35px;
				min-width: 35px;
				font-size: 1.8rem;
				.horizontalDiv,
				.verticalDiv {
					width: 22px;
					height: 2px;
				}
			}
			.choosen-button {
				width: 3rem;
				height: 3rem;
				padding: 0;
			}
			.btn-cart {
				border-radius: 10px;
			}
			.quickView-button {
				display: none;
			}
		}
		.body-product {
			padding: 7px 0 0 4px;
			.price-product {
				font-size: 14.3158px;
				display: flex;
				flex-direction: column;
				.price-old {
					margin: 0 5px 0 0;
					font-size: 12px;
				}
			}
			.title-product a {
				font-size: 12.5263px;
			}
		}
	}
}
