.compare-product-list {
	margin-bottom: 320px;
	.compare-product-list_body {
		.tablist-clear {
			display: flex;
			align-items: center;
			justify-content: space-between;
			.clear-all {
				color: #4d78ef;
			}
		}
		.tab-list {
			display: flex;
			align-items: center;
			.tab-item {
				padding: 12px 15px;
				font-size: 15.7099px;
				line-height: 19px;
				transition: 0.3s;
				align-items: center;
				display: flex;
				background: #f1f1f1;
				color: #9d9d9d;
				border-radius: 5px;
				cursor: pointer;
				margin-right: 15px;
				&:last-child {
					margin-right: 0;
				}
				button {
					margin-left: 5px;
					img {
						width: 12.5px;
						filter: brightness(0) invert(0.6);
					}
				}
			}
			.tab-item.active {
				background: $primary-color;
				color: $light-text;
				button {
					img {
						filter: brightness(0) invert(1);
					}
				}
			}
		}
	}
	.row.p-0.m-0 {
		.container {
			padding: 0;
			margin: 0;
			max-width: 100%;
		}
		border-bottom: 1px solid #f4f4f4;
	}
	.compare-product-list_header {
		h3 {
			font-weight: bold;
		}
	}
	.compare-property-list_header {
		h3 {
			font-size: 20px;
			margin-bottom: 24px;
		}
	}
	.col-lg-3.col-md-4.col-6 {
		h4 {
			opacity: 0;
			user-select: none;
			text-transform: uppercase;
		}
		ul {
			li {
				margin-bottom: 17px;
				border-bottom: 1px solid #f4f4f4;
				h4,
				p {
					font-size: 16px;
					line-height: 19px;
					color: #c6c6c6;
					margin-bottom: 11px;
				}
				p {
					margin-bottom: 15px;
					color: $dark-text;
					span {
						color: #989898;
					}
				}
			}
		}
		&:first-child {
			padding-left: 0;
			ul {
				li {
					h4 {
						user-select: unset;
						opacity: 1;
					}
				}
			}
		}
	}
}

.sticky-top.fixed {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	z-index: 1041;
	align-items: center;
	justify-content: center;
	margin-top: 0 !important;
	animation-name: fixedHeader;
	animation-duration: 0.4s;
	background-color: #fff;
	box-shadow: 0px 3px 14px rgba(0, 0, 0, 0.1);
	h3 {
		margin-top: 20px;
	}
	.container {
		padding: 18px 10px !important;
	}
	.compare-card-one {
		margin-bottom: 0;
		.product-header {
			width: 60px;
			height: 60px;
			.header-buttons,
			.discount {
				display: none;
			}
		}
		.body-product {
			.title-product {
				margin-bottom: 14px;
			}
		}
	}
}

@media (max-width: 991px) {
	.compare-product-list {
		.compare-product-list_body {
			.tablist-clear {
				.tab-list {
					width: 100%;
					flex-wrap: wrap;
					.tab-item {
						padding: 6px;
						margin-bottom: 10px;
						span {
							font-size: 13px;
						}
					}
				}
			}
		}

		.compare-property-list_header {
			h3 {
				font-size: 16px;
			}
		}

		ul li {
			h4,
			p {
				font-size: 12px !important;
			}
		}
	}
}
