/* Product */
.product {
	position: relative;
	margin-bottom: 1rem;
	transition: box-shadow 0.35s ease;
	background-color: #fff;

	.owl-carousel & {
		margin-bottom: 0 !important;
	}
}

.product:hover {
	box-shadow: 0 5px 20px rgba(0, 0, 0, 0.05);
}

.product-media {
	position: relative;
	display: block;
	background-color: transparent;
	margin-bottom: 0;
	overflow: hidden;

	> a {
		display: block;
	}

	.product-label {
		z-index: 3;
	}

	&::before {
		content: "";
		display: block;
		width: 100%;
		background-image: linear-gradient(#f4f4f4 250px, transparent 0);
		background-size: cover;
		background-position: center center;
		background-color: #f4f4f4;
		@include aspect-ratio(517, 517);
	}

	// &::after {
	//   content: "";
	//   position: absolute;
	//   top: -50%;
	//   left: -50%;
	//   right: -50%;
	//   bottom: -50%;
	//   background-size: 60% 100%;
	//   background-image: linear-gradient(
	//     135deg,
	//     rgba(255, 255, 255, 0) 40%,
	//     rgba(255, 255, 255, 0.8) 50%,
	//     rgba(255, 255, 255, 0) 60%
	//   );
	//   background-position: -100% 0;
	//   background-repeat: no-repeat;
	//   animation: skeletonloading 2.5s infinite 0.2s;
	//   transform: none;
	// }

	img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		z-index: 2;
	}
}

.product-media > a {
	display: block;
}

.product-image {
	display: block;
	width: 100%;
	height: auto;
}

.product-image-hover {
	img {
		position: absolute;
		left: 0;
		top: 0;
		opacity: 0;
		width: 100%;
		height: auto;
		transition: all 0.4s ease;
	}
}

.product:hover .product-image-hover img {
	opacity: 1;
}

.product-countdown {
	position: absolute;
	left: 0.8rem;
	right: 0.8rem;
	bottom: 1.3rem;
	z-index: 6;
	transition: all 0.4s ease;
	visibility: visible;
	opacity: 1;

	.countdown-show4 .countdown-section {
		width: calc(25% - 10px);
	}

	.countdown-section {
		font-size: 1rem;
		line-height: 1;
		padding: 1.4rem 0 1.2rem;
		margin-left: 0.5rem;
		margin-right: 0.5rem;
		background-color: rgba(255, 255, 255, 0.9);
		border-radius: 0.4rem;

		&:not(:last-child):after {
			color: #fff;
			content: ":";
			display: inline-block;
			font-weight: 400;
			font-size: 20px;
			line-height: 1;
			position: absolute;
			left: 100%;
			margin-left: 3px;
			margin-top: -1px;
			top: 50%;
			transform: translateY(-50%);
			-ms-transform: translateY(-50%);
		}
	}

	.countdown-period {
		display: block;
		font-weight: 400;
		color: #333333;
		text-transform: lowercase;
		width: 100%;
		padding-left: 0.2rem;
		padding-right: 0.2rem;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	.countdown-amount {
		display: inline-block;
		color: #ef837b;
		font-weight: 500;
		font-size: 1.8rem;
		letter-spacing: -0.03em;
		margin-bottom: 0.6rem;
	}

	&.countdown-primary {
		.countdown-amount {
			color: $primary-color;
		}
	}
}

.product-countdown-container {
	position: absolute;
	left: 1.4rem;
	right: 1.4rem;
	bottom: 1.3rem;
	z-index: 6;
	padding-top: 0.9rem;
	padding-bottom: 0.9rem;
	border-radius: 0.3rem;
	background-color: rgba(255, 255, 255, 0.95);
	letter-spacing: -0.01em;
	transition: all 0.4s ease;
	visibility: visible;
	opacity: 1;

	.product-contdown-title {
		display: block;
		color: #333;
		font-weight: 400;
		font-size: 1.3rem;
		line-height: 1.2;
		margin-bottom: 0.3rem;
		text-align: center;
	}

	.product-countdown {
		position: relative;
		left: auto;
		right: auto;
		bottom: auto;
		z-index: 6;
		line-height: 1;
		opacity: 1;

		.countdown-amount {
			font-weight: 500;
			font-size: 1.4rem;
			line-height: 1;
			margin-bottom: 0;
			text-transform: uppercase;
		}
	}
}

.product:hover {
	.product-countdown,
	.product-countdown-container {
		opacity: 0;
		visibility: hidden;
	}
}

.product-body {
	position: relative;
	padding: 1.6rem 2rem;
	transition: all 0.35s ease;
	background-color: #fff;

	&.product-action-inner {
		padding-right: 4rem;
	}
}

.product-title {
	font-weight: 400;
	font-size: 1.2rem;
	line-height: 1.25;
	letter-spacing: -0.01em;
	color: #333333;
	margin-bottom: 0.2rem;
}

.product.product-list {
	.product-title {
		max-width: 80%;
	}

	.order-last {
		display: flex;
		align-items: center;
	}

	.product-content p {
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
		overflow: hidden;
	}
}

.product-title a {
	color: inherit;

	&:hover,
	&:focus {
		color: $primary-color;
	}
}

.product-cat {
	color: #777;
	font-weight: 300;
	font-size: 1.3rem;
	line-height: 1.2;
	letter-spacing: -0.01em;
	margin-bottom: 0.3rem;
}

.product-cat a {
	color: inherit;

	&:hover,
	&:focus {
		color: #666;
	}
}

.product-price {
	display: flex;
	align-items: center;
	flex-flow: wrap;
	font-weight: 400;
	font-size: 1.6rem;
	line-height: 1.25;
	color: $primary-color;
	margin-bottom: 1.3rem;
}

.new-price {
	color: $primary-color;
	margin-right: 0.8rem;
}

.old-price {
	color: #cccccc;
	text-decoration: line-through;
}

.out-price {
	color: #cccccc;
}

.out-text {
	display: block;
	width: 100%;
	font-size: 1.3rem;
	margin-top: 0.2rem;
}

.product-label {
	position: absolute;
	z-index: 1;
	top: 2rem;
	left: 2rem;
	font-weight: 400;
	font-size: 1.3rem;
	line-height: 1.6rem;
	letter-spacing: -0.01em;
	padding: 0.5rem 0.9rem;
	min-width: 45px;
	text-align: center;
	color: #333333;
	background-color: #fff;

	& + .product-label {
		top: 5rem;
	}

	&.label-sale {
		color: #fff;
		background-color: #ef837b;
	}

	&.label-new {
		color: #fff;
		background-color: #a6c76c;
	}

	&.label-top {
		color: #fff;
		background-color: #7dd2ea;
	}

	&.label-out {
		color: #fff;
		background-color: #ccc;
	}

	&.label-primary {
		color: #fff;
		background-color: $primary-color;
	}

	&.label-secondary {
		color: #fff;
		background-color: $secondary-color;
	}
}

.product-label-text {
	color: $primary-color;
	font-weight: 400;
	font-size: 1.3rem;
	line-height: 1;
	letter-spacing: -0.01em;
	margin-top: -0.1rem;
}

.label-circle {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 4.6rem;
	height: 4.6rem;
	min-width: 0;
	border-radius: 50%;
	line-height: 1;

	& + .label-circle {
		top: 5.6rem;
	}

	&.label-out {
		font-size: 1.1rem;
	}
}

.ratings-container {
	display: flex;
	align-items: center;
	font-size: 1.3rem;
	margin-bottom: 1.7rem;
	line-height: 1;
	white-space: nowrap;
}

.ratings {
	position: relative;
	color: #cccccc;
}

.ratings,
.ratings-val {
	display: inline-block;
	font-size: 1.4rem;
	letter-spacing: 0.1em;
	line-height: 1;
	font-family: "molla";

	&:before {
		content: "\f318""\f318""\f318""\f318""\f318";
	}
}

.ratings-val {
	position: absolute;
	top: 0;
	left: 0;
	white-space: nowrap;
	overflow: hidden;
	color: #fcb941;

	.ratings-primary & {
		color: $primary-color;
	}
}

.ratings-text {
	color: #cccccc;
	letter-spacing: -0.01em;
	margin-left: 0.8rem;
}

.ratings-text a {
	color: inherit;

	&:hover,
	&:focus {
		color: $primary-color;
	}
}

.product-action {
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	display: flex;
	align-items: center;
	background-color: rgba(255, 255, 255, 0.95);
	z-index: 10;
	transition: all 0.35s ease;
	opacity: 0;
	visibility: hidden;
	transform: translateY(100%);
}

.product:hover,
.product:focus {
	.product-action {
		visibility: visible;
		opacity: 1;
		transform: translateY(0);
	}
}

.action-icon-top {
	padding-top: 0.8rem;
	padding-bottom: 0.8rem;

	.btn-product {
		padding-top: 1rem;
		padding-bottom: 1rem;
		flex-direction: column;

		span {
			margin-top: 0.9rem;
		}
	}

	&:before {
		margin-right: 0;
	}
}

.btn-product {
	color: $primary-color;
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 1.8rem;
	line-height: 1;
	transition: all 0.35s ease;
	padding-top: 1.9rem;
	padding-bottom: 1.9rem;
	flex-grow: 1;
	flex-basis: auto;

	& + .btn-product {
		border-left: 0.1rem dotted #e5e5e5;
	}

	&:before {
		font-family: "molla";
		margin-right: 0.9rem;
	}

	span {
		color: #666666;
		font-weight: 400;
		font-size: 1.3rem;
		letter-spacing: -0.01em;
		transition: all 0.35s ease;
	}

	&:hover,
	&:focus {
		outline: none !important;
		span {
			color: $primary-color;
			box-shadow: 0 1px 0 0 $primary-color;
		}
	}
}

.product-action-dark {
	background-color: #333333;

	.btn-product:not(:hover):not(:focus) {
		span {
			color: #fff;
		}
	}

	.btn-product + .btn-product {
		border-left-color: #333333;
	}
}

.product.product-2 .product-action,
.product.product-2 .product-action-dark {
	&:before {
		position: absolute;
		content: "";
		top: 1rem;
		bottom: 1rem;
		right: 50%;
		width: 0.1rem;
		background: #d1d1d3;
		z-index: 1;
	}
}

.product-action-vertical {
	display: flex;
	flex-direction: column;
	position: absolute;
	right: 2rem;
	top: 2rem;
	background-color: transparent;
	z-index: 10;
	visibility: hidden;
	opacity: 0;
	transition: all 0.35s ease;
	transform: translateX(-15px);
}

.product:hover .product-action-vertical {
	visibility: visible;
	opacity: 1;
	transform: translateX(0);
}

.btn-product-icon {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 3rem;
	height: 3rem;
	padding: 0;
	font-size: 1.4rem;
	line-height: 1;
	transition: all 0.35s ease;
	padding: 0;
	border-radius: 50%;
	color: $primary-color;
	background-color: #fff;

	&:before {
		font-family: "molla";
	}

	span {
		display: none;
	}

	&:hover,
	&:focus {
		color: #fff;
		background-color: $primary-color;
	}
}

.btn-product-icon + .btn-product-icon {
	margin-top: 1rem;
}

.btn-cart {
	&:before {
		content: "\e812";
	}
}

.btn-wishlist {
	font-size: 1.6rem;
	&:before {
		content: "\f234";
	}
	&.added-to-wishlist {
		&::before {
			content: "\f233";
		}
	}
}

.product-body .btn-wishlist {
	position: absolute;
	top: 3.4rem;
	right: 2rem;
	color: #ccc;
	padding: 0;
	margin: 0;
	background: none;

	&:before {
		margin-right: 0;
	}

	span {
		display: none;
	}

	&:hover,
	&:focus {
		color: $primary-color;
		background: none;
	}
}

.btn-quickview {
	&:before {
		content: "\f145";
	}
}

.btn-compare {
	&:before {
		content: "\f2d0";
	}
}

.btn-expandable {
	position: relative;

	span {
		position: absolute;
		right: 100%;
		top: 0;
		bottom: 0;
		align-items: center;
		display: block;
		padding-top: 1rem;
		font-weight: 300;
		font-size: 1.1rem;
		white-space: nowrap;
		letter-spacing: 0;
		min-width: 100px;
		padding-left: 1.8rem;
		padding-right: 0.4rem;
		color: #fff;
		background-color: $primary-color;
		transition: all 0.35s ease;
		opacity: 0;
		visibility: hidden;
		border-top-left-radius: 30px;
		border-bottom-left-radius: 30px;
		transform: translateX(15px);
		z-index: -1;
	}

	&:hover,
	&:focus {
		border-top-left-radius: 0;
		border-bottom-left-radius: 0;

		span {
			visibility: visible;
			opacity: 1;
			transform: translateX(0);
		}
	}
}

.product-size {
	display: flex;
	align-items: center;
	padding-top: 0.2rem;
	margin-bottom: 1.6rem;

	a {
		display: flex;
		align-items: center;
		justify-content: center;
		text-align: center;
		width: 2.1rem;
		height: 2.1rem;
		font-weight: 400;
		font-size: 1.2rem;
		padding-top: 0.1rem;
		line-height: 1;
		color: #666666;
		background-color: #f7f8fa;
		border-radius: 0.2rem;
		transition: all 0.35s ease;
		margin-right: 0.5rem;

		&:last-child {
			margin-right: 0;
		}

		&:hover,
		&:focus,
		&.active {
			color: #fff;
			background-color: #333;
		}

		&.disabled {
			color: #cccccc;
			pointer-events: none;
		}
	}
}

.product-nav {
	display: flex;
	align-items: center;
}
.product-nav .product-nav-dots {
	box-sizing: border-box;
	width: auto;
	height: auto;
	padding: 5px 10px;
	border: 0.5px solid #c4c4c4;
	cursor: pointer;
	margin-left: 1px;
}
.product-nav .product-nav-dots.active {
	border: 1.5px solid $primary-color !important;
}
.product-nav .properties-block,
.product-nav .product-nav-dots .properties-block {
	margin-right: 7px;
}

.product-nav .product-nav-dots,
.properties-block {
	box-sizing: border-box;
	width: auto;
	height: auto;
	padding: 5px 10px;
	border: 3px solid #c4c4c4;
	cursor: pointer;
	transition: 0.2s !important;
	margin-left: 7px;
}
.product-nav {
	width: 100%;
	flex-wrap: wrap;
	padding-bottom: 5px;
	&::-webkit-scrollbar {
		height: 10px;
	}
	&::-webkit-scrollbar-thumb {
		background-color: #ccc;
	}
}
.product-nav .properties-block {
	box-sizing: border-box;
	width: max-content !important;
	height: max-content !important;
	padding: 5px;
	border: 3px solid #c4c4c4;
	cursor: pointer;
	white-space: nowrap;
	margin: 0 !important;
	margin-right: 5px !important;
	&.color-properties {
		padding: 0 !important;
		img {
			width: 60px !important;
			height: 75px !important;
		}
		span {
			padding: 5px !important;
		}
	}
	.tooltip-property {
		display: none;
		min-width: 30px;
		width: auto !important;
		position: absolute;
		bottom: -25px;
		left: 80%;
		padding: 0 5px;
		border-radius: 5px;
		color: #fff;
		background-color: #333;
		z-index: 999;
		span {
			&::before {
				position: absolute;
				content: "";
				left: 0;
				top: -5px;
				border: 10px solid #333;
				border-right-color: transparent;
				border-top-color: transparent;
				border-bottom-color: transparent;
			}
		}
	}
	&:hover {
		.tooltip-property {
			display: block !important;
		}
	}
}

.product-nav .properties-block.active {
	border: 3px solid $primary-color;
	color: $primary-color;
}

.product-nav .properties-block.size-properties {
	min-width: 40px;
	min-height: 40px;
	text-align: center;
}
.product-nav .not-allowed {
	color: rgb(180, 180, 180);
	border: 3px solid rgb(229, 229, 229);
	opacity: 0.8;
	transform: scale(0.9);
	position: relative;
	overflow: hidden;
	&::before {
		content: "";
		position: absolute;
		left: -50%;
		right: -50%;
		top: 50%;
		border: 1px solid black;
		transform: translateY(-50%);
		transform: rotate(-45deg);
	}
}

.product-nav .not-allowed.active-not-allowed {
	border-color: $primary-color !important;
}

.product-nav-dots {
	margin-bottom: 0.5rem;

	display: block;
	width: 2rem;
	height: 2rem;
	border-radius: 50%;
	border: 0.2rem solid #fff;
	margin-right: 0.5rem;
	transition: box-shadow 0.35s ease;
	background-repeat: no-repeat;
	background-position: center center;

	&.active,
	&:hover,
	&:focus {
		box-shadow: 0 0 0 0.1rem #cccccc;
	}

	&:last-child {
		margin-right: 0;
	}
}

.product-nav-word {
	margin-bottom: 0.5rem;

	a,
	button,
	span {
		display: block;
		border: 0.2rem solid #fff;
		margin-right: 0.5rem;
		transition: box-shadow 0.35s ease;
		background-repeat: no-repeat;
		background-position: center center;

		&.active,
		&:hover,
		&:focus {
			box-shadow: 0 0 0 0.1rem #cccccc;
		}

		&:last-child {
			margin-right: 0;
		}
	}
}

.product-nav-thumbs {
	padding-top: 0.2rem;
	margin-bottom: 1.6rem;

	a,
	span {
		display: flex;
		align-items: center;
		width: 4.4rem;
		height: 4.4rem;
		border-radius: 0.2rem;
		border: 0.2rem solid #fff;
		margin-right: 0.4rem;
		transition: box-shadow 0.35s ease;

		&.active,
		&:hover,
		&:focus {
			box-shadow: 0 0 0 0.1rem #cccccc;
		}

		&:last-child {
			margin-right: 0;
		}
	}

	img {
		width: 100%;
		border-radius: 0.2rem;
	}
}

.product-disabled {
	pointer-events: none;

	.btn-product,
	.btn-product-icon {
		pointer-events: none;
		color: #fff;
		background-color: #ccc !important;
	}
}

.product.text-center {
	// .product-price,
	.ratings-container,
	.product-nav,
	.product-size {
		justify-content: center;
	}
}

/* Product 2 */
.product.product-2 {
	.product-body {
		padding-bottom: 0.4rem;
	}
	.product-action {
		padding-top: 1rem;
		padding-bottom: 1rem;
	}

	.btn-product {
		padding-top: 0.1rem;
		padding-bottom: 0.1rem;
	}

	.btn-product + .btn-product {
		border-left-style: solid;
	}

	.ratings-container:last-child {
		margin-bottom: 1.6rem;
	}

	.product-action-vertical {
		top: 3rem;
	}

	.product-nav-dots {
		margin-bottom: 2rem;
	}
}

.product-footer {
	display: none;
	padding-top: 1.3rem;
}

/* Product 3 */
.product.product-3 {
	position: relative;
	overflow: hidden;
	background-color: #fff;

	.product-action {
		position: absolute;
		bottom: 100%;
		border-bottom: 0.1rem solid #ebebeb;
		background-color: #fff;
		transform: translateY(0);
		transition-duration: 0.25s;
	}

	.btn-product {
		padding-top: 1.05rem;
		padding-bottom: 1.05rem;

		&:not(:hover):not(:focus) {
			span {
				color: #333;
			}
		}
	}

	.product-price,
	.product-nav {
		margin-bottom: 0;
	}

	.product-body {
		background-color: #fff;
		z-index: 2;
	}

	.product-footer {
		display: block;
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		background-color: #fff;
		transform: translateY(100%);
		visibility: hidden;
		transition: all 0.35s ease;
		padding: 0 2rem 1rem;
	}

	&:hover {
		box-shadow: none;
	}
}

