			right: 0;
			bottom: 1rem;
			height: 0.1rem;
			background-color: #ebebeb;
		}
	}
	.product-detail {
		position: absolute;
		visibility: hidden;
		padding-top: 1rem;
		opacity: 0;
		left: 50%;
		transform: translateX(-50%);
		transition: all 0.3s;
	}
