		max-width: 80px;
	}

	.product-body {
		flex: 1 1 auto;
		padding: 2rem 0 4rem 2rem;
	}

	.product-cat {
		color: #ccc;
		font-weight: 400;
		font-size: 1.3rem;
		text-transform: uppercase;
		margin-bottom: 0.2rem;
	}

	.product-title {
		font-size: 1.4rem;
	}

	.product-price {
		font-size: 1.6rem;
	}

	.product-title {
		margin-bottom: 0.2rem;
	}

	.product-price {
		margin-bottom: 0;

		.old-price {
			text-decoration: none;
		}
	}
}

