.supplier-page-four {
	.supplier-header {
		display: none;
	}
	.page-content {
		.info-supplier {
			.slider-supplier {
				margin: 20px 0;
				height: 400px;
				.splide,
				.splide__track,
				.img-block {
					height: 100%;
					.splide__arrows {
						display: none;
					}

					.splide__pagination {
						.splide__pagination__page {
							background: rgba(0, 0, 0, 0.15);
							border-radius: $border-radius-image;
							height: 12px;
							width: 42px;
							transition: 0.3s;
						}
						.splide__pagination__page.is-active {
							background: rgba(0, 0, 0, 0.3);
							transform: unset;
							width: 90px;
						}
					}

					img {
						object-fit: contain;
					}
				}
			}
		}
		.logo-content {
			.contanet-info {
				.col-12 {
					display: flex;
				}
				.logo {
					margin-right: 20px;
					background: $body-bg;
					border: 1px solid $light-text;
					border-radius: $border-item;
					overflow: hidden;
					width: 100px;

					img {
						object-fit: contain;
					}
				}
				.supplier-name {
					font-size: 22px;
					line-height: 20px;
					letter-spacing: 0.2px;
					color: $secondary-color-dark;
					font-weight: 400;
					margin-bottom: 8px;
				}
				.title-description {
					p {
						margin-bottom: 0;
						font-size: 14px;
						line-height: 16px;
						color: $secondary-color;
						text-align: left;
						display: -webkit-box;
						-webkit-line-clamp: 3;
						-webkit-box-orient: vertical;
						overflow: hidden;
						text-overflow: ellipsis;
					}
				}
			}
		}
		.skeleton-body.skel-shop-products {
			.category-toolbox,
			.category-title {
				display: none;
			}
		}
		.skeleton-body.skel-shop-sidebar {
			.sidebar-filter {
				.filter-price {
					display: none;
				}
			}
		}
		.filter-toolbox-block {
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin: 12px 0 24px 0 !important;
			border: none;
			.sort-b {
				.toolbox {
					.toolbox-center {
						display: none;
					}
				}
			}
			.sort-b,
			.filter-b {
				width: max-content;
				border: none;
				padding: 0;
			}

			.select-custom::after {
				display: none;
			}
			.toolbox-sort,
			.filter-toolbox-block {
				img {
					filter: brightness(0) invert(0);
				}
				select {
					color: $secondary-color-dark;
				}
			}
		}
		.products {
			.row {
				width: 100%;
				margin: 0 auto;
			}
		}
	}
}

@media (max-width: 991px) {
	.supplier-header,
	.category-header {
		display: flex !important;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		padding: 16px 10px;
		box-shadow: inset 0px -0.5px 0px #ececf1;
		h2 {
			margin-left: 4px !important;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			margin: 0;
			color: $secondary-color-dark;
			font-size: 16px;
			line-height: 18px;
			letter-spacing: 0.2px;
			font-weight: bold;
		}

		.header-left {
			flex-basis: unset;
			max-width: unset;
			img {
				width: 24px;
			}
		}

		.back-block {
			.exit-product-page {
				i {
					font-size: 20px;
					margin-right: 10px;
				}
			}
		}
	}
	.supplier-page-four {
		.page-content {
			.info-supplier {
				padding-top: 20px;
				.slider-supplier {
					margin: 16px 0;
					height: 130px;
					.splide,
					.splide__track,
					.img-block {
						.splide__pagination {
							.splide__pagination__page {
								border-radius: $border-item;
								height: 4px;
								width: 12px;
							}
							.splide__pagination__page.is-active {
								width: 24px;
							}
						}
					}
				}
			}
			.logo-content {
				.contanet-info {
					.supplier-name {
						font-size: 22px;
					}
					.title-description {
						p {
							font-size: 12px;
							display: block;
							-webkit-line-clamp: unset;
							-webkit-box-orient: unset;
							overflow: unset;
							text-overflow: unset;
						}
					}
				}
			}

			.products {
				.row {
					width: 100%;
					margin: 0 auto;
				}
			}
		}
	}
}
