.collections-three {
	margin-top: 20px;
	.title {
		display: block;
		text-align: left;
		font-weight: bold;
		h2,
		h3 {
			font-weight: bold;
		}
	}
	.only-desktop {
		display: flex;
		flex-wrap: wrap;
		li {
			width: 20%;
			padding: 10px;
			& > div {
				margin: 0 !important;
			}
		}
	}
	.only-mobile {
		display: none;
	}
}

@media (max-width: 991px) {
	.collections-three {
		.only-desktop {
			display: none !important;
		}
		.only-mobile {
			display: block !important;
			.double-product {
				padding-right: 10px;
				&:last-child {
					padding-right: 0;
				}
				& > div {
					.product-header {
						height: 250px !important;
						a {
							border-radius: 10px !important;
						}
					}
				}
			}
		}
	}
}
