.footer-five {
	.footer-middle {
		padding: 67px 0 10px 0;
		.widget.widget-about {
			.social-links {
				display: flex;
				width: 100%;
				align-items: center;
				a {
					margin-top: 5px;
					margin-right: 10px;
				}
			}
			.question-and-payments {
				.widget-call {
					i {
						display: none;
					}
					padding: 0;
					border: none;
				}
			}
			.footer-logo {
				margin: 0 0 20px 0;
			}
			.footer-text {
				margin: 0 0 40px 0;
				max-width: unset;
				color: #777777;
			}
			.question-and-payments {
				.widget-call,
				.footer-payments {
					p {
						margin-bottom: 7px !important;
						font-size: 1.3rem;
						font-weight: 300;
						letter-spacing: 0;
						line-height: 1.3;
					}
				}
				.footer-payments {
					margin-left: 42px;
				}
				.widget-call {
					a {
						line-height: 1.86;
					}
				}
			}
		}
		.widget {
			p {
				color: #777;
			}
			.widget-title {
				letter-spacing: 0.5px;
				font-weight: bold;
				font-size: 16px !important;
			}
			.widget-list {
				a {
					color: #777777;
					&:hover {
						color: $primary-color;
					}
				}
			}
			.cta-section {
				.container {
					padding: 0;
					margin: 0;
					.cta-wrapper {
						margin-top: 20px;
						display: flex;
						.cta-title,
						.cta-desc {
							display: none;
						}
						.mr-5 {
							margin-right: 0 !important;
						}
						input {
							border-color: #dadada;
							&::placeholder {
								color: #dadada !important;
							}
							height: 100%;
							border-radius: 0 !important;
						}
						.btn {
							border-radius: 0;
							background: #333;
							color: #fff;
							padding-left: 2.2rem;
							padding-right: 2.2rem;
							min-width: 46px;
							height: 46px;
							&:hover {
								background: $primary-color;
							}
							&::after {
								content: "\f274";
								font-family: "molla";
								position: relative;
							}
							span {
								display: none;
							}
						}
					}
				}
			}
		}
	}
	.footer-bottom {
		.container {
			padding: 20px 0;
			display: flex;
			justify-content: space-between;
			.footer-social-links {
				p {
					font-family: inherit;
					font-size: 1.4rem;
					line-height: 1.86;
					color: #777;
					text-transform: none;
					margin: 0 20px 0 0 !important;
				}
				a {
					width: 12px;
				}
			}
		}
	}
}
@media (max-width: 991px) {
	.footer-five {
		display: block !important;
	}
}
@media (max-width: 767px) {
	.footer-five {
		font-size: 12.6px !important;
		.footer-middle {
			.widget.widget-about {
				.footer-logo {
					width: 106px;
				}
			}
			.question-and-payments {
				flex-direction: column;
				.widget-call {
					display: block;
					margin-bottom: 22px;
					a {
						font-size: 18px;
					}
				}
				.footer-payments {
					margin: 0 !important
                    ;
				}
			}
			.widget {
				.widget-title {
					margin-bottom: 18px;
				}
				.cta-section {
					.container {
						width: 100% !important;
						padding: 0 !important;
						.cta-wrapper {
							padding: 0;
							min-width: 100%;
							.mr-5 {
								width: -webkit-fill-available;
							}
						}
					}
				}
				margin-bottom: 27px;
			}
		}
		.footer-bottom {
			.container {
				padding: 20px 10px !important;
				flex-direction: column-reverse;
			}
		}
	}
}

@media (max-width: 560px) {
	.footer-five {
		display: none;
	}
}
