.markets-one {
	.markets-header {
		label {
			margin-bottom: 0 !important;
		}
		.form-control {
			border-radius: 8px;
		}
	}
	.market-children {
		overflow: auto;
		margin-bottom: 20px;
		padding-bottom: 20px;
		.children-item {
			white-space: nowrap;
			padding: 5px 16px;
			margin-right: 10px;
			background-color: #ebf0f7;
			border-radius: 5px;
			cursor: pointer;
			color: black;
			font-size: 16px;
			&.active {
				color: #fff;
				background-color: $primary-color;
			}
		}
	}
	.sidebar,
	.sidebar-filter {
		.filter-price {
			padding: 0 !important;
			button.btn {
				min-width: max-content !important;
				background-color: #f4f6f8;
				padding-left: 10px !important;
				padding-right: 10px !important;
				border: 1px solid #eee !important;
			}
		}
		.breadcrumb-nav-two {
			display: none;
		}
	}
	.toolbox {
		position: relative !important;
		display: flex;
		flex-direction: row !important;
	}
}

@media (max-width: 991px) {
	.markets-one.category-page-5 {
		.container {
			.markets-header > div {
				label {
					margin-left: 10px;
					p {
						display: none;
					}
				}
			}
		}
	}
}
